<style scoped>
  .calendar {
    margin: auto;
    width: 100%;
    min-width: 300px;
    background: rgb(26, 62, 90);
    font-family: "PingFang SC", "Hiragino Sans GB", "STHeiti", "Microsoft YaHei",
    "WenQuanYi Micro Hei", sans-serif;
    user-select: none;
    /* border: 0.0625vw solid rgb(40,179,252) */
  }

  .calendar-tools {
    position: relative;
    height: 40px;
    font-size: 20px;
    line-height: 40px;
    color: #5e7a88;
    border-bottom: solid 1px rgb(40, 179, 252);
  }

  .calendar-tools span {
    cursor: pointer;
  }

  .calendar-prev-year {
    width: 12%;
    float: left;
    text-align: center;
  }

  .calendar-prev-month {
    width: 12%;
    float: left;
    text-align: center;
  }

  .calendar-info {
    padding-top: 3px;
    font-size: 16px;
    line-height: 1.3;
    text-align: center;
  }

  .calendar-info > div.month {
    /* margin:0 auto; */
    height: 20px;
    text-align: center;
    color: #fff;
    overflow: hidden;
    position: relative;
    float: left;
  }

  .calendar-info > div.month .hiddenSpan {
    visibility: hidden;
  }

  .calendar-info > div.month .month-inner {
    position: absolute;
    left: 0;
    top: 0;
    height: 240px;
    transition: top 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
  }

  .calendar-info > div.month .month-inner > span {
    display: block;
    font-size: 14px;
    height: 20px;
    width: auto;
    overflow: hidden;
    text-align: center;
  }

  .calendar-info > div.year {
    font-size: 14px;
    /* line-height: 1; */
    color: #fff;
    float: left;
  }

  .calendar-next-month {
    width: 12%;
    float: right;
    text-align: center;
  }

  .calendar-next-year {
    width: 12%;
    float: right;
    text-align: center;
  }

  .calendar table {
    clear: both;
    width: 100%;
    margin-bottom: 10px;
    border-collapse: collapse;
    color: #fff;
  }

  .calendar td {
    margin: 2px !important;
    padding: 0px 0;
    width: 14.28571429%;
    height: 44px;
    text-align: center;
    vertical-align: middle;
    font-size: 14px;
    line-height: 125%;
    cursor: pointer;
    position: relative;
    vertical-align: top;
  }

  .calendar td.week {
    font-size: 10px;
    pointer-events: none !important;
    cursor: default !important;
  }

  .calendar td.disabled {
    color: rgb(121, 129, 148);
    pointer-events: none !important;
    cursor: default !important;
  }

  .calendar td.disabled div {
    color: #ccc;
  }

  .calendar td span {
    display: block;
    max-width: 40px;
    height: 26px;
    font-size: 16px;
    line-height: 26px;
    margin: 0px auto;
    /* border-radius:20px; */
  }

  /* .calendar td:not(.selected) span:not(.red):hover{
      background:#f3f8fa;
      color:#444;
  }
  .calendar td:not(.selected) span.red:hover{
      background:#f9efef;
  } */
  .calendar td:not(.selected) span:hover {
    background: rgb(40, 179, 252);
  }

  /* .calendar td:not(.disabled) span.red{
      color:#ea6151;
  } */
  .calendar td.selected span {
    background-color: rgb(40, 179, 252);
    color: #fff;
  }

  .calendar td .text {
    position: absolute;
    top: 28px;
    left: 0;
    right: 0;
    text-align: center;

    padding: 2px;
    font-size: 8px;
    line-height: 1.2;
    color: #444;
  }

  .calendar td .isGregorianFestival,
  .calendar td .isLunarFestival {
    color: #ea6151;
  }

  /* .calendar td.selected span.red{
      background-color: #ea6151;
      color: #fff;
  }
  .calendar td.selected span.red:hover{
      background-color: #ea6151;
      color: #fff;
  } */
  .calendar thead td {
    text-transform: uppercase;
    height: 30px;
    vertical-align: middle;
  }

  .calendar-button {
    text-align: center;
  }

  .calendar-button span {
    cursor: pointer;
    display: inline-block;
    min-height: 1em;
    min-width: 5em;
    vertical-align: baseline;
    background: #5e7a88;
    color: #fff;
    margin: 0 0.25em 0 0;
    padding: 0.6em 2em;
    font-size: 1em;
    line-height: 1em;
    text-align: center;
    border-radius: 0.3em;
  }

  .calendar-button span.cancel {
    background: #efefef;
    color: #666;
  }

  .calendar-years {
    position: absolute;
    left: 0px;
    top: 60px;
    right: 0px;
    bottom: 0px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    overflow: auto;
    transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
    opacity: 0;
    pointer-events: none;
    transform: translateY(-10px);
  }

  .calendar-years.show {
    opacity: 1;
    pointer-events: auto;
    transform: translateY(0px);
  }

  .calendar-years > span {
    margin: 1px 5px;
    display: inline-block;
    width: 60px;
    line-height: 30px;
    border-radius: 20px;
    text-align: center;
    border: 1px solid #fbfbfb;
    color: #999;
  }

  .calendar-years > span.active {
    border: 1px solid #5e7a88;
    background-color: #5e7a88;
    color: #fff;
  }

  .calendar-footer {
    padding: 0 0.9vw;
    line-height: 40px;
    height: 40px;
    font-size: 0.9vw;
    border-top: solid 1px rgb(40, 179, 252);
  }

  .calendar-footer span {
    cursor: pointer;
  }

  .calendar-footer span.today {
    float: left;
  }

  .calendar-footer span.clear {
    float: right;
  }

  .calendar-footer span.confirm {
    margin-left: 0.9vw;
    float: right;
  }
</style>

<template>
  <div class="calendar"
    @mousedown.stop>
    <div class="calendar-tools">
      <span class="calendar-prev-year"
        @click="prevYear">
        <svg width="18"
          height="18"
          viewBox="0 0 16 16"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink">
          <g class="transform-group">
            <g transform="scale(0.015625, 0.015625)">
              <path d="M440.32 71.68l87.04 87.04-353.792 353.28 353.792 353.28-87.04 87.04-440.32-440.32z"
                p-id="9938"
                fill="#fff"></path>
              <path d="M936.96 71.68l87.04 87.04-353.28 353.28 353.28 353.28-87.04 87.04-440.32-440.32z"
                p-id="9939"
                fill="#fff"></path>
            </g>
          </g>
        </svg>
      </span>
      <span class="calendar-prev-month"
        @click="prevMonth">
        <svg width="18"
          height="18"
          viewBox="0 0 16 16"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink">
          <g class="transform-group">
            <g transform="scale(0.015625, 0.015625)">
              <path d="M671.968 912c-12.288 0-24.576-4.672-33.952-14.048L286.048 545.984c-18.752-18.72-18.752-49.12 0-67.872l351.968-352c18.752-18.752 49.12-18.752 67.872 0 18.752 18.72 18.752 49.12 0 67.872l-318.016 318.048 318.016 318.016c18.752 18.752 18.752 49.12 0 67.872C696.544 907.328 684.256 912 671.968 912z"
                fill="#fff"></path>
            </g>
          </g>
        </svg>
      </span>
      <span class="calendar-next-year"
        @click="nextYear">
        <svg width="18"
          height="18"
          viewBox="0 0 16 16"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink">
          <g class="transform-group">
            <g transform="scale(0.015625, 0.015625)">
              <path d="M583.68 952.32l-87.04-87.04 353.792-353.28L496.64 158.72l87.04-87.04 440.32 440.32z"
                p-id="3874"
                fill="#fff"></path>
              <path d="M87.04 952.32L0 865.28l353.28-353.28L0 158.72l87.04-87.04 440.32 440.32z"
                p-id="3875"
                fill="#fff"></path>
            </g>
          </g>
        </svg>
      </span>
      <span class="calendar-next-month"
        @click="nextMonth">
        <svg width="18"
          height="18"
          viewBox="0 0 16 16"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink">
          <g class="transform-group">
            <g transform="scale(0.015625, 0.015625)">
              <path d="M761.056 532.128c0.512-0.992 1.344-1.824 1.792-2.848 8.8-18.304 5.92-40.704-9.664-55.424L399.936 139.744c-19.264-18.208-49.632-17.344-67.872 1.888-18.208 19.264-17.376 49.632 1.888 67.872l316.96 299.84-315.712 304.288c-19.072 18.4-19.648 48.768-1.248 67.872 9.408 9.792 21.984 14.688 34.56 14.688 12 0 24-4.48 33.312-13.44l350.048-337.376c0.672-0.672 0.928-1.6 1.6-2.304 0.512-0.48 1.056-0.832 1.568-1.344C757.76 538.88 759.2 535.392 761.056 532.128z"
                fill="#fff"></path>
            </g>
          </g>
        </svg>
      </span>

      <div class="calendar-info allCenter2">
        <!-- {{monthString}} -->
        <div class="year"
          @click.stop="changeYear">{{year}}
        </div>
        <div class="month">
          <span class="hiddenSpan">{{months[month]}}</span>
          <div class="month-inner"
            :style="{'top':-(this.month*20)+'px'}">
            <span v-for="m in months">{{m}}</span>
          </div>
        </div>
      </div>
    </div>
    <table cellpadding="5">
      <thead>
      <tr>
        <td v-for="week in weeks"
          class="week">{{week}}
        </td>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(day,k1) in days"
        style="{'animation-delay',(k1*30)+'ms'}">
        <td v-for="(child,k2) in day"
          :class="{'selected':child.selected,'disabled':child.disabled}"
          @click="select(k1,k2,$event)">
          <span :class="{'red':k2==0||k2==6||((child.isLunarFestival||child.isGregorianFestival) && lunar)}">{{child.day}}</span>
          <div class="text"
            v-if="child.eventName!=undefined">{{child.eventName}}
          </div>
          <div class="text"
            :class="{'isLunarFestival':child.isLunarFestival,'isGregorianFestival':child.isGregorianFestival}"
            v-if="lunar">{{child.lunar}}
          </div>
        </td>
      </tr>
      </tbody>
    </table>

    <div class="calendar-years"
      :class="{'show':yearsShow}">
      <span v-for="y in years"
        @click.stop="selectYear(y)"
        :class="{'active':y==year}">{{y}}</span>
    </div>
    <div class="calendar-footer">
      <span class="today"
        @click="setToday"></span>
      <span class="confirm"
        @click="confirm"></span>
      <span class="clear"
        @click="clearDate"></span>
    </div>
  </div>
</template>

<script>
import calendar from './calendar.js';

export default {
	props: {
		// 
		multi: {
			type: Boolean,
			default: false
		},
		// 
		range: {
			type: Boolean,
			default: false
		},
		// 
		value: {
			type: Array,
			default: function () {
				return [];
			}
		},
		// 
		begin: {
			type: Array,
			default: function () {
				return [];
			}
		},
		// 
		end: {
			type: Array,
			default: function () {
				return [];
			}
		},

		// 10
		zero: {
			type: Boolean,
			default: false
		},
		// 
		disabled: {
			type: Array,
			default: function () {
				return [];
			}
		},
		// 
		lunar: {
			type: Boolean,
			default: false
		},

		// 
		weeks: {
			type: Array,
			default: function () {
				return window.navigator.language.toLowerCase() == 'zh-cn' ? ['', '', '', '', '', '', ''] : ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
			}
		},
		// 
		months: {
			type: Array,
			default: function () {
				return window.navigator.language.toLowerCase() == 'zh-cn' ? ['', '', '', '', '', '', '', '', '', '', '', ''] : ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];
			}
		},
		// 
		events: {
			type: Object,
			default: function () {
				return {};
			}
		},
		dataFormatterChar: {
			type: String,
			default: '/'
		}
	},
	data() {
		return {
			years: [],
			yearsShow: false,
			year: 0,
			month: 0,
			day: 0,
			days: [],
			multiDays: [],
			today: [],
			festival: {
				lunar: {
					'1-1': '',
					'1-15': '',
					'2-2': '',
					'5-5': '',
					'7-7': '',
					'7-15': '',
					'8-15': '',
					'9-9': '',
					'10-1': '',
					'10-15': '',
					'12-8': '',
					'12-23': '',
				},
				gregorian: {
					'1-1': '',
					'2-14': '',
					'3-8': '',
					'3-12': '',
					'4-5': '',
					'5-1': '',
					'5-4': '',
					'6-1': '',
					'7-1': '',
					'8-1': '',
					'9-10': '',
					'10-1': '',
					'12-24': '',
					'12-25': '',
				},
			},
			rangeBegin: [],
			rangeEnd: [],
		};
	},
	computed: {
		getDateFormatter() {
			return (y, m, d) => {
				if (this.zero) return y + this.dataFormatterChar + this.zeroPad(m + 1) + this.dataFormatterChar + this.zeroPad(d);
				return y + this.dataFormatterChar + (m + 1) + this.dataFormatterChar + d;
			};
		},
		getRangeFormatter() {
			return [this.getDateFormatter(...this.rangeBegin), this.getDateFormatter(...this.rangeEnd)];
		}
	},
	watch: {
		events() {
			this.render(this.year, this.month);
		},
		value() {
			this.init();
		},
	},
	mounted() {
		this.init();
	},
	methods: {
		init() {
			let now = new Date();
			this.year = now.getFullYear();
			this.month = now.getMonth();
			this.day = now.getDate();
			if (this.value.length > 0) {
				if (this.range) { //
					this.year = parseInt(this.value[0][0]);
					this.month = parseInt(this.value[0][1]) - 1;
					this.day = parseInt(this.value[0][2]);

					let year2 = parseInt(this.value[1][0]);
					let month2 = parseInt(this.value[1][1]) - 1;
					let day2 = parseInt(this.value[1][2]);

					this.rangeBegin = [this.year, this.month, this.day];
					this.rangeEnd = [year2, month2, day2];
				} else if (this.multi) {//
					this.multiDays = this.value;
					this.year = parseInt(this.value[0][0]);
					this.month = parseInt(this.value[0][1]) - 1;
					this.day = parseInt(this.value[0][2]);
				} else { //
					this.year = parseInt(this.value[0]);
					this.month = parseInt(this.value[1]) - 1;
					this.day = parseInt(this.value[2]);
				}
			}
			this.render(this.year, this.month);
		},
		// 
		render(y, m) {
			let firstDayOfMonth = new Date(y, m, 1).getDay();         //
			let lastDateOfMonth = new Date(y, m + 1, 0).getDate();    //
			let lastDayOfLastMonth = new Date(y, m, 0).getDate();     //
			this.year = y;
			let seletSplit = this.value;
			let i, line = 0, temp = [], nextMonthPushDays = 1;
			for (i = 1; i <= lastDateOfMonth; i++) {
				let day = new Date(y, m, i).getDay(); //06
				let k;
				// 
				if (day == 0) {
					temp[line] = [];
				} else if (i == 1) {
					temp[line] = [];
					k = lastDayOfLastMonth - firstDayOfMonth + 1;
					for (let j = 0; j < firstDayOfMonth; j++) {
						// console.log("",lunarYear,lunarMonth,lunarValue,lunarInfo)
						temp[line].push(Object.assign(
							{day: k, disabled: true},
							this.getLunarInfo(this.computedPrevYear(), this.computedPrevMonth(true), k),
							this.getEvents(this.computedPrevYear(), this.computedPrevMonth(true), k),
						));
						k++;
					}
				}


				if (this.range) { // 
					// console.log("",this.getLunarInfo(this.year,this.month+1,i))
					let options = Object.assign(
						{day: i},
						this.getLunarInfo(this.year, this.month + 1, i),
						this.getEvents(this.year, this.month + 1, i),
					);
					if (this.rangeBegin.length > 0) {
						let beginTime = Number(new Date(this.rangeBegin[0], this.rangeBegin[1], this.rangeBegin[2]));
						let endTime = Number(new Date(this.rangeEnd[0], this.rangeEnd[1], this.rangeEnd[2]));
						let stepTime = Number(new Date(this.year, this.month, i));
						if (beginTime <= stepTime && endTime >= stepTime) {
							options.selected = true;
						}
					}
					if (this.begin.length > 0) {
						let beginTime = Number(new Date(parseInt(this.begin[0]), parseInt(this.begin[1]) - 1, parseInt(this.begin[2])));
						if (beginTime > Number(new Date(this.year, this.month, i))) options.disabled = true;
					}
					if (this.end.length > 0) {
						let endTime = Number(new Date(parseInt(this.end[0]), parseInt(this.end[1]) - 1, parseInt(this.end[2])));
						if (endTime < Number(new Date(this.year, this.month, i))) options.disabled = true;
					}
					if (this.disabled.length > 0) {
						if (this.disabled.filter(v => {
							return this.year === v[0] && this.month === v[1] - 1 && i === v[2];
						}).length > 0) {
							options.disabled = true;
						}
					}
					temp[line].push(options);
				} else if (this.multi) {//
					let options;
					// 
					if (this.value.filter(v => {
						return this.year === v[0] && this.month === v[1] - 1 && i === v[2];
					}).length > 0) {
						options = Object.assign({
							day: i,
							selected: true
						}, this.getLunarInfo(this.year, this.month + 1, i), this.getEvents(this.year, this.month + 1, i));
					} else {
						options = Object.assign({
							day: i,
							selected: false
						}, this.getLunarInfo(this.year, this.month + 1, i), this.getEvents(this.year, this.month + 1, i));
						if (this.begin.length > 0) {
							let beginTime = Number(new Date(parseInt(this.begin[0]), parseInt(this.begin[1]) - 1, parseInt(this.begin[2])));
							if (beginTime > Number(new Date(this.year, this.month, i))) options.disabled = true;
						}
						if (this.end.length > 0) {
							let endTime = Number(new Date(parseInt(this.end[0]), parseInt(this.end[1]) - 1, parseInt(this.end[2])));
							if (endTime < Number(new Date(this.year, this.month, i))) options.disabled = true;
						}
						if (this.disabled.length > 0) {
							if (this.disabled.filter(v => {
								return this.year === v[0] && this.month === v[1] - 1 && i === v[2];
							}).length > 0) {
								options.disabled = true;
							}
						}
					}

					temp[line].push(options);
				} else { // 
					// console.log(this.lunar(this.year,this.month,i));

					let chk = new Date();
					let chkY = chk.getFullYear();
					let chkM = chk.getMonth();
					// 
					if (parseInt(seletSplit[0]) == this.year && parseInt(seletSplit[1]) - 1 == this.month && parseInt(seletSplit[2]) == i) {
						// console.log("",lunarYear,lunarMonth,lunarValue,lunarInfo)
						temp[line].push(Object.assign(
							{day: i, selected: true},
							this.getLunarInfo(this.year, this.month + 1, i),
							this.getEvents(this.year, this.month + 1, i),
						));
						this.today = [line, temp[line].length - 1];
					}
					// 
					else if (chkY == this.year && chkM == this.month && i == this.day && this.value == '') {

						// console.log("",lunarYear,lunarMonth,lunarValue,lunarInfo)
						temp[line].push(Object.assign(
							{day: i, selected: true},
							this.getLunarInfo(this.year, this.month + 1, i),
							this.getEvents(this.year, this.month + 1, i),
						));
						this.today = [line, temp[line].length - 1];
					} else {
						// 
						// console.log("",i,lunarYear,lunarMonth,lunarValue,lunarInfo)
						let options = Object.assign(
							{day: i, selected: false},
							this.getLunarInfo(this.year, this.month + 1, i),
							this.getEvents(this.year, this.month + 1, i),
						);
						if (this.begin.length > 0) {
							let beginTime = Number(new Date(parseInt(this.begin[0]), parseInt(this.begin[1]) - 1, parseInt(this.begin[2])));
							if (beginTime > Number(new Date(this.year, this.month, i))) options.disabled = true;
						}
						if (this.end.length > 0) {
							let endTime = Number(new Date(parseInt(this.end[0]), parseInt(this.end[1]) - 1, parseInt(this.end[2])));
							if (endTime < Number(new Date(this.year, this.month, i))) options.disabled = true;
						}
						if (this.disabled.length > 0) {
							if (this.disabled.filter(v => {
								return this.year === v[0] && this.month === v[1] - 1 && i === v[2];
							}).length > 0) {
								options.disabled = true;
							}
						}
						temp[line].push(options);
					}
				}
				// 
				if (day == 6 && i < lastDateOfMonth) {
					line++;
				} else if (i == lastDateOfMonth) {
					// line++
					let k = 1;
					for (let d = day; d < 6; d++) {
						// console.log(this.computedNextYear()+"-"+this.computedNextMonth(true)+"-"+k)
						temp[line].push(Object.assign(
							{day: k, disabled: true},
							this.getLunarInfo(this.computedNextYear(), this.computedNextMonth(true), k),
							this.getEvents(this.computedNextYear(), this.computedNextMonth(true), k),
						));
						k++;
					}
					// 
					nextMonthPushDays = k;
				}
			} //end for

			// console.log(this.year+"/"+this.month+"/"+this.day+":"+line)
			// 
			if (line <= 5 && nextMonthPushDays > 0) {
				// console.log({nextMonthPushDays:nextMonthPushDays,line:line})
				for (let i = line + 1; i <= 5; i++) {
					temp[i] = [];
					let start = nextMonthPushDays + (i - line - 1) * 7;
					for (let d = start; d <= start + 6; d++) {
						temp[i].push(Object.assign(
							{day: d, disabled: true},
							this.getLunarInfo(this.computedNextYear(), this.computedNextMonth(true), d),
							this.getEvents(this.computedNextYear(), this.computedNextMonth(true), d),
						));
					}
				}
			}
			this.days = temp;
		},
		computedPrevYear() {
			let value = this.year;
			if (this.month - 1 < 0) {
				value--;
			}
			return value;
		},
		computedPrevMonth(isString) {
			let value = this.month;
			if (this.month - 1 < 0) {
				value = 11;
			} else {
				value--;
			}
			// 0
			if (isString) {
				return value + 1;
			}
			return value;
		},
		computedNextYear() {
			let value = this.year;
			if (this.month + 1 > 11) {
				value++;
			}
			return value;
		},
		computedNextMonth(isString) {
			let value = this.month;
			if (this.month + 1 > 11) {
				value = 0;
			} else {
				value++;
			}
			// 0
			if (isString) {
				return value + 1;
			}
			return value;
		},
		// 
		getLunarInfo(y, m, d) {
			let lunarInfo = calendar.solar2lunar(y, m, d);
			let lunarValue = lunarInfo.IDayCn;
			// console.log(lunarInfo)
			let isLunarFestival = false;
			let isGregorianFestival = false;
			if (this.festival.lunar[lunarInfo.lMonth + '-' + lunarInfo.lDay] != undefined) {
				lunarValue = this.festival.lunar[lunarInfo.lMonth + '-' + lunarInfo.lDay];
				isLunarFestival = true;
			} else if (this.festival.gregorian[m + '-' + d] != undefined) {
				lunarValue = this.festival.gregorian[m + '-' + d];
				isGregorianFestival = true;
			}
			return {
				lunar: lunarValue,
				isLunarFestival: isLunarFestival,
				isGregorianFestival: isGregorianFestival,
			};
		},
		// 
		getEvents(y, m, d) {
			if (Object.keys(this.events).length == 0) return false;
			let eventName = this.events[y + '-' + m + '-' + d];
			let data = {};
			if (eventName != undefined) {
				data.eventName = eventName;
			}
			return data;
		},
		// 
		prevMonth(e) {
			e.stopPropagation();
			if (this.month == 0) {
				this.month = 11;
				this.year = parseInt(this.year) - 1;
			} else {
				this.month = parseInt(this.month) - 1;
			}
			this.render(this.year, this.month);
			this.$emit('selectMonth', this.month + 1, this.year);
			this.$emit('prev', this.month + 1, this.year);
		},
		//  
		nextMonth(e) {
			e.stopPropagation();
			if (this.month == 11) {
				this.month = 0;
				this.year = parseInt(this.year) + 1;
			} else {
				this.month = parseInt(this.month) + 1;
			}
			this.render(this.year, this.month);
			this.$emit('selectMonth', this.month + 1, this.year);
			this.$emit('next', this.month + 1, this.year);
		},
		//
		prevYear(e) {
			this.year = parseInt(this.year) - 1;
		},
		// 
		nextYear(e) {
			this.year = parseInt(this.year) + 1;
		},
		// 
		select(k1, k2, e) {
			if (e != undefined) e.stopPropagation();
			// 
			if (this.range) {
				if (this.rangeBegin.length == 0 || this.rangeEndTemp != 0) {
					this.rangeBegin = [this.year, this.month, this.days[k1][k2].day];
					this.rangeBeginTemp = this.rangeBegin;
					this.rangeEnd = [this.year, this.month, this.days[k1][k2].day];
					this.rangeEndTemp = 0;

				} else {
					this.rangeEnd = [this.year, this.month, this.days[k1][k2].day];
					this.rangeEndTemp = 1;
					// 
					if (+new Date(this.rangeEnd[0], this.rangeEnd[1], this.rangeEnd[2]) < +new Date(this.rangeBegin[0], this.rangeBegin[1], this.rangeBegin[2])) {
						this.rangeBegin = this.rangeEnd;
						this.rangeEnd = this.rangeBeginTemp;
					}
					// 10
					let begin = [];
					let end = [];
					if (this.zero) {
						this.rangeBegin.forEach((v, k) => {
							if (k == 1) v = v + 1;
							begin.push(this.zeroPad(v));
						});
						this.rangeEnd.forEach((v, k) => {
							if (k == 1) v = v + 1;
							end.push(this.zeroPad(v));
						});
					} else {
						begin = this.rangeBegin;
						end = this.rangeEnd;
					}
					this.$emit('select', begin, end);
				}
				this.render(this.year, this.month);
			} else if (this.multi) {
				// 
				let filterDay = this.multiDays.filter(v => {
					return this.year === v[0] && this.month === v[1] - 1 && this.days[k1][k2].day === v[2];
				});
				if (filterDay.length > 0) {
					this.multiDays = this.multiDays.filter(v => {
						return this.year !== v[0] || this.month !== v[1] - 1 || this.days[k1][k2].day !== v[2];
					});
				} else {
					this.multiDays.push([this.year, this.month + 1, this.days[k1][k2].day]);
				}
				this.days[k1][k2].selected = !this.days[k1][k2].selected;
				this.$emit('select', this.multiDays);
			} else {
				// 
				if (this.today.length > 0) {
					this.days.forEach(v => {
						v.forEach(vv => {
							vv.selected = false;
						});
					});
				}
				// 
				this.days[k1][k2].selected = true;
				this.day = this.days[k1][k2].day;
				this.today = [k1, k2];
				console.log('');
				this.$emit('select',this.getDateFormatter(this.year, this.month, this.day), ...this.getRangeFormatter);

				// [this.year, this.zero ? this.zeroPad(this.month + 1) : this.month + 1, this.zero ? this.zeroPad(this.days[k1][k2].day) : this.days[k1][k2].day]);
			}
		},
		changeYear() {
			if (this.yearsShow) {
				this.yearsShow = false;
				return false;
			}
			this.yearsShow = true;
			this.years = [];
			for (let i = ~~this.year - 10; i < ~~this.year + 10; i++) {
				this.years.push(i);
			}
		},
		selectYear(value) {
			this.yearsShow = false;
			this.year = value;
			this.render(this.year, this.month);
			this.$emit('selectYear', value);
		},
		// 
		setToday() {
			let now = new Date();
			this.year = now.getFullYear();
			this.month = now.getMonth();
			this.day = now.getDate();
			this.render(this.year, this.month);
			// 
			this.days.forEach(v => {
				let day = v.find(vv => {
					return vv.day == this.day && !vv.disabled;
				});
				if (day != undefined) {
					day.selected = true;
				}

			});
		},
		// 
		confirm() {
			this.$emit('confirm', this.getDateFormatter(this.year, this.month, this.day), ...this.getRangeFormatter);
		},
		// 
		clearDate() {
			this.$emit('clearDate');
		},
		// 
		zeroPad(n) {
			return String(n < 10 ? '0' + n : n);
		},
	}
};

</script>
