<template>
  <div class="uploadSystem maskBackground">
    <div class="uploadContent allCenter"
      v-show="toggle">
      <div class="title">
        <i class="icon biFont icon-upload"></i>
        <span></span>
      </div>
      <div class="content">
        <div class="fileNav unSelect"></div>
        <div class="listDiv">
          <!-- excel -->
          <div class="filesList">
            <Search :searchObject="searchObject"
              @fromChild="getSearchData"></Search>
            <EasyScroll :barOption="srcoll"
              class="fileList"
              v-if="uploadType == 'excel'">
              <div @click="excelList()"
                class="name">
                <i
                  :class="{ expand: isActive }"
                  class="icon biFont icon-arrowRight menuArrow unSelect"
                ></i>
                <i :class="'icon-' + uploadType"
                  class="icon dataSource unSelect"></i>
                <span :title="uploadName"
                  class="unSelect">
                  {{
                  uploadName
                  }}
                </span>
              </div>
              <ul v-show="listShow">
                <li
                  :class="{ liCheck: key == current || item.searchShow }"
                  :key="key"
                  @click="showData(key)"
                  v-for="(item, key) in uploadData"
                >
                  <label>
<!--                    <span class="checkNumber"-->
<!--                      v-show="definite == key"></span>-->
<!--                    <input-->
<!--                      :class="{ checkNumber: definite == key }"-->
                    <input
                      :value="key"
                      @click.stop="inputCheck(key, $event)"
                      class="checkbox checkNumber"
                      type="checkbox"
                      v-model="checkedValue"
                    />
                  </label>
                  <i class="icon dataSource icon-workSheet"></i>
                  <span class="tableName">{{ key }}</span>
                </li>
              </ul>
            </EasyScroll>
            <!--  -->
            <div class="fileList unSelect"
              v-else>
              <div @click="ExpandList(uploadData)"
                class="name">
                <i :class="{expand:isActive}"
                  class="icon biFont icon-arrowRight menuArrow unSelect"></i>
                <i :class="'icon-'+uploadType"
                  class="icon dataSource unSelect"></i>
                <span class="unSelect">{{uploadName}}</span>
              </div>
            </div>
          </div>
          <!--  -->
          <div class="fieldList">
            <div class="fieldTitle unSelect">
              <span class="vertical unSelect"></span>
              <span
                :style="{ width: customInfer ? '2.5vw' : '10vw' }"
                :title="listName"
                class="fieldTitleList unSelect"
              >{{ listName }}</span>
              <div class="separator"
                v-if="uploadType == 'csv'">
                <span class="unSelect"></span>
                <Separator :separtorObj="separtorObj"
                  @separatorBack="separatorEvent"></Separator>
                <input :value="customVal"
                  class="customInfer"
                  type="text"
                  v-show="customInfer"/>
              </div>
            </div>
            <EasyScroll
              :barOption="srcollTable"
              @xScrollToEnd="xScrollToEnd"
              class="previewData"
              ref="Scrollbar"
            >
              <table>
                <!-- excel -->
                <tbody v-if="uploadType == 'excel'">
                <tr>
                  <td :key="index"
                      class="unSelect"
                      v-for="(value,key ,index) in previewKey">{{ key }}
                  </td>
                </tr>
                <tr :key="key"
                  v-for="(item, key) in previewList">
                  <td :key="index"
                    class="unSelect"
                    v-for="(value, index) in item">{{ value }}
                  </td>
                </tr>
                </tbody>
                <!-- jsonxml -->
                <tbody v-if="uploadType == 'json' || uploadType == 'xml'">
                <tr>
                  <td :key="key"
                    class="unSelect"
                    v-for="(item, key) in previewList">{{ key }}
                  </td>
                </tr>
                <tr v-if="!previewLen">
                  <td :key="key"
                    class="unSelect"
                    v-for="(item, key) in previewList">
                    <span v-if="!item.data.data">{{ item.data }}</span>
                    <span v-else-if="!item.data.list">
                        {{
                        item.data.data
                        }}
                      </span>
                    <span @click="item.data.func"
                      class="mouseStyle"
                      v-else>
                        {{
                        item.data.list
                        }}
                      </span>
                  </td>
                </tr>
                <tr v-if="previewLen">
                  <td :key="key"
                    class="unSelect"
                    v-for="(item, key) in previewList">{{ item }}
                  </td>
                </tr>
                </tbody>
                <!-- csv -->
                <tbody v-if="uploadType == 'csv'">
                <tr>
                  <td :key="key"
                    class="unSelect"
                    v-for="(item, key) in previewTitle">{{ item }}
                  </td>
                </tr>
                <tr :key="key"
                  v-for="(item, key) in previewList">
                  <td
                    :key="index"
                    class="unSelect"
                    v-for="(value, index) in item.data"
                  >{{ value }}
                  </td>
                </tr>
                </tbody>
              </table>
            </EasyScroll>
            <div class="dataTips"
              v-show="showDataTips">
              <img src="@/assets/DataSource/img/recommend.png"/>
              200
            </div>
          </div>
        </div>
        <div class="buttonDiv">
          <span
            :class="{
              banGestures: isUploadClick,
              offEvent: canUpload
            }"
            @click="submitEvent"
            class="submit mainBtn"
          ></span>
          <span @click="cancel"
            class="cancel mainBtn"></span>
        </div>
      </div>
    </div>
    <!--    <Tips ref="tips"-->
    <!--      v-bind="{tipsData}"></Tips>-->
    <Confirm :options="confirmData"
      v-if="confirmShow"></Confirm>
    <ManyPrompt :options="manyPromptData"
      class="morePrompt"
      v-if="morePromptShow"></ManyPrompt>

    <!--  -->
    <Loading :styleObj="styleObj"
      v-if="dataIsOk"></Loading>
  </div>
</template>
<script>
  import Separator from '@/components/Selection/SeparatorSelection.vue';
  import Loading from '@/components/Loading/LoadingRotating.vue';
  import ManyPrompt from '@/components/Prompt/MorePrompt.vue';
  import Confirm from '@/components/Confirm/Confirm.vue';
  import Search from '@/components/Search/index.vue';
  import {mapGetters} from 'vuex';

  export default {
    name: 'dataPreview',
    props: ['previewData'],
    components: {
      Confirm,
      ManyPrompt,
      Search,
      Separator,
      Loading
    },
    sockets: {
      bi_DataUpload (msg) {
        console.log(JSON.parse(msg));
        if(JSON.parse(msg).code === 200) {
          this.$emit('uploadBack', 'success');
        }
      },
    },
    computed: {
      ...mapGetters('main', ['getVw']),
      srcoll() {
        return {
          barColor: '#95A0B6', //
          railColor: '#475566', //
          barWidth: this.getVw(5), //
          barMarginRight: 0, //px
          barOpacityMin: 0.3 //
        };
      },
      srcollTable() {
        return {
          barColor: '#95A0B6', //
          railColor: '#475566', //
          barWidth: this.getVw(5), //
          barMarginRight: 0, //px
          barOpacityMin: 0.3, //
          autoHideMode: true, //
          horizrailenabled: true //
        };
      },
      showDataTips() {
        return (
          this.previewList &&
          this.previewList.length >= 199 &&
          this.previewScrollToEnd
        );
      },
      canUpload() {
        return this.checkedValue.length == 0;
      },
      previewKey() {
        return this.previewList? this.previewList[0]: [];
      }
    },
    data() {
      return {
        previewScrollToEnd: null,
        url: '',
        token: localStorage.getItem('token'),
        isUploadClick: false, //
        uploadData: {},
        uploadType: '',
        uploadName: '',
        listShow: true, //
        current: null,
        previewList: null,
        previewTitle: [],
        previewLen: false,
        fileName: null,
        listName: null,
        definite: null,
        checkedValue: [],
        infer: ',',
        inferName: '',
        customVal: '',
        customInfer: false,
        separtorObj: {
          infer: ',',
          inferName: ''
        },
        dataIsOk: false,
        styleObj: {
          width: '39vw',
          height: '65vh'
        },
        toggle: true,
        isActive: true, //
        confirmShow: false,
        morePromptShow: false,
        placeholderDes: '',
        uploadSearch: [],
        dataSearch: [],
        tipsData: {
          text: '',
          rel: '',
          time: 1500
        },
        searchObject: {
          isClearInput: false,
          placeholderDes: '',
          isShowPanel: false,
          beQueriedList: [],
          width: '11vw',
          height: '2.7vh'
        },
        confirmData: {
          title: '',
          btn: [
            {
              text: '',
              callback: null
            },
            {
              text: '',
              callback: null
            },
            {
              text: '',
              callback: null
            }
          ],
          enterKeyEventIndex: 0,
          escKeyEventIndex: 2
        },
        manyPromptData: {
          title: '',
          name: [],
          btn: [
            {
              text: '',
              callback: null
            },
            {
              text: '',
              callback: null
            }
          ],
          enterKeyEventIndex: 0,
          escKeyEventIndex: 2
        }
      };
    },
    mounted() {
      let res = this.previewData;
      this.fileName = res.name;
      let data = typeof res.data == 'string' ? JSON.parse(res.data) : res.data;
      let i = 0;
      if (res == Array) {
        this.uploadData.name = res.name;
        this.uploadData.data = data;
      } else {
        if (res.type == 'csv') {
          this.customVal = res.csv_name == '' ? res.csv_infer : '';
          this.inferName = this.separtorObj.inferName = res.csv_name;
          this.infer = this.separtorObj.infer = res.csv_infer;
        }
        for (let item in data) {
          this.uploadData[item] = {
            data: data[item],
            searchShow: false
          };
          this.uploadSearch.push({
            name: item,
            index: i++
          });
        }
      }
      this.$set(this.searchObject, 'beQueriedList', this.uploadSearch);
      this.uploadName = res.name;
      this.uploadType = res.type;
      this.showData(this.getObjKeyFirst(res.data))
    },
    methods: {
      cancel() {
        this.$emit('previewHideBack', {
          previewSystem: false
        });
        this.current = null;
      },
      getSearchData(data) {
        this.dataSearch = data;
      },
      showData(key) {
        console.log('');
        this.current = key;
        this.listName = key;
        this.definite = null;
        this.previewList = this.uploadData[key].data;
      },
      excelList() {
        this.isActive = !this.isActive;
        this.listShow = !this.listShow;
      },
      separatorEvent(data) {
        if (data.type == 'infer') {
          this.customInfer = false;
          this.infer = data.infer;
          this.inferName = data.name;
        } else {
          this.customInfer = true;
          this.inferName = '';
        }
      },
      submitEvent() {
        this.isUploadClick = true;
        if (this.isUploadClick) {
          this.dataIsOk = true;
          this.current = null;
          let formData = new FormData();
          if (this.uploadType == 'excel') {
            formData.append('sheet', this.checkedValue);
          } else if (this.uploadType == 'csv') {
            let csv_infer = this.customInfer ? this.customVal : this.infer;
            formData.append('csv_infer', csv_infer);
          }
          // 
          formData.append('fileName', this.uploadName);
          formData.append('examfile', this.previewData.examfile);
          formData.append('action_type', '1');
          formData.append('sqlID', '1');
          formData.append('token', this.token);
          this.$post('/api/' + this.uploadType + 'Upload', formData).then(
            res => {
              this.dataIsOk = false;
              if (res.code == '000000') {
                let data = res.data;
                this.uploadEvent(data, formData);
              } else {
                this.tips.show([res.err_msg.split(':')[1]]);
                this.isUploadClick = false;
              }
            },
            error => {
              this.dataIsOk = false;
              this.tips.show(['']);
              this.isUploadClick = false;
            }
          );
        }
      },
      uploadEvent(data, formData) {
        if (
          (data['0000'] && data['500']) ||
          (data['1000'] && data['500']) ||
          data['500']
        ) {
          let name = data['500'].table_name;
          // 
          this.tableUploadRepeat(name, formData);
        } else if (data['0000']) {
          this.tips.show(['...', true]);
          // this.tipsData.text = '...';
          // this.tipsData.rel = 'success';
          // this.$refs.tips.closeTips();
          setTimeout(() => {
            this.$emit('closePreviewBack', {
              previewSystem: false
            });
          }, 1500);
          //api/dataTnfo
         // this.$emit('uploadBack', 'success');
        } else {
          this.tips.show([
            data['1000'].msg ? data['1000'].msg : ','
          ]);
          // this.tipsData.text = data['1000'].msg
          // 	? data['1000'].msg
          // 	: ',';
          // this.tipsData.rel = 'fail';
          // this.$refs.tips.closeTips();
        }
        this.isUploadClick = false;
      },
      requestDataPreview(formData, tag) {
        this.$post(this.url + 'api/' + this.uploadType + 'Upload', formData).then(
          res => {
            let data = res.data;
            if (data['1000']) {
              this.tipsData.text = data['1000'].msg;
              this.tipsData.rel = 'fail';
              this.toggle = true;
            } else if (data['0000']) {
              this.tips.show(['...', true]);
              // this.tipsData.text = '...';
              // this.tipsData.rel = 'success';
              this.$emit('uploadBack', 'success');
              setTimeout(() => {
                this.$emit('closePreviewBack', {
                  previewSystem: false
                });
              }, 1500);
              this.$get('api/dataInfo', {member_sn: localStorage.getItem('userid')}).then(response => {
                console.log(response.data);
              });
            } else {
              this.toggle = true;
              this.tips.show([
                tag == 'rename'
                  ? ''
                  : ''
              ]);
              // this.tipsData.rel = 'fail';
              // this.tipsData.text =
              //   tag == 'rename'
              //   	? ''
              //   	: '';
            }
            this.confirmShow = false;
            this.morePromptShow = false;
            // this.$refs.tips.closeTips();
          },
          error => {
            console.log(error);
            this.tips.show(['']);
            // this.tipsData.text = '';
            // this.tipsData.rel = 'fail';
            // this.$refs.tips.closeTips();
          }
        );
      },
      renameInterface(data) {
        this.$put('api/updateSourceName', {
          list: data
        }).then(
          response => {
            let res = response.data;
            if (res['success']) {
              this.tips.show(['', true]);
              // this.tipsData.rel = 'success';
              // this.tipsData.text = '';
              this.$emit('uploadBack', 'success');
              setTimeout(() => {
                this.$emit('closePreviewBack', {
                  previewSystem: false
                });
              }, 1500);
            } else {
              this.tips.show(['']);
              // this.tipsData.rel = 'fail';
              // this.tipsData.text = '';
            }
            // this.$refs.tips.closeTips();
          },
          error => {
            console.log(error);
            this.tips.show(['']);
            // this.tipsData.rel = 'fail';
            // this.tipsData.text = '';
            // this.$refs.tips.closeTips();
          }
        );
      },
      tableUploadRepeat(repeatName, formData) {
        this.toggle = false;
        this.confirmShow = true;
        this.confirmData.title = '';
        let coverName = [];
        let tableName = [];
        for (let name in repeatName) {
          let data = {};
          data[name] = repeatName[name];
          tableName.push(data);
          coverName.push(repeatName[name]);
        }
        this.confirmData.btn[0].callback = () => {
          formData.delete('action_type');
          formData.delete('sheet');
          formData.append('sheet', coverName);
          formData.append('action_type', '2');
          this.requestDataPreview(formData, 'cover');
        };
        this.confirmData.btn[2].callback = () => {
          this.confirmShow = false;
          this.toggle = true;
        };
        this.confirmData.btn[1].callback = () => {
          this.confirmShow = false;
          this.morePromptShow = true;
          this.manyPromptData.name = tableName;
          this.manyPromptData.btn[0].callback = data => {
            if (tableName.length == data.length) {
              let renameObj = {};
              for (let i = 0; i < tableName.length; i++) {
                for (let id in tableName[i]) {
                  renameObj[id] = data[i];
                }
              }
              renameObj = JSON.stringify(renameObj);
              this.renameInterface(renameObj);
            } else {
              this.tips.show(['']);
              // this.tipsData.rel = 'fail';
              // this.tipsData.text = '';
              // this.$refs.tips.closeTips();
            }
          };
          this.manyPromptData.btn[1].callback = () => {
            this.confirmShow = true;
            this.morePromptShow = false;
          };
        };
      },
      ExpandList(data) {
        let self = this;
        this.previewLen = false;
        this.listName = this.fileName;
        this.isActive = !this.isActive;
        this.previewList = JSON.parse(JSON.stringify(data));
        console.log(this.previewList);
        if (this.uploadType != 'csv') {
          for (let item in this.previewList) {
            let res = self.previewList[item].data;
            if (res instanceof Array) {
              let obj = {
                list: 'list',
                data: res,
                func: function () {
                  self.JsonList(res);
                }
              };
              self.previewList[item].data = obj;
            }
          }
        } else {
          this.previewTitle = [];
          for (let item in this.previewList[0].data) {
            this.previewTitle.push(item);
          }
        }
      },
      JsonList(data) {
        let self = this;
        this.previewList = JSON.parse(JSON.stringify(data));
        for (let item in this.previewList) {
          let Obj = self.previewList[item];
          if (Obj.hasOwnProperty('data')) {
            let res = self.previewList[item].data;
            if (res.length == 0) {
              return;
            } else if (res instanceof Array || res instanceof Object) {
              let obj = {
                list: 'list',
                data: res,
                func: function () {
                  self.JsonList(res);
                }
              };
              self.previewList[item].data = obj;
            } else {
              let obj = {
                list: '',
                data: res
              };
              self.previewList[item].data = obj;
            }
          } else {
            this.previewLen = true;
          }
        }
      },
      inputCheck(key, event) {
        if (event.currentTarget.checked) {
          this.definite = key;
          this.current = key;
          this.previewList = JSON.parse(
            JSON.stringify(this.uploadData[key].data)
          );
          this.listName = key;
          this.$refs['Scrollbar'].scrollTo({y: 0, x: 0});
          // console.log(this.$refs.Scrollbar.$el);
          // this.$refs.Scrollbar.$el.scrollTop = 0;
          // this.$refs.Scrollbar.$el.scrollLeft = 0;
          // this.$refs.Scrollbar.scrollToY(0);
          // this.$refs.Scrollbar.scrollToX(0);
        } else {
          let res = this.checkedValue;
          if (res.length != 0) {
            this.previewList = JSON.parse(
              JSON.stringify(this.uploadData[res[0]].data)
            );
            this.listName = res[0];
          } else {
            this.listName = '';
            this.previewList = '';
          }
          this.definite = null;
          this.current = null;
        }
      },
      xScrollToEnd(val) {
        this.previewScrollToEnd = val ? true : false;
        console.log('', val);
      },
      getObjKeyFirst(obj){
        for (var key in obj)
          return key
      }
    },
    watch: {
      dataSearch: {
        handler(val, old) {
          for (let info in this.uploadData) {
            let data = this.uploadData[info];
            this.$set(data, 'searchShow', false);
          }
          for (let data of val) {
            let i = data.index;
            this.$set(this.uploadData[i], 'searchShow', true);
          }
        },
        immediate: true
      }
    }
  };
</script>
<style scoped>
  .uploadSystem .confirm.maskBackground,
  .uploadSystem .manyPromptData.maskBackground {
    background: none;
  }

  .uploadSystem .icon {
    vertical-align: 0em;
  }

  .uploadSystem .uploadContent {
    width: 39vw;
    height: 65vh;
    background-color: #19202e;
  }

  .uploadSystem .title {
    height: 4vh;
    line-height: 4vh;
    font-size: 0.9vw;
    text-align: center;
    background-color: #273045;
  }

  .uploadSystem .title i {
    margin-right: 0.4vw;
    font-size: 0.8vw;
  }

  .uploadSystem .content .fileNav {
    height: 5.8vh;
    font-size: 0.8vw;
    line-height: 5.8vh;
    padding-left: 1.6vw;
    text-align: left;
  }

  .uploadSystem .content .listDiv {
    border-bottom: 0.0625vw #080b0f solid;
    border-top: 0.0625vw #080b0f solid;
    display: flex;
    height: 46vh;
  }

  .uploadSystem .content .listDiv >>> .search-box {
    margin-bottom: 0.6vh;
    margin-top: 1.6vh;
  }

  .uploadSystem .content .filesList {
    float: left;
    height: 46vh;
    width: 11.1vw;
    margin: 0 1.6vw;
  }

  .uploadSystem .content .fileList {
    text-align: left;
    line-height: 3vh;
    width: 11.1vw;
  }

  .uploadSystem .content .fileList ul li {
    cursor: pointer;
  }

  .uploadSystem .content .name {
    cursor: pointer;
    font-size: 0.7vw;
    margin-top: 1.7vh;
  }

  .uploadSystem .content .name i.menuArrow {
    font-size: 0.6vw;
  }

  .uploadSystem .content .name .expand {
    transform: rotate(90deg);
    -o-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
  }

  .uploadSystem .content .name i {
    margin-right: 0.2vw;
    display: inline-block;
  }

  .uploadSystem .content .name span {
    vertical-align: top;
    width: 8.1vw;
    display: inline-block;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }

  .uploadSystem .fileList ul li {
    padding: 0 1vw;
    font-size: 0.7vw;
    margin-top: 0.5vh;
    position: relative;
    border: solid 0.0625vw transparent;
  }

  .uploadSystem .fileList ul li.liCheck {
    user-select: none;
    background-color: #182b3f;
    border: solid 0.0625vw #28b3fc;
  }

  .uploadSystem .fileList ul li i {
    margin-right: 0.2vw;
    margin-left: 0.2vw;
    font-size: 0.5vw;
  }

  .uploadSystem .fileList input[type="checkbox"].checkbox {
    width: 0.7vw;
    height: 0.7vw;
    outline: none;
    position: relative;
    background: #121721;
    vertical-align: middle;
    -webkit-appearance: none;
    border: #798194 solid 0.0625vw;
  }

  .uploadSystem .fileList input[type="checkbox"].checkbox:checked.checkNumber {
    border: #28b3fc solid 0.0625vw;
  }

  .uploadSystem .fileList span.checkNumber {
    content: "";
    display: block;
    position: absolute;
    top: 0.55vw;
    left: 1.1vw;
    width: 0.5vw;
    z-index: 100;
    height: 0.3vw;
    transform: rotate(-45deg);
    border: solid 0.07vw #28b3fc;
    border-top: none;
    border-right: none;
    background: transparent;
  }

  .uploadSystem .fileList input[type="checkbox"]:checked::after {
