<template>
  <div class="moreOption"
       :class="{
        transparent:isTransparent}"
       v-click-outside="{fn:hideMoreMenu}">
    <span class="biFont icon-option option-btn"
          :class="{'active':listShow}"
          @click="toggleShow"></span>
    <ul class="list"
        @mousedown.stop
        v-show="listShow"
        :class="{'layerWidth':isLayer}">
      <li v-for="(item,index) in buttons"
          v-show="showLayer(item)"
          :key="index"
          @click="btnFun(item.funName)"
          class="unSelect layer-style"
          :class="{
          'isLock':item.isLock,
          'bottom-border':addBorder(index)
          }">
        <div class="layer-content">
          <span class="biFont"
                :class="item.className"></span>
          <span>{{item.name}}</span>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import {mapGetters} from 'vuex';
import {handleReturnData} from '@/utils/setChart';
export default {

	props: {
		chartIndex: {
			type: Number,
			required: false
		},
		isSetOptions: {
			type: Boolean,
			default: true
		},
		tableId: {
			type: String,
			required: false
		},
		level: {
			type: Number,
			required: false
		},
		isLayer: {
			type: Boolean,
			default: false
		},
		containerOptions:{
			type:Object,
			required: false
		}
		// isDelChart: {containerOptions
		// 	type: Boolean,
		// 	required: true
		// }
	},
	data() {
		return {
			//isLayer 
			buttons: [
				{
					name: '',
					className: 'icon-topLayer',
					funName: 'topLayer',
					isLayer: true,
					isLock: false
				},
				{
					name: '',
					className: 'icon-bottomLayer',
					funName: 'bottomLayer',
					isLayer: true,
					isLock: false
				},
				{
					name: '',
					className: 'icon-layerUp',
					funName: 'layerUp',
					isLayer: true,
					isLock: false
				},
				{
					name: '',
					className: 'icon-layerDown',
					funName: 'layerDown',
					isLayer: true,
					isLock: false
				},
				{
					name: '',
					className: 'icon-grouping',
					funName: 'grouping',
					isLayer: true,
					isLock: true
				},
				{
					name: '',
					className: 'icon-cancelGrouping',
					funName: 'cancelGrouping',
					isLayer: true,
					isLock: true
				},
				{
					name: '',
					className: 'icon-edit',
					funName: 'rename',
					isLayer: true,
					isLock: false
				},
				{
					name: '',
					className: 'icon-copy',
					funName: 'copy',
					isLayer: false,
					isLock: false
				},
				{
					name: '',
					className: 'icon-lookUp',
					funName: 'lookUp',
					isLayer: true,
					isLock: false
				},
				{
					name: '',
					className: 'icon-export',
					funName: 'export',
					isLayer: false,
					isLock: false
				},
				{
					name: '',
					className: 'icon-delete',
					funName: 'delete',
					isLayer: false,
					isLock: false
				},
				{
					name: '',
					className: 'icon-reset',
					funName: 'reset',
					isLayer: true,
					isLock: true
				}
			],
			listShow: false,
			isClicked: false,
			singleOperateName: '',
			isShowed: false,
		};
	},
	watch: {
		isChartSaveComplete: {
			immediate: true,
			handler(val) {
				if (this.singleOperateName == 'export') {
					this.$store.commit('chartBus/setSaveComplete');
					if (val) {
						this.$store.commit('canvasPlace/setStateProperty', {key:'shareType',value:2});
						this.singleOperateName = '';
					}
				}
			}
		}
	},
	computed: {
		zIndexStyle() {
			if (!this.isLayer) {
				const zIndex = this.listShow ? 1000 : this.level; //
				return zIndex;
			}
		},
		showLayer() {
		  return (item)=>{
				// debugger;
				let list = this.chartList[this.currentChartIndex];

				if (list) {
					let type = list.containerOptions.chartsType.split('-')[0];
					if (type == 'echarts' && list['containerStatus']['isSetOptions'] && item.funName === 'lookUp') {
						// return  false;
						item.isLayer = false;
					}
					if(list.containerOptions.chartsType=='interactive-dataFilter'&& item.funName === 'copy'){ //
						return  false;
					}
				}
				if (this.isLayer || !item.isLayer) {
					if (	item.funName === 'export' && (this.tableId === '' || this.tableId === 'testTableId')) {
						return false;
					} else {
						return true;
					}
				} else {
					return false;
				}
			};

		},

		...mapGetters('canvasPlace', {
			isOpenPrompta: 'openPrompt',
			currentChartIndex: 'getCurrentChartIndex',//
			currentChartType: 'getCurrentChartType',
			chartList: 'getChartList',//
			currentChartsType: 'getCurrentChartsType',
			containerStatus: 'getContainerStatus'
		}),
		...mapGetters('defaultChartOptions', {
			getChartOptions: 'getEchartsDefaultOption',
			getCategoryAxis: 'getCategoryAxis',
		}),
		...mapGetters('defaultInteractiveOptions', {
			getInteractiveOptions: 'getInteractiveDefault'
		}),
		...mapGetters('defaultMediaOptions', {
			getMediaOptions: 'getMediaDefault'
		}),
		...mapGetters('dataDimension', {
			defaultChartAxis: 'getDefaultChartAxisAll',
			pageFilterList: 'getPageFilterList',
		}),
		...mapGetters('chartBus', {
			timeState: 'returnTime',
			isChartSaveComplete: 'getSaveComplete'
		}),
		isTransparent() {
			if (
				!(this.chartIndex == this.currentChartIndex) &&
          !this.isClicked &&
          this.isSetOptions &&
          !this.isLayer
			) {
				return true;
			}
			return false;
		}
	},
	methods: {
		addBorder(index) {
			if (!this.isLayer) {
				return false;
			} else {
				if (index == 3 || index == 5 || index == 8) {
					return true;
				}
			}
		},
		toggleShow() {
			if (this.isSetOptions) {
				this.isClicked = !this.isClicked;
			}
			this.listShow = !this.listShow;
		},
		hideMoreMenu() {
			if (this.isSetOptions) {
				this.isClicked = false;
			}
			this.listShow = false;
		},
		btnFun(funName) {
			let vm = this;
			this.listShow = false;
			vm.singleOperateName = funName;
			let methods = {
				copy: () => {
					let defaultOptions = {};
					let result = {};
					let params = vm.chartList[vm.currentChartIndex];  //Copy
					let obj = {
						xPosition: parseFloat(params.containerOptions.xPosition) + 30,
						yPosition: parseFloat(params.containerOptions.yPosition),
						tableWidth: params.containerOptions.tableWidth,
						tableHeight: params.containerOptions.tableHeight,
						tableId:'',
					};
					let fullType=params.containerOptions.chartsType;
					let type =fullType.split('-')[0];
					defaultOptions.renderOptions = params.renderOptions;
					if (type == 'echarts') {
						defaultOptions = JSON.parse(JSON.stringify(this.getChartOptions(fullType)));
						defaultOptions.containerOptions = Object.assign(defaultOptions.containerOptions, obj);
						defaultOptions.containerOptions.categoryAxis = vm.getCategoryAxis(vm.currentChartType);  //getCategoryAxis
						vm.dexieDB.aXisList.get(vm.currentChartIndex).then(res => { //axisOptions
							this.$store.commit('canvasPlace/setChartContainer', {
								defaultOptions,
								copyAxisOptions: res.axisOptions
							});
							if (res.axisOptions.xAxisList[0].name != '' && res.axisOptions.yAxisList[0].name != '' || res.axisOptions.cityName.length > 0) {  //XY 3D
								result = handleReturnData(defaultOptions, res.axisOptions);  //
								console.log(result,'');
								vm.$post('api/v2/chart', result).then(data => {
									if (data.code == '000000') {
										vm.$store.commit('canvasPlace/setShowLoading', vm.currentChartIndex);
										vm.initSingleChart(data.data.tableId);
									}
								});
							}
						});
					} else if (type == 'media' || type == 'text') {
						defaultOptions = JSON.parse(JSON.stringify(this.getMediaOptions(fullType)));
						defaultOptions.containerOptions = Object.assign(defaultOptions.containerOptions, obj);
						defaultOptions.renderOptions=params.renderOptions;
						this.$store.commit('canvasPlace/setChartContainer', {defaultOptions});
					} else if (type == 'interactive') {
						defaultOptions = JSON.parse(JSON.stringify(this.getInteractiveOptions(fullType)));
						defaultOptions.containerOptions = {...params.containerOptions,...obj};
						defaultOptions.renderOptions=params.renderOptions;
						defaultOptions.marks = 'copy'; //copy
						this.$store.commit('canvasPlace/setChartContainer', {defaultOptions});
					}

				},
				export: () => {
					vm.listShow = false;
					vm.$store.commit('canvasPlace/setStateProperty', {key:'shareType',value:2});
				},
				delete: () => {
					/**
             * 
             * tableId='testTableId'
             */
					if (vm.tableId && vm.tableId !== 'testTableId') {
						/**
               * 
               */
						vm.delectChart(vm.tableId);
					} else {
						/**
               * 
               */
						vm.resetCurrentChartData();
					}
				},
				rename: () => {
					this.$store.commit('canvasPlace/containerOptionSetter', {key:'isLayerEdit',value:true});
				},
				lookUp: () => {
					vm.$store.commit('canvasPlace/setStateProperty', {key:'showSignal',value:false});
					setTimeout(() => {
						vm.$store.commit('canvasPlace/setStateProperty', {key:'showSignal',value:true});
					}, 200);
				}
			};
			methods[funName] && methods[funName]();
		},
		/**
       * 
       * @param {*}
       * @param {*} params  tableId
       */
		initSingleChart(params) {
			let vm = this;
			const sendObject = {
				projectId: sessionStorage.getItem('projectId'),
				tableId: params
			};
			vm.$get('api/v2/oneChart', sendObject).then(res => {
				if (res.code === '000000') {
					let componentData = res.data[0];
					let categoryAxis = componentData.containerOptions.categoryAxis.toLowerCase();
					componentData['containerOptions']=Object.assign(componentData['containerOptions'],{
						layerIndex:vm.currentChartIndex,
						layerChartIcon:'#icon-' + componentData['containerOptions']['chartsType'].split('-')[1],
					});
					// let hanldeComponentData=beforeThemeHandle(componentData);
					vm.$store.commit('canvasPlace/changeIndexChart', {
						data: componentData,
					});
					vm.$store.commit('canvasPlace/setCurrentChartId',vm.currentChartIndex);
					//containerOptionstableId
					vm.$store.commit('canvasPlace/setCurrentTableId', {
						tableId: params,
						index: vm.currentChartIndex,
						flag: 1
					});
				} else {
					vm.tips.show([res.err_msg.split(':')[1]]);
					vm.$store.commit('canvasPlace/setShowLoading', vm.currentChartIndex);
				}
			}).catch(error => {
				console.error(error);
				vm.tips.show(['']);
				vm.$store.commit('canvasPlace/setShowLoading', vm.currentChartIndex);
			});
		},
		/**
       * 
       * indexDB
       * chartList
       */
		resetCurrentChartData() {
			/**indexDB */
			const vm = this;
			/**
         * echarts/text/media/interactive
         */
			// console.log(vm.currentChartsType,'list moreOptions');
			const chartLargeType = vm.currentChartsType.split('-')[0];
			let defaultOptions = {}; //
			/**
         * 
         * canvasPlace.jscurrentChartAxis vuex
         * {
         * 	containterStatus:{},
         * 	containerOptions:{},
         * 	chartOptions/textOptions/mediaOptions/interactiveOptions
         * }
         */
			let currentTypeDefaultObject = {};
			currentTypeDefaultObject['renderOptions'] = defaultOptions['renderOptions'];
			switch (chartLargeType) {
			case 'echarts':
				handleClearSave(chartLargeType);

				handleClearDb();
				//options
				defaultOptions = vm.getChartOptions(vm.currentChartsType);
				break;
			case 'text':
				handleClearSave(chartLargeType);
				defaultOptions = vm.getMediaOptions(vm.currentChartsType);

				break;
			case 'media':
				handleClearSave(chartLargeType);
				defaultOptions = vm.getMediaOptions(vm.currentChartsType);

				break;
			case 'interactive':
				defaultOptions = vm.getInteractiveOptions(vm.currentChartsType);
				break;
			default:
				break;
			}

			/**
         * 5s
         */
			function handleClearSave(chartLargeType) {
				let dbType = chartLargeType;
				if (dbType == 'echarts') {
					dbType = 'saveOption';
				} else {
					dbType =
              'save' +
              dbType.charAt(0).toUpperCase() +
              dbType.slice(1) +
              'Option';
				}
				vm.dexieDB[dbType]
					.delete(vm.chartIndex)
					.then()
					.catch(error => console.error(error));
			}

			/**
         * 
         * axisList
         */
			function handleClearDb() {
				vm.dexieDB.aXisList
					.put({
						index: vm.chartIndex,
						axisOptions: vm.defaultChartAxis
					})
					.then(() => {
					})
					.catch(error => console.error(error));
				vm.$store.commit('dataDimension/setUpdateCount');
				vm.$store.commit('dataDimension/setCurrentChartAxis', vm.defaultChartAxis);
			}

			/**
         * chartList
         */
			//containterStatus
			vm.$set(
				currentTypeDefaultObject,
				'containerStatus',
				JSON.parse(JSON.stringify(vm.containerStatus))
			);
			//containerOptions
			vm.$set(
				currentTypeDefaultObject,
				'containerOptions',
				defaultOptions.containerOptions
			);
			/**isDeletetrue */
			vm.$set(currentTypeDefaultObject.containerStatus, 'isDeleted', true);
			/**
         * vuex
         * vuexcurrentChartIndex-1
         * vuexcanvasPlace.jscurrentChartAxis
         **/
			vm.$store.commit('canvasPlace/deleteCurrentChart', {
				index: vm.chartIndex,
				data: JSON.parse(JSON.stringify(currentTypeDefaultObject))
			});
		},
		// 
		delectChart(id) {
			let vm = this;
			const delObject = {
				projectId: sessionStorage.getItem('projectId'),
				tableId: id
			};
			vm.$del('api/v2/chart', delObject)
				.then(res => {
					if (res.code == '000000') {
						vm.resetCurrentChartData();
						vm.tips.show(['',true]);
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
					vm.$emit('frameModal', false);
				})
				.catch(error => {
					console.log(error,'');
					vm.tips.show(['']);
					vm.$emit('frameModal', false);
				});
		},

	},
};
</script>
<style scoped>
  .moreOption {
    color: #fff;
    font-size: 0.8vw;
    cursor: pointer;
  }

  .moreOption span.option-btn {
    overflow: hidden;
    cursor: pointer;
  }

  .moreOption .list {
    white-space: nowrap;
    position: fixed;
    z-index: 1;
    background-color: #252f40;
    box-shadow: 0vw 0.1vw 0.3vw 0vw rgba(0, 0, 0, 0.3);
  }

  .moreOption .layerWidth {
    width: 5.6vw;
  }

  .list li {
    width: auto;
    height: 1.5vw;
    line-height: 1.5vw;
    text-align: center;
    padding: 0 0.6vw;
  }

  .list .layer-style {
    text-align: left;
    padding-left: 0.5vw;
  }

  .layer-style.bottom-border {
    border-bottom: 0.0625vw solid #1b2333;
  }

  .list .isLock {
    color: #676e7a;
  }

  .list li > span {
    font-size: 0.5vw;
    transform: scale(0.9);
  }

  .list li:hover {
    background-color: #131721;
    color: #28b3fc;
  }

  .option-btn.active {
    color: #28b3fc;
  }

  .list .layer-style .layer-content span:nth-child(2) {
    margin-left: 0.4vw;
  }

  .layer-content {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }

  .layer-content span {
    display: block;
    float: left;
    font-size: 0.6vw;
  }
</style>
