<!---->
<template>
  <div class="container">
    <div class="bg"
      v-show="!isSetOptions"></div>
    <v-chart :options="percentOptions"
      ref="instance"
      class="echarts"
      v-show="isSetOptions"
      autoresize></v-chart>
  </div>
</template>
<script>
import {echartsCommonMixin} from '../../mixin/echartsCommonMixin';
import 'echarts/lib/chart/bar';

export default {
	name: 'echarts-percentageBar',
	mixins: [echartsCommonMixin],
	data() {
		return {
			percentOptions: {}
		};
	},
	watch: {
		options: {
			immediate: true,
			handler() {
				this.calculation();
			}
		}
	},
	methods: {
		calculation() {
			this.percentOptions = {
				backgroundColor: this.options.backgroundColor,
				color: this.options.color,
				grid: this.options.grid,
				legend: this.options.legend,
				series: this.options.series,
				title: this.options.title,
				xAxis: this.options.xAxis,
				yAxis: this.options.yAxis,
				tooltip: {
					axisPointer: this.options.tooltip.axisPointer,
					show: this.options.tooltip.show,
					type: this.options.tooltip.type,
					formatter: function (params) {
						return params.name + ':' + (Number(params.value) / 100.00).toFixed(3) + '%';
					}
				},
			};
		}
	},
	mounted() {
	}
};
</script>
<style scoped>
  .bg {
    background-image: url("~echartsBg/percentageBar.png");
    background-repeat: no-repeat;
  }
</style>
