<template>
<!--  <div class="wrapper">-->
<!--    <div class="chart-basis"-->
<!--      @click="toggleContentShow">-->
<!--      <span></span>-->
<!--      <span class="biFont icon-arrowLeft icon-fontsize"-->
<!--            :class="[isShowContent?'leftRotate-90':'leftRotate-0']"></span>-->
<!--    </div>-->
    <div
      ref="contentBox"
      class="content-box">
      <ul>
        <li class="optionsItem">
          <span class="title"></span>
          <span class="title"></span>
          <toggle-button v-model="pushState"
            @change="togglePushSwitch"
            :sync="true"
            :labels="{checked: '', unchecked: ''}"
            :css-colors="true"/>
        </li>
        <li class="optionsItem">
<!--          <input-common :inputData="inputData"-->
<!--            validateType='number'-->
<!--            :minVal='10'-->
<!--            :styleObj='inputStyleObj'-->
<!--            unit=''-->
<!--            @inputBlur="onInputBlur"></input-common>-->
          <select-menu :ordinaryList="leftDistanceList"
                       :selectTitle="''"
                       :heightLightLine="titleLeftDistance"
                       @getReturnData="result"></select-menu>
        </li>
      </ul>
    </div>
</template>

<script>
/**
	 * 
	 * 
	 * 
	 * 
	 * 
	 * 
	 */
import {ToggleButton} from 'vue-js-toggle-button';
import InputCommon from './BasicComponent/InputCommon';
import {mapGetters, mapMutations} from 'vuex';
import SelectMenu from '@/components/Dropdown/Dropdown';
import {mixin} from './BasicComponent/mixin';
export default {
	name: 'PushSetting',
	mixins:[mixin],
	components: {
		'toggle-button': ToggleButton,
		'select-menu': SelectMenu
	},
	data() {
		return {
			isShowContent: false,
			pushState: false,
			pushTime: 10,//10s
			//input
			titleLeftDistance: {value: '10', label: '10s'},
			leftDistanceList: [
				{value: '10', label: '10s'},
				{value: '30', label: '30s'},
				{value: '60', label: '60s'}
			],

			//
			lastOpenTime: 0,
			//
			openPeriodTime: 0,
			//
			lastCloseTime: 0,
			//
			closePeriodTime: 0,
			//
			toggleTimer: null,
			switchFunArr: []
		};
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter',
		}),
		result(data){
		  let vm = this;
			vm.titleLeftDistance = vm.leftDistanceList.find(e => e.value === data);
			vm.modifyPush();
		},
		//
		modifyPush() {
			this.$put('api/pushTask', {
				id: sessionStorage.getItem('projectId'),
				pushTime: this.titleLeftDistance.value,//
				pushState: this.pushState,
			}).then(res => {
			}).catch(reason => {
				console.error(reason);
			});
		},
		toggleContentShow() {
			this.isShowContent = !this.isShowContent;
		},
		togglePushSwitch(val) {
			if(this.toggleTimer){clearTimeout(this.toggleTimer);}
			this.toggleTimer=setTimeout(()=>{
				this.modifyPush();
			},500);
		},
		onInputBlur(val) {
			this.pushTime = val;
		},
	},
	computed: {
		...mapGetters('chartBus', {
			chartDataList: 'returnData',
			chartIndex: 'returnDataIndex'
		}),
		...mapGetters('canvasPlace', {
			pushInfo: 'getPushInfo'
		}),
	},
	watch: {
		pushInfo: {
			handler(val) {
				this.pushState = val.pushState;
				this.pushTime = val.pushTime;
			},
			deep: true,
			immediate:true,
		},

		pushTime(val) {
			this.modifyPush();
		},


	},
	created() {
	},
};
</script>

<style scoped>
  li{
    padding: 0.4vw 0.5vw;
  }
  .optionsItem {
    /*margin-bottom: 0.8vw;*/
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .optionsItem .title {
    display: block;
    flex: 1;
    color: #acbbda;
  }

  span,
  input {
    user-select: none;
  }

</style>

