<template>
  <div class="proportionRing"
    :class="contentColor">
<!--      :class="contentColor"-->
    <div class="circular-progress aCenter"
      :style="setFocusStyle"
      @dragleave="dragleave"
      @dragover="dragover($event)"
      @drop="axisDrop($event)">
      <svg xmlns='http://www.w3.org/2000/svg'
        viewBox='0 0 100 100'
        aria-labelledby='title'
        class="svgText"
        role='graphic'>
        <text x="50%"
          y="40%"
          dy=".3em"
          :style="svgText"
          text-anchor="middle">{{proportion}}%
        </text>
        <circle cx="50" cy="50" r="40" :style="circleRote" class="bgCircle"></circle>
        <circle cx="50" cy="50" r="40" :style="valueSet" class="fillCircle"></circle>
      </svg>
      <input class="fieldName aCenter"
        @dragstart.prevent
        :class="{offEvent:!$attrs.containerStatus.isSetOptions}"
        :value="proportionName"
        :style="pctBottom"
        maxlength="25"
        @input='requestDrop'
        @blur="nameInputBlur($event)">
    </div>
  </div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex';
import {debounceTailIncomponent} from '@/utils/utilsMethods';//echart
export default {
	name: 'proportionRing',
	props: {
		containerOptions: {
			type: Object,
			required: true
		},
		options: {
			type: Object,
			required: true
		},
		chartIndex: {
			type: Number
		},
		tableId: {
			type: String
		},
		yFiles: {
			type: Array
		}
	},
	data() {
		return {
			throttleUpdateOptions:()=>{},
			timerID:null,
			// proportion: 100,
			yFileDataS: [],
			// proportionName: '',
			clickData: '',
			modify: false,
		};
	},
	computed: {
		...mapGetters('dataDimension', {
			dragObject: 'getDragObject',
			isFocusDrag: 'isFocusDrag',
			currentHoverIndex: 'getCurrentHoverIndex'
		}),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',
			currentTableOptions: 'getCurrentChartOptions'
		}),
		proportionName(){
		  // return this.options.series[0]&&this.options.series[0].name||'';
		  return this.options.data.name||'';
		  // return this.containerOptions.seriesName.name||'';
		},
		proportion(){
			let value =this.containerOptions.seriesData.value||0;
			// let value =this.options.series[0]&&this.options.series[0].value||'';
			// let value =this.options.data.value||0;
		  return value>100?100:value;
		},
		circleWidth(){
		  return Math.min(this.containerOptions.tableWidth,this.containerOptions.tableHeight);
		},
		// x,y,w,h,
		tablePositionOptions() {
			const {tableWidth, tableHeight, xPosition, yPosition} = this.containerOptions;
			return {tableWidth, tableHeight, xPosition, yPosition};
		},
		valueSet() {
			// stroke-dashoffset
			let vm = this;
			return {
				'stroke-dashoffset': (1 - vm.proportion / 100) * (2 * (22 / 7) * 40),
				'stroke': vm.options.style.fillColor,
			};
		},
		circleRote() {
			let vm = this;
			return {
				'stroke': vm.options.style.emptyColor,
			};
		},
		viewBox() {
			let vm = this;
			return {
				width: vm.w + 'px',
				height: vm.h - 20 + 'px',
				lineHeight: vm.h + 'px',
			};
		},
		//
		contentColor() {
			if (this.chartIndex === this.currentHoverIndex && this.isCurrentChart) {
				let type = this.dragObject.inputType;
				let obj = {
					int: 'content-int',
					time: 'content-date',
					char: 'content-char'
				};
				return obj[type] || '';
			}
			return '';
		},
		setFocusStyle() {
			// if (this.isFocusDrag && this.isCurrentChart) {
			return {
				'width':this.circleWidth+'px',
				// 'border-color': '#28b3fc',
				// 'box-shadow': '0 0.1vw 0.2vw 0.2vw rgba(40, 179, 252,0.5) inset',
				// 'border-radius': '0.1vw'
			};
			// };
		},
		/** */
		isCurrentChart() {
			return this.currentChartIndex === this.chartIndex;
		},
		svgText() {
			let vm = this;
			return {
				fill: vm.options.style.valueFontColor,
				fontSize: vm.options.style.valueFontSize + 'px',
			};
		},
		pctBottom() {
			let vm = this;
			return {
				fontSize: vm.options.style.titleFontSize/100*this.circleWidth + 'px',
				color: vm.options.style.titleFontColor
			};
		}
	},
	watch: {
		// tablePositionOptions(val,oldValue) {
		//   console.log('');
		// 	if (this.$attrs.containerStatus.isSetOptions) {
		// 	  this.throttleUpdateOptions();
		// 		// this.requestDrop();
		// 	}
		// },
		// modify: {
		// 	handler() {
		// 		if (this.modify) {
		// 			setTimeout(() => {
		// 	      this.updateOptions();
		// 			},5000.0001);
		// 		}
		// 	}
		// }
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter',
			containerOptionSetter: 'containerOptionSetter',
		}),
		dragleave() {
			this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
		},
		dragover(e) {
			let vm = this;
		  if(this.isCurrentChart){
				vm.$store.commit('dataDimension/setCurrentHoverIndex', vm.chartIndex);
			}
			e.stopPropagation();
			e.preventDefault();
		},
		axisDrop() {
		  if(this.isCurrentChart){
				//
		    if(this.dragObject.tableIndex===this.containerOptions.xSourceId
          &&this.dragObject.name===this.containerOptions.xField)return false;

				this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
				this.$store.dispatch('canvasPlace/containerOptionSetterAll',[
					{	key: 'xField',
						value: this.dragObject.name},
					{
						key: 'xSourceId',
						value: this.dragObject.tableIndex
					},
					{
						key: 'xSourceName',
						value: this.dragObject.tableName
					},
					{
          	key: 'yField',
          	value: this.dragObject.name
					},
					{
          	key: 'ySourceId',
          	value: this.dragObject.tableIndex
					},
					{
          	key: 'ySourceName',
          	value: this.dragObject.tableName
					}
				]);
				this.renderOptionSetter({
					key: 'data.name',
					// id: this.clickData.id,
					value: this.dragObject.name
				});
				this.updateOptions();
				// this.getYopData({
				// 	yFieldDa: this.dragObject.name,
				// 	ySourceIds: this.dragObject.tableIndex,
				// 	ySourceNames: this.dragObject.tableName
				// });
				// this.getSingeChartData();

			}
		},
	  // clickFocus(){//
		// 	this.clickData = this.currentTableOptions;
		// },
		requestDrop(e) {
			e=e||window.event;
			const target = e.target || e.srcElement;
			let vm = this;
			// if (this.$attrs.containerStatus.isSetOptions){
			vm.renderOptionSetter({
				key: 'data.name',
				// id: this.clickData.id,
				value: target.value
			});
			vm.containerOptionSetter({
				key: 'seriesData.name',
				// id: this.clickData.id,
				value: target.value
			});
			// }
		},
		nameInputBlur(e) {
			this.updateOptions();
			// vm.modify = true;
			// vm.renderOptionSetter({
			// 	key: 'series.0.name',
			// 	// id: this.clickData.id,
			// 	value: target.value
			// });
			// if (vm.proportionName === ''){
		  //   vm.proportionName = vm.options.data.name;
			// }
		},
		// 
		updateOptions() {
			let vm = this;
			vm.modify = false;
			let sendOptions={
			  ...vm.containerOptions,
				projectId: sessionStorage.getItem('projectId'),
				renderOptions:JSON.stringify(vm.options),
			};
			vm.$put('api/v2/chart',[sendOptions]
			).then(res => {
				this.getSingeChartData();
			});

		},
		getSingeChartData() {
			let vm = this;
			vm.$get('api/v2/oneChart', {
				projectId: sessionStorage.getItem('projectId'),
				tableId: vm.tableId
			}).then(res => {
			  let data=res.data[0];
				data.containerStatus={
					isSetOptions:true,
					dataDimensionShow:false
				};
				data.renderOptions=JSON.parse(data.renderOptions);
				data.containerOptions.xSourceId=data.containerOptions.x.xSourceId;
				data.containerOptions.xField=data.containerOptions.x.xField;
				// data.renderOptions.data={
				//   value:data.containerOptions.seriesData.value,
				// 	name:data.containerOptions.seriesData.name,
				// };
				vm.$store.commit('canvasPlace/changeIndexChart',{
				  index:this.chartIndex,
					data,
				});
			});
		}
	},
	mounted() {
		let vm = this;
		if (vm.tableId) {
			vm.$store.commit('canvasPlace/setRenderedStatus');
		}
	}
};
</script>

<style scoped>
  .svgText {
    user-select: none;
  }
  .fieldName {
    display: inline-block;
    width: auto;
    background: rgba(1, 1, 1, 0);
    text-align: center;
    top: 60%;
  }

  .proportionRing {
    height: 100%;
    overflow: hidden;
  }

  input::-moz-placeholder {
    color: #fff;
  }

  input::-webkit-input-placeholder {
    color: #fff;
  }

  input:-ms-input-placeholder {
    color: #fff;
  }

  .circular-progress {
    position: relative;
    display: inline-block;
    /*width: 140px;*/
    width: 100%;
    /*height: 140px;*/
    height: 100%;
    border-radius: 50%;
    /*color: #fff;*/
  }

  .circular-progress svg {
    width: 100%;
    height: 100%;
  }
  .circular-progress svg .fillCircle{
    transform: rotate(-90deg);
    transform-origin: 50% 50%;
  }

  .circular-progress circle {
    stroke-width: 5;
    fill: none;
    stroke-linecap: round;
  }

  .circular-progress circle:nth-of-type(1) {
    /*stroke: rgb(14, 65, 74);*/
  }

  .circular-progress circle:nth-of-type(2) {
    /*stroke: rgb(0, 234, 237);*/
    stroke-dasharray: 251.4285714286;
    stroke-dashoffset: 75.4285714286;
  }


  /**/
  .content-int {
    background-color: rgba(17, 190, 110, 0.3);
    border-color: #0fff00;
    box-shadow: 0.01vw 0.01vw 0.2vw 0 rgb(16, 255, 0);
    border-radius: 0.1vw;
  }

  .content-char {
    border-color: #28b3fc;
    background-color: rgba(40, 179, 252, 0.3);
    box-shadow: 0.01vw 0.01vw 0.2vw 0 rgb(40, 179, 252);
    border-radius: 0.1vw;
  }

  .content-date {
    background-color: #2e3325;
    border-color: #ffff00;
    box-shadow: 0.01vw 0.01vw 0.2vw 0 rgb(255, 255, 0);
    border-radius: 0.1vw;
  }
</style>
