<template>
  <div class='show-container'
    v-click-outside="{fn:toClose}"
    v-show='show'>
    <!-- -->
    <Loading  v-if="dataIsOk"
      :styleObj="styleObj"></Loading>
    <div class='show-header'>
      <div class="header-content">
        <span class="biFont icon-chakan"></span>
        <span></span>
      </div>
      <span class="biFont icon-clear"
        style='cursor:pointer'
        @click='toClose'></span>
    </div>

    <div class='data-content'>
      <span class='data-title'>{{title}}</span>
      <div class='data-table'>
        <div class='table'>
          <div class='table-header'>
            <span v-for='(head,index ) in colHeaders'
              :key='index'>{{head}}</span>
          </div>
          <div class='table-list'>
            <ul v-for='(val,Index) in dataList' :key='Index'  >
              <li v-for='name in val' :key='name'>{{name}} </li>
            </ul>
          </div>
        </div>

      </div>

    </div>

  </div>
</template>

<script>
import {mapGetters} from 'vuex';
import Loading from '@/components/Loading/LoadingRotating.vue';

export default {
	name: 'dataShow',
	props: {
		show: {
			type: Boolean,
			required: true,
		}
	},
	components: {
		Loading
	},
	data() {
		return {
			// lastActivedIndex:-1,
			viewStyle: {},
			colHeaders: [],
			dataList: [],
			title: '',
			styleObj: {
				width: '100%',
				height: '100%'
			},
			dataIsOk: false,
		};
	},
	computed: {
		...mapGetters('dataDimension', {
			pageFilterList: 'getPageFilterList',

		}),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',//
			chartList: 'getChartList',//
		}),
		vwToPx() {
			return this.$store.getters['main/vwToPx'];
		},

		opt() {
			return {
				barColor: 'rgb(149,160,182)', //
				railColor: 'rgba(71, 85, 102, 1)', //
				barWidth: 5, //
				barMarginRight: 0, //px
				barOpacityMin: 0.5, //
				borderWidth: this.vwToPx(0.2)
			};
		}
	},
	methods: {
		toClose() {
		  // debugger;
			if(this.show){
				this.$store.commit('canvasPlace/setStateProperty', {key:'showSignal',value:false});
			}
		},
		getData(arr, i) {
			let vm = this;
			let length = arr.length;
			let params = {};
			switch (arr[i].inputType) {
			case 'int':
				params.type = 'number';
				break;
			case 'char':
				params.type = 'text';
				break;
			default:
				params.type = 'time';
			}
			params.sourceId = arr[i].tableIndex;
			params.field = arr[i].name;
			vm.colHeaders.push(arr[i].name);
			vm.$get('api/tableData', params)   //
				.then(res => {
					if (res.code == '000000') {
						vm.dataList.push(res.data);
					}
					if (++i < length) {
						this.getData(arr, i);
					} else {
						vm.countProperties(vm.dataList);
					}
				});
		},
		countProperties(obj) {   //
			let nowLength;
			let maxLength = 0;
			for (let n in obj) {
				obj[n].constructor === Array ? nowLength = obj[n].length : nowLength = Object.keys(obj[n]).length;
				nowLength > maxLength ? maxLength = nowLength : '';
			}
			for (let j in obj) {
				if (obj[j].constructor === Array) {
					this.dataList[j].length = maxLength;
				} else {
					let length = maxLength - Object.keys(obj[j]).length;
					for (let i = 0; i < length; i++) {
						this.dataList[j]['add' + i] = null;
					}
				}
			}
			this.dataIsOk = false;
		},

	},
	watch: {
		show() {
			let vm = this;
			vm.dataList = [];
			vm.colHeaders = [];
			vm.dataIsOk=true;
			if (vm.show) {
				vm.title = vm.chartList[vm.currentChartIndex].renderOptions.title.text;
				let arr1 = vm.pageFilterList.filter((element, index, self) => {
					if (element.name) {
						return self.findIndex(el => el.name == element.name) === index;
					}
				});
				vm.getData(arr1, 0);
			}
		},
		// currentChartIndex(val){
		//   if(val>-1){
		//     this.lastActivedIndex=val;
		// 	}
		// }
	}
};
</script>

<style scoped>
  * {
    moz-user-select: -moz-none;
    -moz-user-select: none;
    -o-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  .show-container {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 34.1vw;
    height: 32.1vw;
    font-size: 0.8vw;
    color: #fff;
    background-color: rgba(25, 32, 46, 1);
  }

  .show-container .show-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 1.9vw;
    padding: 0 0.6vw;
    background-color: rgba(39, 48, 69, 1);
  }

  .show-container .biFont {
    margin-right: 0.1vw;
    font-size: 0.7vw;
  }

  .header-content {
    flex: 1;
    text-align: center;
  }

  .data-content {
    width: 30vw;
    margin: 1vw auto;
  }

  .data-title {
    padding-left: 0.5vw;
  }

  .data-title:before {
    content: '';
    width: 0.1vw;
    height: 0.9vw;
    background-color: rgba(40, 179, 252, 1);
    position: absolute;
    margin-top: 0.2vw;
    left: 2.1vw;
  }

  .data-content .data-table {
    margin-top: 1vw;
    height: 24.7vw;
    border-radius: 0.1vw;
    overflow: scroll;
  }

  .table {
    width: 29.6vw;
    min-height: 24.4vw;
    border: 0.0625vw solid rgba(71, 85, 102, 1);

  }

  .table .table-header {
    display: flex;
  }

  .table .table-header span {
    flex: 1;
    height: 1.8vw;
    line-height: 1.8vw;
    text-align: center;
    background-color: rgba(39, 48, 69, 1);
  }
  .table-header span:not(:last-child){
    border-right: 0.0625vw solid rgba(71, 85, 102, 1);
  }
    .table .table-list {
    width: 100%;
    display: flex;
  }

  .table-list ul {
    flex: 1;
    border-bottom: 0.0625vw solid rgba(71, 85, 102, 1);
  }

  .table-list  li {
    line-height: 1.8vw;
    text-align: center;
    border-top: 0.0625vw solid rgba(71, 85, 102, 1);
    height: 1.8vw;
  }
  .table-list ul:not(:last-child) {
    border-right: 0.0625vw solid rgba(71, 85, 102, 1);

  }

  .data-content >>> .data-table::-webkit-scrollbar {
    /*  */
    width: 0.2vw;
    height: 0.2vw;
  }

  .data-content >>> .data-table::-webkit-scrollbar-track-piece {
    /*  */
    background-color: #475566;
    border-radius: 0.2vw;
  }

  .data-content >>> .data-table::-webkit-scrollbar-thumb {
    /*  */
    border-radius: 0.2vw;
    background-color: #95a0b6;
  }

  .data-content >>> .data-table::-webkit-scrollbar-thumb:hover {
    background-color: #95a0b6;
  }

</style>
