// echarts
import 'echarts/lib/component/tooltip';
import 'echarts/lib/component/legend';
import 'echarts/lib/component/legendScroll';
import 'echarts/lib/component/dataZoom';
import 'echarts/lib/component/geo';
import 'echarts/lib/component/visualMap';
import 'echarts/lib/component/title';

/**/
const context = require.context('@/components/CanvasPlace', true, /\.vue$/);
const contextFilter = context
	.keys()
	.filter(file => file.indexOf('/components') === -1);
const componentList = {};
contextFilter.forEach(function(file) {
	let splitArr = file.split('/');
	let len = splitArr.length;
	let type = splitArr[len - 3];
	let name = splitArr.pop().replace(/\.vue$/, '');
	let component = `${type}-${name}`;
	componentList[component] = r =>
		require.ensure([], () => r(context(file).default), 'canvasPlace');
});
export  const asyncComponent={
	components:{
		...componentList,
	}
};
