<template>
  <div class="phone-box">
    <div class="safeAreaTop"></div>
    <div class="phoneContainer"
      ref="phoneContainer">
      <div class="phoneBody"
        :style="{
               backgroundImage: `url(${bgPhoneImg})`,
               backgroundColor: bgPhoneColor}"
      >
        <easy-scroll :barOption="reactiveScrollOpt"
          :wrapStyle="wrapStyle"
          ref="scroller"
          @scrolling="scrolling"
          :viewStyle="viewStyle">
          <div class="phone-content"
            :style="{
              height:mobilePagesNum*100+'%'
            }"
            @mousedown.self="screenScrollStart($event)"
            :class="{onScroll:onScroll}"
            @dragover="dragover($event)"
            @drop="dropChart($event)"
          >
            <!--            @mousemove="screenScrollMove($event)"-->
            <!--            @mouseup="screenScrollEnd($event)"-->
            <div class="scrollLayer"
              v-show="onScroll"></div>
            <template v-for="(item,index) in chartListFilter">
              <vue-draggable-resizable
                v-if="item.containerOptions.showInMobileLayout"
                ref="draggable"
                :data-key="index"
                :parent="true"
                :key="index"
                :prevent-deactivation="false"
                :x="Number(item.containerOptions.xPositionMobile)"
                :y="Number(item.containerOptions.yPositionMobile)"
                :w="getMobileWidth(item.containerOptions.tableWidthMobile)||1"
                :h="Number(item.containerOptions.tableHeightMobile) || 1"
                @activated="activated(item.id, item)"
                @resizing="(x, y,right,bottom, width, height) => resizing(x, y,right,bottom, width, height, item.id,item)"
                @resizestop="(x, y,right,bottom, width, height) => setSize(x, y, right,bottom,width, height, item.id, item)"
                @dragging="(left, top,right,bottom) => dragging(left, top,right,bottom,item.id,item)"
                @dragstop="(left, top,right,bottom) => setPosition(left, top,right,bottom, item.id, item)">
                <template v-for="(handle, index) in handles"
                  :slot="handle">
                  <span class="handle-span"
                    :key="index"></span>
                </template>
                <div class="dragging-amplifier"></div>
                <span class="closeBtn biFont icon-clear"
                  :style="{zIndex:item.containerOptions.level + 4 || 4}"
                  @click="hideChart(item.id)"></span>
                <component
                  v-bind="$attrs"
                  :chartIndex="index"
                  :is="item.containerOptions.chartsType"
                  :options="item.renderOptions"
                  :containerOptions="item.containerOptions"
                  :containerStatus="item.containerStatus"
                  :style="{ 'z-index': item.containerOptions.level + 3 || 3 }"
                  :tableId="item.containerOptions.tableId"
                  :isSetOptions="item.containerStatus.isSetOptions"
                  :isShowLoading="item.containerStatus.isShowLoading"
                  :editable="true">
                </component>
              </vue-draggable-resizable>
            </template>
          </div>
        </easy-scroll>
      </div>
    </div>
  </div>
</template>
<script>

import {mapGetters} from 'vuex';
import {asyncComponent} from './mixnin/asyncComponent';//
import {debounceTail} from '@/utils/utilsMethods';//echart

export default {
	name: 'Phone',
	mixins: [asyncComponent],
	data() {
		return {
			wrapStyle:{
				heightL:'100%'
			},
			mobilePagesNum: 2,
			onScroll: false,
			scrollTop: 0,
			screenScrollStartY: 0,
			viewStyle: {
				width: '100%',
				height: '100%'
			},
			maxBottom: 10,//
			phoneScreenWidth: 0,
			phoneScreenHeight: 0,
			listData: '',
			handles: ['tl', 'tm', 'tr', 'mr', 'br', 'bm', 'bl', 'ml'],
		};
	},

	computed: {
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',//
			chartList: 'getChartList',//
			chartListFilter: 'chartMobileFilter',
			currentPhoneIndex: 'getCurrentPhoneIndex',//
			isPhoneLayoutSaved: 'getPhoneLayoutSavedState',//
			canvasBgColor: 'getCanvasBgColor',//
			imgBase64: 'getCanvasBgImg',//
			bgPhoneColor: 'getCanvasBgPhoneColor',//
			bgPhoneImg: 'getCanvasBgPhoneImg',//
		}),
		// chartListFilter() {
		// 	return this.chartList.filter((item) => {
		// 		return item.containerOptions.showInMobileLayout && item.containerStatus.isSetOptions;
		// 	});
		// },
		...mapGetters('chartBus', {
			timeState: 'returnTime',
			chartDataList: 'returnData',//
			isChartSaveComplete: 'getSaveComplete',
		}),
		...mapGetters('main', ['getVw']),
		getMobileWidth() {
			return  (width)=> {
				return ~~((width * this.phoneScreenWidth)+0.5);
			};
		},
		reactiveScrollOpt() {
			return {
				autoHideMode: true,
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barWidth: this.getVw(4), //
				barMarginRight: 3, //px
				barOpacityMin: 3, //
				borderWidth: 2,
				zIndex: 99999
			};
		},
	},
	methods: {
		checkSize(element){
			element.checkSize();
		},
		updateParentSize(element){
			element.updateParentSize();
		},
	  scrolling(x, y) {//
		  this.scrollTop = y;
	  },
	  screenScrollStart(event) {//
		  event = event || window.event;
		  this.onScroll = true;
		  this.screenScrollStartY = event.clientY;
		  // this.screenScrollStartY=
	  },
	  screenScrollMove(event, vm) {
		  if (!vm.onScroll) return false;
		  event = event || window.event;
		  let target = event.target || event.srcElement;
		  if (target.getAttribute && target.getAttribute('class') && target.getAttribute('class').indexOf('scrollLayer') > -1) {
			  let distanceY = event.clientY - vm.screenScrollStartY;
			  vm.$refs['scroller'].scrollTo({y: vm.scrollTop - distanceY});
			  vm.screenScrollStartY = event.clientY;
		  }
	  },
	  screenScrollEnd(event, vm) {

		  // vm.screenScrollStartY = event.clientY;
		  vm.onScroll = false;
	  },
	  hideChart(index) {
		  this.$store.commit('canvasPlace/containerOptionSetter', {
			  key: 'showInMobileLayout',
			  value: false,
			  id: index
		  });
		  this.$store.commit('canvasPlace/resetMobileChartOptionsByIndex', index);
	  },
	  dragover(event) {
		  event = event || window.event;
		  event.preventDefault();
		  event.stopPropagation();
	  },
	  dropChart(event) {
		  event = event || window.event;
		  let yPositionDeviate = event.dataTransfer.getData('yPositionDeviate');
		  // console.log(yPositionDeviate);
		  // let yPosition=yPositionDeviate>0?yPositionDeviate:0;
		  let chartIndex = event.dataTransfer.getData('chartIndex');
		  let yPosition = this.scrollTop + event.offsetY - yPositionDeviate > 0 ? event.offsetY - yPositionDeviate : 0;
		  this.$store.commit('canvasPlace/containerOptionSetter', {
			  key: 'yPositionMobile',
			  value: yPosition,
			  id: chartIndex
		  });
		  this.$store.commit('canvasPlace/containerOptionSetter', {
			  key: 'showInMobileLayout',
			  value: true,
			  id: chartIndex
		  });
		  event.preventDefault();
		  // event.target.appendChild(document.getElementById(data));
		  // event.stopPropagation();
	  },
		activated(index, chart) {
			this.index = index;
			this.chart = chart;
			let vm = this;
			if (index !== vm.currentChartIndex) {
				let array = vm.chartList.map(ele => {
					return ele.containerOptions.level || 0;
				});
					// vm.$store.dispatch('canvasPlace/chartOnDeactivated');
				vm.$store.commit('canvasPlace/setCurrentChartIndex', index);
				vm.$store.commit('chartBus/setAmendChartData', {
					chart: chart,
					index: index
				});
				//indexDB
				let chartType = chart.containerOptions.chartsType.split('-')[0];
				if (chartType.indexOf('chart')>-1) {
					vm.$store.commit('dataDimension/setUpdateCount');
					// vm.dexieDB.aXisList.get(index).then(res => {
					// 	vm.$store.commit(
					// 		'dataDimension/setCurrentChartAxis',
					// 		res.axisOptions
					// 	);
					// });
				}
			}
		},
		resizing(x, y,right,bottom, width, height, index, item) {//
		},
		setSize(x, y, right,bottom,width, height, index, item) {//
			// this.$store.commit('canvasPlace/setChartOptionsAmendCount');
			// x = x.toFixed(4);
			// y = y.toFixed(4);
			x = ~~(x+0.5);
			y =  ~~(y+0.5);
			width = Number((width / this.phoneScreenWidth).toFixed(4));
			height = height.toFixed(4);
			let mobilePosition = {
				x,
				y
			};
			let mobileSize = {
				width,
				height
			};
			this.$store.commit('canvasPlace/setChartPosition', {mobilePosition});
			this.$store.commit('canvasPlace/setChartSize', {mobileSize});
			// if (item.containerStatus.isSetOptions === true) {
			// 	this.saveDataIndexDb(index);
			// }
		},
	  dragging(left, top,right,bottom, index, item) {//
	    let element = this.$refs.draggable.find(ele => ele.$vnode.key == index);
		  let layoutHeight = this.phoneScreenHeight * this.mobilePagesNum;
	    let chartBottom= Number(item.containerOptions.tableHeightMobile) + top;
		  if (layoutHeight - chartBottom <= 2){
		  this.mobilePagesNum += 1;
				// this.throttlecheckSize(element);
				this.throttleUpdateParentSize(element);
		  // element.updateParentSize(this.phoneScreenHeight * this.mobilePagesNum,this.phoneScreenHeight * this.mobilePagesNum-chartBottom);
		  // element.checkParentSize();

		  // element.updateParentSize(this.phoneScreenHeight * this.mobilePagesNum);
		  // element.updateBottom(this.phoneScreenHeight * this.mobilePagesNum-chartBottom);
		  // for (let i = 0; i < this.$refs['draggable'].length; i++) {
			//   const ele = this.$refs['draggable'][i];
			//   ele.updateParentSize(this.phoneScreenHeight * this.mobilePagesNum,this.phoneScreenHeight * this.mobilePagesNum-chartBottom);
		  // }
		  }
		  // this.mobilePagesNum=maxBottom/this.screenHeight>2?Math.ceil(maxBottom/this.screenHeight):2;
		},
		setPosition(x, y,right,bottom ,index, item) {//
			// let element = this.$refs.draggable.find(ele => ele.$vnode.key == index);
			// let layoutHeight = this.phoneScreenHeight * this.mobilePagesNum;
			// let chartBottom = Number(item.containerOptions.tableHeightMobile) + y;
			if (x === item.containerOptions.xPosition && y === item.containerOptions.yPosition) return false;
			x = ~~(x+0.5);
			y =  ~~(y+0.5);
			// x = x.toFixed(4);
			// y = y.toFixed(4);
			let mobilePosition = {
				x,
				y
			};
			this.$store.commit('canvasPlace/setChartPosition', {mobilePosition});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
			// if (layoutHeight - chartBottom < 2){
			// }
			// if (item && item.containerStatus.isSetOptions) {
			// 	this.saveDataIndexDb(index);
			// }
		},
		saveDataIndexDb(index) {

			let dataObj = {
				id: index,
				chartArray: this.chartDataList
			};
			let type=this.chartDataList.chart.containerOptions.chartType.split('-')[0];
			if (type==='echarts') {
				let dataObj = {
					id: index,
					chartArray: this.chartDataList
				};
					// this.dexieDB.saveOption.put(dataObj).then(function(res) {});
					// this.$store.commit('chartBus/setTimeSetting', true);
					// this.$store.commit('canvasPlace/setChartOptionsAmendCount');
				return false;
			}
			if (type==='text') {
				let dataObj = {
					id: index,
					textArray: this.chartDataList
				};
					// this.dexieDB.saveTextOption.put(dataObj).then(function(res) {});
					// this.$store.commit('chartBus/setTimeSetting', true);
					// this.$store.commit('canvasPlace/setChartOptionsAmendCount');
				return false;
			}
			if (type==='media') {
				let dataObj = {
					id: index,
					mediaArray: this.chartDataList
				};
					// this.dexieDB.saveMediaOption.put(dataObj).then(function(res) {});
					// this.$store.commit('chartBus/setTimeSetting', true);
					// this.$store.commit('canvasPlace/setChartOptionsAmendCount');
				return false;
			}
		},

	},
	watch: {
		currentPhoneIndex: {
			handler(val) {
				if (this.listData.length)
					this.listData.push(this.chartList[val]);
			}
		},
		getCanvasPhoneDomArr() {
			// console.log(this.getCanvasPhoneDomArr,'arr');
		}
	},
	created() {
		let vm = this;
		// document.addEventListener('mousedown',this.screenScrollStart);
		document.addEventListener('mousemove', (e) => this.screenScrollMove(e, vm));
		document.addEventListener('mouseup', (e) => this.screenScrollEnd(e, vm));
	},
	mounted() {
		let vm = this;
		// this.throttlecheckSize=throttle(vm.checkSize);
		this.throttleUpdateParentSize=debounceTail(vm.updateParentSize);
		this.$nextTick(() => {
			let {width, height} = this.$refs['phoneContainer'].getBoundingClientRect();
			this.$set(this, 'phoneScreenWidth', Number(width.toFixed(2)));
			this.$set(this, 'phoneScreenHeight', Number(height.toFixed(2)));

		});
	  if (this.isPhoneLayoutSaved) {
		  return true;
	  } else {
			// let maxBottom = 0;
		  for (let i = 0; i < this.chartListFilter.length; i++) {
			  const element = this.chartListFilter[i];
			  let index = element.id;
			  let mobilePosition = {y: this.maxBottom};
			  this.maxBottom += Number(element.containerOptions.tableHeightMobile);
			  this.$store.commit('canvasPlace/setMobileChartPosition', {index, mobilePosition});
				// this.$store.commit('canvasPlace/setChartSize', {index});
				// 	console.log(this.chartListFilter[i]);
		  }

	  }


	}
};

</script>

<style scoped>
  .phone-box {
    width: 21vw;
    min-width: 400px;
    height: 85vh;
    color: #fff;
    user-select: none;
    background-image: url('~@/assets/Visualization/img/iphone.png');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
  }

  .safeAreaTop {
    z-index: 99999;
    position: absolute;
    top: 2.2%;
    left: 50%;
    margin-left: -25%;
    width: 50%;
    height: 2.5%;
    background-size: 100% 100%;
    background-image: url('~@/assets/Visualization/img/safeAreaTop.png');
  }

  .phoneContainer {
    padding: 0 5.3% 0 5.2%;
    position: absolute;
    left: 0;
    top: 2.5%;
    width: 100%;
    height: 95.1%;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    overflow: hidden;
    border-radius: 14% 14% 14% 14%;
  }

  .phoneContainer .phoneBody {
    background-size: 100% 100%;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
  }

  .phone-content {
    top: 2.7%;
    position: relative;
    width: 100%;
    overflow: hidden;
    cursor: url("data:image/png;base64,"), auto;
  }

  .phone-content .scrollLayer {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 9999;
  }

  .phone-content /deep/ .container {
    border-color: transparent;
    overflow: hidden;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .phone-content /deep/ .draggable {
    position: relative;
    z-index: 1;
    cursor: auto;
    border-color: transparent;
  }

  .phone-content /deep/ .draggable .closeBtn {
    font-size: 12px;
    top: 0.4vw;
    right: 0.4vw;
    cursor: pointer;
    position: absolute;
    border-color: transparent;
  }


  .phone-content /deep/ .draggable .dragging-amplifier {
    z-index: 0;
    /*background: red;*/
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .phone-content /deep/ .draggable:hover .dragging-amplifier {
    position: absolute;
    left: -1vw;
    top: -1vw;
    width: calc(100% + 2vw);
    height: calc(100% + 2vw);
  }

  .wrapper /deep/ .draggable.resizing .dragging-amplifier {
    left: -1.5vw;
    top: -1.5vw;
    width: calc(100% + 3vw);
    height: calc(100% + 3vw);
  }

  .phone-content /deep/ .container .echarts {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  /*.phone-content.onScroll /deep/ .echarts {*/
  /*  cursor: url("data:image/png;base64,"), auto;*/
  /*}*/

  .phone-content /deep/ .handle {
    border: none;
    background-color: transparent;
    z-index: 999;
  }

  .phone-content /deep/ .handle-tl {
    transform-origin: 0 0;
    width: 0.6vw;
    height: 0.6vw;
    left: 0;
    top: 0;
  }

  .phone-content /deep/ .handle-tl span {
    transform-origin: 0.15vw 0.15vw;
    position: absolute;
    background-color: transparent;
    border-width: 0.15vw 0 0 0.15vw;
    border-style: solid;
    border-color: #28b3fc;
    width: 0.75vw;
    height: 0.75vw;
    left: -0.15vw;
    top: -0.15vw;
  }

  .phone-content /deep/ .handle-tm {
    transform-origin: 50% 0;
    top: 0;
    width: 1.5vw;
    height: 0.3vw;
    margin-left: 0;
    transform: translateX(-50%);
  }

  .phone-content /deep/ .handle-tm span {
    transform-origin: 50% 100%;
    position: absolute;
    width: 100%;
    height: 0.15vw;
    left: 0;
    top: -0.15vw;
    background-color: #28b3fc;
  }

  .phone-content /deep/ .handle-tr {
    transform-origin: 100% 0;
    width: 0.6vw;
    height: 0.6vw;
    top: 0;
    right: 0;
  }

  .phone-content /deep/ .handle-tr span {
    transform-origin: 0.6vw 0.15vw;
    position: absolute;
    border-width: 0.15vw 0.15vw 0 0;
    border-style: solid;
    border-color: #28b3fc;
    width: 0.75vw;
    height: 0.75vw;
    top: -0.15vw;
    left: 0;
  }

  .phone-content /deep/ .handle-mr {
    transform-origin: 100% 50%;
    width: 0.3vw;
    height: 1.5vw;
    margin-top: 0;
    right: 0;
    transform: translateY(-50%);
  }

  .phone-content /deep/ .handle-mr span {
    transform-origin: 100% 50%;
    width: 0.3vw;
    height: 100%;
    position: absolute;
    background-color: transparent;
    top: 0;
    right: -0.15vw;
    border-right-width: 0.15vw;
    border-right-color: #28b3fc;
    border-right-style: solid;
  }

  .phone-content /deep/ .handle-bm {
    transform-origin: 50% 100%;
    bottom: 0;
    width: 1.5vw;
    height: 0.3vw;
    margin-left: 0;
    transform: translateX(-50%);
  }

  .phone-content /deep/ .handle-bm span {
    position: absolute;
    transform-origin: 50% 100%;
    width: 100%;
    height: 0.45vw;
    background-color: transparent;
    bottom: -0.15vw;
    left: 0;
    border-bottom-width: 0.15vw;
    border-bottom-color: #28b3fc;
    border-bottom-style: solid;
  }

  .phone-content /deep/ .handle-bl {
    transform-origin: 0 100%;
    width: 0.6vw;
    height: 0.6vw;
    left: 0;
    bottom: 0;
  }

  .phone-content /deep/ .handle-bl span {
    transform-origin: 0.15vw 0.6vw;
    position: absolute;
    background-color: transparent;
    border-width: 0 0 0.15vw 0.15vw;
    border-style: solid;
    border-color: #28b3fc;
    width: 0.75vw;
    height: 0.75vw;
    left: -0.15vw;
    bottom: -0.15vw;
  }

  .phone-content /deep/ .handle-ml {
    transform-origin: 0 50%;
    width: 0.3vw;
    height: 1.5vw;
    margin-top: 0;
    left: 0;
    transform: translateY(-50%);
  }

  .phone-content /deep/ .handle-ml .handle-span {
    transform-origin: 0.15vw 50%;
    position: absolute;
    width: 0.15vw;
    height: 100%;
    background-color: #28b3fc;
    top: 0;
    left: -0.15vw;
  }

  .phone-content /deep/ .handle-br {
    transform-origin: 100% 100%;
    width: 0.6vw;
    height: 0.6vw;
    right: 0;
    bottom: 0;
  }

  .phone-content /deep/ .handle-br span {
    transform-origin: 0.6vw 0.6vw;
    position: absolute;
    border-width: 0 0.15vw 0.15vw 0;
    border-style: solid;
    border-color: #28b3fc;
    width: 0.75vw;
    height: 0.75vw;
    top: 0;
    left: 0;
  }

</style>
