<template>
  <div>
    <div class="biFont icon-hidden left"
      :class="{hiddenColor:axisShow}"
      :title="axisShow?'':''"
      @click="toggleAxisShow"
      :style="{
       width:axisMargin-1+'px',
       height:axisMargin+'px'}"></div>
    <canvas
      :class="{hidden:!axisShow}"
      :width="width"
      :height="height"
      ref="ruler"></canvas>
  </div>
</template>
<script>
export default {
	props: {
		width: Number,
		height: Number
	},
	data() {
		return {
			canvas: null,
			context: null,
			axisShow: true,
			bgColor: '#0e111a',
			axisMargin: 15,//x 1515*15
			negativeNum: 20,//()
			tickColor: 'grey',
			ticksLineLength: 5,
			ticksLineWidth: 0.5,
			textColor: 'grey',
			axisLineWidth: 0.5,
			axisColor: '#393f4c',
			cornerTickColor: 'grey',
			cornerTickSpacing: 3,//
			cornerTickWidth: 0.5,
			cornerTickLength: 2
		};
	},
	computed: {
		higherRankSpacing() {
			const obj = {
				1: 50,
				0.9: 60,
				0.8: 80,
				0.7: 120,
				0.6: 160,
				0.5: 200,
				0.4: 250,
				0.3: 300,
				0.2: 400,
				0.1: 500
			};
			return obj[this.scaleLevel];
		},
		scaleLevel() {//:0.3-1
			return this.$store.getters['canvasPlace/getScaleLevel'];
		},
		screenWidth() {//
			return this.$store.getters['main/getScreenWidth'];
		},
		screenHeight() {//
			return this.$store.getters['main/getScreenHeight'];
		},
		axisWidth() {//x
			return this.width - this.axisMargin;
		},
		axisHeigh() {//y
			return this.height - this.axisMargin;
		},
		axisOriginX() {//x
			return this.axisMargin;
		},
		axisOriginY() {//y
			return this.height;
		},
		xTicksNum() {//x
			return this.axisWidth / this.xTickSpacing;
		},
		yTicksNum() {//y
			return this.axisHeigh / this.yTickSpacing;
		},
		xTickSpacing() {//x
			return (this.higherRankSpacing / 5) * this.scaleLevel;
		},
		yTickSpacing() {//y
			return (this.higherRankSpacing / 5) * this.scaleLevel;
		},
		xTicksNegativeNum() {//x
			return this.negativeNum / this.xTickSpacing;
		},
		yTicksNegativeNum() {//y
			return this.negativeNum / this.yTickSpacing;
		}
	},
	mounted() {
		this.canvas = this.$refs.ruler;
		this.context = this.canvas.getContext('2d');
		this.drawAxes();
	},
	methods: {
		drawAxes() {
			let context = this.context;
			context.fillStyle = this.bgColor;
			context.fillRect(0, 0, this.width, this.height);
			context.strokeStyle = this.cornerTickColor;
			context.lineWidth = this.cornerTickWidth;
			this.$options.methods.drawCorner(this, context);
			context.strokeStyle = this.axisColor;
			context.lineWidth = this.axisLineWidth;
			this.$options.methods.drawXaxis(this, context);
			this.$options.methods.drawYaxis(this, context);
			context.lineWidth = this.ticksLineWidth;
			context.strokeStyle = this.tickColor;
			context.fillStyle = this.textColor;
			// this.$options.methods.drawXticksNegative(this, context);
			// this.$options.methods.drawYticksNegative(this, context);
			this.$options.methods.drawXticks(this, context);
			this.$options.methods.drawYticks(this, context);
			context.restore();
		},
		//
		toggleAxisShow() {
			this.axisShow = !this.axisShow;
		},
		drawCorner(_this, context) {
			let tickNum = _this.axisMargin / _this.cornerTickSpacing;
			context.beginPath();
			for (var i = 1; i < tickNum; i++) {
				context.moveTo(_this.axisMargin, i * _this.cornerTickSpacing);
				context.lineTo(
					_this.axisMargin - _this.cornerTickLength,
					i * _this.cornerTickSpacing
				);
				context.moveTo(i * _this.cornerTickSpacing, _this.axisMargin);
				context.lineTo(
					i * _this.cornerTickSpacing,
					_this.axisMargin - _this.cornerTickLength
				);
			}
			context.stroke();
		},
		drawXaxis(_this, context) {
			context.beginPath();
			context.moveTo(_this.axisOriginX, _this.axisMargin);
			context.lineTo(_this.width, _this.axisMargin);
			context.stroke();
		},
		drawYaxis(_this, context) {
			context.beginPath();
			context.moveTo(_this.axisOriginX, _this.axisMargin);
			context.lineTo(_this.axisOriginX, _this.axisOriginY);
			context.stroke();
		},
		drawXticks(_this, context) {
			var deltaX;
			let negativeNum = _this.negativeNum;
			context.beginPath();
			for (var i = 0; i < _this.xTicksNum; ++i) {
				if (i % 5 === 0) {
					deltaX = _this.ticksLineLength;
					context.moveTo(
						_this.axisMargin + _this.xTickSpacing * i + negativeNum,
						1
					);
					context.lineTo(
						_this.axisMargin + _this.xTickSpacing * i + negativeNum,
						10
					);
					context.textAlign = 'left';
					context.font = '10px Verdana';
					context.fillText(
						Math.ceil((i * _this.xTickSpacing) / _this.scaleLevel),
						_this.axisMargin + 5 + _this.xTickSpacing * i + negativeNum,
						10
					);
				} else {
					deltaX = _this.ticksLineLength / 2;
				}
				context.moveTo(
					_this.axisOriginX + i * _this.xTickSpacing + negativeNum,
					_this.axisMargin
				);
				context.lineTo(
					_this.axisOriginX + i * _this.xTickSpacing + negativeNum,
					_this.axisMargin - deltaX
				);
			}
			context.stroke();
		},
		drawYticks(_this, context) {
			var deltaY;
			let negativeNum = _this.negativeNum;
			context.beginPath();
			for (var i = 0; i < _this.yTicksNum; ++i) {
				if (i % 5 === 0) {
					deltaY = _this.ticksLineLenth;
					context.moveTo(
						_this.axisMargin,
						_this.axisMargin + _this.yTickSpacing * i + negativeNum
					);
					context.lineTo(
						1,
						_this.axisMargin + _this.yTickSpacing * i + negativeNum
					);
					context.textAlign = 'left';
					let j = 0;
					let number = Math.ceil(
						(i * _this.yTickSpacing) / _this.scaleLevel
					).toString();
					let len = number.length,
						textMargin = 10;
					while (j < len) {
						context.fillText(
							number[j],
							5,
							_this.axisMargin +
								15 +
								_this.yTickSpacing * i +
								textMargin * j +
								negativeNum
						);
						j++;
					}
				} else {
					deltaY = _this.ticksLineLength / 2;
					context.moveTo(
						_this.axisOriginX,
						i * _this.yTickSpacing + _this.axisMargin + negativeNum
					);
					context.lineTo(
						_this.axisOriginX - deltaY,
						i * _this.yTickSpacing + _this.axisMargin + negativeNum
					);
				}
			}
			context.stroke();
		},
		drawXticksNegative(_this, context) {
			var deltaX;
			let negativeNum = _this.negativeNum;
			context.beginPath();
			for (var i = 1; i < _this.xTicksNegativeNum; ++i) {
				if (i % 5 === 0) {
					deltaX = _this.ticksLineLength;
					context.moveTo(
						_this.axisMargin + negativeNum - _this.xTickSpacing * i,
						1
					);
					context.lineTo(
						_this.axisMargin + negativeNum - _this.xTickSpacing * i,
						10
					);
					context.textAlign = 'left';
					context.fillText(
						i * _this.xTickSpacing,
						_this.axisMargin + negativeNum - _this.xTickSpacing * i + 5,
						10
					);
				} else {
					deltaX = _this.ticksLineLength / 2;
				}
				context.moveTo(
					_this.axisOriginX + negativeNum - i * _this.xTickSpacing,
					_this.axisMargin
				);
				context.lineTo(
					_this.axisOriginX + negativeNum - i * _this.xTickSpacing,
					_this.axisMargin - deltaX
				);
			}
			context.stroke();
		},
		drawYticksNegative(_this, context) {
			var deltaY;
			let negativeNum = _this.negativeNum;
			context.beginPath();
			for (var i = 1; i < _this.yTicksNegativeNum; ++i) {
				if (i % 5 === 0) {
					deltaY = _this.ticksLineLenth;
					context.moveTo(
						_this.axisMargin,
						_this.axisMargin + negativeNum - _this.yTickSpacing * i
					);
					context.lineTo(
						1,
						_this.axisMargin + negativeNum - _this.yTickSpacing * i
					);
					context.textAlign = 'left';
					let j = 0;
					let number = (i * _this.yTickSpacing).toString();
					let len = number.length,
						textMargin = 10;
					while (j < len) {
						context.fillText(
							number[j],
							5,
							_this.axisMargin + 15 + _this.yTickSpacing * i + textMargin * j
						);
						j++;
					}
				} else {
					deltaY = _this.ticksLineLength / 2;
					context.moveTo(
						_this.axisOriginX,
						i * _this.yTickSpacing + _this.axisMargin
					);
					context.lineTo(
						_this.axisOriginX - deltaY,
						i * _this.yTickSpacing + _this.axisMargin
					);
				}
			}
			context.stroke();
		}
	},
	watch: {
		scaleLevel(scale) {
			let context = this.context;
			context.clearRect(this.axisMargin, 0, this.width, this.axisMargin + 1);
			context.clearRect(0, this.axisMargin, this.axisMargin + 1, this.height);
			context.strokeStyle = this.axisColor;
			context.lineWidth = this.axisLineWidth;
			this.drawXaxis(this, context);
			this.drawYaxis(this, context);
			context.lineWidth = this.ticksLineWidth;
			context.strokeStyle = this.tickColor;
			context.fillStyle = this.textColor;
			this.drawXticks(this, context);
			this.drawYticks(this, context);
			context.restore();
		},
		// screenWidth() {
		// 	let context = this.context;
		// 	context.clearRect(this.axisMargin, 0, this.width, this.axisMargin + 1);
		// 	context.clearRect(0, this.axisMargin, this.axisMargin + 1, this.height);
		// 	context.strokeStyle = this.axisColor;
		// 	context.lineWidth = this.axisLineWidth;
		// 	this.drawXaxis(this, context);
		// 	this.drawYaxis(this, context);
		// 	context.lineWidth = this.axisLineWidth;
		// 	this.drawXticks(this, context);
		// 	this.drawYticks(this, context);
		// },
		height(val) {
		  this.$nextTick(()=>{
				let context = this.context;
				context.clearRect(this.axisMargin, 0, this.width, this.axisMargin + 1);
				context.clearRect(0, this.axisMargin, this.axisMargin + 1, this.height);
				context.strokeStyle = this.axisColor;
				context.lineWidth = this.axisLineWidth;
				this.drawXaxis(this, context);
				this.drawYaxis(this, context);
				context.lineWidth = this.ticksLineWidth;
				context.strokeStyle = this.tickColor;
				context.fillStyle = this.textColor;
				this.drawXticks(this, context);
				this.drawYticks(this, context);
				context.restore();

			});
			setTimeout(() => {
			}, 500);
		}
	}
};
</script>
<style scoped>
  .left {
    font-size: 12px;
    color: #fff;
    position: absolute;
    cursor: pointer;
    left: 0;
    top: 0;
    text-align: center;
  }
  .hiddenColor{
    color:grey
  }
</style>
