<template>
  <div class="customize maskBackground">
    <div class="customContainer allCenter">
      <div class="title">
        <i class="icon biFont icon-customize"></i>
        <span></span>
      </div>
      <div class="content">
        <div class="customName">
          <span class="unSelect"></span>
          <input v-model="customName"
            type="text"></div>
        <div class="customEditor">
          <div class="formula"
            v-show="false">
            <div><i class="icon biFont icon-function"></i></div><input type="text">
          </div>
          <div class="table">
            <hot-table :settings="setHotTable"
              licenseKey="non-commercial-and-evaluation"
              ref="customizeHot"></hot-table>
          </div>
        </div>
        <div class="buttonDiv">
          <span class="submit mainBtn"
            @click="submit"></span>
          <span class="cancel mainBtn"
            @click="cancel"></span>
        </div>
      </div>
    </div>
<!--    <Tips ref="tips"-->
<!--      v-bind="{tipsData}"></Tips>-->
  </div>
</template>

<script>
// import Tips from '@/components/Tips/Tips.vue';
import { HotTable } from '@handsontable/vue';
import 'handsontable/dist/handsontable.css';
import 'handsontable/languages/zh-CN';

export default {
	name: 'customizeData',
	components: {HotTable },
	data() {
		return {
			url: '',
			token: localStorage.getItem('token'),
			customName: '',
			tipsData: {
				text: '',
				rel: '',
				time: 1500
			},
			setHotTable: {
				height: '46.5vh',
				rowHeaders: [1, 2, 3, 4, '+'], // 
				colHeaders: ['A', 'B', 'C', 'D', '+'], // or 
				stretchH: 'none', //last:none
				enterBeginsEditing: false,
				minSpareRows: 1,
				className: 'htLeft',
				currentColClassName: 'currentCol', // 
				manualColumnResize: true, //truefalse
				autoWrapRow: true, // 
				language: 'zh-CN'
			}
		};
	},
	methods: {
		submit() {
			if (this.customName == '') {
				this.tips.show(['']);
				// this.tipsData.text = '';
				// this.tipsData.rel = 'fail';
				// this.$refs.tips.closeTips();
				return;
			}
			let table = this.handsonTable.getData();
			// let customData = [];
			// for (let index = 0; index < table.length; index++) {
			// 	let item = table[index];
			// 	let data = [];
			// 	console.log(item);
			// 	for (let i = 0; i < item.length; i++) {
			// 		let element = item[i];
			// 		if (element != null) {
			// 			data.push(element);
			// 		}
			// 	}
			// 	if (data.length != 0) {
			// 		customData.push(data);
			// 	}
			// }
			console.log(table);
			let data = {
				dataInfo: JSON.stringify({
					tableName: this.customName,
					data: table
				}),
				action_type: '1'
			};
			this.$post('api/dataCustomize', data).then(
				response => {
					if (response.code == '000000') {
						this.tips.show(['',true]);
						// this.tipsData.rel = 'success';
						// this.tipsData.text = '';
						setTimeout(() => {
							this.$emit('customizeHide', { bool: false, success: '' });
						}, this.tipsData.time);
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
						// this.tipsData.rel = 'fail';
						// this.tipsData.text = response.err_msg.split(':')[1];
					}
					// this.$refs.tips.closeTips();
				},
				error => {
					console.log(error);
					this.tips.show([',']);
					// this.tipsData.rel = 'fail';
					// this.tipsData.text = ',';
				}
			);
		},
		cancel() {
			this.$emit('customizeHide', false);
		}
	},
	mounted() {
		let self = this;
		this.handsonTable = this.$refs.customizeHot.hotInstance;
		// ;
		this.handsonTable.addHook('afterOnCellMouseDown', function(event, coords) {
			let rowIndex = self.handsonTable.getRowHeader().length - 1;
			let colIndex = self.handsonTable.getColHeader().length - 1;
			if (coords.col == colIndex) {
				self.handsonTable.alter('insert_col', colIndex);
			} else if (coords.row == rowIndex) {
				self.handsonTable.alter('insert_row', colIndex);
				let rowHeader = self.handsonTable.getRowHeader();
				let num = rowHeader.length - 1;
				rowHeader.pop();
				rowHeader.pop();
				rowHeader.push(num, '+');
				self.setHotTable.rowHeaders = rowHeader;
			}
		});
	}
};
</script>
<style scoped >
.customize .customContainer {
  width: 47.7vw;
  height: 69.1vh;
  background-color: #19202e;
}

.customize .customContainer .title {
  height: 3.4vh;
  line-height: 3.4vh;
  font-size: 0.8vw;
  text-align: center;
  background-color: #273045;
}
.customize .customContainer .title i {
  font-size: 0.8vw;
  vertical-align: 0;
  margin-right: 0.2vw;
}

.customize .customContainer .customEditor {
  border-bottom: 0.0625vw #080b0f solid;
  border-top: 0.0625vw #080b0f solid;
}

.customize .customContainer .customName {
  height: 4.8vh;
  font-size: 0.8vw;
  line-height: 4.8vh;
  padding-left: 1.6vw;
  text-align: left;
}

.customize .customContainer .customName input {
  width: 9.1vw;
  height: 2.3vh;
  background-color: #121721;
  border: solid 0.0625vw #475566;
  margin-left: 0.5vw;
  color: #fff;
  padding-left: 0.2vw;
}

.customize .customContainer .customName input:focus,
.customize .customContainer .customEditor .formula input:focus {
  border: solid 0.0625vw #28b3fc;
}

.customize .customContainer .customEditor {
  padding: 1.2vh 2.3vw 2.9vh;
}

.customize .customContainer .customEditor .formula {
  display: flex;
  margin-bottom: 1.1vh;
  width: 43.2vw;
}

.customize .customContainer .customEditor .formula div {
  width: 1.5vw;
  height: 2.6vh;
  background-color: #273045;
  border: solid 0.0625vw #475566;
  border-right: none;
  display: inline-block;
  text-align: center;
  line-height: 2.3vh;
}

.customize .customContainer .customEditor .formula input {
  background-color: #121721;
  border: solid 0.0625vw #475566;
  vertical-align: top;
  height: 2.6vh;
  width: 100%;
  color: #fff;
  padding-left: 0.2vw;
}

.customize .customContainer .customEditor .table {
  width: 43.2vw;
  height: 46.5vh;
