}

.customize .customContainer .customEditor .table >>> th.ht__active_highlight {
  background-color: #184562 !important;
  border: solid 0.0625vw #28b3fc !important;
}
.customize .table >>> .ht_clone_left .wtSpreader .htCore .tbody tr:last-child {
