.customize .customContainer .customEditor .table >>> .handsontable th:hover {
  background-color: #184562 !important;
  border: solid 0.0625vw #28b3fc !important;
}

.customize .customContainer .customEditor .table >>> .handsontable td:hover {
  background-color: #1f2838;
}

.customize .customEditor .table >>> .htCore tr:first-child td:hover::after {
  content: "";
  color: #ffffff;
  position: absolute;
  z-index: 1;
  background-color: #28b3fc;
  font-size: 0.6vw;
  text-align: center;
  width: 3.6vw;
  height: 1.1vw;
  line-height: 1.1vw;
  white-space: nowrap;
  top: calc(100% + 0.1vw);
  margin: auto;
  left: 0;
  right: 0;
}

.customize .customEditor .table >>> .htCore tr:first-child td {
  overflow: visible;
  position: relative;
}

.customize .customEditor .table >>> .handsontable table.htCore {
  border-top: solid 0.0625vw #475566;
  border-right: solid 0.0625vw #475566;
}

.customize .customEditor .table >>> .handsontable td,
.customize .customEditor .table >>> .handsontable th {
  height: 3.1vh !important;
  line-height: 3.1vh;
  cursor: pointer;
  white-space: nowrap;
  background-clip: border-box;
  border: solid 0.0625vw #475566;
  border-right: solid 0.0625vw transparent;
  border-top: solid 0.0625vw transparent;
}

.customize .customEditor .table >>> .handsontable th {
  font-size: 0.7vw;
