<template>
  <div class="search-box">
    <input type="text"
      v-model.trim="searchVal"
      @blur="unfocused"
      @focus='focused(1)'
      :placeholder="paramsObject.placeholderDes"
      spellcheck="false"
      @click="inputReset"
      @keyup.down="down"
      @keyup.up="up"
      @keyup.enter="choiceOptions(isActiveLi)"
      :style="{width:`${paramsObject.width}`,height:`${paramsObject.height}`}">
    <span class="biFont icon-search search vCenter" @click='focused(0)'></span>
    <div class="select-box"
      v-show="isShowSelect && paramsObject.isShowPanel">
      <easy-scroll :barOption="opt">
        <ul v-if="multipleArr.length" style="height:8vw">
            <li v-for="(options,index) in multipleArr"
              :key="options.id"
              @click="choiceOptions(index)"
              :class="{heightLightLine:isActiveLi==index}">{{options.name}}</li>
        </ul>
        <div class="none-data"
          v-else-if="isShowSelect && paramsObject.isShowPanel">
          <p></p>
        </div>
      </easy-scroll>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';

export default {
	name: 'searchChild',

	props: {
		searchObject: {
			type: Object,
			required: true
		}
	},
	data() {
		return {
			searchVal: '',
			isShowSelect: false,
			isActiveLi: -1,
			recordChoiceLi: false,
			boxHeight: '1vw',
			toChildArr: [],
			paramsObject: {
				isClearInput: false,
				placeholderDes: '',
				isShowPanel: true,
				beQueriedList: [],
				width: '10.2vw',
				height: '1.2vw'
			}
		};
	},
	methods: {
		down() {
			if (this.isActiveLi < this.multipleArr.length - 1) {
				this.isActiveLi += 1;
			} else {
				this.isActiveLi = 0;
			}
		},
		up() {
			if (this.isActiveLi == 0) {
				this.isActiveLi = this.multipleArr.length - 1;
			} else {
				this.isActiveLi -= 1;
			}
		},
		hideResult() {
			console.log('aaa');
			this.isShowSelect = false;
		},
		inputReset() {
			if (this.searchVal) {
				this.isShowSelect = true;
				this.$emit('inputReset');
			}
		},
		choiceOptions(index, text) {
			if (index > -1 && this.multipleArr.length) {
				this.searchVal = text;
				this.recordChoiceLi = true;
				this.isActiveLi = -1;
			}
			this.isShowSelect = false;
		},
		toChild(arr) {
			if (arr.length) {
				this.$emit('fromChild', arr);
			}
		},
		search() {
			let vm = this;
			vm.toChildArr = [];
			vm.toChildArr = vm.searchObject.beQueriedList.filter(
				e => e.name.search(vm.searchVal) != -1
			);
			vm.toChild(vm.toChildArr);
		},
		unfocused() {
			this.isShowSelect= false;
		},
	  focused(val){
			this.$emit('focused',val);
		},
	},
	mounted() {
		this.paramsObject = Object.assign(this.paramsObject, this.searchObject);
		this.isShowSelect = false;
	},
	computed: {
		multipleArr: function () {
			let vm = this;
			let returnArr = [];
			returnArr = vm.toChildArr.filter(e => e.name.search(vm.searchVal) != -1);
			if (vm.paramsObject.isShowPanel) {
				vm.boxHeight = `${returnArr.length}vw`;
				return returnArr;
			} else {
				vm.isShowSelect = false;
				return [];
			}
		},
		...mapGetters('main', ['getVw']),
		opt(){
		   return {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barWidth: this.getVw(4), //
				barMarginRight: 3, //px
				barOpacityMin: 0.1, //
				borderWidth:2
			};
		},

		isClear: function() {
			return this.searchObject.isClearInput;
		}
	},
	watch: {
		//
		isClear: {
			immediate: true,
			handler(newVal, oldVal) {
				if (newVal) {
					this.searchVal = '';
				}
			}
		},
		searchVal: function (newVal, oldVal) {
			this.search();
			this.$emit('inputReset',newVal);
			if (newVal) {
				this.isShowSelect=!this.recordChoiceLi;
	  } else {
				this.$emit('fromChild', []);
		  this.$emit('inputReset',newVal);
				this.isShowSelect = false;
				this.recordChoiceLi = false;
			}
		}
	}
};
</script>
<style scoped>
  .search-box {
    display: absolute;
    flex-direction: column;
    align-items: center;
    position: relative;
  }

  .search-box input {
    background-color: #11151f;
    outline: none;
    padding: 0.01vw 0.2vw;
    color: #fff;
    font-size: 0.4vw;
    border: solid 0.0625vw #384564;
  }

  input::-webkit-input-placeholder {
    font-size: 0.6vw;
    color: #798194;
  }

  input::-moz-input-placeholder {
    font-size: 0.6vw;
    color: #798194;
  }

  input::-ms-input-placeholder {
    font-size: 0.6vw;
    color: #798194;
  }

  .search-box .search {
    font-size: 0.4vw;
    position: absolute;
    right: 5%;
    color: #99a4bb;
    cursor: pointer;
  }

  input[type="text"]:focus {
    border-color: #00b9ff;
  }
  .search-box .select-box {
    width: 100%;
    background-color: #11151f;
    border: solid 0.0625vw #475566;
    border-top: none;
    font-size: 0.8vw;
    color: #ffffff;
    /* overflow: auto; */
    position: absolute;
    /*margin-top: 1.2vw;*/
    z-index: 21;
  }

  .search-box .select-box ul {
    list-style: none;
    font-size: 0.6vw;
    text-align: left;
    padding: 0;
    margin: 0;
    color: #ffffff;
  }

  .search-box .select-box ul > li {
    line-height: 0.8vw;
    padding: 0.2vw;
    cursor: pointer;
  }

  .search-box .select-box ul > li:hover {
    background-color: #252e42;
  }

  .search-box .select-box ul > li.heightLight {
    background-color: #252e42;
  }

  .search-box .select-box .none-data {
    text-align: center;
    transform: scale(0.7);

  }
</style>
