export default {
	namespaced: true,
	state: {
		// 
		data: '',
		// 
		dataIndex: '',
		oldChartData: '',
		// 
		timeClear: false,
		// 
		routerJump: false,
		resultData: false,
		routerClick: true, //
		isChartSaveCompleteCount: 0,
		pictureChoice: {
			base64: '',
			width: 200,
			height: 200,
			background: 'rgba(255,153,0,1)',
			borderRadius: 5,
		}
	},
	getters: {
		// 
		returnData: state => state.data,
		// console.group('vuex',state.data)
		// 
		returnDataIndex: state => state.dataIndex,
		//  
		returnTime: state => state.timeClear,
		getResultData: state => state.resultData,
		returnRouterState: state => state.routerClick,
		getSaveComplete: state => state.isChartSaveCompleteCount,
		returnPicture: state => state.pictureChoice
	},
	mutations: {
		setSaveComplete(state) {
			state.isChartSaveCompleteCount++;
		},
		setAmendChartData: (state, obj) => {//
			state.data = obj;
			state.dataIndex = obj.index;
		},
		setTimeSetting: (state, time) => {
			state.timeClear = time;
		},
		// false
		setFalse: (state, time) => {
			state.resultData = time;
		},
		setRouter: (state, click) => {
			state.routerClick = click;
		},
	}
};
