<template>
  <div>
    <Loading v-if="dataIsOk"
      :style={...nonTransform}
      :styleObj="styleObj"></Loading>
    <div class="maskBackground"
      v-show="isShowPVM">
      <div class="share-box allCenter2">
        <div class="title">
          <span class="biFont icon-embed"></span>
          <span></span>
          <span class="biFont icon-clear close-share"
            @click="cancalPreView"></span>
        </div>
        <div class="content">
          <div class="link-content">
            <div class="link-flex">
              <span></span>
              <input type="text"
                v-model="preViewLink"
                ref="inputFocus"
                spellcheck="false"
                readonly
                v-clipboard="preViewLink"
                @success="handleSuccess"
                @error="handleError">
              <span class="copy-link"
                v-clipboard="preViewLink"
                @success="handleSuccess"
                @error="handleError"></span>
            </div>
            <!-- <div class="link-flex">
            <span></span>
            <input type="textArea"
              readonly>
            <span class="copy-link"></span>
          </div> -->
            <div class="link-flex">
              <span></span>
              <div class="radiobox">
                <div class="item"
                  v-for="(r,rIndex) in pickList"
                  :key="rIndex"
                  @click="getRadioValue(rIndex,r)">
                  <div class="exCircle">
                    <div class="inCircle allCenter"
                      v-show="rIndex==isIndex"
                      :style="inCirclesStyle"></div>
                  </div>
                  <span>{{r.name}}</span>
                </div>
              </div>
              <span></span>
            </div>
          </div>
          <button @click="publish"></button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import Loading from '@/components/Loading/LoadingRotating.vue';
export default {
	name: 'Modal',
	components: {
		Loading
	},
	data() {
		return {
			isIndex: 0,
			isPick: 0,
			preViewLink: '',
			share_sn: {},
			isShowPVM: false,
			copyBtn: null, //
			pickList: [
				{
					id: 0,
					name: ''
				},
				{
					id: 1,
					name: '7'
				},
				{
					id: 2,
					name: '1'
				}
			],
			dataIsOk: false,
			styleObj: {
				width: '100vw',
				height: '100vh'
			}
		};
	},
	computed: {
		...mapGetters('main', ['getVw']),
		...mapGetters('config', {
			baseUrl: 'baseUrl'
		}),
		nonTransform() {
			return { transform: `scale(${1 / this.scalelevel})` };
		},
		...mapGetters('canvasPlace', {
			shareType: 'getShareType',
			tableId: 'getCurrentTableId',
			scalelevel: 'getScaleLevel'
		}),
		inCirclesStyle() {
			return {
				width: parseInt(this.getVw(9)) + 'px',
				height: parseInt(this.getVw(9)) + 'px'
			};
		},
		joinTableId() {
			if (this.shareType == 2) {
				return this.tableId;
			} else {
				return '';
			}
		},
		validTime() {
			if (this.isPick == 1) {
				return 7;
			} else if (this.isPick == 2) {
				return 1;
			} else {
				return '';
			}
		}
	},
	methods: {
		/**
		 * 
		 * /7/1
		 *  */
		getRadioValue(i, r) {
			const vm = this;
			vm.isPick = r.id;
			vm.isIndex = i;
			vm.dataIsOk = true;
			if (vm.shareType != 0) {
				const shareObj = {
					share_sn: vm.share_sn,
					valid_time: vm.validTime
				};
				vm.$put('api/shareUnique', shareObj)
					.then(res => {
						if (res.code == '000000') {
							vm.dataIsOk = false;
						} else {
							vm.tips.show(['']);
						}
					})
					.catch(error => console.error(error));
			}
		},
		/**
		 * 
		 * 
		 * */
		getShareLinkMsg() {
			let vm = this;
			vm.dataIsOk = true;
			const shareObj = {
				project_sn: sessionStorage.getItem('projectId'),// 
				table_sn: vm.joinTableId,// 
				share_type: vm.shareType,//
				is_pwd: 0,//
				valid_time: vm.validTime//
			};
			vm.$get('api/shareUnique', shareObj)
				.then(res => {
					if (res.code == '000000') {
						/** */
						let data = res.data;
						vm.share_sn = data.share_sn;
						const address = `${window.location.host}/previewPages?share_sn=${data.share_sn}&password=${data.password}`;
						vm.preViewLink = address;
						vm.isShowPVM = true;
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
					vm.dataIsOk = false;
				})
				.catch(error => {
					console.error(error);
					// vm.tips.show(['']);
				});
		},
		/***/
		handleSuccess(e) {
			console.log(1212121);
			this.tips.show(['', true]);
		},
		handleError(e) {},
		cancalPreView() {
			this.isShowPVM = false;
			this.preViewLink = '';
			this.isIndex = 0;
			this.isPick = 0;
			this.$store.commit('canvasPlace/setStateProperty', {key:'shareType',value:0});
		},
		publish() {
			this.isShowPVM = false;
			this.isIndex = 0;
			this.isPick = 0;
			window.open(window.location.protocol+'//'+ this.preViewLink, '_blank');
			this.preViewLink = '';
			this.$store.commit('canvasPlace/setStateProperty', {key:'shareType',value:0});
		}
	},
	mounted() {
		this.isIndex = 0;
		this.isPick = 0;
	},
	watch: {
		/**
		 * vuexshareType
		 * !=0modal
		 */
		shareType: {
			immediate: true,
			handler(val) {
				if (val != 0) {
					//
					this.getShareLinkMsg();
					// this.isShowPVM = true;
				} else {
					this.isShowPVM = false;
				}
			}
		}
	}
};
</script>
<style scoped>
.share-box {
  background-color: #19202e;
  text-align: center;
  position: fixed;
  width: 31.4vw;
}

.share-box .title {
  height: 3.3vh;
  width: 100%;
  background-color: #273045;
  line-height: 3.3vh;
  font-size: 0.8vw;
}
.share-box .title .close-share {
  float: right;
  font-size: 0.6vw;
  margin-right: 1vw;
  cursor: pointer;
}
.share-box .title .close-share:hover {
  color: #28b3fc;
}

.content {
  padding: 2.5vw 2vw 0.5vw 2vw;
}

.content .link-flex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.8vw;
}
.content .link-flex span {
  display: block;
  font-size: 0.7vw;
  user-select: none;
}
.content .link-flex input {
  color: #fff;
  background-color: #121721;
  border: solid 0.0625vw #384564;
  width: 80%;
  outline: none;
  height: 2.9vh;
  padding: 0vw 0.5vw;
  /* user-select: textArea; */
}
.content .link-flex input:hover {
  cursor: pointer;
  border-color: #00b9ff;
}

.content .link-flex input[type="text"]:focus {
  border-color: #00b9ff;
}
.content .link-flex .copy-link {
  color: #28b3fc;
  cursor: pointer;
}
.link-flex .radiobox {
  width: 80%;
  height: 1vw;
  position: relative;
}
.link-flex .radiobox .item {
  width: auto;
  float: left;
  text-align: center;
  height: 0.8vw;
  line-height: 0.8vw;
}
.link-flex .radiobox .item span {
  display: inline-block;
  width: auto;
  white-space: nowrap;
  margin-left: 0.2vw;
}

.link-flex .radiobox .exCircle {
  float: left;
  width: 0.78vw;
  height: 0.78vw;
  border-radius: 50%;
  border: 0.0625vw solid #ffffff;
  margin-left: 1.5vw;
  cursor: pointer;
  position: relative;
}
.link-flex .radiobox .exCircle:first-child {
  margin-left: 0.9vw;
}

.link-flex .radiobox .exCircle .inCircle {
  position: absolute;
  border-radius: 50%;
  background-color: #28b3fc;
}
.link-flex .radiobox .exCircle span {
  display: block;
  margin-left: 1vw;
  line-height: 0.6vw;
  cursor: pointer;
}
.share-box button {
  background-color: #273045;
  width: 3.6vw;
  height: 1.4vw;
  line-height: 1.275vw;
  text-align: center;
  font-size: 0.6vw;
  cursor: pointer;
  outline: none;
  color: #fff;
  border: solid 0.0625vw #273045;
  float: right;
  /* margin-top: 1vw; */
  margin-bottom: 1vw;
}

.share-box button:hover {
  background-color: #1c3d57;
  border-color: #28b3fc;
}
</style>


