<template>
  <div class="loading maskBackground"
    v-show="loadingShow"
    :style="styleObj">
    <div class="loadingContent allCenter">
      <div class="loadingLogo aCenter">loading...</div>
      <div class="loadingCon">
        <div class="loadingShow"
          ref="loadingShow"
          :style="{width: plsStyleWidth}"></div>
      </div>
      <div class="loadingNum aCenter"
        ref="loadingNum">{{loadingNum}}</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
// 
// styleObjloading100%
// dataIsOk dataIsOkfalse
export default {
	name: 'loadingProgress',
	props: {
		dataIsOk: { type: Boolean, default: false },
		styleObj: {
			width: {
				type: String,
				default: '100%'
			},
			height: {
				type: String,
				default: '100%'
			}
		}
	},
	data() {
		return {
			loadingShow: true,
			loadingWidth: 0,
			pageLoad: false,
			loadingTimer: null
		};
	},
	watch: {
		// dataIsOk100%
		dataIsOk(val) {
			console.log(val);
			if (val) {
				clearInterval(this.loadingTimer);
				this.loadingWidth = 10;
				this.complete();
			} else {
				this.loadingWidth = 0;
				this.loadingShow = true;
				this.initLoadingTimer();
			}
		},
		// loadingWidth
		loadingWidth(val) {
			if (val >= 9.5) {
				clearInterval(this.loadingTimer);
				this.loadingTimer = setInterval(() => {
					this.loadingWidth += 0.01;
				}, 100);
			}
			if (val >= 9.89) { //99%
				clearInterval(this.loadingTimer);
				return false;
			}
		}
	},
	mounted() {
		this.initLoadingTimer();
		console.log(this.dataIsOk);
	},
	methods: {
		complete() {
			this.loadingShow = false;
		},
		initLoadingTimer() {
			this.loadingTimer = setInterval(() => {
				this.loadingWidth += 0.5;
			}, 100);
		}
	},
	computed: {
		plsStyleWidth() {
			return this.loadingWidth * 10 + '%';
		},
		loadingNum() {
			let percentage = (this.loadingWidth * 10).toFixed(2);
			return percentage + '%';
		}
	}
};
</script>

<style scoped>
.loading {
  bottom: 0;
  right: 0;
  margin: auto;
}

.loadingContent {
  width: 12vw;
  height: 15vh;
  text-align: center;
}

.loadingLogo {
  top: -1.5vw;
  color: #7ad1ff;
  font-size: 0.5vw;
  zoom: 0.9;
  letter-spacing: 0.1vw;
}

.loadingCon {
  width: 12vw;
  height: 0.2vh;
  background-color: #1e1f1f;
  border-radius: 0.2vh;
}

.loadingShow {
  transition: width 0.5s;
  height: 0.2vh;
  border-radius: 0.2vh;
  background-color: #28b3fc;
  position: absolute;
  left: 0;
}
.loadingNum {
  color: #7ad1ff;
  margin-top: 1.5vh;
  font-size: 0.5vw;
  zoom: 0.9;
}
</style>
