export function interactiveDataFill(item) {
	let type=item.containerOptions.chartsType.split('-')[1];
	return (interactiveDataFillSet[type]&&interactiveDataFillSet[type](item))||item;
}

let interactiveDataFillSet = {
	dataFilter(item) {
		switch (item.renderOptions.screenType) {
		case 'time':
			item.containerOptions.minWidth = 14;
			item.containerOptions.minHeight = 6;
			item.containerOptions.minWidthUnitunit = 'vw';
			item.containerOptions.minHeightUnit = 'vw';
			break;
		case 'text':
			item.containerOptions.minWidth = 10;
			item.containerOptions.minHeight = 8;
			item.containerOptions.minWidthUnitunit = 'vw';
			item.containerOptions.minHeightUnit = 'vw';
			break;
		case 'number':
			item.containerOptions.minWidth = 14;
			item.containerOptions.minHeight = 6;
			item.containerOptions.minWidthUnitunit = 'vw';
			item.containerOptions.minHeightUnit = 'vw';
			break;
		}
		return item;
	},
};

