<template>
  <div>
    <div class="chart-basis"
      @click="isShowSetting = !isShowSetting">
      <span></span>
      <span class="biFont icon-arrowLeft icon-fontsize"
            :class="[isShowSetting?'leftRotate-90':'leftRotate-0']"></span>
    </div>
    <!--    v-show="isShowSetting"-->
    <div class="content-box" v-show="isShowSetting">
      <ul>
        <li class="optionsItem">
          <select-color :defaultColor="circleColor"
            @changeColor="circleColorBright"></select-color>
        </li>
        <li class="optionsItem">
          <select-color :defaultColor="grayColor"
            @changeColor="circleColorGray"></select-color>
        </li>
        <li class="optionsItem">
          <select-color :defaultColor="valueColor"
            @changeColor="numberColor"></select-color>
        </li>
        <li class="optionsItem">
          <span class="title"></span>
          <slider-data :opt="valueOpt"
            :min="1"
            @rangedata="rangeNumberSize"
            @inputData="rangeNumberSize"></slider-data>
        </li>
        <li class="optionsItem">
          <select-color :defaultColor="titleColor"
            @changeColor="circleTitleColor"></select-color>
        </li>
        <li class="optionsItem">
          <span class="title"></span>
          <slider-data :opt="titleOpt"
            @rangedata="rangeTitleSize"
            @inputData="rangeTitleSize"></slider-data>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import ColorSelect from './BasicComponent/ColorSetting';
import SliderData from './BasicComponent/Slider';
import { getRgba} from '@/utils/optionSetting';
import {mapGetters, mapMutations} from 'vuex';

export default {
	name: 'ProportionSetting',
	data() {
		return {
			isShowSetting: true,
			valueColor: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			circleColor: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			grayColor: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			titleColor: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			valueOpt: {
				//grid
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			titleOpt: {
				//grid
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
		};
	},
	components: {
		'select-color': ColorSelect,
		'slider-data': SliderData
	},
	computed: {
		...mapGetters('chartBus', {
			textReceive: 'returnData',
			chartIndex: 'returnDataIndex'
		}),
		...mapGetters('canvasPlace', {
			currentContainerOptions:'getCurrentContainerOptions',
			currentRenderOptions: 'getCurrentChartRenderOptions'
		})
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter'
		}),
		// 
		saveDataIndexDb({name, pare}) {
			let vm = this;
			vm.renderOptionSetter({
				key: name,
				id: vm.chartIndex,
				value: pare
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		},
		circleColorBright(data) {
			let rgba = getRgba(data.rgba);
			this.saveDataIndexDb({
				name: 'style.fillColor',
				pare: rgba
			});
		},
		circleColorGray(data) {
			let rgba = getRgba(data.rgba);
			this.saveDataIndexDb({
				name: 'style.emptyColor',
				pare: rgba
			});
		},
		numberColor(data) {
			let rgba = getRgba(data.rgba);
			this.saveDataIndexDb({
				name: 'style.valueFontColor',
				pare: rgba
			});
		},
		circleTitleColor(data) {
			let rgba = getRgba(data.rgba);
			this.saveDataIndexDb({
				name: 'style.titleFontColor',
				pare: rgba
			});
		},
		rangeNumberSize(data) {
			if (this.valueOpt.valueRange !== data) {
				this.valueOpt.valueRange=data;
				this.saveDataIndexDb({
					name: 'style.valueFontSize',
					pare: data
				});
			}
		},
		rangeTitleSize(data) {
		  console.log(data);
			if (this.titleOpt.valueRange !== data) {
				this.saveDataIndexDb({
					name: 'style.titleFontSize',
					pare: data
				});
				this.titleOpt.valueRange=data;
			}
		},
	},
	watch:{
		currentRenderOptions:{
			immediate:true,
			deep:true,
			handler(val) {
				let options=val;
				this.valueOpt.valueRange=options.style.valueFontSize;
				this.titleOpt.valueRange=options.style.titleFontSize;
				this.valueColor.rgba=options.style.valueFontColor;
				this.titleColor.rgba=options.style.titleFontColor;
				this.circleColor.rgba=options.style.fillColor;
				this.grayColor.rgba=options.style.emptyColor;
			}
		}
	}

};
</script>

<style scoped>
  .optionsItem {
    margin-bottom: 0.8vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .chart-basis .rotateIcon {
    transform: rotate(-90deg);
    /* transition: 0.5s; */
  }

  .chart-basis .rotateIconUp {
    /* transform: rotate(-90deg); */
    transition: 0.5s;
  }

  .optionsItem .title {
    display: block;
    flex: 1;
    color: #acbbda;
  }
</style>
