import Vue from 'vue';
import {echartsCommonContainer} from '@/store/commonData/chartOptions';
export default {
	namespaced: true,
	state: {
		media: {
			'text-textArea': {
				dataDimension: {},
				containerOptions: {
				  ...echartsCommonContainer,
					tableWidth: 296,
					tableHeight: 60,
					chartsType: 'text-textArea',
					layerName: '',
					isShowMockData: true,
					layerChartIcon: '#icon-textArea'
				},
				renderOptions: {
					shadow: {
						width: 10,
						color: 'rgba(255,0,0,1)',
					},
					textContent: '',
					borderStyle: {
						lineWidth: 0,
						lineColor: 'rgba(63,173,255,1)',
						lineType: 'solid',
						borderRadiusWidth: 0,
					},
					textStyle: {
						align: 'center',
						fontSize: 30,
						color: 'rgba(255,255,255,1)',
						indent: 0,
						letterSpacing: 2,
						verticalAlign: 'center',
						fontWeight: 'bold',
						fontStyle: 'unset',
						decoration: 'unset',
						lineHeight: 20,
						textshadow: 4
					},
					backgroundColor: 'rgba(255,255,255,0)',
					// topShow: 10,
					// rightShow: 10,
					// leftShadow: 10,
					// bottomShadow: 10,
					// blur: 5,
					// boxShadowColor: 'rgba(255,0,0,1)',
					// textDecorationUn: '',
					// textDecorationOver: '',
				}
			},
			'media-image': {
				dataDimension: {},
				containerOptions: {
				  ...echartsCommonContainer,
					tableWidth: 400,
					tableHeight: 320,
					chartsType: 'media-image',
					layerName: '',
					isShowMockData: true,
					layerChartIcon: '#icon-image'
				},
				renderOptions: {
					backgroundColor: '',
					backgroundImage: '',
					shadow: {
						width: 10,
						color: 'rgba(255,0,0,1)',
					},
					borderStyle: {
						lineWidth: 4,
						lineColor: 'rgba(255,153,0,1)',
						lineType: 'solid',
						borderRadiusWidth: 0,
					},
					width: 200,
					height: 200,
					// innerStrokes: 4,
					// innerColor: 'rgba(255,153,0,1)',
					// innerType: 'dashed',
				}
			}
		},
	},
	getters: {
		getMedia: state => chartType => state.media[chartType],
		getMediaOptions: state => chartType => state.media[chartType].renderOptions,
		getMediaDefault: state => chartType => state.media[chartType],
	},
	mutations: {
		sendMediaOptions(state, params) {
			const containerparams = params['mediaData']['containerOptions'];
			let renderOptions, sendMediaObject;
			let type=containerparams.chartsType;
			sendMediaObject = {
				projectId: sessionStorage.getItem('projectId'),
				chartsType: containerparams.chartsType,
				layerName: containerparams.layerName,
				tableId: containerparams.tableId,
				xPosition: containerparams.xPosition, //x
				yPosition: containerparams.yPosition,
				tableWidth: containerparams.tableWidth, //
				tableHeight: containerparams.tableHeight,
				level: containerparams.level,
				series: [{
					seriesName: '',
					seriesType: ''
				}],
			};
			if (params.operationType == 'create') {
				sendMediaObject.renderOptions= JSON.stringify(this.getters['defaultMediaOptions/getMediaOptions'](type)),
				Vue.prototype.$post('api/v2/chart', sendMediaObject).then(res => {
					// state.media['media-image']['renderOptions'].backgroundImage = '';//
					// state.media['media-image']['renderOptions'].backgroundColor = '';//
					if (res.code == '000000') {
						this.commit('canvasPlace/setCurrentTableId', {index: params['index'], tableId: res.data.tableId,flag:1});

					} else {
						this.tips.show([res.err_msg.split(':')[1]]);
					}
				}).catch(error => {
					console.error(error);
					this.tips.show(['']);
				});
			} else {
				sendMediaObject.renderOptions=JSON.stringify(params.renderOptions);
				Vue.prototype.$put('api/v2/chart', [sendMediaObject]).then(() => {
				}).catch(error => console.error(error));
			}
		}
	}
};
