<template>
  <div class="IoTData maskBackground">
    <div class="content allCenter"
      v-show="IoTDataShow">
      <div class="nav">IoT
        <span class="biFont icon-clear closeBtn"
          @click="IoTDataBack"></span>
      </div>
      <div class="tableName">
        <span class="title">
          IoT
        </span>
        <input type="text"
          v-model="tableName">
      </div>
      <div class="fieldInputSection">
        <ul>
          <li v-for="(field,index) in fieldList"
            :key="index">
            <span class="title">
              {{field.name}}
            </span>
            <input type="text"
              class="name"
              v-model="field.field"
              placeholder="">
            <PublicSelector class="selector"
              @onSelected="getOptions(index,...arguments)"
              :opt="{
                showTitle:false,
                showAllSelectName:false,
                allSelectName:''
              }"
              :resetAllSelect='field.reset'
              :selectStyle="{height:'1.6vw'}"
              :inputStyle="inputStyle"
              :selectOptions='selectOptions'></PublicSelector>
            <input type="text"
              v-model="field.length"
              @input="filterNAN($event,index)"
              @blur="checkMaxLength(index,field.maxLength)"
              class="length"
              placeholder=""
              v-show="field.type==='char'||field.type==='varchar'">
          </li>
        </ul>
        <span class="addField"></span>
      </div>
      <div class="footer">
        <div class="nextStep"
          @click="nextStep"></div>
      </div>
    </div>
    <div class="serviceInfo allCenter2"
      v-show="serviceShow">
      <div class="nav">
        <span class="biFont icon-clear closeBtn"
          @click="closeSeriveInfo"></span>
      </div>
      <div class="contentSection">
        <div class="intro">
          <p class="text">json</p>
          <br />
          <br />
          <p class="example">{deviced:21322113data:{key1:value,
            key2:value}}</p>
          <br />
        </div>
        <div class="resInfo">
          <ul>
            <li v-for="(item,index) in resInfoList"
              :key="index">
              <span class="title">{{item.title}}:</span>
              <span class="info">{{item.info}}</span>
            </li>
          </ul>
        </div>
      </div>
      <div class="copy">
        
      </div>
    </div>
    <Confirm :options="confirmData"
      v-if="confirmShow"></Confirm>
    <ManyPrompt :options="manyPromptData"
      v-if="promptShow"></ManyPrompt>

    <!--  -->
    <Loading v-if="dataIsOk"
      :styleObj="styleObj"></Loading>
  </div>
</template>
<script>
import Loading from '@/components/Loading/LoadingRotating.vue';
import Selection from '@/components/Selection/Selection.vue';
import ManyPrompt from '@/components/Prompt/MorePrompt.vue';
import Confirm from '@/components/Confirm/Confirm.vue';
// import Tips from '@/components/Tips/Tips.vue';
import PublicSelector from '@/components/PublicSelector';

require('@/assets/DataSource/css/vue2-scrollbar.css');

export default {
	name: 'IoTData',
	props: ['message', 'savedId'],
	components: {
		PublicSelector,
		Confirm,
		ManyPrompt,
		Loading
	},
	data() {
		return {
			tableName: '',
			fieldList: [
				{
					name: '',
					field: '',
					type: '',
					length: ''
				},
				{
					name: '',
					field: '',
					type: '',
					length: ''
				},
				{
					name: '',
					field: '',
					type: '',
					length: ''
				}
			],
			resInfoList: [
				{
					title: '',
					info: ''
				},
				{
					title: '',
					info: ''
				},
				{
					title: 'ID',
					info: ''
				}
			],
			selectOptions: [
				'tinyint',
				'int',
				'bigint',
				'char',
				'varchar',
				'date',
				'time',
				'text',
				'longtext'
			],
			inputStyle: {
				width: '10vw',
				height: '1.6vw',
				'background-color': 'rgba(39, 48, 69, 1)'
			},
			token: localStorage.getItem('token'),
			IoTDataShow: true,
			serviceShow: false,
			dataIsOk: false,
			confirmShow: false,
			promptShow: false,
			deleteShow: null,
			styleObj: {
				width: '39.4vw',
				height: '64.3vh'
			},
			confirmData: {
				title: '',
				tableName: null,
				btn: [
					{
						text: '',
						callback: null
					},
					{
						text: '',
						callback: null
					},
					{
						text: '',
						callback: null
					}
				]
			},
			selections: {
				placeholder: '',
				w: '16.9vw',
				inputVal: this.message,
				data: this.$store.state.dataSource.databaseType
			},
			manyPromptData: {
				title: '',
				name: [],
				btn: [
					{
						text: '',
						callback() {}
					},
					{
						text: '',
						callback() {}
					}
				]
			}
		};
	},
	methods: {
		getOptions(index, options) {
			this.$set(this.fieldList[index], 'type', options.name);
			this.$set(this.fieldList[index], 'length', '');
			if (options.name == 'char') {
				this.$set(this.fieldList[index], 'maxLength', 255);
			}
			if (options.name == 'varchar') {
				this.$set(this.fieldList[index], 'maxLength', 65530);
			}
		},
		IoTDataBack() {
			this.IoTDataShow = false;
			this.$emit('IoTDataClose');
		},
		nextStep() {
			let vm = this;
			let filterList = vm.fieldList.filter(item => item.field && item.type);
			if (!vm.tableName) {
				vm.tips.show(['']);
				return false;
			}
			if (!filterList.length) {
				vm.tips.show(['']);
				return false;
			}
			let sendList = filterList.map(item => {
				let { field, type, length } = item;
				if (length) {
					return { field, type, length };
				} else {
					return { field, type };
				}
			});

			vm.$post('api/dataMqttFound', {
				tablename: vm.tableName,
				field: sendList
			}).then(res => {
				if (res.code == '000000') {
					let data = res.data;
					vm.$set(vm.resInfoList[0], 'info', data.host);
					vm.$set(vm.resInfoList[1], 'info', data.port);
					vm.$set(vm.resInfoList[2], 'info', data.table_id);
					vm.IoTDataShow = false;
					vm.serviceShow = true;
					vm.resetSendList();
				} else {
					vm.tips.show([res.err_msg.split(':')[1]]);
				}
			});
		},
		closeSeriveInfo() {
			this.serviceShow = false;
			this.IoTDataShow = true;
		},
		filterNAN(e, index) {
			let value = this.fieldList[index].length.replace(/\D/g, '');
			this.$set(this.fieldList[index], 'length', value);
		},
		checkMaxLength(index, maxLength) {
			let value = this.fieldList[index].length;
			if (maxLength && value > maxLength) {
				this.$set(this.fieldList[index], 'length', maxLength);
			}
		},
		resetSendList() {
			this.tableName = '';
			this.$set(this, 'fieldList', [
				{
					name: '',
					field: '',
					type: '',
					length: '',
					reset: true
				},
				{
					name: '',
					field: '',
					type: '',
					length: '',
					reset: true
				},
				{
					name: '',
					field: '',
					type: '',
					length: '',
					reset: true
				}
			]);
		}
	},
	mounted() {}
};
</script>
<style scoped>
.IoTData .confirm.maskBackground,
.IoTData .manyPromptData.maskBackground {
  background: none;
}

.IoTData i {
  vertical-align: 0;
}

.IoTData .content {
  width: 36vw;
  height: 31.6vw;
  background-color: #19202e;
  box-sizing: border-box;
}

.IoTData .nav {
  background-color: rgb(39, 48, 69);
  height: 1.9vw;
  font-size: 0.8vw;
  text-align: center;
  line-height: 1.9vw;
}
.IoTData .nav .closeBtn {
  cursor: pointer;
  position: absolute;
  right: 1vw;
  font-size: 0.7vw;
  /* margin-right: 1vw; */
}
.IoTData .tableName {
  align-items: center;
  justify-content: center;
  display: flex;
  padding: 0 2vw;
  height: 3.8vw;
  border-bottom: 0.0625vw solid rgb(8, 11, 15);
}
.IoTData .tableName .title {
  width: 2.5vw;
  margin-right: 0.5vw;
  text-align: right;
}
.IoTData .tableName input {
  padding-left: 0.6vw;
  height: 1.6vw;
  flex-grow: 1;
  background-color: rgb(18, 23, 33, 1);
  border: 0.0625vw solid rgb(71, 85, 102);
  color: #fff;
}
.fieldInputSection {
  margin-top: 1.6vw;
  padding: 0 2vw;
  height: 20.5vw;
  position: relative;
  border-bottom: 0.0625vw solid rgb(8, 11, 15);
}
.fieldInputSection ul li {
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  margin-bottom: 1vw;
}
.fieldInputSection ul li .title {
  text-align: right;
  width: 2.5vw;
  margin-right: 0.5vw;
}
.fieldInputSection ul li input.name {
  padding-left: 0.6vw;
  height: 1.6vw;
  flex-grow: 1;
  background-color: rgb(18, 23, 33, 1);
  border: 0.0625vw solid rgb(71, 85, 102);
  color: #fff;
}
.fieldInputSection ul li input.length {
  width: 10vw;
  margin-left: auto;
  margin-top: 0.5vw;
  padding-left: 0.6vw;
  height: 1.6vw;
  background-color: rgb(18, 23, 33, 1);
  border: 0.0625vw solid rgb(71, 85, 102);
  color: #fff;
}
.fieldInputSection .addField {
  cursor: pointer;
  padding: 0 0.2vw;
  position: absolute;
  color: rgb(116, 124, 142);
  right: 2vw;
  bottom: 1.4vw;
  border: solid 0.0625vw rgba(71, 85, 102, 1);
}
.footer .nextStep {
  cursor: pointer;
  padding: 0 0.4vw;
  position: absolute;
  right: 2vw;
  bottom: 1vw;
  background-color: rgba(28, 61, 87, 1);
  border: solid 0.0625vw rgba(40, 179, 252, 1);
}
.serviceInfo {
  width: 21.3vw;
  height: 26.2vw;
  background-color: #19202e;
}
.serviceInfo .nav {
  position: relative;
  background-color: rgb(39, 48, 69);
  height: 1.9vw;
  font-size: 0.8vw;
  text-align: center;
  line-height: 1.9vw;
}
.serviceInfo .nav .closeBtn {
  cursor: pointer;
  position: absolute;
  right: 1vw;
  font-size: 0.7vw;
  /* margin-right: 1vw; */
}
.serviceInfo .contentSection {
  padding: 2.8vw 2.1vw 5.4vw;
  /* padding: 5.4vw; */
  height: calc(100% - 1.9vw);
}
.serviceInfo .contentSection .intro {
  /* padding-left: 2.1vw; */
  padding-right: 0.6vw;
  color: #fff;
}

.serviceInfo .contentSection .intro .text {
  text-indent: 2em;
  font-size: 0.8vw;
  line-height: 1.5vw;
}
.serviceInfo .contentSection .intro .example {
  color: rgba(40, 179, 252, 1);
  font-size: 0.8vw;
}
.serviceInfo .contentSection .resInfo {
  width: 100%;
  height: 8.3vw;
  padding: 0.6vw;
  color: rgb(147, 157, 179);
  font-size: 0.7vw;
  background-color: rgba(18, 23, 33, 1);
  border: solid 0.0625vw rgba(71, 85, 102, 1);
}
.serviceInfo .copy {
  cursor: pointer;
  padding: 0 0.4vw;
  position: absolute;
  right: 2vw;
  bottom: 1vw;
  background-color: rgba(28, 61, 87, 1);
  border: solid 0.0625vw rgba(40, 179, 252, 1);
}
</style>
