<template>
  <div class="type-select">
    <span class="select-title">{{selectTitle}}</span>
    <div>
      <div class="select-box"
        @click="showSelect"
        :class="{'borderColor':isRotate}">
        <span v-show="obj.name">
          <svg class="icon"
            aria-hidden="true"
            v-if="isEchart">
            <use :xlink:href="obj.baseShapeUrl"></use>
          </svg>
          {{obj.name}}
        </span>
        <span class="placeholder-label"
          v-show="!obj.name"></span>
        <span class="biFont icon-triangleDown down"
          :class="{'rotate':isRotate}"></span>
      </div>
      <div class="search-input"
        v-show="isRotate">
        <search-child v-show="isEchart"
          :searchObject="searchObject"
          @fromChild="getChildSearch"
          @inputReset="inputReset"></search-child>
        <easy-scroll :barOption="opt"
          v-show="isEchart">
          <ul class="select-ul">
            <li v-for="(item,index) in DataList"
              :key="index"
              @click="getNode(item)">
              <svg class="icon"
                aria-hidden="true"
                v-if="isEchart">
                <use :xlink:href="item.baseShapeUrl"></use>
              </svg>
              {{item.name}}
            </li>
          </ul>
        </easy-scroll>
        <ul class="select-ul"
          v-show="!isEchart">
          <li v-for="(item,index) in DataList"
            :key="index"
            @click="getNode(item)">{{item.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import '@/assets/SiderBar/js/iconfont.js';
import SearchChild from '@/components/Search';
export default {
	name: 'SelectChild',
	components: {
		'search-child': SearchChild,
	},
	props: {
		ordinaryList: {
			type: Array,
			required: true
		},
		isEchart: {
			type: Boolean,
			required: true
		},
		selectTitle: {
			type: String,
			required: true
		}
	},
	data() {
		return {
			DataList: [],
			obj: {},
			isRotate: false,
			searchObject: {
				isClearInput: false,
				placeholderDes: '',
				isShowPanel: false,
				beQueriedList: this.ordinaryList,
				width: '6.7vw',
				height: '1vw'
			},
			opt: {
				barColor: '#343a47', //
				barWidth: 3, //
				railColor: '#000000', //
				barMarginRight: 0, //px
				barOpacityMin: 0.3, //
				horizrailenabled: true, //
				vBarHeight: '20vh'
			}
		};
	},
	methods: {
		showSelect() {
			this.isRotate = !this.isRotate;
		},
		getNode(item) {
			this.obj = item;
			const selectData = {
				title: this.selectTitle,
				data: item
			};
			this.$emit('fromSelectChild', selectData);
			this.isRotate = false;
		},
		//
		getChildSearch(data) {
			if (data.length) {
				this.DataList = data;
			} else {
				this.DataList = this.ordinaryList;
			}
		},
		inputReset() {
			this.isClearInput = false;
		}
	},
	mounted() {
		this.DataList = this.ordinaryList;
	}
};
</script>
<style scoped>
.type-select {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 0.6vw;
  align-items: baseline;
  z-index: 10;
}
.type-select .select-title {
  display: block;
  color: #acbbda;
}
.select-box {
  width: 7.2vw;
  height: 1.1vw;
  background-color: #11151f;
  border: solid 0.0625vw #475566;
  position: relative;
  color: #fff;
  text-align: left;
  padding-top: 0.05vw;
  padding-bottom: 0.05vw;
  font-size: 0.6vw;
  user-select: none;
  cursor: pointer;
}
.select-box span:first-child {
  margin-left: 0.24vw;
}
.select-box .down {
  transform: scale(0.8);
  position: absolute;
  right: 0.2vw;
  top: 0.1vw;
  font-size: 0.5vw;
  cursor: pointer;
}
.select-box .rotate {
  transform: rotate(180deg) scale(0.8);
}
.type-select .borderColor {
  border-color: #28b3fc;
}
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
/**.**/
.search-input {
  /* height: 10vw; */
  position: absolute;
  width: 7.2vw;
  padding-top: 0.1vw;
  background-color: #11151f;
  display: flex;
  flex-direction: column;
  border-right: solid 0.0625vw #475566;
  border-left: solid 0.0625vw #475566;
  border-bottom: solid 0.0625vw #475566;
  align-items: center;
}
.select-ul {
  width: 6.7vw;
  /* height: 10vw; */
  list-style: none;
  padding: 0;
  margin-top: 0.1vw;
  color: #fff;
}
.select-ul > li {
  border: solid 0.0625vw transparent;
  text-align: left;
  line-height: 1.1vw;
  margin-top: 0.2vw;
  margin-bottom: 0.2vw;
  font-size: 0.6vw;
  padding-left: 0.4vw;
  user-select: none;
}
.select-ul > li > svg {
  margin-right: 0.2vw;
}
.select-ul > li:hover {
  cursor: pointer;
  border-color: #28b3fc;
  background-color: #182b3f;
}
.placeholder-label {
  font-size: 0.6vw;
  color: #798194;
  margin-left: 0.1vw;
  margin-top: 0;
}
</style>
