const axios=require('axios');
import { store } from '@/store/store';
import Vue from 'vue';
import { getQueryString, getCookie } from '@/utils/utilsMethods';

// axios.defaults.baseURL = store.getters['config/baseUrl'];
let token = getQueryString('source') === 'jd' ? getQueryString('token') : getCookie('token');
let http=axios.create({
	headers:{
		token,
	},
	timeout:180000,
	baseURL:store.getters['config/baseUrl'],
});

// axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';
