// 		'token': token
// 	};
// 	return config;
// }, (error) => {
// 	return Promise.reject(error);
// });
http.interceptors.response.use(
	response => {
		if (response.data.code == '200007' || response.data.code == '200008') {
			let ssoUrl = store.getters['config/ssoUrl'];
			Vue.prototype.tips.show([
				'' + `<a href=${ssoUrl + window.location.href} style="color:rgb(36, 131, 255);padding-left: 0.5vw"></a>`,
				false,
				2000
			]);
		}
		return response;

	},
	error => {
		return Promise.reject(error);
	});

/**
 * get
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url, params = {},addTimeStamp) {
	return new Promise((resolve, reject) => {
		http.get(url, {
			params: {
				...params,
				t: Date.now()
			}
		})
			.then(response => {
				resolve(response.data);
			})
			.catch(err => {
				reject(err);
			});
	});
}


/**
 * post
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url, data = {}) {
	return http.post(url, data)
		.then(response => {
			return Promise.resolve(response.data);
		}, err => {
			return Promise.reject(err);
		});
}
