/**
 * patch
 * @param url
 * @param data
 * @returns {Promise}
 */

export function patch(url, data = {}) {
	return new Promise((resolve, reject) => {
		http.patch(url, data)
			.then(response => {
				resolve(response.data);
			}, err => {
				reject(err);
			});
	});
}

/**
 * put
 * @param url
 * @param data
 * @returns {Promise}
 */

export function put(url, data = {}) {
	return new Promise((resolve, reject) => {
		http.put(url, data)
			.then(response => {
				resolve(response.data);
			}, err => {
				reject(err);
			});
	});
}

/**
 * delete
 * @param url
 * @param data
 * @returns {Promise}
 */

export function del(url, data = {data: {}}) {
	return new Promise((resolve, reject) => {
		http.delete(url, { data })
			.then(response => {
				resolve(response.data);
			}, err => {
				reject(err);
			});
	});
}
