<template>
  <div class='chevron-left'>
    <div @click='developer = !developer'
      class="chart-basis mobile-phone">
      <span></span>
      <span class="biFont icon-arrowLeft icon-fontsize"
        :class="{rotateIcon:developer}"></span>
    </div>
    <div v-show='developer'>
      <ul class="content-box show-skipped">
        <li class="optionsItem">
          <select-color :defaultColor="phoneBgColor"
            @changeColor="phoneColorData"></select-color>
        </li>
        <li>
          <ChoicePhoto :afferent="phoneBgUrl"
            @baseUrl="phoneUrlData"></ChoicePhoto>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import ColorSelect from './BasicComponent/ColorSetting';
import ChoicePhoto from '@/components/Picture/PictureUpload.vue';
import { getRgba} from '@/utils/optionSetting';

export default {
	name: 'PhoneSetting',
	components: {
		'select-color': ColorSelect,
		ChoicePhoto
	},
	data() {
		return {
			developer: true,
			phoneBgColor: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			phoneBgUrl: {
				photoUrl: this.imgBase64,
				title: '',
				selectImg: '',
				showWith: 8.5,
				showHeight: 6,
				size: 1024,
				accept: 'image/jpeg,image/png,image/gif'
			}
		};
	},
	methods: {
		phoneColorData(data) {
			let vm = this;
			let rgba = getRgba(data.rgba);
		  vm.$store.commit('canvasPlace/setCanvasBgMobileColor', rgba);//
		  vm.$store.commit('canvasPlace/setCanvasBGAmendCount');
			// vm.phoneBgColor.titleColor = vm.colorTransformation(rgb);
			vm.phoneBgColor.rgba = rgba;
		},
		phoneUrlData(data) {
			let vm = this;
			console.log({data}, '');
			if (data.baseUrl) {
				let imgType = data.imgType;
				if (imgType === 'gif' && data.imgSize >= 1024) {
					vm.tips.show(['1Mbgif']);
					vm.bgBaseUrl.photoUrl = '';
				} else {
					if (data.imgSize >= 2048) {
						vm.tips.show(['2MB']);
						vm.bgBaseUrl.photoUrl = '';
					} else {
						vm.$store.commit('canvasPlace/setCanvasBgMobileImg', data.baseUrl);//
						this.$store.commit('canvasPlace/setCanvasBGAmendCount');
					}
				}
			} else {
				vm.$store.commit('canvasPlace/setCanvasBgMobileImg', data.baseUrl);//
				this.$store.commit('canvasPlace/setCanvasBGAmendCount');
			}
		},
	}
};
</script>

<style scoped>
  .optionsItem {
    margin-bottom: 0.8vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .chevron-left {
    height: calc(100vh - 4.5vw - 1vh);
    background: rgb(23, 30, 43)
  }

  .show-skipped {

  }

  .mobile-phone {
    background: #273045;
    cursor: pointer;
    color: #fff;
    width: 15.2vw;
    height: 1.9vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.7vw;
  }

  .content-box {
    padding: 0.4vw 0.5vw;
  }

  .chart-basis .rotateIcon {
    transform: rotate(-90deg);
    /* transition: 0.5s; */
  }

  .optionsItem .span-title {
    display: block;
    flex: 1;
    color: #acbbda;
  }
</style>
