<!---->
<template>
  <div @mousedown="getContainerSize"
    class="container">
    <div class="bg"
      v-show="!isSetOptions"></div>
    <v-chart :options="options"
      autoresize
      ref="instance"
      v-show="isSetOptions"></v-chart>
      @mousedown="startClick"
      @datazoom="dataZoom"
      @mouseup="drillDown"
  </div>
</template>
<script>
import {echartsCommonMixin} from '../../mixin/echartsCommonMixin';
import 'echarts/lib/chart/bar';
import {mapGetters} from 'vuex';

export default {
	name: 'echart-basicBar',
	data() {
		return {
			startPositon: [],
			lastData: {}
		};
	},
	computed: {
		...mapGetters('canvasPlace', {
			ScreenSign: 'getScreenSign',
		}),
		dataZoomEndValue() {
			return this._get(this.options, 'dataZoom[0].endValue', -1);
		},
		dataLength() {
			return this._get(this.options, 'series[0].data', []).length;
		},
		xData(){
		  return this._get(this.$attrs.contianerOptions,'xData',[]);
		}
	},
	mixins: [echartsCommonMixin],
	methods: {
		startClick(params) {
      console.log(123456);
			let e = params.event.event || window.event;
			this.$set(this.startPositon, 0, e.clientX);
			this.$set(this.startPositon, 1, e.clientY);
		},
		saveDataIndexDb(params) {
			let index = this.options.chartId;
			this.$store.commit('canvasPlace/renderOptionSetter', {...params, id: index});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount', {...params, index: index});
		},
		dataZoom(params) {
			console.log(this.$refs.instance.computedOptions);
		},
		drillDown(params) {
			let vm = this;
			let e = params.event.event || window.event;
			let startX = this.startPositon[0];
			let startY = this.startPositon[1];
			let endX = e.clientX;
			let endY = e.clientY;
			let dx = Math.abs(endX - startX);
			let dy = Math.abs(endY - startY);
			let dis = Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2));
			if (dis > 5 || params.name === this.options.screenText) return false;
			let seriesIndex = params.seriesIndex;
			let screenText = [params.name];
			this.lastData[seriesIndex] = this.options.series[seriesIndex].data;
			console.log(111111111111111)
      console.log(this.options)
			let sendOptions = {
				sizerId: this.ScreenSign.pageSn,
				// projectId: sessionStorage.getItem('projectId'), //ID
				screenData: [{
					selectAllData: false,
					sizerSn: vm.ScreenSign.pageSn,
					sizerType: 1, //    1.  2.
					screenText,//
					screenType: 'text',//
					screenSourceId: vm.options.xSourceId, //id
					screenField: vm.options.xField, //
				}]
			};
      console.log(vm.options);
      vm.$put('api/sizer', sendOptions).then(res => {
				if (res.code === '000000') {
					vm.$store.commit('canvasPlace/renderOptionSetter', {
						key: 'screenText',
						value: params.name,
						id: this.options.chartId
					});
					this.$store.dispatch('canvasPlace/setIndexdbChartOptions', {
						index: this.options.chartId, callback: () => {
							console.log('');
							vm.$store.dispatch('canvasPlace/screeningRequest'); //++
						}
					});
				} else {
					vm.tips.show([res.err_msg.split(':')[1]]);
				}
			});
		},
		dragstart() {
			console.log(1);
		},
		clearFilter() {
		},
		// watchDataZoomEndValue() {
		// 	let vm = this;
		// 	this.$watch('dataZoomEndValue', function (val) {
		// 		if (val > -1) {
		// 			if (vm.dataLength >= 1 && val > vm.dataLength) {
		// 				vm.saveDataIndexDb({
		// 					key: 'dataZoom.0.endValue',
		// 					value: vm.dataLength - 1
		// 				});
		// 			}
		// 		}
		// 	}, {immediate: true});
		// },
		/*// watchDataZoomStartValue() {
		// 	let vm = this;
		// 	this.$watch('dataLength', function (val) {
		// 		if (val >= 1) {
		// 			if (val >= 1 && vm.dataZoomEndValue > val) {
		// 				vm.saveDataIndexDb({
		// 					key: 'dataZoom.0.endValue',
		// 					value: val - 1
		// 				});
		// 			}
		// 		}
		// 	}, {immediate: true});
		// },*/

	},
	watch: {
		xData:{
		  deep:true,
			immediate: true,
			handler(val) {
		    console.log(val);
			  let vm=this;
				if (val.length >= 1) {
					if (val.length >= 1 && vm.dataZoomEndValue > val.length) {
						vm.saveDataIndexDb({
							key: 'dataZoom.0.endValue',
							value: val.length - 1
						});
					}
				}
			}
		},
		'options.tableId': {
			immediate: true,
			handler(tableId) {
				if (tableId) {
					let vm = this;
					this.$watch('dataZoomEndValue', function (val) {
						if (val > -1) {
							if (vm.dataLength >= 1 && val > vm.dataLength) {
								vm.saveDataIndexDb({
									key: 'dataZoom.0.endValue',
									value: vm.dataLength - 1
								});
							}
						}
					}, {immediate: true});
					this.$watch('dataLength', function (val) {
						if (val >= 1) {
							if (val >= 1 && vm.dataZoomEndValue > val) {
								vm.saveDataIndexDb({
									key: 'dataZoom.0.endValue',
									value: val - 1
								});
							}
						}
					}, {immediate: true});
				}
			}
		},
	}
};
</script>
<style scoped>
  .bg {
    background-image: url("~echartsBg/basicBar.png");
    background-repeat: no-repeat;
  }
</style>
