<template>
  <div class="dataTableInfo">
    <div class="title">
      <i class="icon biFont dataSourceFont"
        v-if="dataTitle.icon"
        :class="'icon-'+dataTitle.icon"></i>
      <span class="unSelect dataSourceTitle">{{dataTitle.title}}</span>
      <i class="icon biFont icon-add upload"
        @click="upload"></i>
    </div>
    <div class="dataList">
      <!-- <input class='unSelect popText'
        v-show='popSearch'
        v-model='inputValue'
        placeholder=''/> -->
      <Search :searchObject="searchObject"
        @fromChild="getSearchData"
        @focused='hidePopSearch'
        @inputReset='getValue'
        v-click-outside='{fn:hidePopSearch}'
        :class="{popSelect:popSearch}"
        class="unSelect"></Search>
      <div class="dataList-container unSelect">
        <EasyScroll :barOption="scroll">
          <ul v-show='appendAis'
            :style='touchPosition'
            class="menuList">
            <li v-for='(ais,q) in listAis'
              :key='q'
              v-click-outside='{fn:closeAddAixMenu}'
              @mousedown.stop='offAppendAis($event,ais["dataType"])'>{{ais.name}}
            </li>
          </ul>
          <ul class="list unSelect"
            v-click-outside="{fn:listEditor,arg:listShow}">
            <li v-for="(item,index) in tableData"
              :key="index"
              :name="item.db_source_name"
              class="dataSourceInfo unSelect">
              <label v-if="item.db_status==0"
                class="unSelect tableNameLabel">
                <i class="icon biFont icon-arrowRight menuArrow"
                  :class="{expand:item.show}"></i>
                <i class="icon dataSource listType"
                  :class="'icon-'+item.db_source_type"></i>
                <input class="tableNameUpload unSelect disabledName"
                  disabled
                  :value="item.db_source_name"
                  :class="{blur:menuOperate==index&&disabledToggle}"
                  :ref="item.id">
                <span class="aCenter uploading">{{item.db_source_name}}</span>
              </label>
              <div v-if="item.db_status==1"
                @mouseenter="operateShow(index)"
                :class="{searchHighlighted:item.searchShow,active:activeHigh==index||menuActive==index}"
                @mousedown="fieldClick(index,item)"
                class="unSelect">
                <i class="icon biFont icon-arrowRight menuArrow"
                  :class="{expand:item.show}"></i>
                <i class="icon dataSource listType"
                  :class="'icon-'+item.db_source_type"></i>
                <!--                <div class="tableName unSelect disabledName"-->
                <!--                  :title="item.db_source_name"-->
                <!--                  @mousedown.stop-->
                <!--                  disabled-->
                <!--                  :class="{blur:menuOperate==index&&disabledToggle}"-->
                <!--                  :ref="item.id"-->
                <!--                  v-if="inputEditNum!=index"><span class="vCenter">{{item.db_source_name}}</span></div>-->
                <input class="tableName tableRename"
                  autocomplete="off"
                  :id="index"
                  @mousedown.stop
                  :class="{menuFocus:inputEditNum==index}"
                  :ref="item.source_sn"
                  v-model.trim="item.db_source_name"
                  @blur="renameInput(item,$event)"
                  @keyup.enter="keyBlur(item.source_sn)">
                <div class="operate">
                  <i class="icon biFont icon-refresh menuRefresh vCenter"
                    :class="{refreshRotate:refreshNum==index}"
                    v-show="refreshToggle(item.db_source_type)&&inputEditNum!==index"
                    @mousedown.stop.prevent="refreshData(item,index)"></i>
                  <label class="biFont icon-edit menuEdit vCenter"
                    :for="index"
                    @mousedown.stop.prevent="menuEdit(index,item.db_source_name)"></label>
                  <i class="icon biFont icon-delete menuDelete vCenter"
                    @mousedown.stop.prevent="menuDelete(item)"></i>
                </div>
              </div>
              <ul v-if="item.db_status==1"
                v-show="item.show"
                class="unSelect">
                <li v-for="(tableInfo,i) in item.field"
                  :key="tableInfo.Field"
                  :type="tableInfo.Type"
                  class="unSelect"
                  :class="tableInfo.Type"
                  @mouseenter="tableInfoEnter(tableInfo,item,index)"
                  @mouseleave="tableInfoEnterLeave(tableInfo,item,index)"
                  @click.stop="chosen(tableInfo,item,index)"
                  :draggable="isDrag"
                  @dragstart="dragstart"
                  @drag="drag"
                  @dragend="dragend"
                  @mousedown="getFieldMsg(tableInfo,i,item,$event)"
                  @touchstart="touchstart($event,tableInfo,i,item)"
                  @touchmove="touchMoving($event,tableInfo,item,index)"
                  @touchend.prevent="touchend($event,tableInfo,item,index)">
                  <i class="icon dataSource listIcon"
                    :class="'icon-'+tableInfo.Type"></i>
                  <span class="unSelect"
                    :title="tableInfo.Field">{{tableInfo.Field}}</span>
                </li>
              </ul>
            </li>
          </ul>
        </EasyScroll>
      </div>
    </div>
    <div class='popContainer'
      v-show='popSearch'>
    </div>
    <loadingRotating v-if="rotatingShow"
    :styleObj="rotatingStyle"></loadingRotating>
  </div>
</template>
<script>
import Search from '@/components/Search/index.vue';
import {mapGetters} from 'vuex';

export default {
	name: 'dataSourceList',
	components: {
		Search
	},
	props: ['dataTitle', 'requestUrl'],
	computed: {
		...mapGetters('main', ['getVw', 'vwToPx', 'vhToPx']),
		...mapGetters('dataSource', {
			getDataObj: 'getDataSource',
			getDataRefresh: 'getDataRefresh'
		}),
		...mapGetters('dataDimension', {
			isOnDragging: 'isOnDragging',
			dragObject: 'getDragObject'
		}),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',
			currentChartType: 'getCurrentChartType',
			hasDateDimension: 'hasDateDimension'
		}),
		...mapGetters('main', ['getVw']),
		refreshToggle() {
			return (type) => {
				let bool = false;
				switch (type) {
				case 'sqlserver':
				case 'postgresql':
				case 'mongodb':
				case 'oracle':
				case 'mysql':
					bool = true;
					break;
				default:
					bool = false;
					break;
				}
				return bool;
			};
		},
		scroll() {
			return {
				barColor: '#95A0B6', //
				barWidth: this.getVw(6), //
				railColor: '#475566', //
				barMarginRight: 0, //px
				barOpacityMin: 0.3, //
			};
		},
		rightPaneHover() {
			return this.$store.state.dashboard.rightPaneHover;
		},
		touchPosition() {
			return {
				top: this.yDistance + 'px',
				left: this.xDistance + 'px'
			};
		},
		//tableData  
		tableData(){
			console.log(this.isSearch)
			return this.isSearch?this.searchData:this.dataTableInfo
		}
	},
	data() {
		return {
			isSearch:false, // false
			rotatingShow: true,
			rotatingStyle: {
			  width:'100%',
				height:'100%',
				position:'absolute'
			},
			userid: localStorage.getItem('userid'),
			initializeData: null,
			isDrag: false,
			ondragging: false,
			dataTableInfo: [],
			dataTableSearchInfo:[],
			searchData:[],
			relation: '',
			inputEditNum: null,
			menuOperate: null,
			activeHigh: null, //
			menuActive: null, //
			refreshNum: null, //
			totalPageNum: 1, //1
			currentPageNum: 1, //1
			requestNum: 25, //
			totalNum: null, //
			disabledToggle: false,
			currentDateType:null,
			currentIndex:0,
			charShow: false,
			dateShow: false,
			listShow: null,
			intShow: false,
			listId: null,
			placeholderDes: '',
			dataInfoSearch: [],
			dataSearch: [],
			rename: '',
			tipsData: {
				text: '',
				rel: '',
				time: 1500
			},
			//
			tableId: '',
			fieldName: '',
			inputType: '',
			tableName: '',
			searchObject: {
				isClearInput: false,
				placeholderDes: '',
				isShowPanel: false,
				beQueriedList: [],
				width: '100%',
				height: '2.7vh'
			},
			listAis: [
				{name: 'X', 'dataType': 'xAxisList'},
				{name: 'Y', 'dataType': 'yAxisList'}
			],
			appendAis: false,//
			yDistance: 0,//
			xDistance: 0,//X
			popSearch: false,//
			inputValue: '',
			isMobile: false, //
		};
	},
	methods: {
		/**
			 * start
			 * mousedown
			 */
		getFieldMsg(data, index, item, event) {
			this.currentDateType=data.Type;
			if (this.$route.path === '/screen') {
				if (this.currentChartIndex > -1) {
					this.isDrag = true;
					this.tableId = item.source_sn;
					this.tableName = item.db_source_name;
					this.fieldName = data.Field;
					this.inputType = data.Type;
					const dragObject = {
						isSelectInput: false,
						isChoice: true,
						tableIndex: item.source_sn,
						tableName: item.db_source_name,
						name: data.Field,
						inputType: data.Type,
						isFilterChar: false,
						isFilterNumber: false,
						xOp: '',
						yOp: data.Type == 'char' ? 'count' : 'sum',
						categoryAxis: '',
						orderCondition: '',
						orderOp: ''
					};
					// console.log(this.hasDateDimension(),'');
					if (this.hasDateDimension() && event.type === 'touchstart') {
						this.appendAis = true;//
					}
				} else {
					this.tips.show(['']);
					// this.tipsData.rel = 'fail';
					// this.tipsData.text = '';
					// this.$refs.tips.closeTips();
				}
			}
		},
		offAppendAis(e,type) {
			this.appendAis = false;
			e = e || window.event;
			console.log(type);
			this.touchAddAxis(e,type);
		},
		closeAddAixMenu() {
			this.appendAis = false;
		},
		/**
			 *  
			 * vuex
			 */
		dragstart(e) {
			e = e || window.event;
			const dragObject = {
				isSelectInput: false,
				isChoice: true,
				tableIndex: this.tableId,
				tableName: this.tableName,
				name: this.fieldName,
				inputType: this.inputType,
				isFilterChar: false,
				isFilterNumber: false,
				xOp: '',
				yOp: this.inputType == 'char' ? 'count' : 'sum',
				categoryAxis: '',
				orderCondition: '',
				orderOp: ''
			};
			this.$store.commit('dataDimension/setIsFocusDrag', true);
			this.$store.commit('dataDimension/setDragObject', dragObject);//vuex
			this.$store.commit('dataDimension/setOnDragging', true);
		},
		drag(e) {
			e = e || window.event;
			let vm = this;
			if ('ontouchend' in document || navigator.maxTouchPoints) {
				var event = event || window.event;
				let x = event.changedTouches[0].clientX;
				let y = event.changedTouches[0].clientY;
				let element = document.elementFromPoint(x, y);
				if (element.getAttribute && element.getAttribute('data-class') == 'dataDimension-list') {
					let currentIndex = Number(element.getAttribute('currentIndex'));
					let number = Number(element.getAttribute('data-number'));
					this.$store.commit('dataDimension/setCurrentHoverIndex', currentIndex);
					this.$store.commit('dataDimension/setColorIndex', number);
				} else {
					this.$store.commit('dataDimension/setColorIndex', -1);
					this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
				}
			}
		},
		dragend(e) {
			let vm = this;
			const event = e || window.event;
			vm.ondragging = false;
			vm.$store.commit('dataDimension/setIsFocusDrag', false);
			vm.$store.commit('dataDimension/setOnDragging', false);
			if ('ontouchend' in document || navigator.maxTouchPoints) {//
				let x = event.changedTouches[0].clientX;
				let y = event.changedTouches[0].clientY;
				const element = document.elementFromPoint(x, y);
				if (element.getAttribute && element.getAttribute('data-class') && element.getAttribute('data-class').indexOf('dataDimension-list') > -1) {
					let type = element.getAttribute('data-type');
					let currentIndex = Number(element.getAttribute('currentIndex'));
					vm.dexieDB.aXisList
						.get(currentIndex)
						.then(res => {
							let currentAxisData = res.axisOptions;
							vm.$set(currentAxisData[type], 0, vm.dragObject);
							vm.dexieDB.aXisList
								.put({
									index: currentIndex,
									axisOptions: currentAxisData
								})
								.then(res => {
								})
								.catch(error => console.error(error));
							vm.$store.commit(
								'dataDimension/setCurrentChartAxis',
								currentAxisData
							);
							vm.$store.commit('dataDimension/setAmendChartCount');
							vm.$store.commit('dataDimension/setUpdateCount');
						})
						.catch(reason => console.error(reason));
					vm.$store.commit('dataDimension/setCurrentHoverIndex', -1);
				}
			}
		},
		//
		touchAddAxis(e,type) {
			let vm = this;
			// const event = e || window.event;
			vm.ondragging = false;
			vm.$store.commit('dataDimension/setIsFocusDrag', false);
			vm.$store.commit('dataDimension/setOnDragging', false);
			if ('ontouchend' in document || navigator.maxTouchPoints) {//
				// let x = event.changedTouches[0].clientX;
				// let y = event.changedTouches[0].clientY;
				// const element = document.elementFromPoint(x, y);
				// if (element.getAttribute && element.getAttribute('data-class') && element.getAttribute('data-class').indexOf('dataDimension-list') > -1) {
				// let type = this.currentDateType;
				let currentIndex =this.currentIndex;
				console.log(currentIndex);
				vm.dexieDB.aXisList
					.get(currentIndex)
					.then(res => {
						let currentAxisData = res.axisOptions;
						vm.$set(currentAxisData[type], 0, vm.dragObject);
						vm.dexieDB.aXisList
							.put({
								index: currentIndex,
								axisOptions: currentAxisData
							})
							.then(res => {
							})
							.catch(error => console.error(error));
						vm.$store.commit(
							'dataDimension/setCurrentChartAxis',
							currentAxisData
						);
						vm.$store.commit('dataDimension/setAmendChartCount');
						vm.$store.commit('dataDimension/setUpdateCount');
					})
					.catch(reason => console.error(reason));
				vm.$store.commit('dataDimension/setCurrentHoverIndex', -1);
			}
			// }
		},
		// lzj
		touchstart(event, data, index, item) {
			let e = event || window.event;
			if ('ontouchend' in document || navigator.maxTouchPoints) {
				this.yDistance = e.changedTouches[0].clientY + this.vhToPx(2);
				this.xDistance = e.changedTouches[0].clientX + this.vwToPx(2.6);
				this.getFieldMsg(data, index, item, e);
			}
			this.dragstart(e);
			this.$store.commit('dataDimension/setOnTouch', true);
			// window.addEventListener('touchmove', this.drag);
			this.isMobile = true;
		},
		// lzj
		touchMoving(e, tableInfo, item, index) {
			this.drag(e);
			this.tableInfoEnter(tableInfo, item, index);
		},
		// lzj
		touchend(e, tableInfo, item, index) {
			this.tableInfoEnterLeave(tableInfo, item, index);
			// window.removeEventListener('touchmove', this.drag);
			this.$store.commit('dataDimension/setOnTouch', false);
			this.dragend(e);
		},
		/**
			 * end
			 */
		listEditor() {
			this.activeHigh = null;
			this.intShow = false;
			this.charShow = false;
			this.dateShow = false;
			this.$emit('chosenBack', {
				dataSource: null,
				dataModeling: null
			});
		},
		forbidEditor() {
			// this.inputEditNum = null;
		},
		keyBlur(name) {
			this.$refs[name][0].blur();
			this.inputEditNum = null;
		},
		getSearchData(data) {
			// console.log('getSearchData', data);
			// this.dataSearch = data;
			// this.dataTableInfo = this.dataTableSearchInfo
		},
		hideConfirm() {
			this.menuActive = null;
		},
		operateShow(index) {
			this.menuOperate = index;
			this.disabledToggle = true;
		},
		operateHide() {
			this.menuOperate = null;
			this.inputEditNum = null;
		},
		fieldClick(index, item) {
			this.listId = item.id;
			this.clearColor();
			//
			if (index != -1) {
				this.activeHigh = index;
				this.menuActive = index;
				let isShow = this.dataTableInfo[index].show;
				this.$set(this.dataTableInfo[index], 'show', !isShow);
				console.log(this.dataTableInfo);
			}
			if (this.requestUrl == 'api/dataInfo') {
				this.$emit('dataInfoBack', item);
			} else {
				this.$emit('modelInfoBack', item);
			}
		},
		renameFocus(name) {
			console.log(name);
			this.rename = name;
		},
		renameInput(item, e) {
			const name = item.db_source_name;
			this.$set(item, 'name', name.replace(/\s+/g, ''));
			if (item.name === this.rename) {
				return;
			} else if (item.name.trim() === '') {
				this.tips.show(['']);
			} else {
				this.inputEditNum = null;
				let data = {};
				data[item.id] = item.name;
				data = JSON.stringify(data);
				this.$put('api/updateSourceName', {
					list: data
				}).then(
					response => {
						let res = response.data;
						if (res['success']) {
							this.requestData('refresh');
							this.tips.show(['', true]);
						} else if (res['fail']) {
							this.tips.show(['']);
						}
					},
					error => {
						console.log(error);
						this.tips.show(['']);
					}
				);
			}
		},
		refreshData(item, index) {
			this.refreshNum = index;
			this.$get('api/refreshData', {
				action_type: '2',
				config_sn: item.config_sn,
				tablename: item.db_source_name,
				type: item.db_source_type
			}).then(
				response => {
					if (response.code == '000000') {
						this.tips.show(['...', true]);
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
					}
					this.refreshNum = null;
				},
				error => {
					console.log(error);
					this.tips.show(['']);
					this.refreshNum = null;
				}
			);
		},
		menuEdit(index, name) {
			this.activeHigh = null;
			this.inputEditNum = index;
			this.rename = name;
			console.log(name);
		},
		menuDelete(item) {
			this.$post('api/delUploadSource', {
				source_sn: item.source_sn,
				id: item.id
			}).then(
				response => {
					if (response.code == '000000') {
						this.$store.commit('dataSource/deleteDataSource', item.source_sn);
						this.tips.show([response.err_msg.split(':')[1], true]);
						this.tipsData.rel = 'success';
						if (this.listId == item.id) {
							this.requestData('refresh').then(() => {
								for (
									let index = 0;
									index < this.dataTableInfo.length;
									index++
								) {
									let element = this.dataTableInfo[index];
									if (element.db_status != 0) {
										this.fieldClick(index, element); //
										return false;
									}
								}
							});
						} else {
							this.requestData('refresh');
						}
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
						this.tipsData.rel = 'fail';
					}
				},
				error => {
					console.log(error);
					this.tips.show(['']);
					// this.tipsData.rel = 'fail';
					// this.tipsData.text = '';
					// this.$refs.tips.closeTips();
				}
			);
		},
		upload() {
			this.$emit('fileUploadBack', {
				fileSystem: true
			});
		},
		tableInfoEnter(data, item, index) {
			this.addStyle(data, item, index);
		},
		tableInfoEnterLeave() {
			// alert(111);
			// this.ditDel
			// ete = null;
			this.intShow = false;
			this.charShow = false;
			this.dateShow = false;
		},
		chosen(data, item, index) {
			this.menuActive = index;
			this.addStyle(data, item, index, 'click');
		},
		addStyle: function (data, item, index, str) {
			this.clearColor();
			//
			this.intShow = false;
			this.charShow = false;
			this.dateShow = false;
			this.activeHigh = null;
			switch (data.Type) {
			case 'int':
				this.intShow = !data.show;
				break;
			case 'char':
				this.charShow = !data.show;
				break;
			case 'time':
				this.dateShow = !data.show;
				break;
			}
			this.$set(data, 'show', !data.show);
			if (this.requestUrl == 'api/dataInfo' && str == 'click') {
				this.$emit('dataInfoChosen', {
					field: data,
					data: item
				});
			} else if (this.requestUrl == 'api/modelInfo') {
				this.menuActive = index;
				this.$emit('modelInfoChosen', item);
			}
		},
		clearColor() {
			// li
			let res = this.dataTableInfo;
			for (let obj in res) {
				if (res[obj].constructor != Array) {
					let fields = res[obj];
					for (let index in fields.field) {
						fields.field[index].show = false;
					}
				}
			}
		},
		pageRequestData() {
			let data = {
				page: this.currentPageNum,
				num: this.requestNum
			};
			this.currentPageNum++;
			this.pagingRequest(data);
		},
		requestData(str) {
      let self = this;
			let data = null;
			if (self.requestUrl == 'api/dataInfo') {
				data = {
					member_sn: this.userid
				};
			}
				self.$get(self.requestUrl, data).then(response => {
            if (str === 'initialize') {
						//loading
						self.rotatingShow=false;
						self.$emit('requestSuccess', 'success');
					}
					self.dataTableInfo = [];
					if (response.code == '000000') {
						let dataSource = JSON.parse(JSON.stringify(response.data));
						if (self.requestUrl == 'api/modelInfo') {
							self.$store.commit(
								'dataSource/setRelationData',
								dataSource.relation
							);
							self.$store.commit('dataSource/setZoomNum', dataSource.zoom);
							delete dataSource.relation;
							delete dataSource.zoom;
						}
						let setData = {
							data: dataSource,
							url: self.requestUrl
						};
						self.requestDataComm(str, setData);
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
						// self.tipsData.rel = 'fail';
						// self.tipsData.text = response.err_msg.split(':')[1];
						// self.$refs.tips.closeTips();
					}
				},
				error => {
					console.log(error);
				}
				);
		},
		pagingRequest(data) {
      let self = this;
			self.$get('api/pageModelInfo', data).then(
				response => {
					//loading
					self.rotatingShow=false;
					self.$emit('requestSuccess', 'success');
					if (response.code == '000000') {
						self.$store.commit(
							'dataSource/setZoomNum',
							response.data.zoom
						);
						self.totalNum = JSON.parse(JSON.stringify(response.data.count));//
						self.totalPageNum = Math.ceil(self.totalNum / self.requestNum);// 53/25 3
						self.$store.commit('dataSource/setModelPage', {
							total: self.totalPageNum,// 3
							current: self.currentPageNum //4
						});
						delete response.data.count;
						delete response.data.zoom;
						let dataSource = JSON.parse(JSON.stringify(response.data.data));
						let setData = {
							data: dataSource,
							url: self.requestUrl,
							model: 'page'
						};
						if (self.totalPageNum == 1) {
							self.requestRelation();
							setTimeout(() => {
								self.requestDataComm('initialize', setData);
							}, 150);
						} else {
							self.requestDataComm('initialize', setData);
						}
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
						// self.tipsData.rel = 'fail';
						// self.tipsData.text = response.err_msg.split(':')[1];
						// self.$refs.tips.closeTips();
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		requestDataComm(str, setData) {
			let self = this;
			self.dataTableInfo = [];
			self.$store.commit('dataSource/setDataSource', setData);
			self.$store.commit('dataSource/setDataRefresh', false);
			self.$store.commit('dataSource/setConnectEnd', false);
			self.$store.commit('dataSource/setCount');
			let res = JSON.parse(JSON.stringify(self.getDataObj));
			for (let obj in res) {
				if (res[obj].constructor != Array) {
					let fields = res[obj];
					self.dataInfoSearch.push({
						name: fields.db_source_name,
						attr: obj
					});
					for (let index in fields.field) {
						fields.field[index].show = false;
					}
					fields.show = false;
					fields.searchShow = false;
					self.dataTableInfo.push(fields);
				}
			}
			self.$set(self.searchObject, 'beQueriedList', self.dataInfoSearch);
			// let data = self.dataTableInfo[0];
			let len = Object.getOwnPropertyNames(res).length;
			if (str === 'initialize' && len >= 2) {
				//
				//
				for (let index = 0; index < self.dataTableInfo.length; index++) {
					let element = self.dataTableInfo[index];
					if (element.db_status != 0) {
						self.fieldClick(index, element); //
						return false;
					}
				}
			} else {
				self.$emit('uploadSource', 'success');
			}
		},
		requestRelation() {
			this.$get('api/relationship').then(
				response => {
					let dataSource = JSON.parse(JSON.stringify(response.data));
					this.$store.commit('dataSource/setRelationData', dataSource.relation);
					this.$emit('relationshipSource', 'success');
				},
				error => {
					console.log(error);
				}
			);
		},
		hidePopSearch(val) {  // 
			if (this.isMobile) {  //
				if (val) {
					this.popSearch = true;
				} else {
					this.popSearch = false;
				}
			}
			//
			if(val == 0){

				//
				// let searchData = this.dataTableInfo.map(dir=>{
				// 	return {
				// 		...dir,
				// 		show:true,
				// 		field:dir.field.filter(field=>field.Field.includes(this.inputValue))
				// 	}
				// }).filter(dir=>dir.field&&dir.field.length)

				//
				let searchData = this.dataTableInfo.filter(dir=>dir.db_source_name.includes(this.inputValue)).map(dir=>({...dir,show:true}))
				this.searchData = searchData
				this.isSearch = true
			}
		},
		getValue(val) {
			console.log(val)
			if(val!=undefined){
				this.inputValue = val;
			    this.hidePopSearch(0)
			}
			// console.log(this.inputValue)
			if(!this.inputValue){
				this.isSearch = false
			}
		},
	},
	watch: {
		dataSearch: {
			handler(val, old) {
				for (let info in this.dataTableInfo) {
					let data = this.dataTableInfo[info];
					this.$set(data, 'searchShow', false);
				}
				for (let i of val) {
					let index = i.attr;
					this.$set(this.dataTableInfo[index], 'searchShow', true);
				}
			},
			immediate: true
		},
		getDataRefresh: {
			handler(val, old) {
				if (val) {
					this.requestData('refresh');
				}
			}
		},
		totalPageNum: {
			handler(val, old) {
				let self = this;
				let pageInterval = setInterval(() => {
					if (this.currentPageNum <= this.totalPageNum) {
						let data = {
							page: this.currentPageNum,
							num: this.requestNum
						};
						this.pagingRequest(data);
						this.currentPageNum++;
					} else {
						this.requestRelation();
						clearInterval(pageInterval);
						pageInterval = null;
						setTimeout(() => {
							this.$emit('requestCompleted', 'completed');
						}, 500);
					}
				}, 100);
			}
		}
	},
	mounted() {
		// this.rotatingShow=true;
		this.$store.commit('dataSource/setDataSource', 'clearData');
		if (this.requestUrl == 'api/dataInfo') {
			this.requestData('initialize');
		} else {
			this.pageRequestData();
		}
		if (document.documentElement.clientWidth <= 1024) {
			this.isMobile = true;

		}
	}
};
</script>

