import Vue from 'vue';
import Vuex from 'vuex';
import main from './main.js';
import dashboard from './dashboard.js';
import dataSource from './dataSource.js';
import dataDimension from './dataDimension.js';
import defaultChartOptions from './defaultChartOptions.js';
import defaultMediaOptions from './defaultMediaOptions.js';
import moduleCharts from './moduleCharts.js';
import defaultInteractiveOptions from './defaultInteractiveOptions.js';
import userCenter from './userCenter.js';
import canvasPlace from './canvasPlace.js';
import chartBus from './chartBus.js'; // 
import config from './config.js'; // 
import handleChart from './handleChart.js'; // 
import publicTools from './publicTools'; // 
import voiceCommand from './voiceCommand';// 
Vue.use(Vuex);

export const store = new Vuex.Store({
	modules: {
		main,
		dashboard,
		dataSource,
		dataDimension,
		userCenter,
		canvasPlace,
		chartBus,
		defaultChartOptions,
		defaultMediaOptions,
		defaultInteractiveOptions,
		config,
		handleChart,
		publicTools,
		moduleCharts,
		voiceCommand
	}
});
