<template>
  <div>
    <component
      :is="currentComponent"
      style="zIndex:100000"
      v-bind="$attrs"
      v-on="$listeners">
    </component>
  </div>
</template>
<script>
export default {
	name: 'async-component',
	data() {
		return {
		  asyncRegisterModule:null,
			currentComponent:'',
		};
	},
	props: {
		componentUrl: {
			type: String,
			default: ''
			// default: 'http://localhost:3000/pluginJs/haha.js'
		},
		storeModuleUrl: {
			type: String,
			default: ''
		},
		storeModuleName: {
			type: String,
			default: ''
		},
	},
	created() {
		if (!this.storeModuleUrl){
			this.asyncRegisterModule=Promise.resolve('store');
		  return false;
		}

		if (!window.SyncStoreMuduleCache) {
			window.SyncStoreMuduleCache = {};
		}
		if(window.SyncStoreMuduleCache[this.storeModuleUrl]){
			this.asyncRegisterModule=Promise.resolve('store');
		}else{
			this.asyncRegisterModule=this.axios.get(this.storeModuleUrl).then(res=>{
				let parseRes=new Function(`return ${res.data}`)();
				this.$store.registerModule(this.storeModuleName,parseRes);
				window.SyncStoreMuduleCache[this.storeModuleUrl]=parseRes;
				return Promise.resolve('store');
			// let component = Vue.extend(result);
			// new component({
			// 	el:this.$el
			// });
			}).catch((reason)=>{
			  console.log(reason);
				return Promise.reject(reason);
			});
		}
	},
	beforeMount() {
		// this.axios.get(this.componentUrl).then(res=> {
		// 	this.currentComponent = new Function(`return ${res.data}`)();
		// });
		this.asyncRegisterModule.then(res=>{
		  console.log(res);
			if (this.componentUrl){
			  if(!window.SyncComponentCache){
					window.SyncComponentCache = {};
				}
			  if(!window.SyncComponentCache[this.url]){
					this.axios.get(this.componentUrl).then(res=>{
						// let result=new Function(`return ${res.data}`)();
						// let parseRes=
						this.currentComponent=new Function(`return ${res.data}`)();
						return Promise.resolve('');
						// let component = Vue.extend(result);
						// new component({
						// 	el:this.$el
						// });
					}).catch((reason)=>{
				  console.log(reason);
					});
				}
			}
		});
	}
};
</script>

