<!---->
<template>
  <!--  <div>-->
  <!--    <div class="chart-basis"-->
  <!--      @click="showEvent">-->
  <!--      <span></span>-->
  <!--      <span class="biFont icon-arrowLeft"-->
  <!--            :class="[isShowSetting?'leftRotate-90':'leftRotate-0']"></span>-->
  <!--    </div>-->
  <!--  :class="[isShowSetting?'heightStart':'heightOver']"-->
  <div
    class="content-box"
    ref="contentBox">
    <ul>
      <li class="optionsItem"
        v-show="titleShow">
        <input-common :inputData="inputData"
          :styleObj="inputStyleObj"
          @onChange="getInputData"
        ></input-common>
      </li>
      <li class="optionsItem">
        <multipleInput :inputData="chartSizeData"
          inputTitle=""></multipleInput>
      </li>
      <li class="optionsItem">
        <multipleInput :inputData="chartPositionData"
          inputTitle=""></multipleInput>
      </li>
      <li class="optionsItem">
        <span class="span-title"></span>
        <silder-data :opt="settingTitleTop"
          v-on:inputData="rangeTitleTop"
          v-on:rangedata="rangeTitleTop"></silder-data>
      </li>
      <li class="optionsItem">
        <select-menu :heightLightLine="titleLeftDistance"
          :ordinaryList="leftDistanceList"
          :selectTitle="''"
          @getReturnData="getLeftDistanceType"></select-menu>
      </li>
      <li class="optionsItem"
        v-show="titleShow">
        <select-color :defaultColor="titleObject"
          v-on:changeColor="getTitleColor"></select-color>
      </li>
      <li class="optionsItem">
        <select-menu :heightLightLine="coloringSchemeDistance"
          :ordinaryList="coloringScheme"
          @getReturnData="changeTheme"
          selectTitle=""></select-menu>
      </li>
      <li class="optionsItem">
        <select-color :defaultColor="bgObject"
          v-on:changeColor="getBgcolor"></select-color>
      </li>
      <!--        <li class="optionsItem" v-show="xAxisThicknessShow&&yAxisTHickness">-->
      <!--          <span class="span-title"-->
      <!--            ></span>-->
      <!--          <div class="li-line-box">-->
      <!--            <silder-data v-show="xAxisThicknessShow"-->
      <!--              :opt="settingX"-->
      <!--              v-on:rangedata="rangeDatax"></silder-data>-->
      <!--            <silder-data v-show="yAxisTHickness"-->
      <!--              :opt="settingY"-->
      <!--              v-on:rangedata="rangeDatay"></silder-data>-->
      <!--          </div>-->
      <!--        </li>-->
      <li class="optionsItem">
        <div class="type-input">
          <span></span>
          <div class="svg-details">
            <svg aria-hidden="true"
              class="icon svg-icon-size">
              <use :xlink:href="baseShapeUrl"></use>
            </svg>
            {{chartType}}
          </div>
        </div>
      </li>
      <li class="optionsItem"
        v-show="xAxisColor&&yAxisClor">
        <span class="span-title"></span>
        <div class="li-line-box">
          <div class="space-bottom"
            v-show="xAxisColor">
            <span class="color-font">X</span>
            <select-color :defaultColor="lineXObject"
              v-on:changeColor="getXcolor"></select-color>
          </div>
          <div class="space-bottom"
            v-show="yAxisClor">
            <span class="color-font">Y</span>
            <select-color :defaultColor="lineYObject"
              v-on:changeColor="getYcolor"></select-color>
          </div>
        </div>
      </li>
      <li v-show="gridThisckness">
        <div class="li-line-box">
          <select-color :defaultColor="borderLineObject"
            v-on:changeColor="getBorderColor"></select-color>
          <div class="border-flex-row">
            <div></div>
            <div class="border-flex">
              <span class="color-font space-bottom space-top"></span>
              <silder-data :opt="settingBorder"
                @on:inputData="inputGridWidth"
                v-on:rangedata="rangeBorder"></silder-data>
            </div>
          </div>
        </div>
      </li>
    </ul>
  </div>
  <!--  </div>-->
</template>

<script>
/**
   * 
   * 
   * 
   * 
   * 
   * 
   */

import SilderData from './BasicComponent/Slider';
import ColorSelect from './BasicComponent/ColorSetting';
import InputCommon from './BasicComponent/InputCommon';
import multipleInput from './BasicComponent/multipleInput';
import SelectMenu from '@/components/Dropdown/Dropdown';
import {mapGetters, mapMutations} from 'vuex';
import {getRgba} from '@/utils/optionSetting';
import {baseChartList} from '@/assets/SiderBar/js/basicChartData.js';
import {mixin} from './BasicComponent/mixin';

export default {
	name: 'BasicSetting',
	mixins: [mixin],
	components: {
		'multipleInput': multipleInput,
		'silder-data': SilderData,
		'select-color': ColorSelect,
		'input-common': InputCommon,
		'select-menu': SelectMenu
	},
	data() {
		return {
			rangeName: 'Y',
			rangeNameY: 'X',
			rgb: 'RGB(25,77,51)',
			isShowSetting: true, //
			baseShapeArr: baseChartList,
			baseShapeUrl: '',
			chartType: '',
			titleObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			bgObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			lineXObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			lineYObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			borderLineObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			//
			inputData: {
				inputTitle: '',
				inputValue: ''
			},
			inputStyleObj: {
				inputWidth: '8vw',
				inputHeight: '1vw',
				placeholderDes: ''
			},
			//
			chartSizeData: [
				{
					value: '',
					placeholder: '',
					onBlur: (value) => {
						this.$store.commit('canvasPlace/containerOptionSetter', {key: 'tableWidth', value});
					},
					style: {
						width: '3.725vw',
						height: '1vw',
						marginRight: '0.5vw'
					}
				},
				{
					value: '',
					placeholder: '',
					onBlur: (value) => {
						this.$store.commit('canvasPlace/containerOptionSetter', {key: 'tableHeight', value});
					},
					style: {
						width: '3.725vw',
						height: '1vw',
					},
				},
			],
			//
			chartPositionData: [
				{
					value: '',
					placeholder: 'x',
					onBlur: (value) => {
						this.$store.commit('canvasPlace/containerOptionSetter', {key: 'xPosition', value});
					},
					style: {
						width: '3.725vw',
						height: '1vw',
						marginRight: '0.5vw'
					}
				},
				{
					value: '',
					placeholder: 'y',
					onBlur: (value) => {
						this.$store.commit('canvasPlace/containerOptionSetter', {key: 'yPosition', value});
					},
					style: {
						width: '3.725vw',
						height: '1vw',
					},
				},
			],
			settingX: {
				name: 'X',
				max: 100,
				min: 0,
				valueRange: 1
			},
			settingY: {
				name: 'Y',
				max: 100,
				min: 0,
				valueRange: 1
			},
			settingBorder: {
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			settingTitleTop: {
				//titled
				name: '',
				max: 100,
				min: 0,
				valueRange: 1,
				garidyValue: 1
			},
			settingTitleLeft: {
				//titled
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			titleLeftDistance: {},
			leftDistanceList: [
				{value: 'left', label: ''},
				{value: 'center', label: ''},
				{value: 'right', label: ''}
			],

			coloringSchemeDistance: {},
			coloringScheme: [
				{value: 1, label: ''},
				{value: 2, label: ''},
			],
			coloringSchemeObj: {
				1: {value: 1, label: ''},
				2: {value: 2, label: ''},
			},
			dataObj: '',
			listShowBasic: false,
			titleShow: true, //
			xAxisColor: false, //x
			yAxisClor: false, //y
			xAxisThicknessShow: false, //x
			yAxisTHickness: false, //y
			gridThisckness: false //
		};
	},
	computed: {
		inpNum: {
			get: function () {
				return this.oldNum;
			},
			set: function (newValue) {
				this.oldNum = newValue.replace(/[^\d]/g, '');
			}
		},
		...mapGetters('canvasPlace', {
			currentContainerOptions: 'getCurrentContainerOptions',
			currentRenderOptions: 'getCurrentChartRenderOptions'
		})
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter',
		}),
		showEvent() {
			this.isShowSetting = !this.isShowSetting;
		},
		// 
		saveDataIndexDb({name, pare}) {
			this.renderOptionSetter({
				key: name,
				value: pare
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		},
		getInputData(val) {
			this.saveDataIndexDb({name: 'title.text', pare: val});
		},
		// x00000
		rangeDataX(data) {
			if (this.settingX.valueRange[0] !== data[0]) {
				this.saveDataIndexDb({
					name: 'xAxis.axisLine.lineStyle.width',
					pare: data
				});
			}
		},
		inputDataEvent(data) {
			this.saveDataIndexDb({
				name: 'title.top',
				pare: JSON.stringify(data) + '%'
			});
			this.TitleTop(data); //title top
		},
		//
		getLeftDistanceType(data) {
			this.saveDataIndexDb({name: 'title.left', pare: data});
			this.TitleLeft(data);// title left
		},
		inputGridWidth(data) {
		},
		// y
		rangeDataY(data) {
			if (this.settingY.valueRange[0] !== data[0]) {
				this.saveDataIndexDb({
					name: 'yAxis.axisLine.lineStyle.width',
					pare: data
				});
			}
		},
		// 
		rangeBorder(data) {
			if (this.settingBorder.valueRange !== data) {
				this.saveDataIndexDb({name: 'grid.borderWidth', pare: data});
			}
		},
		rangeTitleTop(data) {
			// console.group(data + '%','');
			if (this.settingTitleTop.valueRange !== data) {
				this.saveDataIndexDb({
					name: 'title.top',
					pare: JSON.stringify(data) + '%'
				});
			}
		},
		// },
		// 
		getTitleColor(data) {
			let rgba = getRgba(data.rgba);
			if (this.titleObject.rgba !== rgba) {
				this.saveDataIndexDb({name: 'title.textStyle.color', pare: rgba});
			}
		},
		// 
		getBgcolor(data) {
			let rgba = getRgba(data.rgba);
			if (this.bgObject.rgba !== rgba) {
				this.saveDataIndexDb({name: 'backgroundColor', pare: rgba});
			}
		},
		// x
		getXcolor(data) {
			let rgba = getRgba(data.rgba);
			if (this.lineXObject.rgba !== rgba) {
				this.saveDataIndexDb({
					name: 'xAxis.axisLine.lineStyle.color',
					pare: rgba
				});
			}
		},
		// Y
		getYcolor(data) {
			let rgba = getRgba(data.rgba);
			if (this.lineYObject.rgba !== rgba) {
				this.saveDataIndexDb({
					name: 'yAxis.axisLine.lineStyle.color',
					pare: rgba
				});
			}
		},
		// 
		getBorderColor(data) {
			let rgba = getRgba(data.rgba);
			if (this.borderLineObject.rgba !== rgba) {
				this.saveDataIndexDb({name: 'grid.borderColor', pare: rgba});
			}
		},
		showTitle(chartData) {
			let vm = this;
			vm.titleShow = true;
			vm.inputData.inputValue = chartData.title.text; //
			// console.group(chartData.title.textStyle.color);
			vm.titleObject.rgba = chartData.title.textStyle.color; //
			// vm.titleObject.titleColor = colorTransformation(
			// 	chartData.title.textStyle.color
			// );
			vm.titleObject.rgba = chartData.title.textStyle.color;

			vm.TitleTop(JSON.parse(chartData.title.top.split('%')[0])); //title top
			vm.TitleLeft(chartData.title.left); //title left
			// vm.ThemeSetting(Number(chartData.Visual.theme)>0?0:Number(chartData.Visual.theme));
		},
		xAxis(chartData) {
			let vm = this;
			vm.xAxisColor = true;
			vm.lineXObject.rgba = chartData.xAxis.axisLine.lineStyle.color;
			//x
		},
		yAxis(chartData) {
			let vm = this;
			vm.yAxisClor = true;
			vm.lineYObject.rgba = chartData.yAxis.axisLine.lineStyle.color;
			//y
		},
		xAxisThickness(chartData) {
			let vm = this;
			vm.xAxisThicknessShow = true;
			// console.log('xThickness',chartData);
			vm.settingX.valueRange = chartData.xAxis.axisLine.lineStyle.width; //x
		},
		yAxisThickness(chartData) {
			let vm = this;
			vm.yAxisTHickness = true;
			vm.settingY.valueRange = chartData.yAxis.axisLine.lineStyle.width; //y
		},
		gridThickness(chartData) {
			let vm = this;
			vm.gridThisckness = true;
			vm.settingBorder.valueRange = JSON.parse(chartData.grid.borderWidth); //
			vm.borderLineObject.rgba = chartData.grid.borderColor;
			// vm.borderLineObject.titleColor = colorTransformation(
			// 	chartData.grid.borderColor
			// );
		},
		//
		TitleTop(data) {
			let vm = this;
			vm.settingTitleTop.valueRange = data;
			vm.settingTitleTop.garidyValue = data;
		},
		//
		TitleLeft(data) {
			let vm = this;
			vm.titleLeftDistance = vm.leftDistanceList.find(e => e.value == data);
			vm.settingTitleLeft.garidyValue = data;
		},

		changeTheme(data) {
			let vm = this;
			vm.saveDataIndexDb({name: 'Visual.theme', pare: data});
			this.$store.commit('canvasPlace/changeChartTheme');

		},
		// rgbahex

	},
	watch: {
		currentContainerOptions: {
			immediate: true,
			deep: true,
			handler(val) {
				let vm = this,
					options = val;
				let chartTypes = vm.baseShapeArr[0].children.find(obj => {
					return (
						obj.chartType == options.chartsType
					);
				});
				vm.chartType = chartTypes.name;
				vm.baseShapeUrl = chartTypes.baseShapeUrl;
			}
		},
		currentRenderOptions: {
			immediate: true,
			deep: true,
			handler(val) {
				let vm = this,
					renderOptions = val;
				if (renderOptions.title) {
					vm.showTitle(renderOptions);
				}
				// 
				if (renderOptions.backgroundColor) {
					vm.bgObject.rgba = renderOptions.backgroundColor; //
					// vm.bgObject.titleColor = colorTransformation(
					// 	renderOptions.backgroundColor
					// );
				}
			}

		},
		'currentRenderOptions.Visual': {
			deep: true,
			immediate: true,
			handler(val) {
				if (val.theme) {
					// debugger;
					this.$set(this, 'coloringSchemeDistance', this.coloringSchemeObj[val.theme]);
				}
			}
		},
		'currentContainerOptions.tableWidth': {//
			handler(val) {
				this.$set(this.chartSizeData[0], 'value', ~~(0.5 + Number(val)));
			},
			immediate: true,
		},
		'currentContainerOptions.tableHeight': {//
			handler(val) {
				this.$set(this.chartSizeData[1], 'value', ~~(0.5 + Number(val)));
			},
			immediate: true,
		},
		'currentContainerOptions.xPosition': {//x
			handler(val) {
				this.$set(this.chartPositionData[0], 'value', ~~(0.5 + Number(val)));
			},
			immediate: true,
		},
		'currentContainerOptions.yPosition': {//y
			handler(val) {
				this.$set(this.chartPositionData[1], 'value', ~~(0.5 + Number(val)));
			},
			immediate: true,
		},
		// 'chartDataList.chart.containerOptions.tableWidth'(val){
		// 	console.log(val);
		// 	this.$set(this.chartSizeData[0],'value',val);
		// },
		// 'chartDataList.chart.containerOptions.tableHeight'(val){
		//   this.$set(this.chartSizeData[1],'value',val);
		// },
	},

};
</script>
<style scoped>
  li {
    padding: 0.4vw 0.5vw;
  }

  .optionsItem {
    /*margin-bottom: 0.8vw;*/
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
  }

  .optionsItem .span-title {
    display: block;
    flex: 1;
    color: #acbbda;
  }

  .optionsItem .li-line-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .border-flex-row {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
  }

  .border-flex {
    display: flex;
    flex-direction: column;
  }

  .space-bottom {
    display: flex;
    flex-direction: column;
    margin-bottom: 0.5vw;
  }

  .li-line-box .space-top {
    margin-top: 0.5vw;
  }

  .li-line-box .color-font {
    color: #acbbda;
    display: block;
    margin-bottom: 0.3vw;
  }

  /* helen's style */

  /**/
  .expand-enter-active,
  .expand-leave-active {
    transition: height 1s ease;
  }

  .expand-leave-active,
  .expand-enter {
    height: 0 !important;
  }

  .expand-leave,
  .expand-enter-active {
    height: 22.9vw;
  }

  .chart-basis .rotateIcon {
    transform: rotate(-90deg);
  }

  .icon {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }

  .type-input {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 0.6vw;
    align-items: center;
  }

  .type-input span {
    display: block;
    color: #acbbda;
  }

  .type-input .svg-details {
    background-color: #11151f;
    border: solid 0.0625vw #475566;
    color: #ffffff;
    width: 8vw;
    text-align: left;
    padding: 0 0.2vw;
  }
</style>
