<template>
  <div class="dimension-wrapper">
    <div v-if="currentChartIndex>-1 && currentChart.containerOptions.chartsType.indexOf('chart')>-1">
      <easy-scroll :barOption="opt"
        :viewStyle="viewStyle">
        <div class="dimension-box">
          <div class="containter-axis">
            <span class="axis-title">{{xAxisTitle}}</span>
            <div v-for="(x,index) in xAxisList"
              :key="index"
              class="axis-size">
              <div class="axis-data flex-box"
                :class="[x.inputType,inputBorderColor('xAxisList',index)]"
                @click="trigger(index,'xAxisList',$event)"
                v-touch:longtap="(e)=>toggleyop('xAxisList',index,e,x)"
                @click.right.prevent="toggleyop('xAxisList',index,$event,x)"
                :ref="`valueAxis${index}`">
                <div class="title-style"
                  v-show="x.isChoice">
                <span v-show="x.isChoice"
                  :class="`dataSource icon-${x.inputType}`"></span>
                  <span v-show="x.isChoice">{{x.name}}</span>
                </div>
                <span v-show="!x.isChoice"
                  class="placeholder-size"></span>
                <span v-show="x.isChoice"
                  class="data-dimension-clear"></span>
                <span v-show="!x.isChoice"
                  class="axis-triangleDown triangleDown"
                  :class="{'rotate':isRotate}"></span>
              </div>
              <data-select :chartIndex="currentChartIndex"
                :dataSelectPostion="dataSelectPostion"
                :axisData="x"
                :axisIndex="index"
                :axisType="'xAxisList'"
                :closeDataSelect="closeDataSelect"
                referencePosition="rightPanl"></data-select>
              <filter-datalist v-show="x.isSelectInput"
                :params="{index:index,propertyName:'xAxisList'}"></filter-datalist>
            </div>
          </div>
          <div class="containter-axis">
            <span class="axis-title">{{yAxisTitle}}</span>
            <div v-for="(y,index) in yAxisList"
              :key="index"
              class="axis-size">
              <div class="axis-data flex-box"
                :class="[y.inputType,inputBorderColor('yAxisList',index)]"
                @click="trigger(index,'yAxisList',$event)"
                v-touch:longtap="(e)=>toggleyop('yAxisList',index,e,y)"
                @click.right.prevent="toggleyop('yAxisList',index,$event,y)"
                :ref="`valueAxis${index}`">
                <!-- @click.right.prevent="toggleyop(index,'yAxisList',$event)" -->
                <div class="title-style"
                  v-show="y.isChoice">
                <span v-show="y.isChoice"
                  :class="`dataSource icon-${y.inputType}`"></span>
                  <span v-show="y.isChoice">{{y.name}}</span>
                </div>
                <span v-show="!y.isChoice"
                  class="placeholder-size"></span>
                <span v-show="y.isChoice"
                  class="data-dimension-clear"
                  @click.stop="clearInput(index,'yAxisList')"></span>
                <span v-show="!y.isChoice"
                  class="axis-triangleDown triangleDown"
                  :class="{'rotate':isRotate}"></span>
              </div>
              <data-select :chartIndex="currentChartIndex"
                :dataSelectPostion="dataSelectPostion"
                :axisData="y"
                :axisIndex="index"
                :axisType="'yAxisList'"
                :closeDataSelect="closeDataSelect"
                referencePosition="rightPanl"></data-select>
              <filter-datalist v-show="y.isSelectInput"
                :params="{index:index,propertyName:'yAxisList'}"></filter-datalist>
            </div>
          </div>
          <div class="containter-axis"
            v-show="dataSeries.length">
            <span class="axis-title"></span>
            <div v-for="(s,index) in dataSeries"
              :key="index"
              class="axis-size">
              <div class="axis-data flex-box"
                :class="[s.inputType,inputBorderColor('dataSeries',index)]"
                @click.stop="trigger(index,'dataSeries',$event)">
                <div class="title-style"
                  v-show="s.isChoice">
                <span v-show="s.isChoice"
                  :class="`dataSource icon-${s.inputType}`"></span>
                  <span v-show="s.isChoice">{{s.name}}</span>
                </div>
                <span v-show="!s.isChoice"
                  class="placeholder-size"></span>
                <span v-show="s.isChoice"
                  class="data-dimension-clear"></span>
                <!-- @click.stop="clearInput(index,'dataSeries')" -->
                <span v-show="!s.isChoice"
                  class="axis-triangleDown triangleDown"
                  :class="{'rotate':isRotate}"></span>
              </div>
              <filter-datalist v-show="s.isSelectInput"
                :params="{index:index,propertyName:'dataSeries'}"></filter-datalist>
            </div>
          </div>

        <div class="containter-axis">
          <span class="axis-title"></span>
          <div v-for="(item,index) in pageFilterList"
            :key="index"
            v-show="item.name"
            class="axis-size">
            <div class="axis-data border-style flex-box"
              :class="item.inputType"
              @click="pageFilterType(index,item)">
              <div class="title-style"
                v-show="item.isChoice">
                <span v-show="item.isChoice"
                  :class="`dataSource icon-${item.inputType}`"></span>
                <span v-show="item.isChoice">{{item.name}}</span>
              </div>
              <span v-show="!item.isChoice"
                class="placeholder-size"></span>
              <span v-show="item.isChoice"
              class="data-dimension-filter"></span>
<!--              <span v-show="!item.isChoice"-->
<!--                :class="triangDown(item.name)"-->
<!--                class="axis-triangleDown"></span>-->
            </div>
            <!--  -->
            <!-- <transition name="slide-fade"> -->
            <div class="filters-box pages-filters-char"
              v-show="item.isFilterChar">
              <span class="filter-title"></span>

                <div class="checkbox">
                  <div class="frame-box checkbox-all">
                    <div class="frame"
                      :class="colorAllCheckClass(index)"
                      @click="allSelect(index)">
                      <div class="rotate-hook"
                        :class="colorAllCheckClass(index)"></div>
                    </div>
                    <span @click="allSelect(index)"></span>
                  </div>
                  <easy-scroll :barOption="innerOpt">
                    <ul>
                      <li v-for="(row,cIndex) in checkData[index]"
                        :key="cIndex">
                        <div class="frame-box">
                          <div class="frame"
                            :class="colorClass(row,cIndex, index)"
                            @click="singleSelect(row, cIndex,index)">
                            <div class="rotate-hook"
                              :class="colorClass(row,cIndex,index)"></div>
                          </div>
                          <span @click="singleSelect(row, cIndex,index)">{{row.name}}</span>
                        </div>
                      </li>
                    </ul>
                  </easy-scroll>
                </div>
                <div class="filter-footer">
                <span class="title"
                  @click='useTextFilter(item,index)'></span>
                  <span class="title"
                    @click="clearTextFilter(index)"></span>
                </div>
              </div>
              <!-- </transition> -->
              <!--  -->
              <div class="filters-box pages-filters-number"
                v-show="item.isFilterNumber">
                <!--              <span class="filter-title"></span>-->
                <!--              <div class="selectbox">-->
                <!--                <div class="selectbox-container">-->
                <!--                  <div class="selectbox-content"-->
                <!--                    @click="showSelectOption(1)"-->
                <!--                    :class="selectBorderColor(1)">-->
                <!--                    <span>{{optionsOne}}</span>-->
                <!--                    <span v-show="optionsOne==''"></span>-->
                <!--                    <span class="selectbox-triangleDown triangleDown  rotate"></span>-->
                <!--                  </div>-->
                <!--                  <ul class="select-options"-->
                <!--                    v-show="isShowSelectOne">-->
                <!--                    <li v-for="item in filterOptionsList"-->
                <!--                      :key="item.value"-->
                <!--                      @click="getSelectOptions(1,item)">-->
                <!--                      <span class="filter-select-label">{{item.label}}</span>-->
                <!--                    </li>-->
                <!--                  </ul>-->
                <!--                </div>-->
                <!--                <input class="input-content"-->
                <!--                  type="text">-->
                <!--              </div>-->
                <!--              <div class="radiobox">-->
                <!--                <div class="exCircle"-->
                <!--                  v-for="(r,rIndex) in pickList"-->
                <!--                  :key="r.id"-->
                <!--                  @click="getRadioValue(rIndex,r)">-->
                <!--                  <div class="inCircle allCenter"-->
                <!--                    v-show="r.isShow"-->
                <!--                    :style="inCirclesStyle"></div>-->
                <!--                  <span>{{r.name}}</span>-->
                <!--                </div>-->
                <!--              </div>-->
                <!--              <div class="selectbox">-->
                <!--                <div class="selectbox-container">-->
                <!--                  <div class="selectbox-content"-->
                <!--                    @click="showSelectOption(2)"-->
                <!--                    :class="selectBorderColor(2)">-->
                <!--                    <span>{{optionsTwo}}</span>-->
                <!--                    <span v-show="optionsTwo==''"></span>-->
                <!--                    <span class="selectbox-triangleDown triangleDown  rotate"></span>-->
                <!--                  </div>-->
                <!--                  <ul class="select-options"-->
                <!--                    v-show="isShowSelectTwo">-->
                <!--                    <li v-for="item in filterOptionsList"-->
                <!--                      :key="item.value"-->
                <!--                      @click="getSelectOptions(2,item)">-->
                <!--                      <span class="filter-select-label">{{item.label}}</span>-->
                <!--                    </li>-->
                <!--                  </ul>-->
                <!--                </div>-->
                <!--                <input class="input-content"-->
                <!--                  type="text">-->
                <!--              </div>-->

                <!--              <span class="filter-data-title"></span>-->
                <div class="silderbox">
                  <!--                <input type="text"-->
                  <!--                  v-model="valueRange[0]">-->
                  <screen-Slider :valueRange="getValueRange(index)"
                    :minValue="getValueRange(index)[0]"
                    :maxValue="getValueRange(index)[1]"
                    type="dataDimension"
                    @getValue='getScreenValue(item,$event)'
                    style="width:100%"></screen-Slider>
                  <!--                <input type="text"-->
                  <!--                  v-model="valueRange[1]">-->
                </div>
                <!--              <div class="filter-footer">-->
                <!--                <span class="title"></span>-->
                <!--                <span class="title"></span>-->
                <!--              </div>-->
              </div>
            </div>
          </div>

          <div class="containter-axis">
            <span class="axis-title"></span>
            <div class="axis-size">
              <div class="axis-data flex-box axis-data-border"
                :class="[reportFilter.inputType,reportFocus()]"
                @click="triggerReport()">
                <div class="title-style"
                  v-show="reportFilter.isChoice">
                <span v-show="reportFilter.isChoice"
                  :class="`dataSource icon-${reportFilter.inputType}`"></span>
                  <span v-show="reportFilter.isChoice">{{reportFilter.name}}</span>
                </div>
                <span v-show="!reportFilter.isChoice"
                  class="placeholder-size"></span>
                <span v-show="reportFilter.isChoice"
                  class="data-dimension-clear"
                  @click.stop="clearReport"></span>
                <span v-show="!reportFilter.isChoice"
                  class="axis-triangleDown triangleDown "
                  :class="{'rotate':isReportRotate}"></span>
              </div>
              <filter-datalist v-show="reportFilter.isSelectInput"
                :params="{index:'',propertyName:'reportFilter'}"></filter-datalist>
            </div>
          </div>
        </div>
      </easy-scroll>
    </div>
    <div v-else-if="currentChartIndex>-1"
      class="noDataTips">
      <span class="biFont icon-success tipsIcon"></span>
      <p class="tipsText"></p>
    </div>
  </div>
</template>
<script>
import FilterDataList from '../RightPane/FilterDataList';
import DataSelect from '@/components/DataSelect/DataSelect';
import ScreenSlider from './components/ScreenSlider';
import {mapGetters} from 'vuex';
export default {
	name: 'DataDimension',
	components: {
		'filter-datalist': FilterDataList,
		'screen-Slider': ScreenSlider,
		'data-select': DataSelect
	},
	data() {
		return {
			focusIndex: -1,
			focusType: '',
			isReportFocus: false,
			isRotate: false,
			isReportRotate: false,
			/** */
			isShowSelectOne: false,
			isShowSelectTwo: false,
			optionsOne: '',
			optionsTwo: '',
			filterOptionsList: [
				{
					label: '',
					value: 'Include'
				},
				{
					label: '',
					value: 'UnInclude'
				},
				{
					label: '',
					value: 'Isbegin'
				},
				{
					label: '',
					value: 'UnIsbegin'
				},
				{
					label: '',
					value: 'Equal'
				},
				{
					label: '',
					value: 'UnEqual'
				}
			],
			/** */
			checkData: [],  //
			curCheckIndex: [],
			/** */
			valueRange: [],//
			/** */
			isPick: -1,
			pickList: [
				{
					id: 1,
					name: '',
					isShow: false
				},
				{
					id: 2,
					name: '',
					isShow: false
				},
				{
					id: 3,
					name: '',
					isShow: false
				}
			],
			/** */
			opt: {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barWidth: 4, //
				barMarginRight: 6, //px
				barOpacityMin: 0.3, //
				zIndex: 'auto'
				//
			},
			innerOpt: {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barWidth: 4, //
				barMarginRight: 3, //px
				barOpacityMin: 0.3, //

				//
			},
			viewStyle: {
				padding: '0 1vw'
			},
			/** */
			tipsData: {
				text: '',
				rel: '',
				time: 1500
			},
			/**dataselect */
			dataSelectPostion: {
				left: '0px',
				top: '0px'
			},
			closeDataSelect: false,
			type: '',//
		};
	},
	computed: {
		...mapGetters('main', ['getVw', 'vwToPx','getScreenWidth']),
		...mapGetters('dataDimension', {
			currentChartAxis: 'getCurrentChartAxis',
			// axisReport: "getAxisReport"
			defaultChartAxis: 'getDefaultChartAxis',
			defaultChartAllAxis: 'getDefaultChartAxisAll',
			reportFilter: 'getAxisReport',
			pageFilterList: 'getPageFilterList',
		}),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',
			dimensionDefault: 'getDimensionDefault',
			currentChartType: 'getCurrentChartType',
			currentChart: 'getCurrentChartOptions',
			currentIsSetOptions: 'getIsSetOptions',
			getChartType: 'getChartsType',
			chartList: 'getChartList',
			ChartsType: 'getCurrentChartsType',
			ScreenSign: 'getScreenSign'
		}),
		...mapGetters('defaultChartOptions', {
			getCategoryAxis: 'getCategoryAxis'
		}),

		xAxisList() {
			if (this.currentChartAxis.xAxisList) {
				return this.currentChartAxis.xAxisList;
			} else {
				return this.defaultChartAxis;
			}
		},
		yAxisList() {
			if (this.currentChartAxis.yAxisList) {
				return this.currentChartAxis.yAxisList;
			} else {
				return this.defaultChartAxis;
			}
		},
		xAxisTitle() {
			if (this.getChartType(this.currentChartIndex) === 'echarts-3dWorldMap') {
				return '';
			} else {
				return 'X';
			}
		},
		yAxisTitle() {
			if (this.getChartType(this.currentChartIndex) === 'echarts-3dWorldMap') {
				return '';
			} else {
				return 'Y';
			}
		},
		dataSeries() {
			if (this.currentChartAxis.dataSeries) {
				return this.currentChartAxis.dataSeries;
			} else {
				return this.defaultChartAxis;
			}
		},
		inCirclesStyle() {
			return {
				width: parseInt(this.getVw(9)) + 'px',
				height: parseInt(this.getVw(9)) + 'px'
			};
		},
		/**xy */
		isResetChart() {
			let vm = this;
			let object = vm.currentChartAxis;
			if (object.xAxisList) {
				let mergeAxisList = JSON.parse(
					JSON.stringify([
						...object.xAxisList,
						...object.yAxisList,
						...object.dataSeries
					])
				);
				const isNull = mergeAxisList.every(e => e.name == '');
				return isNull;
			} else {
				return false;
			}
		},
		getValueRange() {
			return (index) => {
				return this.valueRange[index] || [0, 9999];
			};
		}

	},
	watch: {
		currentChartIndex(newValue,oldValue) {
			let vm = this;
			vm.focusIndex = -1;
			vm.focusType = '';
			vm.isReportFocus = false;
			if (vm.currentChartIndex > -1 && vm.currentChart.containerOptions.chartsType.indexOf('chart')>-1) {
				vm.dexieDB.aXisList.get(vm.currentChartIndex).then(res => {
				  console.log(res);
					vm.$store.commit(
						'dataDimension/setCurrentChartAxis',
						res.axisOptions
					);
				});
			} else {
				vm.$store.commit(
					'dataDimension/setCurrentChartAxis',
					vm.defaultChartAllAxis
				);
			}
		},
		/**  ps:00++*/

	},
	methods: {
		triangDown(name) {
			if (name != '') {
				return 'triangleDown';
			} else {
				return 'darkColor';
			}
		},
		/**
			 * 
			 */
		trigger(index, type, event) {
			const ele = event.srcElement || event.target;
			if (ele.className.indexOf('data-dimension-clear') > -1) {
				this.clearInput(index, type);
			} else {
				if (this.currentChartIndex > -1 && this.currentChartAxis.xAxisList) {
			    this.triggerSelect(index, type);
					this.isRotate = !this.isRotate;
					this.focusIndex = index;
					this.focusType = type;
				} else {
					this.tips.show(['']);
				}
			}
		},
		triggerSelect(index, type) {
			console.log('x',index, type);
			this.$store.commit('dataDimension/toggleIsChoice', {
				index: index,
				type: type
			});
		},
		triggerReport() {
			if (this.currentChartIndex > -1) {
				this.$store.commit('dataDimension/toggleReport');
				if (this.focusIndex > -1) {
					this.focusIndex = -1;
				}
				this.isReportRotate = !this.isReportRotate;
				this.isReportFocus = !this.isReportFocus;
			} else {
				this.tips.show(['']);
			}
			// this.$store.commit("dataDimension/toggleReport");
		},
		/**
			 * 
			 */
		showSelectOption(number) {
			if (number == 1) {
				this.isShowSelectOne = !this.isShowSelectOne;
			} else {
				this.isShowSelectTwo = !this.isShowSelectTwo;
			}
		},
		selectBorderColor(number) {
			if (number == 1 && this.isShowSelectOne) {
				return 'select-focus';
			} else if (number == 2 && this.isShowSelectTwo) {
				return 'select-focus';
			} else {
				return 'select-unfocus';
			}
		},
		/** */
		inputBorderColor(type, index) {
			if (type == this.focusType && index == this.focusIndex) {
				return 'axis-data-focus';
			} else {
				return 'axis-data-unfocus';
			}
		},
		reportFocus() {
			if (this.isReportFocus) {
				return 'axis-data-focus';
			} else {
				return 'axis-data-unfocus';
			}
		},
		/** */
		getSelectOptions(number, option) {
			if (number == 1) {
				this.optionsOne = option.label;
				this.isShowSelectOne = !this.isShowSelectOne;
			} else {
				this.optionsTwo = option.label;
				this.isShowSelectTwo = !this.isShowSelectTwo;
			}
		},
		/**
			 * 
			 */
		rangeValue(value) {
			this.valueRange = value;
		},
		colorClass(info, cindex, index) {
			if (info.checked) {
				if (cindex == this.curCheckIndex[index]) {//index
					return 'rotate-curHook';
				} else {
					return 'rotate-comHook';
				}
			} else {
				return '';
			}
		},
		/**
			 * 
			 */
		colorAllCheckClass(index) {
			if (this.checkData.length > 0) {  //
				if (this.isAllSelected(index)) {
					if (this.curCheckIndex[index] == 'all') { //all
						return 'rotate-curHook';//
					} else {
						return 'rotate-comHook';//()
					}
				} else {
					return '';
				}
			}
		},
		allSelect(index) {
			let checked = true;
			this.curCheckIndex[index] = 'all';  //
			if (this.isAllSelected(index)) {  //
				checked = false;
			}
			this.checkData[index] = this.checkData[index].map(e => {
				e.checked = checked;
				return e;
			});
		},
		// 
		singleSelect(row, cIndex, index) {
			this.curCheckIndex[index] = cIndex;
			row.checked = !row.checked;
		},
		/**   */
		pageFilterType(index, item) {
			let vm = this;
			let data;
			vm.tableId = item.tableIndex;
			switch (item.inputType) {
			case 'int':
				vm.type = 'number';
				break;
			case 'char':
				vm.type = 'text';
				break;
			default:
				vm.type = 'time';
			}
			let params = {
				sourceId: item.tableIndex,
				field: item.name,
				type: vm.type
			};
			vm.$get('api/tableData', params)   //
				.then(res => {
					if (res.code == '000000') {
						if (item.inputType == 'int') {  //
							let arr = [res.data.min, res.data.max];
							vm.$set(vm.valueRange, index, arr);
						} else {
							data = res.data.map((item, cur) => {
								let obj = {
									name: item,
									checked: false,
								};
								return obj;
							});
							vm.$set(vm.checkData, index, data);
						}
					}
				}).catch(error => {
					console.log(error);
				});
			if (item.name != '') {
				this.$store.commit('dataDimension/togglePageFilterType', {
					index: index,
					inputType: item.inputType
				});
			}
		},
		/** */
		getRadioValue(i, r) {
			r.isShow = !r.isShow;
			this.pickList.forEach((e, index) => {
				if (index != i) {
					e.isShow = false;
				}
			});
		},
		/**
			 * 
			 * aXisList
			 * 
			 * */
		clearInput(index, type) {
			let vm = this;
			vm.focusIndex = -1;
			if (vm.currentChartAxis[type].length > 2) {
				vm.currentChartAxis[type].splice(index, 1);
			} else {
				const categoryAxis = vm.currentChartAxis[type][index]['categoryAxis'];
				let dimensionDefault = JSON.parse(JSON.stringify(vm.dimensionDefault));
				dimensionDefault.categoryAxis = categoryAxis;
				vm.$set(vm.currentChartAxis[type], index, dimensionDefault);
			}

			vm.dexieDB.aXisList
				.put({
					index: vm.currentChartIndex,
					axisOptions: vm.currentChartAxis
				})
				.then(() => {
				})
				.catch(error => console.error(error));
			vm.$store.commit(
				'dataDimension/setCurrentChartAxis',
				vm.currentChartAxis
			);
			vm.$store.commit('dataDimension/setUpdateCount');
			if (!vm.isResetChart) {
				vm.$store.commit('canvasPlace/setIsSetOptions', {
					index: vm.currentChartIndex,
					clearSingleAxis: true
				});
			}
			vm.$store.commit('dataDimension/setAmendChartCount');
		},
		/** */
		clearReport() {
			this.$store.commit('dataDimension/clearReport');
		},
		/** */
		clearTextFilter(index) {
			this.checkData[index] = this.checkData[index].map(e => {
				e.checked = false;
				return e;
			});
		},
		// 
		useTextFilter(item, index) {
			let vm = this;
			let params = {
				// screenState: 2,//
				// projectId: sessionStorage.getItem('projectId'), //ID
				sizerId: this.ScreenSign.pageSn,
				screenData: [{
					selectAllData:false,
					screenStart:item.screenStart||'' ,//
					screenEnd:item.screenEnd|| '' ,//
					sizerType: 1, //    1.  2.
					screenText: item.screenText || [],//
					screenType: vm.type,//
					screenSourceId: item.tableIndex, //id
					screenField: item.name//
				}]
			}
				;
			if (index != -1) {
				vm.checkData[index].filter(item => {  //Name
					if (item.checked) {
						params.screenData[0].screenText.push(item.name);
					}
				});
			}

			if (!params.screenData[0].screenText.length&&index != -1) {
				vm.tips.show(['']);
				return false;
			}
      console.log(params);
			vm.$put('api/sizer', params).then(res => {
				if (res.code == '000000') {
					// vm.$store.commit('dataDimension/setAmendChartCount'); //++
					vm.$store.dispatch('canvasPlace/screeningRequest');//
				}

			});

		},
		/**yop */
		toggleyop(axisType, index, event, info) {
			let x = event.x;
			if (event.type === 'touchend') {
				x = event.changedTouches[0].clientX;
			}
			if (
			// axisType != this.getCategoryAxis(this.currentChartType) &&
				info.name
			) {
				this.$store.commit('dataDimension/setValueAxisfilterPanl', 'rightPanl');
				this.$store.commit('dataDimension/setValueAxisfilterCount', {
					index: index,
					axisType: axisType
				});
				const path = event.path || (event.composedPath && event.composedPath());
				const ele = path.find(ele => {
					return ele.className.indexOf('axis-data') > -1;
				});
				let boundingXDistance = this.getScreenWidth - x;
				let eleClient = ele.getBoundingClientRect();
				let h = eleClient.top + ele.clientHeight / 2;//
				this.dataSelectPostion.top = h + 'px';
				this.dataSelectPostion.top = h + 'px';
				const minDistance = 68 + this.vwToPx(0.5); //
				let safeZone = minDistance + 68;  //
				// if (boundingXDistance < this.vwToPx(3.6) + this.vwToPx(0.5)) {
				if (boundingXDistance < safeZone) {  //
					if (boundingXDistance >= 68) {  //
						this.$store.commit('dataDimension/setSafety', 1);
					} else {
						this.$store.commit('dataDimension/setSafety', 0);
					}
					this.dataSelectPostion.left = x - minDistance + 'px'; //
				} else {
					this.$store.commit('dataDimension/setSafety', 1);
					this.dataSelectPostion.left = x + this.vwToPx(0.5) + 'px';
				}
			} else {
				this.closeDataSelect = true;
			}
		},
		/**checked true */
		isAllSelected(index) {
			if (this.checkData[index]) {
				return this.checkData[index].every(e => e.checked);
			}
		},
		/*screenText  */
		getScreenValue(item, data) {
			item.screenStart = data[0];
			item.screenEnd=data[1];
			this.useTextFilter(item, -1);
		},
	},
	mounted() {
		for (let i in this.pageFilterList) {
			this.curCheckIndex[i] = -1;  //
		}
	}
};
</script>
