<template>
    <div class="siderbar-right"
      data-class='unableDeactive'>
      <!--   -->
      <div v-show="!isPhoneLayout">
        <transition name="fade">
          <sider-items v-show="rightPaneShow"></sider-items>
          <!-- PC   -->
        </transition>
        <!--    PC-->
        <transition name="slide">
          <sider-shrink v-show="!rightPaneShow"></sider-shrink>
        </transition>
        <transition name="fade">
          <right-pane v-show="rightPaneShow||rightPaneHover"></right-pane>
        </transition>
      </div>
      <!--    -->
      <transition name="fade">
        <siwtchphone-right v-show="isPhoneLayout"></siwtchphone-right>
      </transition>
      <PhoneSetting class='fa-mortar-board' v-show="isPhoneLayout"></PhoneSetting>
      <!--  -->
    </div>
</template>
<script>
import SiderItems from './SiderItems';
import SiderShrink from './SiderShrink';
import SwitchPhoneRight from '../RightPane/PhoneLayout';
import PhoneSetting from '../RightPane/components/PhoneSetting';
import { RightPane } from '../RightPane';
import { mapState,mapGetters} from 'vuex';
export default {
	components: {
		'sider-items': SiderItems,
		'sider-shrink': SiderShrink,
		'right-pane': RightPane,
	  'siwtchphone-right':SwitchPhoneRight,
		PhoneSetting
	},
	data(){
	    return{
		};
	},
	computed: {
		...mapState('dashboard', ['rightPaneShow', 'rightPaneHover']),
		...mapGetters('canvasPlace', {
			isPhoneLayout: 'getPhoneLayoutState',
		}),
	},
	watch: {
	},
};
</script>
<style scoped>
  .fa-mortar-board{
    position: fixed;
    left: 15px;
    top: calc(2.6vw + 14px);
  }
.fade-enter-active {
  pointer-events: none;
  animation: fadeInRight 0.6s;
}
.fade-leave-active {
  pointer-events: none;
  animation: fadeInRight 0.6s reverse;
}
@keyframes fadeInRight {
  from {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
  }

  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
.slide-enter-active {
  animation: fadeInRight 1s;
}
.slide-leave-active {
  animation: fadeInRight 1s reverse;
}
</style>
