<template>
  <div class="wrapper"
    data-class="unableDeactive"
    :class="{ hover: isHover }"
       v-show="!isPhoneLayout">
    <div class="title"
      v-show="isHover">
      {{ titleArr[index] }}
      <span class="menu-right biFont icon-doubleArrowRight"
        @click="offHover"></span>
    </div>
    <div class="content">
      <dataSourceList class="dataSourceList"
        :dataTitle="dataSourceTitle"
        :requestUrl="requestUrl"
        v-show="index == 0"
        id="dataSourceList"></dataSourceList>
      <DataDimension
        v-show="index == 1"
        class="dataDimension"></DataDimension>
        <ChartOptions
          v-if="index == 0||index == 2"
          :class="{hidden:index !== 2}"
          class="chartOptions"></ChartOptions>
      <asyncComponent
        v-show="index == 3"
        componentUrl="https://24e-img.oss-cn-shenzhen.aliyuncs.com/static/bi/component/ChartRecommended/ChartRecommended.js">
      </asyncComponent>
<!--      <chart-recommended v-show="index === 3"></chart-recommended>-->
    </div>
  </div>
</template>
<script>
import DataSourceList from '@/pages/DataSource/index';
import DataDimension from './DataDimension';
import ChartOptions from './ChartOptions';
import asyncComponent from '@/components/AsyncComponent';
import { mapState,mapGetters } from 'vuex';
export default {
	name: 'RightPane',
	components: {
		DataSourceList,
		DataDimension,
		ChartOptions,
		asyncComponent
	},
	data() {
		return {
			dataSourceTitle: {
				title: '',
				icon: ''
			},
			dataCreateChart: '',
			layoutCenter:'true',
			requestUrl: 'api/dataInfo'
		};
	},
	methods: {
		offHover() {
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneShow',value:false});
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneHover',value:false});
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneIndex',value:-1});
		}
	},
  computed: {
		...mapState('dashboard', {
			titleArr: state => state.rightPaneTitleArr,
			isHover: state => state.rightPaneHover,
			index: state => state.rightPaneIndex
		}),
		...mapGetters('canvasPlace', {
			isPhoneLayout: 'getPhoneLayoutState',
		}),
	},
  watch: {
	  index(val){
      console.log(val);
	  }
  }
};
</script>
<style scoped>
.wrapper {
  position: absolute;
  width: 15.2vw;
  right: 0;
  height: calc(100vh - 4.2vw - 0.12vh);
  background-color: #141924;
  padding: 2vh 0.5vw 0;
}
.wrapper.hover {
  padding: 0;
  overflow: hidden;
  height: 66.6vh;
  right: 2.6vw;
  top: 5.4vh;
  border: solid 0.0625vw #475566;
}
.wrapper > .title {
  padding-left: 1.3vw;
  background-color: #273045;
  color: #fff;
  font-size: 0.9vw;
  width: 100%;
  height: 3.2vh;
  line-height: 3.2vh;
}
.wrapper .content {
  width: 100%;
  height: 100%;
  position: relative;
}
.wrapper.hover .content {
  width: 100%;
  padding: 0.9vw 0.4vw;
  height: calc(100% - 3.2vh);
}
.wrapper .content > div {
  position: relative;
  width: 100%;
  height: 100%;
  padding-bottom: 1vw;
}

.wrapper >>> .dataTableInfo {
  width: 100%;
  height: 100%;
  background: transparent;
}

.wrapper >>> .field-list {
  height: calc(100% - 1.2vw);
}
.wrapper >>> .field-list .el-scrollbar__view {
  padding-bottom: 1vw;
}
.wrapper .dataSourceList >>> .dataList {
  height: calc(100% - 4vh);
  /* padding: 1vw */
}
.wrapper .dataSourceList >>> .dataList ul.list {
  padding-right: 1.1vw;
  width: 100%;
}
.wrapper .dataSourceList >>> .dataList .dataList-container {
  height: calc(100% - 4.3vh);
}
.wrapper .content .dataDimension {
  position: absolute;
  top: 0;
  left: -0.6vw;
  width: calc(100% + 1.2vw);
}

.wrapper.hover .content .dataDimension {
  width: calc(100% + 1.2vw);
}

.wrapper .content .chartOptions {
  pointer-events: visible;
  position: absolute;
  top: 0;
  left: -0.6vw;
  width: calc(100% + 1.2vw);
}
.wrapper .content .chartOptions.hidden{
  pointer-events: none;
}

.wrapper.hover .content .chartOptions {
  width: calc(100% + 1.2vw);
}
/* .wrapper.hover .dataSourceList >>> .dataList .dataList-container {
		height: calc(56.2vh - 2.1vw);
	} */
/* .dataSourceList >>> .maskBackground {
  position: fixed;
  height: calc(100vh - 2.6vw);
  top: 2.6vw;
} */
.dataSourceList >>> .maskBackground .fileContent {
  position: fixed;
}
.wrapper >>> .dimension-box {
  width: 100%;

  margin-left: 0;
}
.menu-right {
  cursor: pointer;
  font-size: 0.625vw;
  margin-right: 0.7vw;
  float: right;
}
</style>
