<template>
  <transition
    enter-class="boxAnita"
    enter-active-class="boxAnita"
    leave-class="boxAnitaLeave"
    leave-active-class="boxAnitaLeave"
  >
    <div class="maskBackground">
      <!-- :style="{width:boxWidth+`vw`}" -->
      <div class="box allCenter2" :style="{width:`${options.boxWidth || boxWidth}vw`}">
        <div class="title">
          <span v-if="options.icon" :class="`${options.icon}`"></span>
          <span>{{options.title}}</span>
        </div>
        <div :class="['content',{visualCont:options.isVisual}]">
          <div v-if="options.describe" class="desc">{{options.describe}}</div>
          <div
            class="flex-box"
            :class="changeFlex(options.name)"
            v-for="(item,index)  in options.name"
            :key="index"
          >
            <!--options.isVisual-->
            <div class="createVisual" v-show="options.isVisual"></div>
            <span class="label-title" :class="changeBox(options.name)">{{item}}</span>
            <input
              v-if="options.isVisual"
              :type="options.inputType"
              ref="input"
              :placeholder="options.placeholder"
              :class="['inputBox',{redTips:options.isVisual && options.tipsMessage != ''}]"
              :style="{width:`${options.inputWidth ||inputWidth}vw`}"
              v-model="visualMessage"
              :maxlength="options.isVisual? 30: false"
            />
            <input
              v-else
              :type="options.inputType"
              ref="input"
              :placeholder="options.placeholder"
              :class="['inputBox']"
              :style="{width:`${options.inputWidth ||inputWidth}vw`}"
              v-model="message[index]"
              :maxlength="options.isVisual? 30: false"
            />
          </div>
          <div class="tipsMessage" v-show="showTips">{{options.tipsMessage}}</div>
          <div
            class="button-box"
            :class="[changeBtnBox(options.name),{buttonTips: options.isVisual}]"
          >
            <span
              v-for="(item,index) in options.btn"
              :class="['btn',{visualBtn: options.isVisual}]"
              :key="index"
              @click="funArr[index](message)"
            >{{item.text}}</span>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import { modal } from "../mixin/modal";
export default {
  name: "Modal",
  mixins: [modal],
  data() {
    return {
      message: [],
      boxWidth: 17,
      inputWidth: 10.5,
      inputHeight: 1.4,
      visualMessage: ''
    };
  },
  computed: {
    isSingleInput() {},
    changeFlex: () => nameList => {
      if (nameList.length == 1 && nameList[0] == "") {
        return "flex-box-center";
      } else {
        return "flex-box-around";
      }
    },
    showTips() {
      console.log(this.options.placeholder);
      return this.options.placeholder;
    }
  },
  methods: {
    test(a) {
      console.log(a);
    },
    changeBox(nameList) {
      if (nameList.length > 0 && nameList[0] != "") {
        this.boxWidth = 22;
        this.inputWidth = 13.6;
      }
    },
    changeBtnBox(nameList) {
      if (nameList.length > 0 && nameList[0] != "") {
        return "buttonCreate";
      } else {
        return "buttonDiv";
      }
    }
  },
  mounted() {
    if (this.options.name.length === 1) {
      this.$refs.input[0].focus();
    }
  },
  watch: {
    visualMessage: {
      deep:true,
      handler(val) {
        console.log(val)
        this.$emit('setTips',val)
      }
    }
  }
  //   
  //propsoptions
  //callback
  //title
  //text
  //btn,
  //text
  //callback
  // describe
  // placeholder
  //  type
  //name
  //enterKeyIndexescKeyIndex ,btn
  //   
  //  options: {
  //     title: "",
  //      icon:""
  // 		describe:"",
  // 		inputType:"",
  // 		placeholder:"",
  //     name:[],
  //     btn: [
  //       {
  //         textArea: "",
  //         callback(data) {
  //           alert(data);
  //         }
  //       },
  //       {
  //         textArea: "",
  //         callback(data) {
  //           alert(data);
  //         }
  //       }
  //     ]
  //   }
  //     enterKeyEventIndex:0,
  //     escKeyEventIndex:1,
};
</script>
<style scoped>
.boxAnita {
  animation: anita1 0.5s;
}
.boxAnitaLeave {
  animation: anita2 0.5s;
}
@keyframes anita1 {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes anita2 {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.box {
  background-color: #19202e;
  text-align: center;
  position: fixed;
}

.box .title {
  height: 3.3vh;
  background-color: #273045;
  line-height: 3.3vh;
  font-size: 0.8vw;
}

.content {
  padding: 1vw 1.6vw 0.8vw;
  margin-top: 0.6vw;
  line-height: 3vh;
}

.content .desc {
  height: 2vw;
  font-size: 0.8vw;
  line-height: 2vw;
  text-align: center;
  letter-spacing: 0.1vh;
}

.flex-box {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.flex-box-center {
  justify-content: center;
}

.flex-box-around {
  justify-content: space-around;
}

.box .inputBox {
  color: #fff;
  padding-left: 0.3vw;
  background-color: #121721;
  border: solid 0.0625vw #475566;
  outline: none;
  height: 2.5vh;
}

.box .inputBox:focus {
  border-color: #28b3fc;
}

.box .buttonDiv {
  margin-top: 3vh;
  margin-bottom: 1vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-left: -0.6vw;
}

.button-box {
  display: flex;
  flex-direction: row;
}
.box .buttonTips {
  justify-content: flex-end;
  margin-top: 2vh;
  margin-right: 0;
}
.box .buttonCreate {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 0.5vw;
  margin-right: 0.48vw;
  margin-top: 1.5vw;
}

.box .btn {
  display: block;
  background-color: #273045;
  margin-left: 0.6vw;
  width: 3.6vw;
  height: 1.4vw;
  line-height: 1.275vw;
  text-align: center;
  font-size: 0.6vw;
  cursor: pointer;
  outline: none;
  border: solid 0.0625vw #273045;
}

.box .btn:hover {
  background-color: #1c3d57;
  border-color: #28b3fc;
}

.createVisual {
  color: rgba(255, 255, 255, 1);
  margin-right: 0.5vw;
}

.tipsMessage {
  height: 2.5vh;
  line-height: 2.5vh;
  margin-left: 4.6vw;
  font-size: 0.7vw;
  text-align: left;
  color: rgba(255, 0, 0, 1);
}
.redTips {
  border-color: rgba(255, 0, 0, 1) !important;
}
.visualCont {
  padding: 2vw 2.1vw 0.5vw;
}
.box .visualBtn {
  margin-left: 1.3vw;
}
</style>


