<template>
  <div :class="{'loadingTop':!loadingShow}">
    <siderBar></siderBar>
    <siderBarRight :shrink="rightPaneShow"></siderBarRight>
    <!--  -->
    <loading :dataIsOk="loadingShow"
      :styleObj="styleObj"></loading>
<!--  -->
    <div class="workPlace"
      @click.right.prevent
      :style="{transform:!isPhoneLayout?`translateX(${workPlaceLeft}vw)`:`translateX(0vw)`,width:workPlaceWidth+'vw'}">

      <ruler :width="vwToPx(ruler.width)"
        :height="vhToPx(ruler.height)"></ruler>
      <!--      pc-->
      <CanvasPlace v-if="!isPhoneLayout"
        class="canvasPlace"
        @dataIsOk="dataIsok"
        :style="{width:canvasInfo.width+'px',
                 height:canvasInfo.height+'px',
                 transform:`scale(${scaleLevel}`+')'
                }"
        :isSendChart="isSendChart"></CanvasPlace>
      <!--      -->
      <phoneLayout class="phoneStyle"
        ref="phoneLayout"
        v-else></phoneLayout>
      <!--XY-->
      <data-select :chartIndex="listType.selectedIndex"
        :curChartType="currentChart?currentChart.containerOptions.chartsType:''"
        :dataSelectPostion="listType.dataSelectPostion"
        :axisData="listType.dataType"
        :axisIndex="listType.index"
        :axisType="listType.axisType"
        :closeDataSelect="closeDataSelect"
        :referencePosition="'chartPanl'"></data-select>
      <!-- @setSendChartStatus="getSendChartStatus" -->
      <grid class="grid"
        :width="vwToPx(100)"
        :height="vhToPx(100)"></grid>
    </div>
    <bi-footer :height="footer.height"
      :canvasInfo="canvasInfo"
      @dataIsOk="dataIsok"></bi-footer>
    <Modal :options="promptData"
      v-if="isShowModal"></Modal>
<!--    <Tips :tipsData="tipsData"-->
<!--      ref="tips"></Tips>-->
    <DataShow :show='dataShow'></DataShow>
<!--    -->
<!--    <chart-recommend></chart-recommend>-->
<!--    <geo-mapjson></geo-mapjson>-->
  </div>
</template>

<script>
import {CanvasPlace} from './components/CanvasPlace';
import {SiderBar} from './components/SiderBar';
import {SiderBarRight} from './components/SiderBarRight';
import {Ruler} from './components/Ruler';
import {Grid} from './components/Grid';
import {Footer} from './components/Footer';
import {mapState, mapGetters, mapActions} from 'vuex';
import Modal from '@/components/Prompt/Modal';
import Loading from '@/components/Loading/LoadingProgress.vue';
import phoneLayout from './components/CanvasPlace/Phone';
import DataSelect from '@/components/DataSelect/DataSelect';
import DataShow from './components/CanvasPlace/components/dataShow';
// import ChartRecommend from './components/ChartRecommendation/ChartRecommendation';
// import geoMapjson from './components/ChartRecommendation/geoMap';//
import {throttle} from '@/utils/utilsMethods';

export default {
	name: 'visualization',
	components: {
		CanvasPlace,
		SiderBar,
		SiderBarRight,
		Ruler,
		Grid,
		'bi-footer': Footer,
		Modal,
		// Tips,
		Loading,
		phoneLayout,
		'data-select': DataSelect,
		DataShow,
		// 'chart-recommend':ChartRecommend,
		// 'geo-mapjson':geoMapjson
	},
	data() {
		return {
			throttleResetMobileChart: () => {
			},
			loadingShow: false,
			isSendChart: false,
			styleObj: {
				width: '100%',
				height: '100%'
			},
			grid: {
				width: 1000,
				height: 1000
			},
			ruler: {
				width: 95.8,
				height: 92.4
			},
			canvasInfo: {
				width: 1920,
				height: 1080
			},
			footer: {
				width: 95.8,
				height: 1.6
			},
			tipsData: {
				text: '',
				rel: '',
				time: 1500
			},
			rightPaneShow: false,
			isShowModal: false,
			transformState: true,//
			promptData: {
				title: '!',
				describe: '',
				inputType: 'text',
				placeholder: '',
				name: [],
				btn: [
					{
						text: '',
						callback: data => {
							this.isShowModal = false;
							this.saveCharts();
						}
					},
					{
						text: '',
						callback: data => {
							this.isShowModal = false;
						}
					}
				],
				enterKeyEventIndex: 0,
				escKeyEventIndex: 1
			},
			fatherProjectId: {},
			fatherProjectName: {},
			closeDataSelect: false,
			isShow: false,
		};
	},
	beforeRouteEnter(to, from, next) {
		let fatherProjectId = localStorage.getItem('fatherProjectId');
		if (fatherProjectId) {
			next();
		} else {
			next({name: 'main'});
		}
	},
	beforeMount() {
	  this.throttleResetMobileChart=throttle(this.ResetMobileChart,1000);
		this.$forceUpdate();
	},
	created() {
		this.$store.commit('dashboard/setStateProperty', {key:'leftSideWidth',value:2.6});
		this.$store.commit('dashboard/setStateProperty', {key:'leftSideOpen',value:false});
		this.$store.commit('canvasPlace/resetPhoneLayoutState');
	},
	methods: {
	  ...mapActions('canvasPlace', {
	    ResetMobileChart: 'resetMobileChartOptions'
	  }),
		// 	this.isSendChart = false;
		// getInitStatus(status) {
		// 	this.dataIsOk = status;
		// },
		dataIsok(val) {
			this.loadingShow = val;
		},
		// getSendChartStatus() {
		saveCharts() {
			let vm = this;
			if (this.routerState == true) {
				vm.$store.commit('chartBus/setRouter', false);
				vm.isSendChart = true;
				/**
					 * vuexindexDB
					 */
				// vm.dexieDB.chartsOption.clear();
				// vm.dexieDB.saveOption.clear();
				// vm.dexieDB.aXisList.clear();
				// vm.dexieDB.saveOption.clear();
				// vm.$store.commit('CanvasPlace/clearArrChart');
				// vm.$store.commit('dataDimension/setClearAxis');
			} else {
				console.log('');
			}
		},
		// },
		setScaleValue(data) {
			// this.scaleLevel = this.toPoint(`${data}%`);
			this.scaleLevel = data / 100;
		}
		// toPoint(percent) {
		// 	var str = percent.replace('%', '');
		// 	str = str / 100;
		// 	return str;
		// }
	},
	computed: {
		...mapState('dashboard', {
			workPlaceWidth: state => state.workPlaceWidth
		}),
		...mapGetters('chartBus', {
			timeState: 'returnTime',
			routerState: 'returnRouterState',
		}),
		...mapGetters('canvasPlace', {
			scaleLevel: 'getScaleLevel',
			isPhoneLayout: 'getPhoneLayoutState',
			currentChartIndex: 'getCurrentChartIndex',
			currentChart: 'getCurrentChartOptions',
			currentChartType: 'getCurrentChartType',
			dataShow: 'getDataShow'
		}),

		...mapGetters('main', ['vwToPx', 'vhToPx']),
		...mapGetters('dashboard', {
			workPlaceLeft: 'getWorkPlaceLeft',
			footerWidth: 'getFooterWidth'
		}),
		...mapGetters('dataDimension', {
			currentChartAxis: 'getCurrentChartAxis',
			defaultChartAxis: 'getDefaultChartAxis',
			defaultChartAllAxis: 'getDefaultChartAxisAll',
			reportFilter: 'getAxisReport',
			pageFilterList: 'getPageFilterList',
			listType: 'getSelectList',
		}),

	},
	watch: {
		isPhoneLayout(val) {
			this.$store.commit('canvasPlace/setCurrentChartIndex', -1);
			if (!val) {
				this.throttleResetMobileChart();
			}
		},
		// currentChartIndex(newValue,oldValue) {
		// 	let vm = this;
		// 	vm.focusIndex = -1;
		// 	vm.focusType = '';
		// 	vm.isReportFocus = false;
		// 	if (vm.currentChartIndex > -1
		//     && vm.currentChart.containerOptions.chartsType.indexOf('chart')>-1) {
		//
		// 		vm.dexieDB.aXisList.get(vm.currentChartIndex).then(res => {
		// 		  console.log('111111');
		//
		// 			vm.$store.commit(
		// 				'dataDimension/setCurrentChartAxis',
		// 				res.axisOptions
		// 			);
		// 		});
		// 	} else {
		// 		vm.$store.commit(
		// 			'dataDimension/setCurrentChartAxis',
		// 			vm.defaultChartAllAxis
		// 		);
		// 	}
		// }
	}
};
</script>
<style scoped>
  .loadingTop >>> .maskBackground {
    top: 5.2vw !important;
  }

  .workPlace {
    overflow: hidden;
    position: absolute;
    top: 0;
    height: 100%;
    transition: transform 0.8s;
  }

  .workPlace .canvasPlace {
    transform-origin: 0 0;
    z-index: 1;
    position: absolute;
    left: 34px;
    top: 34px;
    background: transparent;
    box-shadow: 0 0 1.526vw 0 rgba(0, 0, 0, 0.9);
    transition: 0.2s all ease-in-out;
  }

  .workPlace .grid {
    z-index: 0;
    position: absolute;
    left: 15px;
    top: 14px;
  }

  .workPlace .table .tr {
    border-bottom: 2px solid #131822;
    height: 10px;
  }

  .workPlace .table .tr:first-child {
    height: 11px;
  }

  .workPlace .table .tr:nth-child(5n) {
    border-bottom-color: #1e2635;
  }

  .workPlace .table .tr:nth-child(5n) .td {
    height: 8px;
  }

  .workPlace .table .td {
    display: block;
    float: left;
    border-right: 2px solid #131822;
    width: 10px;
    height: 10px;
  }

  .workPlace .table .td:first-child {
    width: 11px;
  }

  .workPlace .table .td:nth-child(5n) {
    border-right-color: #1e2635;
  }

  .sider {
    position: absolute;
  }

  .siderbar-right {
    position: absolute;
    right: 0;
    top: 0;
    z-index: 2;
  }

  .phoneStyle {
    position: absolute;
    z-index: 1;
    top: 34px;
    left: 50%;
    margin: 0px auto;
    transform: translateX(-50%)
  }
</style>
