<template>
  <div class="type-select"
       v-click-outside="{fn:hideMoreMenu}">
    <span class="select-title">{{selectTitle}}</span>
    <div>
      <div class="select-box"
           @click="showSelect"
           :class="{'borderColor':isRotate}">
        <span v-show="heightLightLine.value">
          {{heightLightLine.label}}
        </span>
        <span class="placeholder-label"
              v-show="!heightLightLine.value"></span>
        <span class="biFont icon-triangleDown down"
              :class="{'rotate':isRotate}"></span>
      </div>
      <div class="search-input"
           :class="{'level':isRotate}"
           v-show="isRotate">
        <ul class="select-ul">
          <li v-for="(item,index) in ordinaryList"
              :key="index"
              @click="getNode(item,index)"
          >{{item.label}}</li>
          <!--            @mouseenter="enter(item)"-->
          <!--            @mouseleave="leave()"-->
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
	name: 'dropdown',
	props: {
		ordinaryList: {
			type: Array,
			required: true
		},
		heightLightLine: {
			type: Object,
			require: true
		},
		selectTitle: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			selectedItem:{},
			isRotate: false,
		};
	},
	computed: {
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex'
		}),
	},
	watch: {
		'selectedItem.value':{
			deep:true,
			handler(newValue,oldValue){
				if(newValue){
					this.$emit('getReturnData', newValue);
				}
			}
		}
	},
	methods: {
		showSelect() {
			this.isRotate = !this.isRotate;
		},
		getNode(item) {
			this.selectedItem=null;
			this.$set(this,'selectedItem',item);
			this.isRotate = false;
		},
		hideMoreMenu() {
			this.isRotate = false;
		}
	},
};
</script>

<style scoped>
  .type-select {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 0.6vw;
    align-items: baseline;
  }
  .type-select .select-title {
    display: block;
    color: #acbbda;
  }
  .select-box {
    width: 8vw;
    height: 1.1vw;
    background-color: #11151f;
    border: solid 0.0625vw #475566;
    position: relative;
    color: #fff;
    text-align: left;
    font-size: 0.6vw;
    user-select: none;
    cursor: pointer;
  }
  .select-box span:first-child {
    margin-left: 0.24vw;
  }
  .select-box .down {
    transform: scale(0.8);
    position: absolute;
    right: 0.2vw;
    top: 0.1vw;
    font-size: 0.5vw;
    cursor: pointer;
  }
  .select-box .rotate {
    transform: rotate(180deg) scale(0.8);
  }
  .type-select .borderColor {
    border-color: #28b3fc;
  }
  .icon {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }

  .search-input {
    position: absolute;
    width: 8vw;
    background-color: #11151f;
    display: flex;
    flex-direction: column;
    border-right: solid 0.0625vw #475566;
    border-left: solid 0.0625vw #475566;
    border-bottom: solid 0.0625vw #475566;
    align-items: center;
  }
  .level {
    z-index: 6;
  }
  .select-ul {
    width: 100%;
    list-style: none;
    padding: 0;
    color: #fff;
  }
  .select-ul > li {
    text-align: left;
    line-height: 1.1vw;
    font-size: 0.6vw;
    padding-left: 0.4vw;
    user-select: none;
  }
  .select-ul > li:hover {
    cursor: pointer;
    background-color: #252e42;
  }
  .select-ul > li.heightLight {
    background-color: #252e42;
  }

  .placeholder-label {
    font-size: 0.6vw;
    color: #798194;
    margin-left: 0.1vw;
    margin-top: 0;
  }
</style>
