<template>
  <div :style="fatherStyle"
    class="father-edit">
    <!-- @input="divInput($event)" -->
    <textarea spellcheck="false"
      :readonly="!editable"
      :style="editStyle"
      @input="divInput($event)"
      v-model="context">
			    <!-- v-text="options.textContent" -->
    </textarea>
    <span class="placeholder allCenter2"
      v-show="!context"></span>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
	name: 'TextArea',
	props: {
		options: [Object, Number],
		editable: [String, Boolean],
		tableId: {
			type: String
		}
	},
	data() {
		return {
			// editable: 'plaintext-only',
			containerOptions: '',
			textOptions: '',
			context: ''
		};
	},
	computed: {
		...mapGetters('main', ['getVw']),
		fatherStyle() {
			let vm = this;
			return {
				// width: vm.getVw(vm.w) + 'px',
				// maxHeight: vm.getVw(vm.h) + 'px',
				// height: vm.getVw(vm.h) + 'px',
				borderRadius: vm.getVw(vm.options.borderStyle.borderRadiusWidth) + 'px',
				border:
          vm.options.borderStyle.lineWidth +
          'px ' +
          vm.options.borderStyle.lineColor +
          ' ' +
          vm.options.borderStyle.lineType
			};
		},
		editStyle() {
			let vm = this;
			return {
				// minWidth: vm.textOptions.width + 'px',
				// width: vm.getVw(vm.w - vm.options.borderStyle.lineWidth * 2) + 'px',
				// maxHeight: vm.getVw(vm.h) + 'px',
				// height: vm.getVw(vm.h - vm.options.borderStyle.lineWidth * 2) + 'px',
				background: vm.options.backgroundColor,
				borderRadius: vm.getVw(vm.options.borderStyle.borderRadiusWidth) + 'px',
				// boxShadow: `${vm.textOptions.topShow}px ${vm.textOptions.rightShow}px ${vm.textOptions.leftShadow}px ${vm.textOptions.bottomShadow}px ${vm.textOptions.blur}px ${vm.textOptions.boxShadowColor}`,
				fontSize: vm.context
					? vm.getVw(Number(vm.options.textStyle.fontSize)) + 'px'
					: '1vw',
				textAlign: vm.options.textStyle.align,
				// justifyContent: vm.textOptions.textJustify,
				color: vm.context ? vm.options.textStyle.color : 'transparent',
				textIndent: Number(vm.options.textStyle.indent) + 'px',
				lineHeight: Number(vm.options.textStyle.lineHeight) + 'px',
				letterSpacing: Number(vm.options.textStyle.letterSpacing) + 'px',
				fontWeight: vm.options.textStyle.fontWeight,
				fontStyle: vm.options.textStyle.fontStyle,
				textDecoration: vm.options.textStyle.decoration
			};
		}
	},
	mounted() {
		this.context = this.options.textContent;
		if (this.tableId != '') {
			this.$store.commit('canvasPlace/setRenderedStatus');
		}
	},
	methods: {
		divInput(e) {
			if (e.data && e.isComposing) {
				this.context += e.data;
			}
			this.$store.commit('canvasPlace/renderOptionSetter', {
				key: 'textContent',
				value: this.context
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		}
	}
};
</script>

<style scoped>
.father-edit {
  width: 100%;
  height: 100%;
  /*border: 1px solid red;*/
  display: inline-block;
  /*box-sizing: border-box;*/
  font-family: '', serif;
  overflow: hidden;
  position: relative;
}
textarea {
  padding: 1vw;
  overflow: hidden;
  display: block;
  box-sizing: border-box;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  position: absolute;
  resize: none;
  border: none;
  outline: none;
  caret-color: #fff;
  font-family: SourceHanSansCN-Regular;
}
.placeholder {
  color: grey;
  font-size: 1vw;
  user-select: none;
  pointer-events: none;
  font-family: SourceHanSansCN-Regular;
}
</style>

