<template>
  <div class="separator-component"
    v-click-outside="{fn:closIsDrop}">
    <div class="separator-show"
      @click="toggleDrop">
      <input class="unSelect"
        :value="inputVal"
        disabled>
      <span class="triangle vCenter"
        :class="{rotating:isDrop}"></span>
    </div>
    <div class="separator-list"
      v-if="isDrop">
      <ul>
        <li v-for="(item,index) in options"
          :key="index"
          @click="choose(item.name,item.separator)">{{item.name}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
	// 
	name: 'separatorSelection',
	props: ['separtorObj'],
	data() {
		return {
			isDrop: false,
			inputVal: '',
			options: [
				{ name: '', separator: ',' },
				{ name: '', separator: ':' },
				{ name: '', separator: '=' },
				{ name: '', separator: ';' },
				{ name: '', separator: ' ' },
				{ name: '', separator: 'custom' }
			],
			judge: new Map([
				['', ['', ',']],
				['', ['', ':']],
				['', ['', '=']],
				['', ['', ';']],
				['', ['', ' ']],
				['', ['', 'custom']],
				['default', ['', 'custom']]
			])
		};
	},
	mounted() {
		let name = this.separtorObj.inferName;
		let action = this.judge.get(name) || this.judge.get('default');
		// console.log(action);
		this.inputVal = action[0];
		this.inputVal == ''
			? this.choose(action[0], action[1])
			: (this.infer = action[1]);
	},
	methods: {
		closIsDrop() {
			this.isDrop = false;
		},
		toggleDrop() {
			this.isDrop = !this.isDrop;
		},
		choose(name, infer) {
			if (infer == 'custom') {
				this.isDrop = false;
				this.inputVal = name;
				this.$emit('separatorBack', { type: 'custom', name: name });
			} else {
				this.isDrop = false;
				this.inputVal = name;
				this.$emit('separatorBack', {
					type: 'infer',
					infer: infer,
					name: name
				});
			}
		}
	}
};
</script>

<style>
.separator-component {
  position: relative;
  display: inline-block;
}

.separator-component .separator-show input {
  width: 10vw;
  height: 3vh;
  color: #fff;
  cursor: pointer;
  font-size: 0.5vw;
  padding-left: 0.4vw;
  background-color: #121721;
  border: solid 0.0625vw #475566;
}

.separator-component .separator-show input:focus {
  border: solid 0.0625vw #28b3fc;
}

.separator-component .separator-show .triangle {
  width: 0;
  height: 0;
  border-left: 0.2vw solid transparent;
  border-right: 0.2vw solid transparent;
  border-top: 0.2vw solid #ffffff;
  right: 0.8vw;
}

.separator-component .separator-show .triangle.rotating {
  transform: rotate(180deg);
}

.separator-component .separator-list {
  position: absolute;
  width: 10vw;
  top: 4vh;
}

.separator-component .separator-list ul {
  cursor: pointer;
  font-size: 0.7vw;
  background-color: #121721;
  border: solid 0.0625vw #475566;
  border-top: none;
  text-align: left;
}

.separator-component .separator-list ul li {
  height: 3vh;
  line-height: 3vh;
  padding-left: 0.6vw;
}

.separator-component .separator-list ul li:hover {
  background-color: #252e42;
}
</style>
