import Vue from 'vue';
import App from './App';
import axios from 'axios';
import VueAxios from 'vue-axios';
import router from './router';
import { store } from './store/store';
import messages from '@/assets/common/js/vue-i18n/lang.js';
import VueI18n from 'vue-i18n';
import VueSocketIO from 'vue-socket.io';
// start

import VueDraggableResizable from './components/vue-draggable-resizable';
import vcolorpicker from 'vcolorpicker';
import EasyScroll from '@/components/EasyScroll';
import { tips } from './utils/publicTools';
import loadingRotating from '@/components/Loading/LoadingRotating.vue';
// end

// start

import clickOutside from '@/directives/clickOutside';

// end

import Vue2TouchEvents from 'vue2-touch-events';

import db from './utils/db';
import { get, post, del, put } from './utils/http';
import  {get as _get,set as _set}  from 'lodash';
import VueClipboards from 'vue-clipboards';
Vue.use(VueClipboards);
Vue.use(vcolorpicker);
Vue.use(Vue2TouchEvents);
Vue.use(VueAxios, axios);
Vue.use(VueI18n);
Vue.component('vue-draggable-resizable', VueDraggableResizable);
Vue.component('EasyScroll', EasyScroll);
Vue.component('LoadingRotating', loadingRotating);
Vue.use(Vue.directive('clickOutside', clickOutside));
//
Vue.prototype.dexieDB = db;
Vue.prototype.$get = get;
Vue.prototype.$post = post;
Vue.prototype.$put = put;
Vue.prototype.$del = del;
Vue.prototype._get = _get;
Vue.prototype.$getVw = store.getters['main/getVw'];
Vue.prototype.$getVh = store.getters['main/getVh'];
Vue.prototype.$vwToPx = store.getters['main/vwToPx'];
Vue.prototype.$vhToPx = store.getters['main/vhToPx'];

Vue.prototype.tips = tips;
Vue.config.productionTip = false;
Vue.use(new VueSocketIO({
	connection: 'https://pushdev.24e.co:2120',
	vuex: {
		store,
		options: {
			useConnectionNamespace: true
		},
		actionPrefix: 'SOCKET_',
	},
}));

function lang() {
	// localStorage
	let t = window.localStorage.getItem('language');
	if (t) return t;
	// 
	else return 'cn';
}
const language = lang();

//  window  lang 
window.lang = lang();
//  i18n 
const i18n = new VueI18n({
	locale: language, // set locale
	messages, // set lcoal messages
});


new Vue({
	el: '#app',
	router,
	i18n,
	store,
	render: h => h(App),
});
