<template>
  <div class="type-color-picker"
    ref="colorParent">
    <span>{{defaultColor.titleName}}</span>
    <div class="input-color-picker"
      ref="colorInput"
      v-click-outside="{fn:titleHideColor}"
      @click="getColorHeight($event)">
      <input title=""
        :value="titleColor"
        readonly
        spellcheck="false"
        type="text">
      <div class="colorIcon vCenter">
        <div class="diamond"></div>
        <div class="diamond"></div>
        <div class="diamond"></div>
        <div class="diamond"></div>
      </div>
      <div class="px-color-look"
        :style="{'background-color':titleColor}"></div>
      <div class="color-position"
        :class="{hidden:!showColor}">
        <!-- :class="{hidden:!showColor}" -->
        <Sketch
          class="sketch"
          @click.native="stopAffair"
          @mousemove.native="getMousemo"
          @touchmove.native="getMousemo"
          :style="sketchObj"
          :value="useColor"
          @input="updateValue"
          ref="colorChild"></Sketch>
      </div>
    </div>
  </div>
</template>

<script>
// 
import {Sketch} from 'vue-color';
import {colorTransformation} from '@/utils/optionSetting';
export default {
	name: 'ColorSetting',
	components: {
		Sketch: Sketch
	},
	props: {
		defaultColor: {
			type: Object,
			require: true
		}
	},
	data() {
		return {
			clientHeight: 0,
			bottom: 0,
			right:0,
			disBottom: 0,
			colorWidth:0,
			colorHeight: 0,
			showColor: false,
			titleName: '',
			colorObj: '',
			emitTimer: null
		};
	},
	computed: {
		isRightPaneHover() {
			return this.$store.getters['dashboard/isRightPaneHover'];
		},
		getVh(n) {
			return this.$store.getters['main/getVh'];
		},
		screenHeight() {
			return this.$store.getters['main/screenHeight'];
		},
		useColor(){
			return this.defaultColor.rgba||'rgba(255,255,255,0.5)';
		},
		titleColor(val){
			return colorTransformation(this.useColor);
		},
		sketchObj() {
			let width = this.isRightPaneHover ? '10.65vw' : '11.65vw';
			return {
				top: this.bottom+'px',
				left: this.right+'px',
				width,
			};
		}
	},

	methods: {
	  getMousemo(e) {
			this.propsColor();
		  // if (this.emitTimer) clearTimeout(this.emitTimer);
		  // this.resizeTimer = setTimeout(() => {
		  // 	this.$emit('changeColor', this.colorObj);
		  // }, 500);
	  },
	  propsColor() {
	  	if (this.colorObj){
		    this.$emit('changeColor', this.colorObj);
			}
	  },
	  stopAffair(e) {
		  e.stopPropagation();
		  return false;
	  },
	  // 
	  updateValue(value) {
		  // this.titleColor = "rgb(" + value.rgba.r + "," + value.rgba.g + "," + value.rgba.b + ")";
		  // this.titleColor = value.hex8;
		  this.colorObj = value;
	  },
	  // 
	  titleHideColor() {
		  this.showColor = false;
	  },
	  getColorHeight(e) {
	    e=e||window.event;
		  this.showColor = !this.showColor;
		  let inputCientRect=this.$refs.colorInput.getBoundingClientRect();
		  this.bottom = inputCientRect.bottom;
			this.colorWidth = this.$refs.colorChild.$el.offsetWidth;
			this.right = inputCientRect.right-this.colorWidth;
		  // console.log('' + );
		  this.colorHeight = this.$refs.colorChild.$el.offsetHeight;
		  this.disBottom = this.screenHeight - (this.bottom + this.colorHeight);
	  },
	},
};
</script>

<style scoped>
  .color-position >>> .vc-editable-input span {
    color: #fff;
  }

  .color-position >>> .vc-sketch-field--double input {
    padding-left: 0;
  }

  .type-color-picker {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 0.6vw;
    align-items: baseline;
  }

  .type-color-picker span {
    display: block;
    color: #acbbda;
    user-select: none;
  }

  .input-color-picker {
    cursor: pointer;
    position: relative;
    user-select: none;
  }

  .input-color-picker input {
    outline: none;
    background-color: #11151f;
    border: solid 0.0625vw #475566;
    color: #ffffff;
    padding: 0.1vw 1vw;
    user-select: none;
    width: 8vw;
    cursor: pointer;
  }

  .input-color-picker input[type="text"]:focus {
    border-color: #00b9ff;
  }

  .color-position .sketch .vc-sketch-field span {
    color: #fff;
  }

  .color-position
  .sketch
  .vc-sketch-field
  .vc-sketch-field--double
  .vc-editable-input
  input {
    padding-left: 0;
  }

  .color-position {
    position: absolute;
    right: 0;
    z-index: 20;
  }

  .color-position.fixed {
    position: fixed;
  }


  /**/
  .color-position .sketch {
    position: fixed;
    width: 11.54vw;
    background: rgb(22, 28, 41);
    border: 0.0625vw solid rgb(71, 85, 102);
    border-radius: 0;
  }

  .px-color-look {
    width: 0.6vw;
    height: 0.6vw;
    position: absolute;
    top: 0.25vw;
    left: 0.2vw;
  }

  .colorIcon {
    display: flex;
    cursor: pointer;
    position: absolute;
    flex-flow: row wrap;
    width: 0.6vw;
    height: 0.6vw;
    right: 0.3vw;
  }

  .colorIcon div {
    width: 0.3vw;
    height: 0.3vw;
  }

  .colorIcon div:first-child {
    background-color: #ff0000;
  }

  .colorIcon div:nth-child(2) {
    background-color: #00ff00;
  }

  .colorIcon div:nth-child(3) {
    background-color: #fff100;
  }

  .colorIcon div:nth-child(4) {
    background-color: #00a0e9;
  }
</style>
