export default {
	namespaced: true,
	state: {
		tipsData: {},
		// 
		setTipsDataBl: true
	},
	getters: {
		getTipsData: (state) => {
			return state.tipsData;
		},
	},
	mutations: {
		addTipsData: (state, arr) => {
			state.tipsData = { text: arr[0], bl: arr[1] };
		},
		deleTipsData: (state) => {
			state.tipsData = {};
		},
		setTipsData(state, arr) {
			if (state.setTipsDataBl) {
				let removeTime = 1500;
				if (arr[2]) {
					removeTime = arr[2];
				}
				this.commit('publicTools/addTipsData', arr);
				state.setTipsDataBl = false;
				setTimeout(() => {
					this.commit('publicTools/deleTipsData');
					state.setTipsDataBl = true;
				}, removeTime);
			}
		}
	},
	actions: {
	
	}
};
