import {
	throttle
} from '../../utils/utilsMethods';
export const modal = {
	props: ['options'],
	data() {
		return {
			funArr: [],
			enterKeyEvent: {},
			escKeyEvent: {}
		};
	},
	created() {
		for (let i = 0; i < this.options.btn.length; i++) {
			const element = this.options.btn[i];
			if (element.throttle) {
				this.funArr.push(throttle(element.callback, element.remain));
			} else {
				this.funArr.push(element.callback);
			}
		}
		let vm = this;
		for (let i = 0; i < this.options.btn.length; i++) {
			const element = this.options.btn[i];
			if (element.throttle) {
				this.funArr.push(throttle(element.callback, element.remain));
			} else {
				this.funArr.push(element.callback);
			}
		}
		if (typeof this.options.enterKeyEventIndex == 'number') {
			let idx = vm.options.enterKeyEventIndex;
			vm.enterKeyEvent = function(e) {
				e = e || event;
				let keycode = e.keyCode || e.which || e.charCode;
				if (keycode == 13) {
					vm.funArr[idx](vm.message);
				}
			};
			document.addEventListener('keydown', vm.enterKeyEvent);
		}
		if (typeof this.options.escKeyEventIndex == 'number') {
			let idx = this.options.escKeyEventIndex;
			vm.escKeyEvent = function(e) {
				e = e || event;
				let keycode = e.keyCode || e.which || e.charCode;
				if (keycode == 27) {
					vm.funArr[idx](vm.message);
				}
			};
			document.addEventListener('keydown', vm.escKeyEvent);
		}
	},
	destroyed() {
		let vm = this;
		if (typeof this.options.enterKeyEventIndex == 'number') {
			document.removeEventListener('keydown', vm.enterKeyEvent);
		}
		if (typeof this.options.escKeyEventIndex == 'number') {
			document.removeEventListener('keydown', vm.escKeyEvent);
		}
	}
};
