import {store} from '../store/store';
import {theme1, theme2,pictorialTheme} from './data/chartRenderOptions';
import {chartCategoryTable} from './data/chartCategoryTable';
import {set as _set} from 'lodash';
export function chartDataFill(list) {
	let seriesType = list.renderOptions.series[0].type;
	if(list.renderOptions.Visual.theme=='module') { //(Symbol)
		switch (seriesType) {
		case 'radar':
			return radarFillData(list);
		case 'pictorialBar':
			return  pictorialFillData(list);
		default:
			return list;
		}
	}
	let backupsChart = store.getters['canvasPlace/getChartBackups'];
	let index = Number(list.renderOptions.Visual.theme) || 1; //
	let tableId = list.containerOptions.tableId;
	let categoryAxis = list.containerOptions.categoryAxis && list.containerOptions.categoryAxis.toLowerCase() || '';
	if (backupsChart[tableId]) {//
		// debugger;
		let chartList = JSON.parse(JSON.stringify(backupsChart[tableId]));//
		chartList.renderOptions.Visual.theme = index;
		if (categoryAxis && list.renderOptions[categoryAxis + 'Axis']) {
			chartList.renderOptions[categoryAxis + 'Axis'].data = list.containerOptions.xData;
		}
		chartList['renderOptions']['series'].splice(0, chartList['renderOptions']['series'].length);
		let seriesDataArr = Array.isArray(list.containerOptions.seriesData) ?
			list.containerOptions.seriesData : [list.containerOptions.seriesData];
		for (let i = 0; i < seriesDataArr.length; i++) {
			chartList.renderOptions.series[i] = {...chartList.renderOptions.series[i], ...seriesDataArr[i]};
		}
		list.renderOptions = chartList.renderOptions;
		return initFunction(seriesType, list, index);
	} else {//
		store.commit('canvasPlace/setChartBackups', list); //ID
		// }
		return initFunction(seriesType, list, index);
	}
}

function initFunction(chartType, list, index) {
	list.renderOptions.title.fontFamily = 'SourceHanSansCN-Regular';
	switch (chartType) {
	case 'bar':
		return barFillData(list, index); //bar
	case 'line':
		return lineFillData(list, index);
	case 'pie':
		return pieFillData(list, index);
	case 'scatter':
		return scatterFillData(list, index);
	case 'effectScatter':
		return mapScatterFillData(list, index);
	case 'funnel':
		return funnelFillData(list, index);
	case 'geo3D':
		return mapFillData(list, index);
	case 'bar3D':
		return mapFillData(list, index);
	case 'radar':
		return radarFillData(list, index);
	case  'gauge':
		return gaugeFillData(list, index);
	case 'pictorialBar':
		return  pictorialFillData(list);
	default:
		// console.log('', list);
		return normarlFillData(list, index);
	}
}


function normarlFillData(list) {
	list.renderOptions.color = theme1.colorArray;
	return list;
}


function barFillData(list, theme) {
	let data = list.renderOptions.series;
	let seriesLength = data.length;
	let type = list.containerOptions.chartsType;//
	let chartCategory = chartCategoryTable[type];
	list.renderOptions.xAxis.axisLabel = theme1.axisLabel;//
	_set(list,`renderOptions.series[${seriesLength-1}].barMaxWidth`,50);
	const command = {
		common(commonCommand) {
			let commonArray = commonCommand.length ? commonCommand : [commonCommand];
			const actions = {
				horizontal() {
					theme2.lightBlueEmphasis.normal.color.x = 1;
					theme2.lightBlueEmphasis.emphasis.color.x = 1;
					theme2.lightBlueGradient.normal.color.x = 1;
					theme2.pinkGradient.normal.color.x = 1;
					theme2.deepBlueGradient.normal.color.x = 1;
					theme2.lightBlueEmphasis.normal.color.y = 0;
					theme2.lightBlueEmphasis.emphasis.color.y = 0;
					theme2.lightBlueGradient.normal.color.y = 0;
					theme2.pinkGradient.normal.color.y = 0;
					theme2.deepBlueGradient.normal.color.y = 0;
				},
				stack(startIndex = 0) {
					for (let i = startIndex; i < list.renderOptions.series.length; i++) {
						list.renderOptions.series[i].stack = 'total';
					}
				}
			};
			for (let i = 0; i < commonArray.length; i++) {
				const commandName = commonArray[i];
				actions[commandName.command] && actions[commandName.command](commandName.param || undefined);
			}
		},
		1() {
			list.renderOptions.color = theme1.colorArray;
		},
		2(themeCommand) {
			const command = {
				single() {
					list.renderOptions.series[0].itemStyle = theme2.lightBlueEmphasis;
				},
				multiple(themeCommand) {
					const excute = {
						default() {
							list.renderOptions.series[0].itemStyle = theme2.lightBlueGradient;
							list.renderOptions.series[1].itemStyle = theme2.pinkGradient;
							if (list.renderOptions.series.length > 2) {
								list.renderOptions.series[2].itemStyle = theme2.deepBlueGradient;
							}
						},
						stack() {
							list.renderOptions.series[0].itemStyle = theme2.lightBlueNormal;//
							list.renderOptions.series[1].itemStyle = theme2.pinkNormal;//
						},
					};
					// command.default();
					for (let i = 0; i < themeCommand.length; i++) {
						const commandName = themeCommand[i];
						excute[commandName] && excute[commandName]();
					}
				}
			};
			return themeCommand ? command[themeCommand[0]](themeCommand.slice(1)) : command['single']();
		}

	};

	if (chartCategory) {
		let commonCommand = chartCategory.common&&chartCategory.common.command;
		let themeCommand = chartCategory.theme && chartCategory.theme.command;
		command.common(commonCommand || undefined);
		command[theme] && command[theme](themeCommand);
	} else {
		command[theme] && command[theme]();
	}

	return list;
}

function lineFillData(list, theme) {
	let data = list.renderOptions.series;
	let seriesLength = data.length;
	let type = list.containerOptions.chartsType;//
	let chartCategory = chartCategoryTable[type];
	list.renderOptions.color = theme1.colorArray;
	list.renderOptions.xAxis.axisLabel = theme1.axisLabel;
	// list.renderOptions.dataZoom = null;
	// let theme2=theme2;
	// commonComand
	const command = {
		common(commonCommand) {
			let commonArray = commonCommand.length ? commonCommand : [commonCommand];
			const actions = {
				area(startIndex = 0) {
					list.renderOptions.series[0].itemStyle = theme2.lightBlueGradient;
					for (let i = startIndex; i < list.renderOptions.series.length; i++) {
						const areaStyle = list.renderOptions.series[i].areaStyle;
						list.renderOptions.series[i].areaStyle = {...(areaStyle || {})};
					}
				},
				stack(startIndex = 0) {
					for (let i = startIndex; i < list.renderOptions.series.length; i++) {
					  list.renderOptions.series[i].stack = 'total';
					}
				}

			};
			for (let i = 0; i < commonArray.length; i++) {
				const commandName = commonArray[i];
				actions[commandName.command] && actions[commandName.command](commandName.param || undefined);
			}
		},
		1() {
			list.renderOptions.color = theme1.colorArray;
		},
		2(themeCommand) {
			const command = {
				single() {
					list.renderOptions.series[0].itemStyle = theme2.lightBlueEmphasis;
				},
				multiple(themeCommand) {
					const excute = {
					  area(){
							list.renderOptions.series[0].itemStyle = theme2.lightBlueGradient;
							list.renderOptions.series[1].itemStyle = theme2.pinkGradient;
						},
						default() {
							list.renderOptions.series[0].itemStyle = theme2.lightBlueNormal;
							list.renderOptions.series[1].itemStyle = theme2.pinkNormal;
						},
						mixed() {
							list.renderOptions.series[0].itemStyle = theme2.pinkNormal;
							list.renderOptions.series[1].itemStyle = theme2.lightBlueGradient;
							if(seriesLength>2){
								list.renderOptions.series[2].itemStyle = theme2.deepBlueGradient;
							}
						},
					};
					// command.default();
					for (let i = 0; i < themeCommand.length; i++) {
						const commandName = themeCommand[i];
						excute[commandName] && excute[commandName]();
					}
				}
			};
			return themeCommand ? command[themeCommand[0]](themeCommand.slice(1)) : command['single']();
		}

	};

	if (chartCategory) {
		let commonCommand = chartCategory.common&&chartCategory.common.command;
		let themeCommand = chartCategory.theme && chartCategory.theme.command;
		commonCommand&&command.common(commonCommand || undefined);
		command[theme] && command[theme](themeCommand);
	} else {
		command[theme] && command[theme]();
	}

	return list;
}

