function mapScatterFillData(list) {
	list.renderOptions.series[0].showEffectOn = 'render';
	list.renderOptions.series[0].hoverAnimation = 'true';
	// list.renderOptions.series[0].tooltip.formatter = (param)=>{
	//   return param.seriesName+'<br/>'+param.data.name+':'+param.data.value[2];
	// };
	list.renderOptions.echartsMapType = 'effectScatter';
	// list.renderOptions.series[0].rippleEffect = {brushType: 'stroke'};
	return list;
}

function scatterFillData(list) {
	list.renderOptions.color = theme1.colorArray; //color
	// list.renderOptions.yAxis.splitArea = theme1.splitArea;
	list.renderOptions.xAxis.axisLabel = theme1.axisLabel;
	// list.renderOptions.xAxis.axisLine = theme1.axisLine;
	// list.renderOptions.xAxis.axisLine.show = true;
	// list.renderOptions.xAxis.splitLine = theme1.splitLine;
	// list.renderOptions.xAxis.splitLine.show = false;
	// list.renderOptions.yAxis.axisLine = theme1.axisLine;
	// list.renderOptions.yAxis.axisLine.show = true;
	// list.renderOptions.yAxis.splitLine = theme1.splitLine;
	list.renderOptions.yAxis.axisLabel.color = '#fff';
	list.renderOptions.yAxis.splitLine.show = false;
	list.renderOptions.series[0].itemStyle = theme2.lightBlueEmphasis; //
	list.renderOptions.series[0].itemStyle.normal.color.colorStops[0].color = '#2FDECA';
	list.renderOptions.series[0].itemStyle.normal.color.colorStops[1].color = '#1d7cfa';
	return list;
}

