function funnelFillData(list, index) { //
	// list.renderOptions.series[0].color = randomColor1(list.renderOptions.series[0].data.length);
	list.renderOptions.color = theme1.colorArray;
	if (index === 2) {
		list.renderOptions.visualMap = {};
	}

	return list;
}

function mapFillData(list) {
	// list.renderOptions.geo3D = list.renderOptions.series[0];
	list.renderOptions.geo3D.label = theme1.geo3DLabel;
	// list.renderOptions.series.splice(0, 1);
	// console.log(list,'list');
	return list;
}

function radarFillData(list) { //
	list.renderOptions.radar.indicator = list.containerOptions.extraChartData.indicator; //color
	list.renderOptions.color = theme1.colorArray; //color
	list.renderOptions.radar.splitArea = theme1.radarSplitArea;
	list.renderOptions.radar.axisLine = theme1.radarAxisLine;
	list.renderOptions.radar.splitLine = theme1.radarSplitLine;
	list.renderOptions.visualMap = theme1.visualMap;
	return list;
}
//()
function pictorialFillData(list) {
	list.renderOptions.series=list.renderOptions.series.map((item,index)=>{
		var arr = [];
		if (index > 0) {
			var before = list.renderOptions.series[index - 1];
			arr = item.data.map((v, j) => {
				var seriesRes = v;
				seriesRes += before.data[j];
				item.data[j] = seriesRes;
				return seriesRes;
			});
		} else {
			arr = item.data;

		}
		return {
			name: item.name,
			data: arr,
			type: 'pictorialBar',
			symbol: pictorialTheme.symbolArray[index],
			symbolSize: [15,10],
			itemStyle: {
				normal: {
					color:pictorialTheme.colorArray[index],
				}
			},
			symbolMargin: '0%',
			symbolRepeat: true,
			z: -index,
		};
	});
	return list;
}
