<template>
<!--  <div>-->
<!--    <div class="chart-basis"-->
<!--      @click="showEvent(5)">-->
<!--      <span></span>-->
<!--      <span class="biFont icon-arrowLeft icon-fontsize"-->
<!--            :class="[isShowPropmt?'leftRotate-90':'leftRotate-0']"></span>-->
<!--    </div>-->
<!--    :class="[isShowPropmt?'heightStart':'heightOver']"-->
    <div
      ref="contentBox"
      class="content-box">
      <ul>
        <li class="optionsItem">
          <span class="span-title"></span>
          <slider-data :opt="settingGridWidth"
            v-on:rangedata="rangeGridWidth"
            v-on:inputData="rangeGridWidth"></slider-data>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <slider-data :opt="settingGridHeight"
            v-on:rangedata="rangeGridHeight"
            v-on:inputData="rangeGridHeight"></slider-data>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <slider-data :opt="settingGridLeft"
            v-on:rangedata="rangeGridLeft"
            v-on:inputData="rangeGridLeft"></slider-data>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <slider-data :opt="settingGridTop"
            v-on:rangedata="rangeGridTop"
            v-on:inputData="rangeGridTop"></slider-data>
        </li>
      </ul>
    </div>
<!--  </div>-->
</template>

<script>
import SliderData from './BasicComponent/Slider';
import { mapGetters, mapMutations } from 'vuex';
import {mixin} from './BasicComponent/mixin';
export default {
	name: 'DataPrompt',
	mixins:[mixin],
	components: {
		'slider-data': SliderData
	},
	data() {
		return {
			isShowPropmt: true,
			settingGridWidth: {
				//grid
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			settingGridHeight: {
				//grid
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			settingGridLeft: {
				//grid
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			settingGridTop: {
				//grid
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			}
		};
	},
	computed: {
		...mapGetters('chartBus', {
			// chartDataList: 'returnData',
			chartIndex: 'returnDataIndex'
		}),
		...mapGetters('canvasPlace', {
			currentChartRenderOptions: 'getCurrentChartRenderOptions',
			// chartIndex: 'returnDataIndex'
		})
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter'
		}),
		saveDataIndexDb({ name, pare }) {
			this.renderOptionSetter({
				key: name,
				id: this.geiChildId,
				value: pare
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		},
		//
		showEvent(number) {
			this.isShowPropmt = !this.isShowPropmt;
		},
		rangeGridWidth(data) {
		  console.log(data);
			// console.group(JSON.stringify(data) + '%','')
			if (this.settingGridWidth.valueRange !== data) {
				this.saveDataIndexDb({
					name: 'grid.width',
					pare: JSON.stringify(data) + '%'
				});
			}
		},
		rangeGridHeight(data) {
			// console.group(JSON.stringify(data) + '%','')
			if (this.settingGridWidth.valueRange !== data) {
				this.saveDataIndexDb({
					name: 'grid.height',
					pare: JSON.stringify(data) + '%'
				});
			}
		},
		rangeGridLeft(data) {
			// console.group(JSON.stringify(data) + '%','')
			if (this.settingGridLeft.valueRange !== data) {
				this.saveDataIndexDb({
					name: 'grid.left',
					pare: JSON.stringify(data) + '%'
				});
			}
		},
		rangeGridTop(data) {
			// console.group(JSON.stringify(data) + '%','')
			if (this.settingGridTop.valueRange !== data) {
				this.saveDataIndexDb({
					name: 'grid.top',
					pare: JSON.stringify(data) + '%'
				});
			}
		},
		// 
		gridWidth(data) {
			let vm = this;
			vm.$set(vm.settingGridWidth,'valueRange',data);
			// vm.$set(vm.settingGridWidth,'garidyValue',data);
			// vm.settingGridWidth.valueRange = data;
			// vm.settingGridWidth.garidyValue = data;
		},
		gridHeight(data) {
			let vm = this;
			vm.settingGridHeight.valueRange = data;
			vm.settingGridHeight.garidyValue = data;
		},
		gridTop(data) {
			let vm = this;
			vm.settingGridTop.valueRange = data;
			vm.settingGridTop.garidyValue = data;
		},
		gridLeft(data) {
			let vm = this;
			vm.settingGridLeft.valueRange = data;
			vm.settingGridLeft.garidyValue = data;
		},
		// 
		gridInputWidth(data) {
			this.saveDataIndexDb({
				name: 'grid.width',
				pare: JSON.stringify(data) + '%'
			});
		},
		gridInputHeight(data) {
			this.saveDataIndexDb({
				name: 'grid.height',
				pare: JSON.stringify(data) + '%'
			});
		},
		gridInputLeft(data) {
			if (this.settingGridWidth.valueRange != data) {
				this.saveDataIndexDb({
					name: 'grid.left',
					pare: JSON.stringify(data) + '%'
				});
			}
		},
		gridInputTop(data) {
			if (this.settingGridWidth.valueRange != data) {
				this.saveDataIndexDb({
					name: 'grid.top',
					pare: JSON.stringify(data) + '%'
				});
			}
		}
	},
	watch: {
		currentChartRenderOptions:{
			deep: true,
			immediate: true,
		  handler(val) {
				let vm = this,
					chartData = val;
				// console.log(typeof chartData.grid.width,'536');
				if (
					chartData.grid &&
          chartData.grid.width &&
          typeof chartData.grid.width === 'string'
				) {
					// console.log(chartData.grid.width,'',chartData.grid.width);
					vm.gridWidth(JSON.parse(chartData.grid.width.split('%')[0]));
				}
				if (
					chartData.grid &&
          chartData.grid.height &&
          typeof chartData.grid.height === 'string'
				) {
					// console.log(JSON.parse(chartData.grid.width.split('%')[0]),'',chartData.grid.width);
					vm.gridHeight(JSON.parse(chartData.grid.height.split('%')[0]));
				}
				if (
					chartData.grid &&
          chartData.grid.top &&
          typeof chartData.grid.top === 'string'
				) {
					// console.log(JSON.parse(chartData.grid.width.split('%')[0]),'',chartData.grid.width);
					vm.gridTop(JSON.parse(chartData.grid.top.split('%')[0]));
				}
				if (
					chartData.grid &&
          chartData.grid.left &&
          typeof chartData.grid.left === 'string'
				) {
					// console.log(JSON.parse(chartData.grid.width.split('%')[0]),'',chartData.grid.width);
					vm.gridLeft(JSON.parse(chartData.grid.left.split('%')[0]));
				}
			}
		},
	}
};
</script>

<style scoped>
  li{
    padding: 0.4vw 0.5vw;
  }
.optionsItem {
  /*margin-bottom: 0.8vw;*/
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.optionsItem .span-title {
  display: block;
  flex: 1;
  color: #acbbda;
}

.optionsItem .li-line-box {
  display: flex;
  flex-direction: column;
  /* background: #00a0e9; */
  justify-content: center;
}

.li-line-box .space-bottom {
  margin-bottom: 0.5vw;
}
.li-line-box .space-top {
  margin-top: 0.5vw;
}

.li-line-box .color-font {
  color: #acbbda;
  display: block;
  margin-bottom: 0.3vw;
}
span,
input {
  user-select: none;
}

.rotateIcon {
  transform: rotate(-90deg);
}
</style>



