
//
export function throttle(fn, remain = 1000) {
	let times = 0;
	return function () {
		const args = arguments;
		let context = this;
		if (times == 0) {
			fn.call(context, ...args);
			times = 1;
			setTimeout(() => {
				times = 0;
			}, remain);
		}
	};
}

//
export  function debounceTail(fn, delay=500, ctx) {
	let timerID = null;
	return function () {
		let args = arguments;
		// 
		clearTimeout(timerID);
		// delay
		timerID = setTimeout(function () {
			fn.apply(ctx, args);
		}, delay);
	};
}
