<template>
  <div class="sider-shrink" v-if="!isPhoneLayout">
    <div>
      <ul>
        <li v-for="(item,index) in iconArr"
          @click.stop="addEchart(item)"
          v-bind:key="index">
          <div class="box-size">
            <span class="icon"
              :class="`basicChart ${item.iconName}`"
              :style="{'color':!item.isLock?'#fff':'#676e7a'}"></span>
          </div>
          <span class="details">{{item.name}}</span>
        </li>
        <li @click="changeItems">
          <div
            class="recovery-right">
            <span class="biFont icon-option"></span>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import '@/assets/SiderBar/css/iconfont.css';
import {mapGetters} from 'vuex';

export default {
	name: 'SiderShrink',
	data() {
		return {
			changeColor: '#28b3fc',
			iconArr: [
				{
					iconName: 'icon-bar',
					chartType: 'echarts-basicBar',
					name: '',
					type: 'chart',
					isLock: false
				},
				{
					iconName: 'icon-line',
					chartType: 'echarts-basicLine',
					name: '',
					type: 'chart',
					isLock: false
				},
				{
					iconName: 'icon-pie',
					chartType: 'echarts-basicPie',
					name: '',
					type: 'chart',
					isLock: false
				},
				{
					iconName: 'icon-scatter',
					chartType: 'echarts-basicScatter',
					name: '',
					type: 'chart',
					isLock: false
				},
				{
					iconName: 'icon-radar',
					chartType: 'echarts-basicRadar',
					name: '',
					type: 'chart',
					isLock: false
				},
				{
					iconName: 'icon-nightingaleRose',
					chartType: 'echarts-nightingaleRose',
					name: '',
					type: 'chart',
					isLock: false
				},
				{
					iconName: 'icon-map',
					chartType: 'echarts-choropleth',
					name: '',
					type: 'chart',
					isLock: false
				},
				{
					iconName: 'icon-image',
					chartType: 'media-image',
					name: '',
					type: 'media',
					isLock: false
				},
				{
					iconName: 'icon-text',
					chartType: 'text-textArea',
					name: '',
					type: 'media',
					isLock: false
				},
				// {
				// 	iconName: 'icon-filter',
				// 	chartType: 'interactive-dataFilter',
				// 	name: '',
				// 	type: 'interactive',
				// 	isLock: false
				// },
				// {
				// 	iconName: 'icon-filter',
				// 	chartType: 'interactive-proportionRing',
				// 	name: '',
				// 	type: 'interactive',
				// 	isLock: false
				// },
				// {
				// 	iconName: 'icon-filter',
				// 	chartType: 'echarts-3dWorldMap',
				// 	name: '3d',
				// 	type: 'chart',
				// 	isLock: false
				// },
			]
		};
	},
	methods: {
		changeItems() {
			this.$store.commit('dashboard/setStateProperty', {key:'leftSideWidth',value:11.6});
			this.$store.commit('dashboard/setStateProperty', {key:'leftSideOpen',value:true});
		},

		addEchart(params) {
			// let isMock = params.isMock;
			// console.log(isMock);
			if (!params.isLock) {
			    //
				this.$emit('getChartType', params);
			}

			// this.$store.commit("canvasPlace/setChartContainer", this.getChartOptions(chartType));
		},
	},
	computed: {
		...mapGetters('defaultChartOptions', {
			getChartOptions: 'getEchartsDefaultOption'
		}),
		...mapGetters('canvasPlace', {
			isPhoneLayout: 'getPhoneLayoutState',
		}),
	},
	watch: {
	}
};
</script>
<style scoped>
  @media screen and (max-width: 1400px) {
    div.sider-shrink ul > li {
      margin-top: 15px;
    }
  }

  .sider-shrink {
    position: absolute;
    top: 0;
    left: 0;
    width: 2.6vw;
    height: calc(100vh - 2.6vw);
    padding-top: 1vw;
    background-color: #171e2b;
    border-right: 0.0625vw solid #0b0e14;
    border-top: 0.0625vw solid #0b0e14;
  }

  .sider-shrink ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .sider-shrink ul > li {
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin-top: 1vw;
    position: relative;
  }

  .sider-shrink ul > li > div {
    border: 0.1vh solid transparent;
  }

  .sider-shrink ul > li:hover .details {
    visibility: visible;
  }

  .sider-shrink ul > li:hover span:first-child {
    color: #28b3fc !important;
  }

  .box-size {
    color: #fff;
    width: 1.4vw;
    height: 1.4vw;
    line-height: 1.4vw;
    text-align: center;
    cursor: pointer;
  }

  .box-size .icon {
    font-size: 0.9vw;
  }

  .recovery-right {
    border: none;
    width: 1.4vw;
    height: 1.4vw;
    color: #ffffff;
    cursor: pointer;
    text-align: center;
    line-height: 1.4vw;
  }

  .recovery-right span {
    font-size: 0.9vw;
  }

  .recovery-right:hover {
    color: #28b3fc;
  }

  .details {
    padding: 0 0.2vw;
    display: inline-block;
    visibility: hidden;
    width: auto;
    white-space: nowrap;
    color: #fff;
    background: #28b3fc;
    position: absolute;
    height: 1.1vw;
    line-height: 1.1vw;
    text-align: center;
    top: 0.16vw;
    z-index: 1;
    left: 2.3vw;
    z-index: 999;
  }
</style>
