import light from 'echarts/src/theme/light';

export default {
	namespaced: true,
	state: {
		currentProjectId: '',
		currentHoverIndex: -1,
		count: 0,
		valueAxisfilterCount: 0,
		valueAxisfilterIndex: -1,
		axisType: '',
		valueAxisfilterPanl: 'rightPanl',
		amendChartCount: 0,//
		amendchartRefresh:0,//
		onDragging: false,//
		addDragging:{},//
		dataDimensionColorIndex: -1,
		setOnTouch: false,
		dragObject: {
			tabeleIndex: undefined
		},
		curAxisObject: {},
		isExchangeAxis: false,
		reportObj: {
			isSelectInput: false,
			isChoice: false,
			name: '',
			inputType: '',
			tableIndex: '',
			tableName: ''
		},
		/**
         * 
         * 
         * */
		defaultChartAxis: {
			axisList: [{
				isSelectInput: false,
				isChoice: false,
				name: '',
				inputType: '',
				tableIndex: '',
				tableName: '',
				isFilterChar: false,
				isFilterNumber: false,
				xOp: '',
				yOp: '',
				orderCondition: '',
				orderOp: ''
			}],
			xAxisList: [{
				isSelectInput: false,
				isChoice: false,
				name: '',
				inputType: '',
				tableIndex: '',
				tableName: '',
				isFilterChar: false,
				isFilterNumber: false,
				xOp: '',
				yOp: '',
				orderCondition: '',
				orderOp: ''
			}],
			yAxisList: [{
				isSelectInput: false,
				isChoice: false,
				name: '',
				inputType: '',
				tableIndex: '',
				tableName: '',
				isFilterChar: false,
				isFilterNumber: false,
				xOp: '',
				yOp: '',
				orderCondition: '',
				orderOp: '',
				yPercent:'',
			}],
			dataSeries: [{
				isSelectInput: false,
				isChoice: false,
				name: '',
				inputType: '',
				tableIndex: '',
				tableName: '',
				isFilterChar: false,
				isFilterNumber: false,
				xOp: '',
				yOp: '',
				orderCondition: '',
				orderOp: ''
			}],
			groupData:[{
				groupField:'',
				groupSourceId:'',
				groupSourceName:'',
				groupOp:'',
				groupCategory:'',
				groupCondition:'',
			}]

		},
		isFocusDrag: false,//
		currentChartAxis: {},
		pageFilterList: [],
		safety:1, //  0-- 1--
		listType:{
			index: -1,//
			axisType: '',//XY
			dataType: {},//XY
			selectedIndex:-1,
			/**dataselect */
			dataSelectPostion: {
				left: '0px',
				top: '0px'
			},
		},

	},
	getters: {
		/** */
		getDefaultChartAxis:state => state.defaultChartAxis['axisList'],
		getDefaultChartAxisAll(state) {
			let defaultAxis = {
				xAxisList: state.defaultChartAxis['xAxisList'],
				yAxisList: state.defaultChartAxis['yAxisList'],
				groupData: state.defaultChartAxis['groupData'],
				dataSeries: state.defaultChartAxis['dataSeries'],
			};
			return defaultAxis;
		},
		/** */
		getCurrentChartAxis:state => state.currentChartAxis,
		getPageFilterList(state) {
			if (state.pageFilterList.length) {
			  console.log(111)
				return state.pageFilterList;
			} else {
			  console.log(2222)
				return [...state.defaultChartAxis.xAxisList, ...state.defaultChartAxis.yAxisList];
			}
		},
		/** */
		getAxisReport:state => state.reportObj,
		getUpdateCount:state => //count
		  state.count,
		getAmendChartCount:state => state.amendChartCount,
		getAmendChartRefresh:state => state.amendchartRefresh,
		getDragObject:state =>//
			 state.dragObject,
		getCurAxisObject: state => state.curAxisObject,
		getCurrentHoverIndex: state =>//
			 state.currentHoverIndex,
		isFocusDrag: state =>//
			 state.isFocusDrag,
		isOnDragging:state => state.onDragging,
		getColorIndex:state => state.dataDimensionColorIndex,
		isExchangeAxis:state => state.isExchangeAxis,
		getValueAxisFilterCount(state) {
			return { valueAxisfilterCount: state.valueAxisfilterCount, axisType: state.axisType };
		},
		getValueAxisfilterIndex:state => state.valueAxisfilterIndex,
		getValueAxisfilterPanl: state => state.valueAxisfilterPanl,
		getValueAxisArr: state => categoryAxis => {
			if (state.currentChartAxis['xAxisList']) {
				const axisType = categoryAxis == 'X' ? 'yAxisList' : 'xAxisList';
				return state.currentChartAxis[axisType];
			} else {
				return [];
			}
		},
		getCurSourceId(state) {//tableId 
			if (state.currentChartAxis['xAxisList']) {
				const curExistAxisOptions = [...state.currentChartAxis['xAxisList'], ...state.currentChartAxis['yAxisList']].find(e => e.tableIndex !== '');
				if (curExistAxisOptions !== undefined) {
					return curExistAxisOptions['tableIndex'];
				} else {
					return '';
				}
			} else {
				return '';
			}
		},
		getSelectList(state){ //Xy
			return state.listType;
		},
		getSafety(state){
		  return state.safety;
		}
	},
	mutations: {
	  setAddDragging(state,params){
	    state.addDragging = params;
		},
		setSelectList(state,params){  //xy
			state.listType=params;
		},
		setOnDragging(state, status) {
			let index = this.state.canvasPlace.currentChartIndex;
			if (index > -1) {
				state.onDragging = status;
			}
		},
		setOnTouch(state, status) {
			let index = this.state.canvasPlace.currentChartIndex;
			if (index > -1) {
				state.setOnTouch = status;
			}
		},
		setColorIndex(state, index) {
			state.dataDimensionColorIndex = index;
		},
		setExchangeAxisStatus(state, status) {
			state.isExchangeAxis = status;
		},
		/** */
		setClearAxis(state) {
			state.currentChartAxis = {};
			state.pageFilterList = [];
		},
		/** */
		setDragObject(state, params) {//
		  // console.log(params,'')
			state.dragObject = params;
			state.curAxisObject = params;
		},
		setCurAxisObject(state, params) {
			state.curAxisObject = params;
		},
		/** */
		setxAxisReport(state, params) {
			state.reportObj = params;
		},
		/***/
		setCurrentChartAxis(state, currentChartAxis) {
      console.log('currentChartAxis');
      console.log(currentChartAxis);
      state.currentChartAxis = JSON.parse(JSON.stringify(currentChartAxis));
			state.pageFilterList = JSON.parse(JSON.stringify([...currentChartAxis.xAxisList, ...currentChartAxis.yAxisList,...currentChartAxis.groupData]));

		},
		/** */
		setUpdateCount(state) {
			state.count++;
		},
		/**  */
		setAmendChartCount(state) {
			state.amendChartCount++;
		},
		setAmendchartRefresh(state) {
			state.amendchartRefresh++;
		},
		/** */
		resetAllSelectInput(state) {
			let defaultAxis = {
				xAxisList: state.defaultChartAxis['xAxisList'],
				yAxisList: state.defaultChartAxis['yAxisList'],
				dataSeries: state.defaultChartAxis['dataSeries']
			};
      console.log('state');
      state.currentChartAxis = defaultAxis;
			state.pageFilterList = [...defaultAxis.xAxisList, ...defaultAxis.yAxisList];

		},
		/** */
		toggleIsChoice(state, params) {
			let isSelectInput = !state.currentChartAxis[params.type][params.index].isSelectInput;
			state.currentChartAxis[params.type][params.index].isSelectInput = isSelectInput;
		},
		togglePageFilterType(state, params) {
			if (params.inputType == 'time' || params.inputType == 'char') {
				let isFilterChar = !state.pageFilterList[params.index].isFilterChar;
				state.pageFilterList[params.index].isFilterChar = isFilterChar;
			} else {
				let isFilterNumber = !state.pageFilterList[params.index].isFilterNumber;
				state.pageFilterList[params.index].isFilterNumber = isFilterNumber;
			}
		},
		toggleReport(state) {
			state.reportObj.isSelectInput = !state.reportObj.isSelectInput;
		},
		/**
         *
         * index
         */
		setCurrentAxisIndex(state, index) {
			if (index > -1) {
				state.currentAxisIndex = index;
			}
		},
		clearReport(state) {
			state.reportObj.isSelectInput = false;
			state.reportObj.isChoice = false;
			state.reportObj.name = '';
			state.reportObj.inputType = '';
			state.reportObj.tableIndex = '';
			state.reportObj.tableName = '';
		},
		/** */
		setCurrentHoverIndex(state, index) {
			state.currentHoverIndex = index;
		},
		setIsFocusDrag(state, params) {
			state.isFocusDrag = params;
		},
		setValueAxisfilterCount(state, params) {
			state.valueAxisfilterCount++;
			state.valueAxisfilterIndex = params['index'];
			state.axisType = params['axisType'];

		},
		setValueAxisfilterPanl(state, referencePosition) {
			state.valueAxisfilterPanl = referencePosition;
		},
		setSafety(state,num){
			state.safety=num;
		}

	}
};
