<template>
  <canvas :width="width"
    :height="height"
    ref="grid">
  </canvas>
</template>
<script>

export default {
	props: {
		width: Number,//
		height: Number//
	},
	data() {
		return {
			canvas: {},
			lineWidth: 1,//
			deepColor: '#1e2635',//5
			undertintColor: '#131822'//
		};
	},
	computed: {
		higherRankSpaing() {
			const obj = {
				1: 50,
				0.9: 60,
				0.8: 80,
				0.7: 120,
				0.6: 160,
				0.5: 200,
				0.4: 250,
				0.3: 300,
				0.2: 400,
				0.1: 500
			};
			return obj[this.scalelevel];
		},
		scalelevel() {//:0.3-1
			return this.$store.getters['canvasPlace/getScaleLevel'];
		},
		gridRowsNum() {// /x
			return this.height / this.xTickSpacing;
		},
		gridColsNum() {// /y
			return this.width / this.yTickSpacing;
		},
		xTickSpacing() {//x 
			return (this.higherRankSpaing / 5) * this.scalelevel;
		},
		yTickSpacing() {//y 
			return (this.higherRankSpaing / 5) * this.scalelevel;
		}
	},
	mounted() {
		this.canvas = this.$refs.grid;
		let ctx = this.canvas.getContext('2d');
		this.$nextTick(()=>{
			this.$options.methods.drawGrid(this, ctx);
		});
	},
	methods: {
		draw() {
			let ctx = this.canvas.getContext('2d');
			ctx.clearRect(0, 0, this.width, this.height);
			this.drawGrid(this, ctx);
		},
		drawGrid(_this, ctx) {
			for (var col = 0; col <= _this.gridColsNum; col++) {
				//
				ctx.beginPath();
				if (col % 5 == 0) {
					let x = col * _this.yTickSpacing;
					ctx.moveTo(x + 20, 1);
					ctx.lineTo(x + 20, _this.height);
					ctx.strokeStyle = _this.deepColor;
				} else {
					ctx.beginPath();
					let x = col * _this.yTickSpacing;
					ctx.moveTo(x + 20, 1);
					ctx.lineTo(x + 20, _this.height);
					ctx.strokeStyle = _this.undertintColor;
				}
				ctx.stroke();
			}
			for (var row = 0; row <= _this.gridRowsNum; row++) {
				//
				if (row % 5 == 0) {
					ctx.beginPath();
					var y = row * _this.xTickSpacing;
					ctx.moveTo(0, y + 21);
					ctx.lineTo(_this.width, y + 21);
					ctx.strokeStyle = _this.deepColor;
				} else {
					ctx.beginPath();
					let y = row * _this.xTickSpacing;
					ctx.moveTo(0, y + 21);
					ctx.lineTo(_this.width, y + 21);
					ctx.strokeStyle = _this.undertintColor;
				}
				ctx.stroke();
			}
		}
	},
	watch: {
		scalelevel() {//
		  this.draw();
			// let ctx = this.canvas.getContext('2d');
			// ctx.clearRect(0, 0, this.width, this.height);
			// this.drawGrid(this, ctx);
		},
		// screenWidth() {
		// 	let ctx = this.canvas.getContext('2d');
		// 	ctx.clearRect(0, 0, this.width, this.height);
		// 	this.drawGrid(this, ctx);
		// },
		height() {//F11
			setTimeout(() => {
				this.draw();
			}, 500);
		}
	}
};
</script>
