<template>
  <div class="sider-shrink"  v-if="!isPhoneLayout">
    <div>
      <div class="menu-left"
        @click="changeItems">
        <span class="biFont icon-doubleArrowLeft"></span>
      </div>
      <ul>
        <li v-for="(item,index) in iconArr"
          :key="item.id"
          :class="{ 'active':index == isChoice }">
          <div class="box-size"
            @click="addIconColor(item,index)">
            <span :class="`biFont icon-${item.iconName}`"
              :style="(item.iconName==='settingPush')?{fontSize: 1 + 'vw'}:''"></span>
          </div>
          <span class="tips">{{item.name}}</span>
        </li>
      </ul>d
    </div>
  </div>
</template>
<script>
// import '@/assets/SiderBar/css/iconfont2.css';
import {mapGetters} from 'vuex';
export default {
	name: 'SiderShrink',
	data() {
		return {
			isShowDetails: -1,
			iconArr: [
				{ id: 1, iconName: 'dataBase', name: '' },
				{ id: 2, iconName: 'fieldShow', name: '' },
				{ id: 3, iconName: 'format', name: '' }
				// { id: 4, iconName: 'settingPush', name: '' }
			]
		};
	},
	computed: {
		isChoice() {
			return this.$store.state.dashboard.rightPaneIndex;
		},
		...mapGetters('canvasPlace', {
			isPhoneLayout: 'getPhoneLayoutState',
		}),
	},
	watch: {
	},
	methods: {
		changeItems() {
			this.$store.commit('dashboard/toggleRightPaneShow',true);
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneHover',value:false});
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneIndex',value:0});
		},
		addIconColor(item, index) {
			if (this.isChoice == index) {
				this.$store.commit('dashboard/setStateProperty', {key:'rightPaneIndex',value:-1});
				this.$store.commit('dashboard/setStateProperty', {key:'rightPaneHover',value:false});
				return false;
			}
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneIndex',value:index});
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneHover',value:true});
		}
	}
};
</script>
<style scoped>
.sider-shrink {
  position: absolute;
  right: 0;
  top: 0;
  width: 2.6vw;
  height: 95.4vh;
  background-color: #171e2b;
}
.sider-shrink ul {
  padding: 0 0.3vw;
}
.sider-shrink ul > li {
  color: #fff;
  box-sizing: content-box;
  display: flex;
  justify-content: center;
  margin-top: 1vw;
  position: relative;
  /* border: solid 0.0625vw transparent; */
}
.sider-shrink ul > li:hover,
.sider-shrink ul > li.active {
  color: #28b3fc;
  /* background-color: #0e1219; */
}
.sider-shrink ul > li:hover .tips {
  visibility: visible;
}
.box-size {
  line-height: 1.4vw;
  text-align: center;
  cursor: pointer;
}
.sider-shrink ul > li .box-size > span {
  font-size: 0.8vw;
}
.sider-shrink ul > li .box-size > span :last-child {
  font-size: 1vw;
}

.menu-left {
  border: 0.1vh solid #0e1219;
  height: 1.6vw;
  line-height: 1.6vw;
  text-align: center;
  background-color: #273045;
  cursor: pointer;
}

.menu-left span {
  font-size: 0.8vw;
  color: #ffffff;
}
.tips {
  visibility: hidden;
  color: #fff;
  position: absolute;
  height: 1.1vw;
  line-height: 1.1vw;
  text-align: center;
  right: 2.3vw;
  top: 0.16vw;
}
</style>
