<template>
  <div class="type-input">
    <span>{{inputData.inputTitle}}</span>
    <div class='inputBox'>
      <input :type="validateType"
        v-model="inputVal"
        @input="onChange"
        @blur='checkVal'
        :placeholder="placeholderDes"
        :disabled="inputData.disable"
        maxlength="50"
        :max="200000"
        :style="{width:inputWidth,height:inputHeight}">
      <span class='unit vCenter'
        v-show='unit'>{{unit}}</span>
    </div>
  </div>
</template>
<script>
export default {
	name: 'InputChild',
	props: {
		inputData: {
			type: Object,
			required: true
		},
		styleObj: {
			type: Object,
			required: false
		},
		validateType: {//
			type: String,
			default: 'text'
		},
		minVal: Number,
		unit: {
			type: String,
			required: ''
		}
	},
	data() {
		return {
			inputWidth: '8vw',
			inputHeight: '1vw',
			placeholderDes: '',
			inputVal: ''
		};
	},
	methods: {
		onChange() {
			let vm = this;
			const validateFun = {
				number: () => {
					let value = vm.inputVal.replace(/\D/g, '');
					vm.$set(vm, 'inputVal', value);
				},
			};
			let type = this.validateType;
			validateFun[type] && validateFun[type]();
			this.$emit('onChange', this.inputVal);
		},
		checkVal() {
			if (String(this.minVal).length) {
				if (this.inputVal < this.minVal) {
					this.inputVal = this.minVal;
				}
			}
			this.inputVal=String(this.inputVal).replace(/^0+/,'');//
			this.$emit('inputBlur', this.inputVal);
		}
	},
	watch: {
		'inputData.inputValue'(val) {
			this.inputVal = val;
		}
	},
	mounted() {
		this.inputVal=this.inputData.inputValue;
		if (this.styleObj) {
			this.inputWidth = this.styleObj.inputWidth;
			this.inputHeight = this.styleObj.inputHeight;
			this.placeholderDes = this.styleObj.placeholderDes;
		}
	},
};
</script>
<style scoped>
  .type-input {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 0.6vw;
    align-items: baseline;
  }

  .type-input span {
    display: block;
    color: #acbbda;
  }

  .type-input .inputBox {
    position: relative;
  }

  .type-input input {
    outline: none;
    background-color: #11151f;
    border: solid 0.0625vw #475566;
    color: #ffffff;
    padding: 0.1vw 2.5vw 0.1vw 0.2vw;
    /*padding-right: 2.5vw;*/
    font-size: 0.6vw;
    width: 8.5vw;
  }

  .type-input .inputBox .unit {
    color: #fff;
    right: 0.6vw;
  }

  input::-webkit-input-placeholder {
    color: grey;
  }

  input:focus {
    border-color: #00b9ff;
  }
</style>


