<template>
  <div>
<!--    <div-->
<!--      class="chart-basis"-->
<!--      @click="showEvent">-->
<!--      <span></span>-->
<!--      <span class="biFont icon-arrowLeft icon-fontsize"-->
<!--        :class="{rotateIcon:isShowColor}"></span>-->
<!--    </div>-->
<!--    <div class="content-box"-->
<!--      v-show="isShowColor">-->
      <!--      <div class="color-click">-->
      <!--        <span></span>-->
      <!--        <span></span>-->
      <!--        <span></span>-->
      <!--      </div>-->
      <ul>
        <li class="optionsItem">
          <span class="span-title"></span>
          <toggle-button v-model="tabLes"
            @change="tabSingle"
            :sync="true"
            :labels="{checked: '', unchecked: ''}"
            :css-colors="true"/>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="settingTitleTop"
            v-on:rangedata="rangeTitleFont"
            v-on:inputData="rangeTitleFont"></silder-data>
        </li>
        <li class="optionsItem">
          <select-color :defaultColor="titleObject"
            v-on:changeColor="getYcolor"></select-color>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="settingNumber"
            v-on:rangedata="rangeNumberFont"
            v-on:inputData="rangeNumberFont"></silder-data>
        </li>
        <li class="optionsItem">
          <select-color :defaultColor="numberObject"
            v-on:changeColor="getNumbercolor"></select-color>
        </li>
        <li class="optionsItem">
          <select-color :defaultColor="backgroudObject"
            v-on:changeColor="getBgcolor"></select-color>
        </li>
        <li>
          <choice-Photo :afferent="bgBaseUrl"
            @baseUrl="cardAddress"></choice-Photo>
        </li>
      </ul>
    </div>
<!--  </div>-->
</template>

<script>
import SilderData from './BasicComponent/Slider';
import ColorSelect from './BasicComponent/ColorSetting';
import ChoicePhoto from '@/components/Picture/PictureUpload.vue';
import {getRgba} from '@/utils/optionSetting';
import { mapGetters, mapMutations } from 'vuex';
import {mixin} from './BasicComponent/mixin';
import {ToggleButton} from 'vue-js-toggle-button';
export default {
	name: 'cardChart',
	mixins:[mixin],
	components: {
		'silder-data': SilderData,
		'select-color': ColorSelect,
		'choice-Photo': ChoicePhoto,
		'toggle-button': ToggleButton,
	},
	data() {
		return {
			isShowColor: true,
			tabLes: false,//
			bgBaseUrl: {
				photoUrl: '',
				title: '',
				selectImg: '',
				showWith: 8.5,
				showHeight: 6,
				size: 1024,
				accept: 'image/jpeg,image/png,image/gif'
			},
			settingTitleTop: {
				//
				name: '',
				max: 100,
				min: 0,
				valueRange: 1,
				garidyValue: 1
			},
			settingNumber: {
				//
				name: '',
				max: 100,
				min: 0,
				valueRange: 1,
				garidyValue: 1
			},
			titleObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			numberObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			backgroudObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			}
		};
	},
	computed: {
		// ...mapGetters('chartBus', {
		// 	resultPost: 'getResultData'
		// }),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',
			currentContainerOptions:'getCurrentContainerOptions',
			currentRenderOptions: 'getCurrentChartRenderOptions',
		})
	},
	watch:{
		currentRenderOptions:{
			deep:true,
			immediate:true,
			handler(){
				this.showCard();
			}
		},
		// currentRenderOptions(){//
		// 	this.showCard();
		// }
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter'
		}),
		showCard(){
			let vm = this,
				Style = vm.currentRenderOptions.style;
			vm.settingTitleTop.valueRange = Style.fontSize;
			// vm.titleObject.titleColor = colorTransformation(Style.color);
			vm.titleObject.rgba = Style.color;
			vm.settingNumber.valueRange = Style.numberFont;
			// vm.numberObject.titleColor = colorTransformation(Style.numberColor);
			vm.numberObject.rgba = Style.numberColor;
			// vm.backgroudObject.titleColor = colorTransformation(Style.backgroundColor);
			vm.backgroudObject.rgba = Style.backgroundColor;
			vm.bgBaseUrl.photoUrl = Style.backgroundImage;
			vm.tabLes = Style.switchingMode;
		},
		tabSingle(val){
			this.saveDataIndexDb({
				name: 'style.switchingMode',
				pare: val.value
			});
		},
		cardAddress(data) {
			if (data.baseUrl) {
				let imgType = data.imgType;
				if (imgType === 'gif' && data.imgSize >= 1024) {
					this.tips.show(['1Mbgif']);
					this.bgBaseUrl.photoUrl = '';
				} else {
					if (data.imgSize >= 2048) {
						this.tips.show(['2MB']);
						this.bgBaseUrl.photoUrl = '';
					} else {
						this.renderOptionSetter({
							key: 'style.backgroundImage',
							value: data.baseUrl
						});
						this.$store.dispatch('canvasPlace/setIndexdbChartOptions',{index:this.currentChart});
					}
				}
			} else {
				this.renderOptionSetter({
					key: 'style.backgroundImage',
					value: data.baseUrl
				});
				this.$store.dispatch('canvasPlace/setIndexdbChartOptions',{index:this.currentChart});
				// this.saveDataIndexDb({
				// 	name: 'style.backgroundImage',
				// 	pare: data.baseUrl
				// });
			}
		},
		getBgcolor(data) {
			let rgba=  getRgba(data.rgba);
			// if (this.lineYObject.rgba !== rgb) {
			this.saveDataIndexDb({
				name: 'style.backgroundColor',
				pare: rgba
			});
			// }
		},
		// 
		getYcolor(data) {
			let rgba=  getRgba(data.rgba);
			// if (this.lineYObject.rgba !== rgb) {
			this.saveDataIndexDb({
				name: 'style.color',
				pare: rgba
			});
			// }
		},
		// 
		getNumbercolor(data) {
			let rgba=  getRgba(data.rgba);

			// if (this.lineYObject.rgba !== rgb) {
			this.saveDataIndexDb({
				name: 'style.numberColor',
				pare: rgba
			});
			// }
		},
		rangeTitleFont(data) {
			let vm = this;
			if (vm.settingTitleTop.valueRange !== data) {
				vm.saveDataIndexDb({
					name: 'style.fontSize',
					pare: parseInt(data)
				});
			}
			vm.settingTitleTop.valueRange = data;
		},
		rangeNumberFont(data) {
			let vm = this;
			if (vm.settingNumber.valueRange !== data) {
				vm.saveDataIndexDb({
					name: 'style.numberFont',
					pare: parseInt(data)
				});
			}
			vm.settingNumber.valueRange = data;
		},
		// 
		saveDataIndexDb({ name, pare }) {
			let vm = this;
			vm.renderOptionSetter({
				key: name,
				id: vm.returnDataIndex,
				value: pare
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		},
		showEvent() {
			this.isShowColor = !this.isShowColor;
		},
	}
};
</script>

<style scoped>
  .optionsItem {
    margin-bottom: 0.8vw;
    display: flex;
    flex-direction: row;
    align-items: baseline;
    justify-content: space-between;
  }

  .optionsItem .span-title {
    display: block;
    flex: 1;
    color: #acbbda;
  }

  .m-colorPicker .box {
    background: #161c29 !important;
  }

  .colo-span {
    color: #fff;
    display: inline-block;
    height: 15px;
    vertical-align: top;
    padding-left: 0.7vw;
  }

  .color-click {
    display: inline-block;
    padding: 0.5vw 0.4vw 0.5vw 1vw;
  }

  .chart-basis .rotateIcon {
    transform: rotate(-90deg);
  }
  .optionsItem /deep/ .vue-js-switch .v-switch-core.v-switch {
    top: -1px;
  }
  .optionsItem /deep/ .vue-js-switch .v-switch-label.v-left{
    left: 6px;
  }
  .optionsItem /deep/ .vue-js-switch .v-switch-label.v-right {
    right: 6px;
  }

</style>
