export function seriesDataHandle(chartOptions, yAxisList={}) {
	let series = [];
	yAxisList.forEach((item, idx) => {
		let seriseInd = 0;
		// 	type;
		if (chartOptions.series[idx]) {
			seriseInd = idx;
		}
		// let seriseInd = 0,
		// 	type;
		// if (chartOptions.series[idx]) {
		// 	seriseInd = idx;
		// }
		let type = chartOptions.series[seriseInd].type;
		// if (type === 'bar3D' || type === 'map3D') {
		// 	// const seriesMap = {
		// 	// 	seriesName:'',
		// 	// 	seriesType: 'map3D',
		// 	// };
		// 	const seriesBar = {
		// 		seriesName:item.name,
		// 		seriesType: 'bar3D',
		// 	};
		// 	series.push(seriesMap,seriesBar);
		// }else{
		series.push({
			seriesName:item.yField,
			seriesType: type,
		});

	});
	return series;
}
export function commonOptions(allOptions,allAxisList) {
	let result = {};
	// helen XY
	const categoryAxis = allOptions.containerOptions.categoryAxis || sessionStorage.getItem('categoryAxis');
	let xAxisObject = {};
	let yAxisList = [];
	let orderData = [],
		groupDataList = [];
	const axisType = categoryAxis === 'X' ? 'xAxisList' : 'yAxisList';
	const valieAxis = categoryAxis == 'X' ? 'yAxisList' : 'xAxisList';
	allAxisList.groupData&&allAxisList.groupData.forEach((item,idx) => {
		groupDataList.push({
			groupField: item.name,//group
			groupSourceId: item.tableIndex,//groupID
			groupSourceName: item.tableName,//group
			groupOp: '',//group
			groupCategory: '',//group
			groupCondition: ''//group
		});
	});

	// x
	xAxisObject.xSourceId = allAxisList[axisType][0].tableIndex;
	xAxisObject.xSourceName = allAxisList[axisType][0].tableName;
	xAxisObject.xField = allAxisList[axisType][0].name;
	xAxisObject.xOp = '';
	const orderCategory = filterOrderData(allAxisList[axisType][0], 'category');
	// let leng
	// debugger;
	if (orderCategory) {
		orderData.push(orderCategory);
	}
	allAxisList[valieAxis].forEach(e => {
		if (e.name != '') {
			const yAxisObject = {
				ySourceId: e.tableIndex,
				ySourceName: e.tableName,
				yField: e.name,
				yOp: e.yOp,
				yCondition: '',
				yPercent: e.yPercent || '',
			};
			yAxisList.push(yAxisObject);
			const orderValueAxis = filterOrderData(e, 'value');
			if (orderValueAxis) {
				orderData.push(orderValueAxis);
			}
		}
	});
	let containerOptions = allOptions.containerOptions,
		renderOptions = allOptions.renderOptions,
		seriesData=seriesDataHandle(renderOptions,yAxisList),
		tableId = containerOptions.tableId;
	result = {
		projectId: sessionStorage.getItem('projectId'),
		tableId: tableId,
		chartsType: containerOptions.chartsType,
		categoryAxis: categoryAxis,
		layerName: containerOptions.layerName,
		level: containerOptions.level,
		xSourceId: xAxisObject.xSourceId,
		xSourceName: xAxisObject.xSourceName,
		xField: xAxisObject.xField,
		xOp: xAxisObject.xOp,
		yData: yAxisList,
		orderData: orderData.length? [orderData[orderData.length-1]]:[],
		groupData:groupDataList,
		/* */
		//
		xPosition: containerOptions.xPosition, //x
		yPosition: containerOptions.yPosition,//y
		tableWidth: containerOptions.tableWidth,//
		tableHeight: containerOptions.tableHeight,//
		xPositionMobile: containerOptions.xPositionMobile,//x
		yPositionMobile: containerOptions.yPositionMobile,//y
		tableWidthMobile: containerOptions.tableWidthMobile,//
		tableHeightMobile: containerOptions.tableHeightMobile,//
		showInMobileLayout: containerOptions.showInMobileLayout,
		backgroundColor: containerOptions.backgroundColor,//
		renderOptions: JSON.stringify(renderOptions),
		seriesData,
	};
	return result;
}
