export function setInteractiveData(data) {

	const containerOptions = data.containerOptions;
	const renderOptions = data.renderOptions,
		type = data.containerOptions.chartsType;
	let result={};
	// let result=commonOptions(data)
	switch (type) {
	case 'interactive-cardChart':
		result = cardChart(containerOptions, renderOptions);
		break;
	case 'interactive-timeMonitor':
		result = timeMonitor(containerOptions, renderOptions);
		break;
	// case 'interactive-proportionRing':
	// 	result = interactiveCommonOptions(containerOptions, renderOptions);
	// 	break;
	case 'interactive-dataFilter':
		result = filterOptions(containerOptions, renderOptions);
		break;
	default:
		result = interactiveCommonOptions(containerOptions, renderOptions);
	}
	result.renderOptions=JSON.stringify(renderOptions);
	// console.log(Object.assign(result, {renderOptions:JSON.stringify(renderOptions)}));
	// debugger;
	return result;
}
