function filterOrderData(orderData, axisType) {
	if (orderData.orderOp) {
		const orderObject = {
			orderField: orderData.name,
			orderSourceId: orderData.tableIndex,
			orderSourceName: orderData.tableName,
			orderOp: orderData.orderOp,
			orderCondition: orderData.orderCondition,
			orderCategory: axisType
		};
		return orderObject;
	}
	return '';
}

