<template>
  <div class="type-input">
    <span>{{inputTitle}}</span>
    <div class='inputBox'>
      <input type="text"
        v-for="(data,index) in inputData"
        :key="index"
        v-model="inputDataArr[index]"
        @input="onChange(index)"
        @blur='onBlur(index)'
        :placeholder="data.placeholder"
        :disabled="data.disable"
        maxlength="50"
        :style="data.style">
    </div>
  </div>
</template>
<script>
export default {
	name: 'multipleInput',
	props: {
		inputTitle: String,
		inputData: {
			type: Array,
			require: true
		}
	},
	data() {
		return {
			inputDataArr: [],
			validDataArr: []//
		};
	},

	watch: {
		inputData: {
			handler(val) {
				this.inputDataArr = val.map(ele => ele.value);
				this.validDataArr = val.map(ele => ele.value);
			},
			deep: true,
			immediate: true,
		}
	},
	methods: {
		checkVal(minVal) {
			if (String(minVal).length) {
				if (this.inputVal < minVal) {
					this.inputVal = minVal;
				}
			}
		},
		onChange(index) {
			let value = this.inputDataArr[index].replace(/\D/g, '');
			this.$set(this.inputDataArr, index, value);
			this.inputData[index]['onChange'] && this.inputData[index]['onChange'](value);
		},
		onBlur(index) {
			let value = this.inputDataArr[index];//0
			if (String(value).length) {
				if (~~value !== this.validDataArr[index]) {//
					this.$set(this.inputDataArr, index, ~~value);
					this.$set(this.validDataArr, index, ~~value);
					this.inputData[index]['onBlur'] && this.inputData[index]['onBlur'](~~value);
				}
			} else {
				this.$set(this.inputDataArr, index, this.validDataArr[index]);
			}
		}
	},
	mounted() {
		for (let i = 0; i < this.inputData.length; i++) {
			this.inputDataArr.push(this.inputData.value || '');
			this.validDataArr.push(this.inputData.value || '');
		}
	},
};
</script>
<style scoped>
  .type-input {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: 0.6vw;
    align-items: baseline;
  }

  .type-input span {
    display: block;
    color: #acbbda;
  }

  .type-input .inputBox {
    position: relative;
  }

  .type-input input {
    background-color: #11151f;
    border: solid 0.0625vw #475566;
    color: #ffffff;
    padding: 0 0.2vw;
    font-size: 0.6vw;
    width: 8.5vw;
  }

  .type-input .inputBox .unit {
    color: #fff;
    right: 0.6vw;
  }

  input::-webkit-input-placeholder {
    color: grey;
  }

  input:focus {
    border-color: #00b9ff;
  }
</style>

