<template>
  <div class="wrapper"
       style="backgroundSize:cover"
       ref="canvasWrapper"
       @dragleave="dragleave"
       @dragover="dragSport($event)"
       @drop="dropOver($event)"
       @mousedown="rightMouseClick"
       :style="{
      backgroundImage: `url(${imgBase64})`,
      'background-color': canvasBgColor
    }">
    <!--      :style="{ ...info.style }"-->
    <Modal :options="promptSingleChart"
           v-if="isShowModal"
           data-class="unableDeactive"></Modal>
    <div class="positionInfo"
         v-show="currentChartIndex > -1"
    >
      <span class="count"
            :class="info.className"
            :style="info.style"
            :track-by="info.className"
            v-for="(info, idx) in positionInfo"
            :key="idx">{{ info.count }}</span>
    </div>

    <template v-for="(item, index) in chartListAfterClear">
      <vue-draggable-resizable
        ref="groupDragWrapper"
        id="groupDrag"
        :x="trabsltaesX"
        :y="trabsltaesY"
        :w="newDragWidth"
        :h="newDragHeight"
        @dragging="onDrag"
        @resizing="onResize"
        v-if="isGroupDragShow && index === 0"
        style="position:absolute; border:1px solid red"
        v-for
      >
      </vue-draggable-resizable>
      <vue-draggable-resizable ref="draggable"
                               :draggable="true"
                               v-if="!item.containerStatus.isDeleted"
                               :data-key="index"
                               :key="index"
                               :enableNativeDrag="true"
                               :prevent-deactivation="true"
                               :active="index == currentChartIndex"
                               :x="Number(item.containerOptions.xPosition)"
                               :y="Number(item.containerOptions.yPosition)"
                               :w="Number(item.containerOptions.tableWidth) || 1"
                               :h="Number(item.containerOptions.tableHeight) || 1"
                               :z="Number(item.containerOptions.level) || 0"
                               :minWidth="item.containerOptions.minWidth || 0"
                               :minWidthUnit="item.containerOptions.minWidth?vw:1"
                               @resetWidth="(width)=>resetWidth(index,item.containerOptions.xPosition,width)"
                               :minHeight="item.containerOptions.minHeight || 0"
                               :minHeightUnit="item.containerOptions.minHeight?vw:1"
                               @resetHeight="(height)=>resetHeight(index,item.containerOptions.yPosition,height)"
                               @activated="activated(index, item, $event)"
                               v-click-outside="{ fn: onDeactivated, arg: { item, index } }"
                               :onDragStart="onDragStartCallback"
                               @resizing="
          (x, y,right,bottom, width, height) => resizing(x, y, right,bottom,width, height, index)
        "
                               @resizestop="
          (x, y, right,bottom,width, height) => setSize(x, y, right,bottom,width, height, index, item)
        "
                               @dragging="(left, top,right,bottom) => dragging(left, top,right,bottom,index)"
                               @dragstop="(left, top,right,bottom) => setPosition(left, top,right,bottom, index, item)"
                               :parent="true"
                               :debug="false"
                               :isConflictCheck="false"
                               :snap="true"
                               :snapTolerance="10"
                               :scale-ratio="Number(scaleLevel)"
                               @refLineParams="getRefLineParams"
                               id="chart"
      >
        <!--        :resizable="item.containerOptions.chartsType === 'interactive-dataFilter'?false:true " -->
        <template v-for="(handle, index) in handles"
                  :slot="handle">
          <span class="handle-span"
                :style="{ ...nonTransform }"
                :key="index"></span>
        </template>
        <!--  -->
        <div class="navigatorLine"
             v-show="index === currentChartIndex">
          <div class="line"
               :style="nonTransform"
               :class="line.lineName"
               v-for="(line, idx) in navigatorLine(item)"
               :key="idx"></div>
        </div>


        <!-- , -->
        <!--        <div class="dragging-amplifier"></div>-->
        <Switcher
          v-show="getSwitcherStatus(item, index) && item.containerOptions.chartsType.indexOf('chart')>-1"
          :dataDimensionShow="isDataDimensionShow(item)"
          :index="index"></Switcher>
        <DataDimension class="dataDimension"
                       v-if="item.containerOptions.chartsType.indexOf('chart')>-1"
                       :dataDimensionShow="isDataDimensionShow(item)"
                       :class="{ offEvent: isPreventEvent(item) }"
                       :currentIndex="index"
                       :isSetOptions="item.containerStatus.isSetOptions"
                       :isShowLoading="item.containerStatus.isShowLoading"
                       :curChartType="item.containerOptions.chartsType"
                       :w="Number(item.containerOptions.tableWidth)"
                       :h="Number(item.containerOptions.tableHeight)"
                       :x="Number(item.containerOptions.xPosition)"
                       :y="Number(item.containerOptions.yPosition)">
        </DataDimension>
        <!--        <LoadingRotating v-if="item.containerStatus.isShowLoading"-->
        <!--          :style="{-->
        <!--            zIndex: item.containerOptions.level + 8 || 8,-->
        <!--            position: 'absolute'-->
        <!--          }"-->
        <!--          :styleObj="loadingStyle"></LoadingRotating>-->
        <MoreOption :style="{ ...nonTransform }"
                    :level="Number(item.containerOptions.level + 7 || 7)"
                    :isSetOptions="item.containerStatus.isSetOptions"
                    :chartIndex="index"
                    :tableId="item.containerOptions.tableId"></MoreOption>
        <!-- @setDelChartStatus="getDelStatus" -->
        <!-- :isDelCurChart="index" -->
        <component :is="item.containerOptions.chartsType"
                   :class="{ active: index === currentChartIndex }"
                   :chartIndex="index"
                   :style="{ 'z-index': item.containerOptions.level + 3 || 3 }"
                   :containerOptions="item.containerOptions"
                   :containerStatus="item.containerStatus"
                   :options="item.renderOptions"
                   :isSetOptions="item.containerStatus.isSetOptions"
                   :yFiles="item.containerOptions.y"
                   :isShowLoading="item.containerStatus.isShowLoading"
                   :tableId="item.containerOptions.tableId"
                   :editable="true"
                   :phoneLayoutState="isPhoneLayout">
        </component>
      </vue-draggable-resizable>
    </template>
    <!---->
    <span class="ref-line v-line"
          v-for="item in vLine"
          v-show="item.display"
          :style="{ left: item.position, top: item.origin, height: item.lineLength}"
    />
    <span class="ref-line h-line"
          v-for="item in hLine"
          v-show="item.display"
          :style="{ top: item.position, left: item.origin, width: item.lineLength}"
    />
    <!--END-->
    <div class="rightClickPopup" v-show="isRightClickPopupShow" style="display: flex;flex-direction: column">

      <span @click.stop="toGroup"></span>
      <span @click.stop="cancleToGroup" style="margin-top: 10px"></span>
    </div>
  </div>
</template>
<script>
  import {mapGetters, mapMutations} from 'vuex';
  import DataDimension from '@/components/CanvasPlace/Charts/components/DataDimension';
  import MoreOption from '@/components/CanvasPlace/Charts/components/MoreOption';
  import Switcher from '@/components/CanvasPlace/Charts/components/Switcher';
  import Modal from '@/components/Prompt/Modal';
  import {composedPath} from '@/utils/domMethods';
  import {beforeThemeHandle} from '@/utils/optionSetting.js';
  import {echartsCommonMobileOptions} from '@/store/commonData/chartOptions';
  import defaultChartOptions from '@/store/defaultChartOptions';
  import VueDraggableResizable from 'vue-draggable-resizable-gorkys';
  import 'vue-draggable-resizable-gorkys/dist/VueDraggableResizable.css';

  import {asyncComponent} from './mixnin/asyncComponent';//
  export default {
    name: 'CanvasPlace',
    mixins: [asyncComponent],
    components: {
      MoreOption,
      DataDimension,
      Switcher,
      Modal,
      VueDraggableResizable
    },
    props: {
      isSendChart: {
        type: Boolean,
        required: true
      }
    },
    data() {
      return {
        positionInfo: [],
        handles: ['tl', 'tm', 'tr', 'mr', 'br', 'bm', 'bl', 'ml'],
        gridTime: {},
        chartData: [], //
        loadingStyle: {
          width: '100%',
          height: '100%'
        },
        isTableClear: false,
        isShowModal: false,
        isDelChart: false,
        promptChart: {},
        screenshotTimer: null,
        chartListLength: 0,
        dataDemensionLength: 0,
        dataDemensionFinishiInitNum: 0,
        finishAllDemesionInit: false,
        saveDataBaseData: [],//
        flag: undefined,//
        vLine: [],
        hLine: [],
        selList: [],
        selDiv: null,
        isSelect: false,
        startX: null,
        startY: null,
        selDivX: null,
        selDivY: null,
        isCircleSelect: true,
        isRightClickPopupShow: false,
        trabsltaesX: 1920,
        trabsltaesY: 960,
        newDragWidth: 0,
        newDragHeight: 0,
        isGroupDragShow: false,
      };
    },
    computed: {
      ...mapGetters('main', ['vw']),
      ...mapGetters('canvasPlace', {
        chartList: 'getChartList',
        currentChartIndex: 'getCurrentChartIndex',
        isInitAllChart: 'startInitAllChart',
        dimensionDefault: 'getDimensionDefault',
        canvasBgColor: 'getCanvasBgColor',
        imgBase64: 'getCanvasBgImg',
        scaleLevel: 'getScaleLevel',
        renderedChartCount: 'getRenderedChartCount',
        curProjectId: 'getCurProjectId',
        isPhoneLayoutSaved: 'getPhoneLayoutSavedState',
        isPhoneLayout: 'getPhoneLayoutState',//pc
        isModule: 'getModuleNum',
        aiStore: 'getAichart',// 
      }),
      ...mapGetters('chartBus', {
        timeState: 'returnTime',
        chartDataList: 'returnData',
        isChartSaveComplete: 'getSaveComplete',
        // textReceive: 'returnTextData'
      }),
      ...mapGetters('handleChart', {
        // saveChart: 'returnData', //
        saveText: 'textReturnData', //
        saveMedia: 'mediaReturnData' //
      }),
      ...mapGetters('voiceCommand', {
        getCreatData: 'getCreatChart',
        charCommentDrag: 'getCommentDrag',// 
      }),
      nonTransform() {
        return {transform: `scale(${1 / this.scaleLevel})`};
      },
      navigatorLine() {
        return item => {
          let top = Math.floor(
            Number(item.containerOptions.yPosition) / this.scaleLevel
          );
          let bottom =
            top +
            Math.floor(
              Number(item.containerOptions.tableHeight) / this.scaleLevel
            );
          let left = Math.floor(
            Number(item.containerOptions.xPosition) / this.scaleLevel
          );
          let right =
            left +
            Math.floor(
              Number(item.containerOptions.tableWidth) / this.scaleLevel
            );
          return [
            {
              lineName: 'topLine',
              count: top
            },
            {
              lineName: 'bottomLine',
              count: bottom
            },
            {
              lineName: 'leftLine',
              count: left
            },
            {
              lineName: 'rightLine',
              count: right
            }
          ];
        };
      },
      chartListAfterClear() {//
        if (this.isTableClear && !this.isPhoneLayout) {
          return this.chartList;
        } else {
          return [];
        }
      },
      promptSingleChart() {
        return {
          title: '!',
          describe: '',
          inputType: 'text',
          placeholder: '',
          name: [],
          btn: [
            {
              text: '',
              callback: data => {
                this.timeTrue(true);
              }
            },
            {
              text: '',
              callback: data => {
                let vm = this;
                vm.$store.commit('canvasPlace/setStateProperty', {key: 'shareType', value: 2});
                vm.isShowModal = false;
              }
            }
          ]
        };
      },
      deleteSingleChart() {
        const vm = this;
        return {
          title: '!',
          describe: '',
          inputType: 'text',
          placeholder: '',
          name: [],
          btn: [
            {
              text: '',
              callback: data => {
                vm.isDelChart = true;
              }
            },
            {
              text: '',
              callback: data => {
                vm.isDelChart = false;
                vm.isShowModal = false;
              }
            }
          ]
        };
      },
      getSwitcherStatus() {
        return (item, index) => {
          let isSetOptions = item.containerStatus.isSetOptions;
          return isSetOptions && index == this.currentChartIndex;
        };
      },
      isDataDimensionShow: () => item => item.containerStatus.dataDimensionShow,
      isPreventEvent() {
        return item => {
          if (item.containerStatus.isSetOptions) {
            return !item.containerStatus.dataDimensionShow;
          } else {
            return false;
          }
        };
      }
    },
    watch: {
      isModule(val) {
        if (this.isModule % 2 === 0) {
          this.flag = true;
          this.getChartData({sourceType: 'module'});
        }
      },
      'chartList.length': function (val, oldVal) {
        if (val > oldVal && this.currentChartIndex > -1) {
          let index = this.currentChartIndex;
          this.$store.commit('canvasPlace/setStateProperty', {key: 'lastActivedChartIndex', value: index});
          let item = this.chartList[index]['containerOptions'];
          let top = Number(item.yPosition);
          let bottom = top + Number(item.tableHeight);
          let left = Number(item.xPosition);
          let right = left + Number(item.tableWidth);
          this.positionInfo = [
            {
              className: 'x1',
              style: {
                left: left - 3 + 'px',
                transform: `scale(${1 / this.scaleLevel}) translateX(-100%)`
              },
              count: Math.round(left)
            },
            {
              className: 'x2',
              style: {
                left: right - 3 + 'px',
                transform: `scale(${1 / this.scaleLevel}) translateX(-100%)`
              },
              count: Math.round(right)
            },
            {
              className: 'y1',
              style: {
                top: top + 3 + 'px',
                transform: `scale(${1 / this.scaleLevel})`
              },
              count: Math.round(top)
            },
            {
              className: 'y2',
              style: {
                top: bottom + 3 + 'px',
                transform: `scale(${1 / this.scaleLevel})`
              },
              count: Math.round(bottom)
            }
          ];
        }
      },
      'renderedChartCount'(val) { //val 
        // this.chartListLength=2 this.dataDemensionLength=1 
        if (val == this.chartListLength && this.dataDemensionLength == 0) {
          this.dataIsOk();
          // this.$emit('dataIsOk', true);//
          // this.screenshotTimer = setInterval(this.cutScreen, 1000 * 30);
        }
      },
      // 'dataDemensionLength'(val){
      //   if(val===0){
      //     this.finishAllDemesionInit=true;
      // 	}
      // },
      'dataDemensionFinishiInitNum'(val) {
        if (val == this.dataDemensionLength) {
          this.dataIsOk();
        }
      },
      // timeState(val) {
      // 	if (!val) {
      // 		this.cutScreen();
      // 	}
      // },
      isInitAllChart: {
        immediate: true,
        handler(newValue, oldValue) {
          if (newValue === true) {
            this.getChartData();
          }
        }
      },
      aiStore: {
        handler(newValue) {
          this.getChartData();
        }
      },
      dataFilter(val) {
        if (val) {
          this.filterData();

        }
      }
    },
    methods: {
      onDrag() {
      },
      onResize() {
      },
      cancleToGroup() {
        const canvasPlace = document.querySelector('.canvasPlace');
        const newDrag = document.querySelector('#groupDrag');
        const fileNode = Array.from(newDrag.childNodes).filter((item) => {
          return item.className.indexOf('vdr') !== -1;
        });
        console.log(fileNode);
        fileNode.forEach((item) => {
          let index = parseInt(item.getAttribute('data-key')) ;
          let position = {x:parseInt(item.style.left.split('px')[0]) + parseInt(newDrag.style.left.split('px')[0]),
            y:parseInt(item.style.top.split('px')[0]) + parseInt(newDrag.style.top.split('px')[0]), index};
          this.$store.commit('canvasPlace/setChartPosition', {position});
          item.style.left = parseInt(item.style.left.split('px')[0]) + parseInt(newDrag.style.left.split('px')[0]) + 'px';
          item.style.top = parseInt(item.style.top.split('px')[0]) + parseInt(newDrag.style.top.split('px')[0]) + 'px';
          canvasPlace.appendChild(item);
        });
        this.isGroupDragShow = false;
        this.isRightClickPopupShow = false;
      },
      toGroup() {
        this.isGroupDragShow = true;
        this.isRightClickPopupShow = false;
        const fileNode = Array.from(document.querySelectorAll('.selected'));
        let trabsltaesX = 1920, trabsltaesY = 960;
        let maxX = 0, maxY = 0;
        let maxXIndex = 0, maxYIndex = 0;
        let maxXWidth = 0, maxYHeight = 0;
        fileNode.forEach((item, index) => {
          const translates = item.style;
          let xx, yy;
          xx = parseInt(translates.left.split('px')[0]);
          yy = parseInt(translates.top.split('px')[0]);
          //  x ,y ,  dragDiv  translateXtranslateY
          trabsltaesX = xx < trabsltaesX ? xx : trabsltaesX;
          trabsltaesY = yy < trabsltaesY ? yy : trabsltaesY;
          //  x,y  dragDiv 
          if (xx > maxX) {
            maxX = xx;
            maxXIndex = index;
          }
          if (yy > maxY) {
            maxY = yy;
            maxYIndex = index;
          }
        });
        maxXWidth = parseInt(fileNode[maxXIndex].style.width.split('px')[0]);
        maxYHeight = parseInt(fileNode[maxYIndex].style.height.split('px')[0]);
        this.trabsltaesX = trabsltaesX;
        this.trabsltaesY = trabsltaesY;
        this.newDragWidth = maxX + maxXWidth - trabsltaesX;
        this.newDragHeight = maxYHeight + maxY - trabsltaesY;
        // , 
        setTimeout(() => {
          const newDrag = document.querySelector('#groupDrag');
          fileNode.forEach((item, index) => {
            // dragdiv 
            item.style.left = parseInt(item.style.left.split('px')[0]) - this.trabsltaesX + 'px';
            item.style.top = parseInt(item.style.top.split('px')[0]) - this.trabsltaesY + 'px';
            //  selected class
            item.className = 'draggable resizable vdr';
            newDrag.appendChild(item);
          });
        }, 0);
      },
      rightMouseClick(e) {
        if (e.button === 2) {
          const rightDiv = document.querySelector('.rightClickPopup');
          this.isRightClickPopupShow = true;
          rightDiv.style.left = e.clientX + 300 + 'px';
          rightDiv.style.top = e.clientY + 50 + 'px';
          this.isCircleSelect = false;
        }
      },
      onDragStartCallback(e) {
        if (e.button === 0) {
          this.isRightClickPopupShow = false;
          const fileNode = Array.from(document.querySelectorAll('.selected'));
          for (let i of fileNode) {
            i.className = 'draggable resizable vdr';
          }
          this.isCircleSelect = false;
        }
      },
      // 
      mousedown() {
        const fileNodes = document.querySelectorAll('.wrapper #chart');
        for (let i = 0; i < fileNodes.length; i++) {
          // 
          if (fileNodes[i].className.indexOf('vdr') !== -1) {
            fileNodes[i].className = 'draggable resizable vdr';
            this.selList.push(fileNodes[i]);
          }
        }
        this.isSelect = true;
        const evt = window.event || arguments[0];
        // 
        this.startX = (evt.x || evt.clientX);
        this.startY = (evt.y || evt.clientY);
        // 
        this.selDiv = document.createElement('div');
        // CSS
        this.selDiv.style.cssText = 'position:absolute; width:0px; height:0px; font-size:0px; margin:0px; padding:0px; border:1px dashed #0099FF; z-index:1000; filter:alpha(opacity:60); opacity:0.6; display:none';
        // ID
        this.selDiv.id = 'selectDiv';
        // appendChild()
        document.body.appendChild(this.selDiv);
        // 
        this.selDiv.style.left = this.startX + 'px';
        this.selDiv.style.top = this.startY + 'px';
        this.clearEventBubble(evt);
        document.querySelector('.canvasPlace').onmousemove = () => {
          this.mousemove();
        };
        document.onmouseup = () => {
          this.mouseup(evt);
        };
      },
      mousemove() {
        const evt = window.event || arguments[0];
        if (this.isSelect) {
          if (this.selDiv.style.display === 'none') {
            this.selDiv.style.display = 'block';
          }
          this.selDivX = (evt.x || evt.clientX);
          this.selDivY = (evt.y || evt.clientY);
          this.selDiv.style.left = Math.min(this.selDivX, this.startX) + 'px';
          this.selDiv.style.top = Math.min(this.selDivY, this.startY) + 'px';
          this.selDiv.style.width = Math.abs(this.selDivX - this.startX) + 'px';  //Math.abs()
          this.selDiv.style.height = Math.abs(this.selDivY - this.startY) + 'px';

          var _l = this.selDiv.getBoundingClientRect().left;
          var _t = this.selDiv.getBoundingClientRect().top;
          var _w = this.selDiv.getBoundingClientRect().width;
          var _h = this.selDiv.getBoundingClientRect().height;
          for (let i = 0; i < this.selList.length; i++) {
            var sl = this.selList[i].getBoundingClientRect().width + this.selList[i].getBoundingClientRect().left;
            var st = this.selList[i].getBoundingClientRect().height + this.selList[i].getBoundingClientRect().top;
            if (sl > _l && st > _t && this.selList[i].getBoundingClientRect().left < _l + _w && this.selList[i].getBoundingClientRect().top < _t + _h) {
              // DOM
              // indexOf()
              if (this.selList[i].className.indexOf(' selected') === -1) {
                this.selList[i].className = this.selList[i].className + ' selected';
              }
            } else {
              if (this.selList[i].className.indexOf(' selected') !== -1) {
                this.selList[i].className = 'draggable resizable vdr';
              }
            }
          }
        }
        this.clearEventBubble(evt);
      },
      mouseup(evt) {
        this.isSelect = false;
        if (this.selDiv) {
          document.body.removeChild(this.selDiv);
        }
        this.selList = [], this.selDivX = null, this.selDivY = null, this.selDiv = null, this.startX = null, this.startY = null, evt = null;
      },
      clearEventBubble(evt) {
        // stopPropagation()
        if (evt.stopPropagation)
          evt.stopPropagation();
        else {
          evt.cancelBubble = true;  // 
        }
        if (evt.preventDefault)
          evt.preventDefault();   // 
        else {
          evt.returnValue = false;
        }
      },
      ...mapMutations('canvasPlace', {
        setChartPosition: 'setChartPosition',
      }),
      // 
      getRefLineParams(params) {
        const {vLine, hLine} = params;
        this.vLine = vLine;
        this.hLine = hLine;
      },
      dropOver(event) {
        let vm = this;
        console.log(vm.charCommentDrag, '');
        if (vm.charCommentDrag === false) return;
        const e = event || window.event;
        const target = e.target || e.srcElement;
        let position = e.path[0].getBoundingClientRect();
        /**
         * 
         * */
        this.$store.commit('voiceCommand/setMouseData', {
          position: {
            x: Math.floor(position.x),
            y: Math.floor(position.x)
          }
        });
        this.getCreatData.then(res => {
          // this.initChart(res.data.tableId)
          vm.$store.commit('voiceCommand/setCharCommentDrag', false);
          vm.getChartData();
        });
      },
      dragleave(e) {// 
        e = e || window.event;
        // this.$store.commit('voiceCommand/setCharCommentDrag',false);
      },
      dragSport(e) {
        e = e || window.event;
        if (this.charCommentDrag === false) {
          e.stopPropagation();
        } else if (this.charCommentDrag === true) {
          e.preventDefault();
        }
      },
      initChart(tableId) {
        let vm = this;
        vm.$get('api/v2/oneChart', {
          projectId: sessionStorage.getItem('projectId'),
          tableId: tableId,
        }).then(res => {
          if (res.code === '000000') {
            let componentData = res.data[0];
            // componentData['containerOptions']=Object.assign(componentData['containerOptions'],{
            //   layerIndex:vm.currentIndex,
            //   layerChartIcon:'#icon-' + componentData['containerOptions']['chartsType'].split('-')[1],
            //   xPosition:vm.x,
            //   yPosition:vm.y,
            //   tableWidth:vm.w,
            //   tableHeight:vm.h,
            // });
            // vm.$store.commit('canvasPlace/changeIndexChart', {
            //   data: componentData,
            // });
            //containerOptionstableId
          } else {
            console.error(res);
          }
        }).catch(error => {
          console.error(error);
          vm.$store.commit('canvasPlace/setShowLoading', vm.currentChartIndex);
        });
      },
      dataIsOk() {
        this.$emit('dataIsOk', true);//
        this.screenshotTimer = setInterval(this.cutScreen, 1000 * 30);
      },
      setPositionInfo(left, right, top, bottom) {
        // console.log(rawPosition);
        // let element = this.$refs.draggable.find(ele => ele.$vnode.key == index);
        // const {left, right, top, bottom} = rawPosition;
        this.positionInfo = [
          {
            className: 'x1',
            style: {
              left: left - 3 + 'px',
              transform: `scale(${1 / this.scaleLevel}) translateX(-100%)`
            },
            count: ~~(0.5 + left)
          },
          {
            className: 'x2',
            style: {
              left: -right - 3 + 'px',
              transform: `scale(${1 / this.scaleLevel}) translateX(-100%)`
            },
            count: ~~(0.5 + -right)
          },
          {
            className: 'y1',
            style: {
              top: top + 3 + 'px',
              transform: `scale(${1 / this.scaleLevel})`
            },
            count: ~~(0.5 + top)
          },
          {
            className: 'y2',
            style: {
              top: -bottom + 3 + 'px',
              transform: `scale(${1 / this.scaleLevel})`
            },
            count: ~~(0.5 + -bottom)
          }
        ];
      },
      //---------start-------------
      activated(index, chart,e) {
        this.index = index;
        let vm = this;
        if (index != vm.currentChartIndex) {
          this.$store.commit('canvasPlace/setStateProperty', {key: 'lastActivedChartIndex', value: index});
          // vm.setPositionInfo(index);
          let array = vm.chartList.map(ele => {
            return ele.containerOptions.level || 0;
          });
          let maxZindex = Math.max(...array);
          // vm.$store.dispatch('canvasPlace/chartOnDeactivated');
          vm.$store.commit('canvasPlace/setCurrentChartIndex', index);
          vm.$store.commit('chartBus/setAmendChartData', {
            chart: chart,
            index: index
          });
          //indexDB
          if (chart.containerOptions.chartsType.indexOf('chart') > -1) {
            vm.$store.commit('dataDimension/setUpdateCount');
            vm.dexieDB.aXisList.get(index).then(res => {
              vm.$store.commit(
                'dataDimension/setCurrentChartAxis',
                res.axisOptions
              );
            });
          }
          // vm.activatedIndex = index;
          vm.$store.commit('canvasPlace/setChartZindex', {
            index,
            level: maxZindex + 1
          });
        }
      },
      onDeactivated(params, event) {
        if (this.isPhoneLayout) return;
        event = event || window.event;
        let path = event.path || (event.composedPath && event.composedPath());
        const target = event.target || event.srcElement;
        if (!path) path = composedPath(target);

        function unableDeactive(ele) {//
          return (
            ele.getAttribute && ele.getAttribute('data-class') == 'unableDeactive'
          );
        }

        if (params.index == this.currentChartIndex) {
          if (!path.some(unableDeactive)) {
            /** */
            this.$store.dispatch('canvasPlace/chartOnDeactivated');
            this.$store.commit('dataDimension/resetAllSelectInput');
          }
          this.$store.commit('chartBus/setAmendChartData', {
            chart: params.item,
            index: params.index
          });
        }
      },

      resizing(left, top, right, bottom, width, height, index) {//
        // let x = left.toFixed(4);
        let x = ~~(left + 0.5);
        // let y = top.toFixed(4);
        let y = ~~(top + 0.5);
        width = width.toFixed(4);
        height = height.toFixed(4);
        let position = {
          x,
          y
        };
        let paramsSize = {
          width,
          height
        };
        this.$store.commit('canvasPlace/setChartPosition', {position});
        this.$store.commit('canvasPlace/setChartSize', {paramsSize});
        this.setPositionInfo(left, right, top, bottom);
      },
      setSize(x, y, right, bottom, width, height, index, item) {//
        x = ~~(x + 0.5);
        y = ~~(y + 0.5);
        width = width.toFixed(4);
        height = height.toFixed(4);
        let position = {
          x,
          y,
          index,
        };
        let paramsSize = {
          width,
          height,
          index
        };
        this.$store.commit('canvasPlace/setChartPosition', {position});
        this.$store.commit('canvasPlace/setChartSize', {paramsSize});
        if (item.containerStatus.isSetOptions) {
          this.saveDataIndexDb(index);
        }
      },
      dragging(left, top, right, bottom, index) {//
        left = ~~(left + 0.5);
        top = ~~(top + 0.5);
        let position = {
          x: left,
          y: top,
          index
        };
        this.$store.commit('canvasPlace/setChartPosition', {position});
        this.setPositionInfo(left, right, top, bottom);
      },
      setPosition(left, top, right, bottom, index, item) {//
        // let x = left.toFixed(4);
        // let y = top.toFixed(4);
        let x = ~~(left + 0.5);
        let y = ~~(top + 0.5);
        // console.log(x ,y);
        let position = {
          x,
          y,
          index
        };
        this.setPositionInfo(left, right, top, bottom);
        if (x === item.containerOptions.xPosition && y === item.containerOptions.yPosition) return false;
        this.$store.commit('canvasPlace/setChartPosition', {position});
        // this.$store.commit('canvasPlace/setChartOptionsAmendCount');
        this.$store.commit('chartBus/setAmendChartData', {
          chart: item,
          index: index
        });
        if (item && item.containerStatus.isSetOptions) {
          // console.log(item,'');
          this.saveDataIndexDb(index);
        }
        this.isCircleSelect = true;
      },
      resetWidth(index, left, width) {
        let right = Number(left) + width;
        this.$store.commit('canvasPlace/containerOptionSetter', {
          key: 'tableWidth',
          value: width,
          id: index
        });
        this.positionInfo[1].count = ~~(right + 0.5);
        this.positionInfo[1].style.left = right - 3 + 'px';
      },
      resetHeight(index, top, height) {
        let bottom = Number(top) + height;
        this.$store.commit('canvasPlace/containerOptionSetter', {
          key: 'tableHeight',
          value: height,
          id: index
        });
        this.positionInfo[3].count = ~~(bottom + 0.5);
        this.positionInfo[3].style.top = bottom + 3 + 'px';
      },
      //---------end-------------
      saveDataIndexDb(index) {
        this.$store.commit('chartBus/setTimeSetting', true);
        this.$store.commit('canvasPlace/setChartOptionsAmendCount');

      },
      takeData() {//
        let vm = this;
        vm.saveDataBaseData = [];
        vm.dexieDB.chartsOption.each(res => {
          vm.saveDataBaseData.push(res);
        });
      },

      // 
      getChartData(params = {}) {
        let {sourceType = 'initAPI'} = params;
        /**
         * @
         * @
         * @
         **/
          // 
        let vm = this;
        this.takeData();
        this.$emit('dataIsOk', false);
        // this.dexieDB.chartsOption.clear();
        // this.dexieDB.aXisList.clear();
        // this.dexieDB.saveOption.clear();
        // vuex
        this.$store.commit('canvasPlace/setCurrentChartIndex', -1);
        this.$store.commit('canvasPlace/clearArrChart');
        // 
        let id = sessionStorage.getItem('projectId');
        // import('./123.json').then(res => {
        //   console.log(res);
        //
        //   let success = {
        //     code: '000000',
        //     data: res
        //   };
        this.$store.dispatch('canvasPlace/getChartData', {sourceType, project: id}).then(success => {
          console.log('success');
          console.log(success);
          if (success.code == '000000') {
            this.$store.commit('canvasPlace/setInitAllChart', false);
            this.chartListLength = success.data.length;
            if (this.chartListLength === 0) {
              this.$store.commit('voiceCommand/setList');
            }
            if (success.data.length > 0 && success.data[0].renderOptions === '') {
              // debugger;
              vm.judgment(vm.dataSplicing(success));
            } else {
              vm.judgment(success);
            }
            // 
          }
        }).catch(e => {
          vm.dataIsOk();
          vm.indexDbClear();
          vm.$store.commit('canvasPlace/clearArrChart');
          if (vm.flag) {
            vm.flag = false;
            setTimeout(() => {
              vm.$store.commit('canvasPlace/setModuleType', false);//truefalse
            }, 2000);
          }
          // console.error(e);
        });
      },
      // 
      dataSplicing(success) {
        const initData = defaultChartOptions.state;
        success.data.forEach((item, index) => {
          const type = item.containerOptions.chartsType.split('-');
          item.renderOptions = initData[type[0]][item.containerOptions.chartsType].renderOptions;
          item.renderOptions.series = item.containerOptions.seriesData;
          item.renderOptions = JSON.stringify(item.renderOptions);
        });
        return success;
      },
      judgment(success) {
        if (success.data.length > 0) {
          const vm = this;
          vm.dataDemensionLength = success.data.filter((item) => item.containerOptions.chartsType.indexOf('chart') > -1).length;
          let arr = success.data.filter((item) => item.containerOptions.chartsType.indexOf('chart') > -1);
          console.log(arr);
          // for(let i=0;i<success.data.length;i++){
          //   let item =success.data[i];
          //   let idx=i;

          // }
          success.data.forEach((item, idx) => {
              let dimensionDefault = JSON.parse(JSON.stringify(this.dimensionDefault));
              let containerOptions = item.containerOptions;
              let chartType = item.containerOptions.chartsType;
              let category = item.containerOptions.categoryAxis ? item.containerOptions.categoryAxis.toLowerCase() : 'x';
              let categoryAxis = category === 'x' ? 'xAxisList' : 'yAxisList';
              let valueAxis = category === 'x' ? 'yAxisList' : 'xAxisList';
              containerOptions.layerChartIcon =
                '#icon-' + chartType.split('-')[1];
              containerOptions.isLayerEdit = false;
              containerOptions.layerIndex = idx;
              const {xPositionMobile, yPositionMobile, tableWidthMobile, tableHeightMobile, showInMobileLayout} = containerOptions;
              let obj = {
                id: idx,
                containerOptions: containerOptions,
                containerStatus: {
                  isSetOptions: true,
                  dataDimensionShow: false,
                  isDeleted: false,
                  lastMobileSavedOptions: vm.isPhoneLayoutSaved ? {
                    xPositionMobile,
                    yPositionMobile,
                    tableWidthMobile,
                    tableHeightMobile,
                    showInMobileLayout
                  } : echartsCommonMobileOptions,
                }
              };
              const curChartType = chartType.split('-')[0];
              let renderOptions = beforeThemeHandle(item, idx).renderOptions;
              // console.log(renderOptions);
              vm.$set(obj, 'renderOptions', renderOptions);
              if (curChartType === 'echarts') {
                vm.$set(obj['containerStatus'], 'isShowLoading', true);
              }
              // 
              vm.dexieDB.chartsOption
                .put(obj)
                .then(res => {
                  // debugger;
                })
                .catch(error => console.error(error));
              /**
               * 
               * {
               * 	index,
               * axisOptions:{
               * 	xAxisList:[],//x
               * 	yAxisList:[],
               * 	dataSerise:[],//
               * 	cityName:[],//3d
               * 	columnHeight:[]//3d
               * }
               *
               * }
               */
              let x = containerOptions.x,
                y = containerOptions.y,
                groupDataArray = containerOptions.group;

              //
              if (curChartType === 'echarts') {
                /** */
                  // const categoryAxis = containerOptions.categoryAxis;
                let axisOptions = {
                    xAxisList: [],
                    yAxisList: [],
                    dataSeries: [],
                    groupData: [],
                  };
                // let xAxisList = [];
                // let yAxisList = [];
                // let dataSeries = [],
                // 	groupData = [];
                //category
                let orderData = containerOptions.order;
                const orderCategory = orderData.find(e => e.orderCategory && e.orderCategory == 'category');
                /**
                 * XY
                 * xy
                 * x
                 * y 
                 *
                 * */
                groupDataArray.forEach((item, idx) => {
                  axisOptions.groupData.push({
                    name: item.groupField,
                    tableIndex: item.groupSourceId,
                    tableName: item.groupSourceName,
                    isSelectInput: false,
                    inputType: '',
                    isChoice: true,
                    isFilterChar: false,
                    isFilterNumber: false,
                  });
                });
                if (groupDataArray.length === 0) {
                  axisOptions.groupData.push({
                    categoryAxis: '',
                  });
                }
                // let groupDataInputType = this.getIndbType(groupData);
                const xAxisObject = {
                  name: x.xField,
                  inputType: '',
                  tableIndex: x.xSourceId,
                  tableName: x.xSourceName,
                  isSelectInput: false,
                  isChoice: true,
                  isFilterChar: false,
                  isFilterNumber: false,
                  xOp: x.xOp,
                  yOp: 'count',
                  categoryAxis: 'categoryAxis',
                  orderCondition: '',
                  orderOp: orderCategory == undefined ? '' : orderCategory.orderOp
                };

                axisOptions[categoryAxis].push(xAxisObject);
                y.forEach((e, i) => {

                  const orderValue = orderData.find(o => o.orderField == e.yField && o.orderCategory == 'value');
                  const yAxisObject = {
                    name: e.yField,
                    inputType: '',
                    tableIndex: e.ySourceId,
                    tableName: e.ySourceName,
                    isSelectInput: false,
                    isChoice: true,
                    isFilterChar: false,
                    isFilterNumber: false,
                    yOp: e.yOp,
                    xOp: '',
                    categoryAxis: 'valueAxis',
                    orderCondition: '',
                    orderOp: orderValue == undefined ? '' : orderValue.orderOp
                  };
                  axisOptions[valueAxis].push(yAxisObject);
                  if (y.length >= 2) {
                    dimensionDefault.categoryAxis = 'valueAxis';
                    axisOptions[valueAxis].push(dimensionDefault);
                  }

                  /**2indexDBdataSourceList */
                  axisOptions[categoryAxis].filter(e => e.tableIndex).forEach(element => {
                    vm.dexieDB.dataSourceList.get(element.tableIndex).then(res => {
                      const fieldList = res.dataSourceObject.field;
                      const currentField = fieldList.find(e => e.Field == element.name);
                      //
                      vm.$set(element, 'inputType', currentField.Type);
                    }).then(() => {
                      axisOptions[valueAxis].filter(e => e.tableIndex).forEach(element => {
                        /**   */
                        vm.dexieDB.dataSourceList.get(element.tableIndex).then(res => {
                          const fieldList = res.dataSourceObject.field;
                          console.log('element', element);
                          const currentField = fieldList.find(e => e.Field == element.name);
                          vm.$set(element, 'inputType', currentField.Type);
                          /**3  indexDB   aXisList */
                          const object = {
                            index: idx,
                            axisOptions
                          };
                          vm.dexieDB.aXisList.put(object).then(res => {
                            vm.dataDemensionFinishiInitNum++;
                            vm.$store.commit('dataDimension/setCurrentChartAxis', object.axisOptions);
                          }).catch(error => {
                            console.error('', error);
                          });
                        });
                      });
                    });
                  });
                });
              } else if (curChartType === 'echarts-3dWorldMap') {
                /**3d */
                /**
                 * x,y
                 * gis
                 * xAxisList yAxisList
                 */
                let cityName = [];
                let columnHeight = [];
                const gis = containerOptions.gis;
                const cityNameObject = {
                  name: x.xField,
                  inputType: 'char',
                  tableIndex: x.xSourceId,
                  tableName: x.xSourceName,
                  isSelectInput: false,
                  isChoice: true,
                  isFilterChar: false,
                  isFilterNumber: false,
                  xOp: '',
                  yOp: '',
                  categoryAxis: '',
                  orderCondition: '',
                  orderOp: ''
                };
                cityName.push(cityNameObject);

                y.forEach(ch => {
                  const columnHeightObject = {
                    name: ch.yField,
                    inputType: 'int',
                    tableIndex: ch.ySourceId,
                    tableName: ch.ySourceName,
                    isSelectInput: false,
                    isChoice: true,
                    isFilterChar: false,
                    isFilterNumber: false,
                    yOp: '',
                    xOp: '',
                    categoryAxis: '',
                    orderCondition: '',
                    orderOp: ''
                  };
                  columnHeight.push(columnHeightObject);
                });
                /** */
                let longitudeObject = {
                  name: gis.longitudeField,
                  inputType: 'int',
                  tableIndex: gis.longitudeSourceId,
                  tableName: gis.longitudeSourceName,
                  isSelectInput: false,
                  isChoice: true,
                  isFilterChar: false,
                  isFilterNumber: false,
                  xOp: '',
                  yOp: '',
                  categoryAxis: '',
                  orderCondition: '',
                  orderOp: ''
                };
                const latitudeObject = {
                  name: gis.latitudeField,
                  inputType: 'int',
                  tableIndex: gis.latitudeSourceId,
                  tableName: gis.latitudeSourceName,
                  isSelectInput: false,
                  isChoice: true,
                  isFilterChar: false,
                  isFilterNumber: false,
                  xOp: '',
                  yOp: '',
                  categoryAxis: '',
                  orderCondition: '',
                  orderOp: ''
                };
                /**3  indexDB   aXisList */
                const object = {
                  index: idx,
                  axisOptions: {
                    xAxisList: [longitudeObject],
                    yAxisList: [latitudeObject],
                    dataSeries: [],
                    columnHeight: columnHeight,
                    cityName: cityName
                  }
                };
                vm.dexieDB.aXisList
                  .put(object)
                  .then(() => {
                    vm.dataDemensionFinishiInitNum++;
                  })
                  .catch(error => {
                    console.error('3d', error);
                  });
              } else if (curChartType === 'interactive') {
                // 
                // console.log('');
              }
              /**
               * end
               */
            }
          );
          this.dataErgodic();
        } else {
          this.dataIsOk();
        }
      },
      // 
      getIndbType(datalist) {
        let vm = this;
        datalist.filter(e => e.tableIndex).forEach((item, idx) => {
          vm.dexieDB.dataSourceList.get(item.tableIndex).then(res => {
            const fieldList = res.dataSourceObject.field;
            const currentField = fieldList.find(e => e.Field == item.name);
            //
            vm.$set(item, 'inputType', currentField.Type);
          }).then(() => {
          });
        });
        return datalist;
      },
      dataErgodic() {
        // indexDB 
        let vm = this;
        vm.dexieDB.chartsOption.each(res => {
          vm.$store.commit('canvasPlace/setChartList', res);
        }).then(res => {
          console.log('');
          this.dataIsOk(); // loading
        });
      },
      indexDbClear() {
        let vm = this,
          p1 = vm.dexieDB.aXisList.clear().then(() => ''),
          p2 = vm.dexieDB.chartsOption.clear().then(() => ''),
          p3 = vm.dexieDB.saveOption.clear().then(() => ''),
          p4 = vm.dexieDB.saveTextOption.clear().then(() => ''),
          p5 = vm.dexieDB.saveMediaOption.clear().then(() => ''),
          p6 = vm.dexieDB.saveInteractiveOption.clear().then(() => '');
        Promise.all([p1, p2, p3, p4, p5, p6])
          .then(res => {
            vm.isTableClear = true;
          })
          .catch(reason => {
            console.error(reason);
          });
      },
      // getDelStatus(status){
      // 	if(status){
      // 		this.isShowModal=true;
      // 		this.promptChart = this.deleteSingleChart;
      // 	}else{
      // 		this.promptChart = this.promptSingleChart;
      // 	}
      // }
      // getData(data){
      //   console.log(data.value,'');
      // },
      cutScreen() {
        let vm = this;
        const sendObject = {
          projectId: localStorage.getItem('fatherProjectId'),
          locationPointId: sessionStorage.getItem('projectId'),
        };
        vm.$put('api/screenShot', sendObject)
          .then(res => {
            console.log(res);
            if (res.code == '000000') {
              return true;
            } else {
              vm.tips.show([res.err_msg.split(':')[1]]);
            }
          }).catch(error => console.error(error));
      }
    },
    beforeDestroy() {
      clearInterval(this.screenshotTimer);
    },
    mounted() {
      this.indexDbClear();
      this.$store.commit('canvasPlace/clearArrChart');
      document.querySelector('.canvasPlace').onmousedown = (e) => {
        if (e.button === 0 && this.isCircleSelect) {
          this.chartListAfterClear.forEach((item, index) => {
            this.onDeactivated({item, index}, e);
          });
          this.isRightClickPopupShow = false;
          this.mousedown(e);
        }
      };
    }
  };
</script>
