    margin-left: 0;
    transform: translateX(-50%);
  }

  .wrapper /deep/ .handle-bm span {
    position: absolute;
    transform-origin: 50% 100%;
    width: 100%;
    height: 0.45vw;
    background-color: transparent;
    bottom: -0.15vw;
    left: 0;
    border-bottom-width: 0.15vw;
    border-bottom-color: #28b3fc;
    border-bottom-style: solid;
  }

  .wrapper /deep/ .handle-tl {
    transform-origin: 0 0;
    width: 0.6vw;
    height: 0.6vw;
    left: 0;
    top: 0;
  }

  .wrapper /deep/ .handle-tl span {
    transform-origin: 0.15vw 0.15vw;
    position: absolute;
    background-color: transparent;
    border-width: 0.15vw 0 0 0.15vw;
    border-style: solid;
    border-color: #28b3fc;
    width: 0.75vw;
    height: 0.75vw;
    left: -0.15vw;
