<template>
  <div class="switcher"
    :class="direction.class"
    :style="{
      transform:
      `scale(${1 / this.scalelevel}) ${direction.rotate}`
      }"
    @click.stop="dataDimensionShowZindex">
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
	name: 'Switcher',
	props: {
		dataDimensionShow: {
			type: Boolean,
			required: true
		},
		index: {
			type: Number,
			required: true
		}
	},
	computed: {
		...mapGetters('canvasPlace', {
			scalelevel: 'getScaleLevel',
			currentChartIndex: 'getCurrentChartIndex'
		}),
		direction() {
			if (!this.dataDimensionShow) {
				return {
					class: 'switcher-right',
					rotate: ''
				};
			} else {
				return {
					class: 'switcher-left',
					rotate: 'rotateZ(180deg)'
				};
			}
		}
	},
	data() {
		return {};
	},
	methods: {
		dataDimensionShowZindex() {
			if (this.currentChartIndex == this.index) {
				this.$store.commit('canvasPlace/toggleDataDimensionShow', this.index);
				this.$store.commit('dataDimension/setExchangeAxisStatus', true);
			}
		}
	}
};
</script>
<style scoped>
.switcher {
  transform-origin: 100% 50%;
  position: absolute;
  width: 0.8vw;
  height: 2vw;
  margin-top: -1vw;
  top: 50%;
  cursor: pointer;
  background-image: url("~screen/DataDimension/dataDimensionBtn.svg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.switcher::before {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  right: 0;
  top: 50%;
  padding: 1vw 0;
  transform: translateY(-50%);
}
.switcher-right {
  right: 0;
}
.switcher-right::before {
  padding-left: 1vw;
}
.switcher-left {
  right: 100%;
}
.switcher-left::before {
  padding-right: 1vw;
}
</style>


