<template>
  <div class="data-select"
    :style="dataSelectPostion"
    v-click-outside="{fn:hideMoreMenu}"
    v-show="selectShow">
    <ul class="list"
      v-show="axisData.categoryAxis == 'valueAxis'">
      <li v-for="(item,index) in showDataSelectList"
        :key="index"
        @click="getyopOption(item.value,'yOp')"
        class="unSelect liHover">
        <span>{{item.name}}</span>
        <!-- <ul v-if="item.subMenu!=undefined">
          <li v-for="(info,i) in item.subMenu"
            :key="i">{{info.name}}</li>
        </ul> -->
      </li>
      <li v-for="item in dataSelectOrderList"
        :key="item.value"
        @mouseleave="hiddenOrderList(2)"
        class="unSelect">
        <div class=" data-select-order"
          :class="{'orderHover':isShowSubMenu}"
          @mouseenter="showOrderList()"
          @mouseleave="hiddenOrderList()"
        >
          <span v-show="!safetyZone"
            class="biFont icon-arrowLeft order-more-left"></span>
          <span>{{item.name}}</span>
          <span v-show="safetyZone==1"
            class="biFont icon-arrowRight order-more"></span>
        </div>
        <ul class="order-list list"
          v-show="isShowSubMenu"
          :class="{'order-list-left':!safetyZone}"
          @mouseleave="hiddenOrderList()">
          <li v-for="(info,i) in item.subMenu"
            :key="i"
            @mouseenter="hoverOrder(i)"
            @mouseleave="hiddenOrder()"
            @click="getyopOption(info.value,'orderOp')"><span>{{info.name}}</span></li>
        </ul>
      </li>
    </ul>
    <ul class="list"
      v-show="axisData.categoryAxis != 'valueAxis'"
      @mouseleave="hiddenOrderList(2)"
    >
      <li v-for="item in dataSelectOrderList"
        :key="item.value"
        class="unSelect">
        <div class="data-select-order"
          :class="{'orderHover':isShowSubMenu}"
          @mouseenter="showOrderList()"
          @mouseleave="hiddenOrderList()">
           <span v-show="!safetyZone"
             class="biFont icon-arrowLeft order-more-left"></span>
          <span>{{item.name}}</span>
          <span v-show="safetyZone==1"
            class="biFont icon-arrowRight order-more"></span>
        </div>
        <ul v-if="isShowSubMenu"
          class="order-list list"
          :class="{'order-list-left':!safetyZone}"
          @mouseleave="hiddenOrderList()">
          <li v-for="(info,i) in item.subMenu"
            :key="i"
            @mouseenter="hoverOrder(i)"
            @mouseleave="hiddenOrder()"
            @click="getyopOption(info.value,'orderOp')"><span>{{info.name}}</span></li>
        </ul>
      </li>
    </ul>
  </div>
</template>
<script>
import {mapGetters} from 'vuex';

export default {
	name: 'DataSelect',
	props: {
		dataSelectPostion: {
			type: Object,
			required: false
		},
		axisData: {
			type: Object,
			required: true
		},
		axisIndex: {
			type: Number
		},
		axisType: {
			type: String,
			require: true
		},
		curChartType: {
			type: String,
			require: true
		},
		closeDataSelect: {
			type: Boolean,
			required: false
		},
		referencePosition: {
			type: String,
			required: true
		},
		chartIndex: {
			type: Number,
			required: true
		}
	},
	computed: {
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',
			getCurrentIsSetOptions: 'getIsSetOptions'
		}),
		...mapGetters('dataDimension', {
			axisfilterCountData: 'getValueAxisFilterCount',
			valueAxisfilterIndex: 'getValueAxisfilterIndex',
			valueAxisfilterPanl: 'getValueAxisfilterPanl',
			safetyZone: 'getSafety',
		}),
		showDataSelectList() {
			if (this.axisData.inputType != 'int') {
				return this.dataSelectList.filter(e => e.value == 'count');
			} else {
				return this.dataSelectList;
			}
		}
	},
	watch: {
		/**
		 * 
		 * 
		 */
		'axisfilterCountData.valueAxisfilterCount': {
			handler() {
				let vm = this;
				if (
					vm.currentChartIndex == vm.chartIndex &&
					vm.valueAxisfilterPanl == vm.referencePosition
				// && vm.curChartType != 'echarts-3dWorldMap'
				) {
					vm.selectShow=vm.getCurrentIsSetOptions(vm.currentChartIndex);
					// vm.axisType == vm.axisfilterCountData.axisType &&
					// vm.valueAxisfilterIndex == vm.axisIndex &&
					// if (
					// 	vm.getCurrentIsSetOptions(vm.currentChartIndex)
					// ) {
					// 	vm.selectShow = true;
					// } else {
					// 	vm.selectShow = false;
					// }
				}
			}
		},
		closeDataSelect: {
			immediate: true,
			handler() {
				this.selectShow = false;
			}
		}
	},
	data() {
		return {
			selectShow: false,
			isShowSubMenu: false,
			dataSelectList: [
				{name: '', value: 'sum'},
				{name: '', value: 'avg'},
				{name: '', value: 'min'},
				{name: '', value: 'max'},
				{name: '()', value: 'count'},
				{name: '', value: 'percent'},
				{name: '', value: 'totalPercent'},

			],
			dataSelectOrderList: [
				{
					name: '',
					value: 'order',
					subMenu: [
						{name: '', value: 'ASC'},
						{name: '', value: 'DESC'}
					]
				}
			],
			categoryAxis: ''
		};
	},
	methods: {
		hideMoreMenu() {
			this.selectShow = false;
		},

		getyopOption(type, selectType) {
			let vm = this;
			vm.$store.commit('canvasPlace/setCurrentChartIndex', vm.chartIndex); //
			vm.selectShow = false;
			vm.dexieDB.aXisList
				.get(vm.currentChartIndex)
				.then(res => {
					let currentAxisOptions = res.axisOptions;
					if(type==='percent' || type==='totalPercent'){
						selectType='yPercent';
					}else{
						currentAxisOptions.yAxisList.forEach(e=>{
							vm.$set(e, 'yPercent', '');
						});
			    // vm.$set(currentAxisOptions[vm.axisType][vm.axisIndex], 'yPercent', '');
					}
					// vm.$set(currentAxisOptions[vm.axisType][vm.axisIndex], [selectType], type);
					vm.$set(currentAxisOptions[vm.axisType][vm.axisIndex],	[selectType],type	);//
					vm.$set(currentAxisOptions,	selectType,type	);//
					vm.$set(currentAxisOptions,	'latestOrder',currentAxisOptions[vm.axisType][vm.axisIndex]);//
					vm.dexieDB.aXisList
						.put({
							index: vm.currentChartIndex,
							axisOptions: currentAxisOptions
						})
						.then(() => {
						})
						.catch(error => console.error(error));
					vm.$store.commit('dataDimension/setUpdateCount');
					vm.$store.commit('dataDimension/setAmendChartCount');
					vm.$store.commit(
						'dataDimension/setCurrentChartAxis',
						currentAxisOptions
					);
				})
				.catch(error => console.error(error));
		},
		showOrderList(index) {
			this.isShowSubMenu = true;
			this.orderActiveIndex = 0;
		},
		hoverOrder(index) {
			this.orderActiveIndex = index;
		},
		hiddenOrder() {
			this.orderActiveIndex = -1;

		},
		hiddenOrderList(i) {
			if (this.orderActiveIndex == -1 || i == 2) {
				this.isShowSubMenu = false;
			}
		}
	},
	mounted() {
	}
};
</script>
<style scoped>
  .data-select {
    position: fixed;
    color: #fff;
    font-size: 0.8vw;
    cursor: pointer;
    z-index: 1;
    width: 100%;
  }

  .data-select span:first-child {
    cursor: pointer;
  }

  .list {
    position: absolute;
    min-width: 3.6vw;
    width: auto;
    background-color: #252f40;
    box-shadow: 0vw 0.1vw 0.3vw 0vw rgba(0, 0, 0, 0.3);
  }

  .list .liHover:hover {
    background-color: #131721;
    color: #28b3fc;
  }

  .list li {
    text-align: center;
    height: 1.5vw;
    line-height: 1.5vw;
    position: relative;
  }

  .list li > span {
    font-size: 0.6vw;
    transform: scale(0.7);
    padding: 0 0.2vw;
  }

  .data-select span:first-child {
    cursor: pointer;
  }

  .list {
    position: absolute;
    min-width: 3.6vw;
    width: auto;
    background-color: #252f40;
    box-shadow: 0vw 0.1vw 0.3vw 0vw rgba(0, 0, 0, 0.3);
  }

  .list .liHover:hover {
    background-color: #131721;
    color: #28b3fc;
  }

  .list li {
    text-align: center;
    height: 1.5vw;
    line-height: 1.5vw;
    position: relative;
  }

  .list li > span {
    font-size: 0.6vw;
    transform: scale(0.7);
  }

  .list li .orderHover {
    background-color: #131721;
    color: #28b3fc;
  }

  .span-color {
    color: #28b3fc;
  }

  .data-select-order {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: baseline;
    height: 1.5vw;

  }

  .data-select-order:hover .order-list {

  }

  .data-select-order span {
    display: block;
    font-size: 0.6vw;
  }

  .data-select-order {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: baseline;
    height: 1.5vw;
  }

  .data-select-order:hover .order-list {
  }

  .data-select-order span {
    display: block;
    font-size: 0.6vw;
  }

  .data-select-order .order-more {
    position: absolute;
    right: 0.1vw;
    transform: scale(0.7) !important;
    /* margin-top: 0.05vw; */
  }

  .data-select-order .order-more-left {
    position: absolute;
    left: 0.1vw;
    transform: scale(0.7) !important;
  }

  .order-list li:hover {
    background-color: #131721;
    color: #28b3fc;
  }

  .order-list {
    z-index: 9999;
    position: absolute;
    box-shadow: 0vw 0.1vw 0.3vw 0vw rgba(0, 0, 0, 0.3);
    right: 0vw;
    left: 100%;
    top: 0;
  }

  .order-list-left {
    z-index: 9999;
    position: relative;
    box-shadow: 0vw 0.1vw 0.3vw 0vw rgba(0, 0, 0, 0.3);
    right: 0%;
    left: -100%;
    top: -100%;
  }
</style>

