<template>
  <div class="img-div"
    :class="{'init-bgimg':options.backgroundColor&&options.backgroundImage}">
    <div v-show="showDefaultImg"
      :style="defalutImg"
      class="img-copywriting init-bgimg"></div>
    <img v-show="!showDefaultImg"
      @dragstart.prevent
      :src="options.backgroundImage"
      class="picture-assembly"
      :style="pictureSetting"
      alt="">
  </div>
</template>
<script>
export default {
	name: 'media-image',
	props: {
		options: Object,
		tableId: {
			type: String
		}
	},
	data() {
		return {
			showDefaultImg: true,
			mediaData: '',
			containerOptions: '',
			mediaOptions: ''
		};
	},
	methods: {

	},
	mounted() {
		if (this.tableId != '') {
			this.$store.commit('canvasPlace/setRenderedStatus');
		}
	},
	computed:{
		pictureSetting() {
			let vm = this,
				imgAddress = '';
			// console.log(vm.options,'');
			if (vm.options.backgroundImage === ' ') {
				//vm.options.backgroundImage  ' '
				imgAddress = '';
			} else {
				imgAddress = vm.options.backgroundImage;
			}
			vm.showDefaultImg = !imgAddress;
			return {
				// width: vm.w + 'px',
				// height: vm.h + 'px',
				borderRadius: Number(vm.options.borderStyle.borderRadiusWidth) + '%',
				border:
          vm.options.innerStrokes +
          'px ' +
          vm.options.innerColor +
          ' ' +
          vm.options.innerType
			};
		},
		defalutImg() {
			let vm = this;
			// console.log(vm.containerOptions.tableWidth - vm.mediaOptions.innerStrokes*2);
			return {
				background: vm.options.backgroundColor
			};
		}
	}
};
</script>

<style scoped>
.img-div {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.picture-assembly{
  width: 100%;
  height: 100%;
}
.init-bgimg {
  width: 100%;
  height: 100%;
  background: url("~echartsBg/image.png") no-repeat;
  background-size: 100% 100%;
}
</style>
