<template>
  <div>
    <div class="chart-basis"
      @click="showEvent(7)">
      <span></span>
      <span class="biFont icon-arrowLeft icon-fontsize"
        :class="{rotateIcon:isShowWidth}"></span>
    </div>
    <div v-show="isShowWidth"
      class="content-box">
      <ul>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="settingBorder"
            v-on:rangedata="rangeBorder"></silder-data>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import SilderData from './BasicComponent/Slider';
export default {
	name: 'DataWidth',
	components: {
		'silder-data': SilderData
	},
	data() {
		return {
			isShowWidth: true,
			settingBorder: {
				name: '',
				max: 100,
				min: 1,
				valueRange: [1]
			}
		};
	},
	methods: {
		//
		rangeBorder(data) {},

		//
		showEvent(number) {
			this.isShowWidth = !this.isShowWidth;
		}
	}
};
</script>

<style scoped>
.optionsItem {
  margin-bottom: 0.8vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.optionsItem .span-title {
  display: block;
  flex: 1;
  color: #acbbda;
}
span,
input {
  user-select: none;
}
.chart-basis .rotateIcon {
  transform: rotate(-90deg);
}
</style>



