<template>
  <div class="filters-box report-filters-text">
    <search-child :searchObject="searchObject"
      @fromChild="getChildSearch"
      @inputReset="inputReset"></search-child>
    <div class="field-list">
      <!-------------------------------------- start---------------------------------------->
      <easy-scroll :barOption="reactiveScrollOpt"
        v-show="isShowFieldList">
        <ul v-show="isShowFieldList"
          class="field-list-ul">
          <li class="field-list-li"
            v-for="(fileItem,index) in fileList"
            :key="index"
            @mouseenter="hoverLi(index)"
            @mouseleave="removeLi()"
            @click="openFileList(index,fileItem)">
            <div class="title"
              :class="{'selected':fileItem.isShowField}">
              <span class="biFont icon-arrowDown down"
                :class="{fieldDropDown:!fileItem.isShowField}"></span>
              <div class="data-source-list">
                <span :class="`dataSource icon-${fileItem.db_source_type}`"
                  class="dataSource-type"></span>
                <span class="table-title"
                  :title="fileItem.db_source_name">{{fileItem.db_source_name}}</span>
              </div>
            </div>
            <div class="field-datalist"
              v-show="fileItem.isShowField">
              <ul>
                <li v-for="(fieldItem,fieldIndex) in fileItem.field"
                  :key="fieldIndex"
                  @click="choiceField(fileItem,fieldItem)"
                  @mouseenter="addHoverLi(index,fieldIndex)"
                  @mouseleave="removeHoverLi()"
                  :class="setLiClass(index,fieldIndex,fieldItem)">
                  <span class="dataSource dataSource-type"
                    :class="setIconClass(fieldItem)"></span>
                  {{fieldItem.Field}}
                </li>
              </ul>
            </div>
          </li>
        </ul>
      </easy-scroll>
        <!-------------------------------------- end-------------------------------------- -->
        <!-------------------------------------- start------------------------------------ -->
        <easy-scroll :barOption="reactiveScrollOpt"
        v-show="isShowSearchList">
        <ul class="search-ul"
          v-show="isShowSearchList">
          <li class="search-list" 
            v-for="(fieldItem,fieldItemIndex) in showSearchData"
            :key="fieldItemIndex"
            @click="choiceField(fieldItem,fieldItem)">
            <div class="resultClass">
              <i class="dataSource search-icon"
                :class="setIconClass(fieldItem)"></i>
              <span class="result-fieldName">{{fieldItem.name}}</span>
              <span class="result-tableName">{{fieldItem.db_source_name}}</span>
            </div>
          </li>
        </ul>
      </easy-scroll>
      <!-- <ul style="margin-top:0.8vw" v-show="!isShowSearchList">
				<li></li>
			</ul>-->
      <!-------------------------------------- end-------------------------------------- -->
    </div>
  </div>
</template>
<script>
import SearchChild from '@/components/Search';
import { mapGetters } from 'vuex';

export default {
	name: 'FilterDataList',
	components: {
		'search-child': SearchChild,
	},
	props: {
		params: {
			type: Object,
			required: true
		}
	},
	computed: {
		...mapGetters('canvasPlace', {
			currentIndex: 'getCurrentChartIndex',
			dimensionDefault: 'getDimensionDefault',
			currentChartType: 'getCurrentChartType'
		}),
		...mapGetters('dataSource', {
			dataSourceList: 'getDataSource',
			count: 'getCount'
		}),
		...mapGetters('dataDimension', {
			axisOptions: 'getAxisOptions',
			getValueAxisArr: 'getValueAxisArr',
			resetCurSourceId: 'getCurSourceId',
			curAxisObject: 'getCurAxisObject'
			// axisReport: "getAxisReport"
		}),
		...mapGetters('defaultChartOptions', {
			getCategoryAxis: 'getCategoryAxis'
		}),
		...mapGetters('main', ['getVw']),
		reactiveScrollOpt() {
			return {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barOpacityMin: 0.3, //
				barWidth: this.getVw(4),
				barMarginRight: this.getVw(3)
			};
		},
		isSourceId() {
			if (this.curSourceId) {
				if (this.curSourceId == this.curAxisObject['tableIndex']) {
					return true;
				} else {
					this.tips.show([',']);
					return false;
				}
			} else {
				return true;
			}
		},
		isNotRepeatField() {
			const curAxisFieldArr = this.getValueAxisArr(
				this.getCategoryAxis(this.currentChartType)
			).map(e => e.name);
			const isFiledName = curAxisFieldArr.find(
				e => e === this.curAxisObject['name']
			);
			// if (this.isSourceId && isFiledName === undefined) {
			if (isFiledName === undefined) {
				return true;
			} else {
				this.tips.show([',']);
				return false;
			}
		}
	},
	watch: {
		showSearchData() {
			if (!this.showSearchData.length) {
				this.isShowSearchList = false;
				this.isShowFieldList = true;
			} else {
				this.isShowSearchList = true;
				this.isShowFieldList = false;
			}
		},
		count() {
			this.initDataSource();
		},
		currentIndex() {
			if (this.curSourceId > -1) {
				this.curSourceId = this.resetCurSourceId;
			}
		}
	},
	data() {
		return {
			fileList: [],
			isShowFieldList: true,
			isShowSearchList: false,
			showSearchData: [],
			recordIndex: -1,
			isChoiceLi: -1,
			curSourceId: '',

			searchObject: {
				isClearInput: false,
				placeholderDes: '',
				isShowPanel: false,
				beQueriedList: [],
				width: '100%'
			},
			currentChartList: []
		};
	},
	methods: {
		openFileList(index, fileItem) {
			fileItem.isShowField = !fileItem.isShowField;
		},
		hoverLi(index) {
			this.recordIndex = index;
		},
		removeLi() {
			this.recordIndex = -1;
		},
		addHoverLi(index, fieldIndex) {
			if (this.recordIndex == index) {
				this.isChoiceLi = fieldIndex;
			}
		},
		removeHoverLi() {
			this.isChoiceLi = -1;
		},
		setLiClass(index, fieldIndex, data) {
			if (this.isChoiceLi == fieldIndex && this.recordIndex == index) {
				return `border-color-${data.Type}`;
			}
		},
		setIconClass(data) {
			return `icon-${data.Type}  icon-color-${data.Type}`;
		},
		choiceField(tableMessage, fieldItem) {
			let vm = this;
			vm.isClearInput = true;
			let xAxis = {
				isSelectInput: false,
				isChoice: true,
				name: fieldItem.Field,
				inputType: fieldItem.Type,
				tableIndex: tableMessage.source_sn,
				tableName: tableMessage.db_source_name,
				isFilterChar: false,
				isFilterNumber: false,
				xOp: '',
				yOp: fieldItem.Type == 'char' ? 'count' : 'sum',
				categoryAxis: '',
				orderOp: '',
				orderCondition: ''
			};
		console.log('');
			vm.$store.commit('dataDimension/setCurAxisObject', xAxis);
			const curCategoryPlace =
        vm.getCategoryAxis(vm.currentChartType).toLowerCase() + 'AxisList';
			//
			let isRepeatField = true;
			if (curCategoryPlace !== vm.params.propertyName) {
				if (this.isNotRepeatField) {
					isRepeatField = true;
				} else {
					isRepeatField = false;
				}
			} else {
				isRepeatField = true;
			}
			this.curSourceId = this.resetCurSourceId;
			// if (this.isSourceId && isRepeatField) {
			if (isRepeatField) {
				handle();
			}
			function handle() {
				if (vm.params.propertyName != 'reportFilter') {
					vm.dexieDB.aXisList.get(vm.currentIndex).then(res => {
						let currentAxisOptions = res.axisOptions;
						let currentAxis = res.axisOptions[vm.params.propertyName];
						const updatePropertyName = vm.params.propertyName;
						const updatePropertyIndex = vm.params.index;
						xAxis.categoryAxis = currentAxis[updatePropertyIndex].categoryAxis;
						vm.$set(
							currentAxisOptions[updatePropertyName],
							updatePropertyIndex,
							xAxis
						);
			      console.log(currentAxisOptions,'');
						vm.dexieDB.aXisList
							.put({
								index: vm.currentIndex,
								axisOptions: currentAxisOptions
							})
							.then(() => {})
							.catch(error => console.error(error));
						if (currentAxisOptions[updatePropertyName].every(e =>
									e.name != '' && currentAxisOptions[updatePropertyName].length >= 2)) {
							/** */
							let dimensionDefault = JSON.parse(
								JSON.stringify(vm.dimensionDefault)
							);
							vm.$set(dimensionDefault, 'categoryAxis', 'valueAxis');
							currentAxisOptions[updatePropertyName].push(dimensionDefault);
							vm.dexieDB.aXisList
								.put({
									index: vm.currentIndex,
									axisOptions: currentAxisOptions
								})
								.then(() => {})
								.catch(error => console.error(error));
						}
						vm.$store.commit('dataDimension/setUpdateCount');
						vm.$store.commit('dataDimension/setAmendChartCount');
						vm.$store.commit(
							'canvasPlace/setCurrentChartType',
							vm.currentIndex
						);
						vm.$store.commit(
							'dataDimension/setCurrentChartAxis',
							currentAxisOptions
						);
					});
				} else {
					vm.$store.commit('dataDimension/setxAxisReport', xAxis);
				}
			}
		},
		getChildSearch(fromChildData) {
			this.showSearchData = fromChildData;
		},
		inputReset() {
			this.isClearInput = false;
		},
		initDataSource() {
			let vm = this;
			vm.fileList = [];
			vm.searchObject.beQueriedList = [];
			//
			vm.dexieDB.dataSourceList.each(res => {
				let fieldObject = res.dataSourceObject;
				//
				if (fieldObject.field) {
					vm.fileList.push(fieldObject);
					fieldObject.field.forEach(c => {
						vm.$set(c, 'source_sn', fieldObject.source_sn);
						vm.$set(c, 'db_source_name', fieldObject.db_source_name);
						vm.$set(c, 'name', c.Field);
						vm.$set(c, 'Field', c.Field);
						vm.searchObject.beQueriedList.push(c);
					});
				}
			});
		}
	},
	mounted() {
		this.initDataSource();
	}
};
</script>
<style scoped>
.search-field-input {
  width: 10vw;
  height: 1vw;
  background-color: #11151f;
  margin-left: 0.3vw;
  outline: none;
  border: solid 0.0625vw #384564;
  padding: 0.3vw;
  color: #fff;
  font-size: 0.4vw;
}
.field-list {
  width: 100%;
  vertical-align: middle;
  padding-top: 0.5vw;
}
.field-list li {
  color: white;
}
.field-list-ul {
  padding-right: 0.625vw;
}
.field-list-li {
  overflow: hidden;
  /* margin-bottom: 0.2vw; */
}
.field-list-li .title {
  padding: 0.15vw;
  background-color: transparent;
  margin: 0.2vw 0;
}
.field-list-li .title:hover {
  background-color: #252e42;
}
.field-list-li .title.selected {
  background-color: #252e42;
}
.field-list .title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  cursor: pointer;
  font-size: 0.5vw;
  width: 100%;
}
.field-list .title > span {
  font-size: 0.5vw;
}
.field-list .title .table-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  /* width: 40%; */
  display: inline-block;
  vertical-align: 1vw;
}

.field-list .title .data-source-list {
  height: 1vw;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
}

.dataSource-type {
  display: block;
  /* vertical-align: 1vw; */
  font-size: 0.6vw;
  line-height: 1vw;
  transform: scale(0.8) !important;
}
.field-list .title .down {
  display: block;
  transform: scale(0.7);
  font-size: 0.6vw;
}
.field-list .title .down.fieldDropDown {
  transform: rotate(-90deg) scale(0.7) !important;
  margin-top: -0.1vw;
}
.field-list .title > div > span {
  margin-left: 0.2vw;
  margin-right: 0.2vw;
  font-size: 0.5vw;
}
.field-list .field-datalist {
  font-size: 0.5vw;
}
.field-list .field-datalist ul {
  margin-bottom: 0.2vw;
  margin-top: 0.3vw;
}
.field-list .field-datalist ul > li {
  line-height: 1vw;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  border: 0.0625vw solid transparent;
  padding-left: 1.95vw;
  padding-top: 0.05vw;
  padding-bottom: 0.05vw;
  margin-top: 0.2vw;
}
.field-list .field-datalist ul > li.border-color-char {
  background-color: #182b3f;
  border-color: #28b3fc;
}
.field-list .field-datalist ul > li.border-color-date {
  background-color: #2e3325;
  border-color: #ffff00;
}
.field-list .field-datalist ul > li.border-color-int {
  background-color: #153325;
  border-color: #0fff00;
}
.field-list .field-datalist ul > li > span {
  display: block;
  transform: scale(0.9);
}
.field-list .field-datalist ul > li > span:first-child {
  font-size: 0.5vw;
  margin-right: 0.3vw;
}
.field-list .field-datalist ul > li > span:nth-child(2) {
  font-size: 0.5vw;
  margin-left: -0.2vw;
}
.search-ul {
  margin-left: 0.5vw;
}
.search-ul .search-list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 0.5vw;
  cursor: pointer;
  overflow: hidden;
}
.search-ul .search-list .resultClass {
  display: block;
  margin-left: 0.5vw;
}
.search-ul .search-list .search-icon {
  font-size: 0.6vw;
  transform: scale(0.8) !important;
  margin-right: 0.3vw;
}

.search-ul .search-list .result-fieldName {
  display: inline-block;
}

.search-ul .search-list .result-tableName {
  width: 100%;
  color: #798194;
  font-size: 0.6vw;
  margin-left: 1vw;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  display: inline-block;
}
.field-list ul:nth-child(2) {
  margin-top: 0.3vw;
  margin-bottom: 0.3vw;
  margin-left: 0.4vw;
  width: 9.7vw;
}
.field-list ul:nth-child(2) > li {
  border-bottom: 0.0625vw solid #11151f;
}
.filters-box {
  height: 15vw;
  width: 100%;
  background-color: #11151f;
  border: solid 0.0625vw #798194;
  z-index: 1;
  padding-top: 0.3vw;
}
.icon-color-char {
  color: #28b3fc;
}
.icon-color-date {
  color: #ffff00;
}
.icon-color-int {
  color: #0fff00;
}

</style>
