<template>
<!--  <div>-->
<!--    <div class="chart-basis"-->
<!--      @click="showEvent(3)">-->
<!--      <span>Y</span>-->
<!--      <span class="biFont icon-arrowLeft icon-fontsize"-->
<!--            :class="[isShowYAxis?'leftRotate-90':'leftRotate-0']"></span>-->
<!--    </div>-->
    <div
      ref="contentBox"
      class="content-box">
      <ul>
        <li class="optionsItem"
          v-show="axiosScale">
          <select-common :ordinaryList="yList"
            :isEchart="false"
            :selectTitle="yaxisShow"
            @fromSelectChild="getSelectData"></select-common>
        </li>
        <li class="optionsItem"
            :class="[showText?'heightStart':'heightOver']">
          <input-common :inputData="inputData"
            :styleObj="inputStyleObj"
            @onChange="getInputData"></input-common>
        </li>
        <li class="optionsItem"
          v-show="showText">
          <select-color :defaultColor="colorObject"
            v-on:changeColor="getTitleColor"></select-color>
        </li>
        <li class="optionsItem"
          v-show="showText">
          <span class="span-title"></span>
          <silder-data :opt="settingBorder"
            v-on:rangedata="rangeBorder"
            v-on:inputData="inputFontS"></silder-data>
        </li>
        <li class="optionsItem"
          v-show="yaisDataZoom">
          <span class="span-title"></span>
          <span><input class='dataFilter startValue'
            :value='valueRangeArray[0]'
            @input="valueRangeOnInput($event,0)"
            @blur="changeValueRange($event,0,'startValue')"
            type='text'
            placeholder=''>
          <input class='dataFilter'
            :value='valueRangeArray[1]'
            @input="valueRangeOnInput($event,1)"
            @blur="changeValueRange($event,1,'endValue')"
            type='text'
            placeholder=''></span>
        </li>
        <li class="optionsItem"
          v-show="axiosScale">
          <select-common :ordinaryList="ruleList"
            :isEchart="false"
            :selectTitle="selectTitle"
            @fromSelectChild="getSelectData"></select-common>
        </li>
      </ul>
    </div>
<!--  </div>-->
</template>

<script>
/**
 * 
 * 
 * 
 * 
 * 
 * 
 */

import SilderData from './BasicComponent/Slider';
import ColorSelect from './BasicComponent/ColorSetting';
import InputCommon from './BasicComponent/InputCommon';
import SelectCommon from './BasicComponent/SelectCommon';
import {getRgba} from '@/utils/optionSetting';
import { mapGetters, mapMutations } from 'vuex';
import {mixin} from './BasicComponent/mixin';
export default {
	name: 'Yaxis',
	mixins:[mixin],
	components: {
		'silder-data': SilderData,
		'select-color': ColorSelect,
		'input-common': InputCommon,
		'select-common': SelectCommon
	},
	data() {
		return {
		  yaisDataZoom:false,
			valueRangeArray: [],
			tempInputFileData:[],
			isShowYAxis: true,
			geiChildId: '',
			colorObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: { r: 25, g: 77, b: 51, a: 0.9 }
			},
			//input
			inputData: {
				inputTitle: 'Y',
				inputValue: ''
			},
			inputStyleObj: {
				inputWidth: '8vw',
				inputHeight: '1vw',
				placeholderDes: ''
			},
			ruleList: [
				{
					id: 1,
					inside: true,
					name: ''
				},
				{
					id: 2,
					inside: false,
					name: ''
				}
			],
			selectTitle: '',
			settingBorder: {
				name: '',
				max: 100,
				min: 1,
				valueRange: 1
			},
			yaxisShow: 'y',
			yList: [
				{
					id: 1,
					inside: true,
					name: ''
				},
				{
					id: 2,
					inside: false,
					name: ''
				}
			],
			dataObj: '',
			axiosScale: false,
			showText: false // title implicit
		};
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter'
		}),
		/**
     * input-common getInputData()
     * select-common  getSelectData()
     */
		//
		getSelectData(data) {
			// console.log('',data.data.inside)
			this.renderOptionSetter({
				key: 'yAxis.axisTick.inside',
				value: data.data.inside
			});
			// this.saveDataIndexDb();
		},
		inputFontS(data) {
			// console.log(data, typeof data, '');
			let vm = this;
			// vm.renderOptionSetter({
			// 	key: 'yAxis.nameTextStyle.fontSize',
			// 	Id: vm.geiChildId,
			// 	value: data
			// });
			vm.settingBorder.valueRange = data; //y
			vm.settingBorder.garidyValue = data; //y
			this.saveDataIndexDb({
				name: 'yAxis.nameTextStyle.fontSize',
				value: data
			});
		},
		// 
		saveDataIndexDb({ name, value }) {
			this.renderOptionSetter({
				key: name,
				value,
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		},
	  valueRangeOnInput(e,index) {
			e=e||window.event;
			let target=e.target||e.srcElement;
			let value = target.value.replace(/\D/g, '');
			e.target.value=value;
			// value=Number(value);
			// this.$set(this.tempInputFileData, index, value);
	  },
		changeValueRange(e,index,key){
			e=e||window.event;
			let target=e.target||e.srcElement;
			let value=target.value;
			if(value==='')e.target.value=this.valueRangeArray[index];
		  if(Number(e.target.value)===this.valueRangeArray[index])return false;
			// value=value===''?this.valueRangeArray[index]:Number(value);
			// let value=this.tempInputFileData[index];
			// console.log(value);
			// let value=this.tempInputFileData[index];
			this.saveDataIndexDb({
				name: `dataZoom.0.${key}`,
				value: Number(value)
			});
		},
		//y
		getInputData(val) {
			this.saveDataIndexDb({ name: 'yAxis.name', value: val });
		},
		// y
		getTitleColor(data) {
			let rgba =getRgba(data.rgba);
			this.saveDataIndexDb({ name: 'yAxis.nameTextStyle.color', value: rgba });
		},
		//y
		rangeBorder(data) {
			let vm = this;
			if (vm.settingBorder.valueRange !== data) {
				vm.saveDataIndexDb({
					name: 'yAxis.nameTextStyle.fontSize',
					value: data
				});
			}
		},
		//
		showEvent(number) {
			this.isShowYAxis = !this.isShowYAxis;
		},
		directionAxis(options) {
			this.axiosScale = true;
		},
		showYAxisText(options) {
			let vm = this;
			vm.showText = true;
			vm.inputData.inputValue = options.name; //y
			vm.colorObject.rgba = options.nameTextStyle.color; //y
			// console.group(options.nameTextStyle.color,'options');
			// vm.colorObject.titleColor = colorTransformation(
			// 	options.nameTextStyle.color
			// ); //y
			vm.settingBorder.valueRange = Number(options.nameTextStyle.fontSize); //y
			vm.settingBorder.garidyValue = Number(options.nameTextStyle.fontSize); //y
		},
	},
	computed: {
		...mapGetters('canvasPlace', {
			currentRenderOptions: 'getCurrentChartRenderOptions'
		}),
		// startValue(){
		//   return this.currentRenderOptions.dataZoom[0].startValue?this.currentRenderOptions.dataZoom[0].startValue:0;
		// },
		// endValue(){
		//   return this.currentRenderOptions.dataZoom[0].endValue?this.currentRenderOptions.dataZoom[0].endValue:0;
		// },
	},
	mounted() {
		let options = this.currentRenderOptions;
		this.tempInputFileData[0]=this._get(options,'dataZoom[0].startValue',0);
		this.tempInputFileData[1]=this._get(options,'dataZoom[0].endValue',100);
		// this.tempInputFileData[0] = options.dataZoom[0].startValue?options.dataZoom[0].startValue:0;
		// this.tempInputFileData[1] = options.dataZoom[0].endValue?options.dataZoom[0].endValue:100;
	},
	watch: {
		currentRenderOptions:{
			immediate:true,
			deep: true,
			handler(val) {
				let vm = this,
					options = val;
				this.yaisDataZoom = false;
				if(this._get(options,'dataZoom[0].orinet')==='vertical'){
					// if (options.dataZoom[0].orient === 'vertical'){
					this.yaisDataZoom = true;
					// this.valueRangeArray[0] = options.dataZoom[0].startValue?options.dataZoom[0].startValue:0;
					// this.valueRangeArray[1] = options.dataZoom[0].endValue?options.dataZoom[0].endValue:100;
					this.valueRangeArray[0] = this._get(options,'dataZoom[0].startValue',0);
					this.valueRangeArray[1] = this._get(options,'dataZoom[0].endValue',100);
				}
				// x
				if (options.xAxis.name) {
					vm.showYAxisText(options.xAxis);
				}
				// y
				if (options.xAxis.axisTick) {
				// vm.directionAxis(options);
				}
			}
		}
		// console.group(vm.chartDataList);
	}
};
</script>

<style scoped>
  li{
    padding: 0.4vw 0.5vw;
  }

.optionsItem {
  /*margin-bottom: 0.8vw;*/
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.startValue {
  margin-right: 0.5vw;
}

.dataFilter {
  width: 4vw;
  height: 1vw;
  background-color: #11151f;
  border: solid 0.0625vw #475566;
  color: #ffffff;
  padding: 0 0.2vw;
  font-size: 0.6vw;
}
.optionsItem .span-title {
  display: block;
  flex: 1;
  color: #acbbda;
}

span,
input {
  user-select: none;
}
.chart-basis .rotateIcon {
  transform: rotate(-90deg);
}
</style>

