<template>
  <div>
    <span class="filter-data-title"></span>
    <div class="silderbox">
      <input type="number"
        :value="value[0]"
        @blur='listenValue($event,0)'
        oninput="value=value.replace(/[^\d]/g,'')">
      <div class="slider-box">
        <vue-slider ref="sliders"
          @mousedown.native.stop
          :dotStyle="dotStyle"
          :railStyle="railStyle"
          :processStyle="processStyle"
          :tooltip-formatter="formatterPercentAge"
          :tooltip="tooltipStatus"
          :interval='intervalValue'
          @change="dataChange"
          v-model="value"
          :lazy="true"
          :min="minValue"
          :max="maxValue "></vue-slider>
      </div>
      <input type="number"
        :value="value[1]"
        @blur='listenValue($event,1)'
        oninput ="value=value.replace(/[^\d]/g,'')">
    </div>
    <div class="filter-footer">
      <span class="title"
        @click='sendData'></span>
      <span class="title"
        @click='clearnData'></span>
    </div>

  </div>
</template>
<script>
import vueSlider from 'vue-slider-component';
import 'vue-slider-component/theme/default.css';
import {mapGetters, mapMutations} from 'vuex';

export default {
	name: 'ScreenSlider',
	components: {
		vueSlider
	},
	props: {
		valueRange: {
			type: [Number, Array],
			required: true
		},
		minValue: {
			type: Number,
		},
		maxValue: {
			type: Number,
			required: true
		},

	},
	/**
	 * 
	 * valueRange[]  [100]   [0,100]
	 * minValue 
	 * maxValue  
	 */
	data() {
		return {
			formatterPercentAge: '{value}%',
			tooltipStatus: 'none',
			intervalValue: 1,
			value:[],
		};
	},
	computed: {
		...mapGetters('main', ['getVw']),
		dotStyle() {
			const dotStyle = {
				position: 'absolute',
				width: parseInt(this.getVw(7)) + 'px',
				height: parseInt(this.getVw(7)) + 'px',
				left: '50%',
				top: '50%',
				'margin-top': -parseInt(this.getVw(7)) / 2 + 'px',
				'margin-left': -parseInt(this.getVw(7)) / 2 + 'px',
				'border-width': this.getVw(2) + 'px',
				'border-style': 'solid',
				'border-color': '#28b3fc',
				'border-radius': '50%',
			};
			return dotStyle;
		},
		processStyle() {
			const processStyle = {
				backgroundColor: '#28b3fc'
			};
			return processStyle;
		},
		railStyle() {
			const railStyle = {
				position: 'relative',
				height: this.getVw(1) + 'px',
				width: '90%',
				'margin-left': '0.3vw',
				backgroundColor: '#acbbda'
			};

			return railStyle;
		},

	},
	methods: {
		/**
		 * 
		 */
		sendData() {
			let vm=this;
			vm.$emit('getValue', vm.value);
		},
		dataChange() {
		},
		clearnData() {
			this.value = [this.minValue, this.maxValue];
		},
		listenValue(e, index) {
			let val = e.target.value;
			if (val < this.minValue) {
				this.$set(this.value, index, Number(this.minValue));
			} else if (val > this.maxValue) {
				this.$set(this.value, index,  Number(this.maxValue));
			}else{
		  this.$set(this.value, index, Number(val));
			}
			if (index == 1 && val < this.valueRange[0]) {
				this.$set(this.value, index,  Number(this.maxValue));
			}
		}
	},

	watch: {
	  valueRange(val){
		  this.value=JSON.parse(JSON.stringify(val));
		},


	}
};
</script>
<style scoped>
  .slider-box {
    width: 6.2vw;
    margin-left: 0.4vw;
    margin-right: 0.4vw;
  }

  .containter-axis .filters-box .filter-footer {
    padding: 1.4vw 0 0.2vw 0;
    cursor: pointer;
  }

  .containter-axis .filters-box .filter-footer .title:hover {
    color: #28b3fc;
  }

  .containter-axis .filters-box .filter-footer span:nth-child(2) {
    float: right;
  }

  .pages-filters-number .filter-data-title {
    padding: 0.3vw 0;
    display: block;
    margin-bottom: 0.2vw;
  }

  .filters-box .silderbox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

  }

  .filters-box .silderbox input {
    background-color: #11151f;
    border: solid 0.0625vw #475566;
    width: 25%;
    outline: none;
    color: #fff;
    padding: 0 0.1vw;
    font-size: 0.6vw;
    text-align: center;
  }

  .filters-box .radiobox {
    width: 100%;
    padding-top: 0.5vw;
    margin-bottom: 0.5vw;
    height: 1vw;
    position: relative;
  }

  .filters-box .radiobox .exCircle {
    float: left;
    width: 0.78vw;
    height: 0.78vw;
    border-radius: 50%;
    border: 0.0625vw solid #ffffff;
    margin-left: 1.5vw;
    cursor: pointer;
    position: relative;
  }

  .filters-box .radiobox .exCircle:first-child {
    margin-left: 0.9vw;
  }

  .filters-box .radiobox .exCircle .inCircle {
    position: absolute;
    border-radius: 50%;
    background-color: #fff;
  }

  .filters-box .radiobox .exCircle span {
    display: block;
    margin-left: 1vw;
    line-height: 0.6vw;
    cursor: pointer;
  }

  input[type="text"]:focus {
    border-color: #00b9ff;
  }
</style>


