<template>
  <div>
    <div class="sider-shrink"
      v-show="isPhoneLayout">
      <div class="sider-items-left">
        <span class="biFont icon-dataSourceList alignCenter"></span>
        <span class="alignCenter"></span>
      </div>
<!--      <div-->
<!--        class="biFont icon-save sider-items-left"-->
<!--        @click="saveMobileOptions"-->
<!--        title=""></div>-->
    </div>
    <div class="phoneStyle">
      <div class="icon-style"
        v-for="(item,index) in chartListFilter"
        :draggable="!item.containerOptions.showInMobileLayout"
        @dragstart="dragstart($event,item.id,item)"
        :class="{
           allow:!item.containerOptions.showInMobileLayout
        }"
        :key="index">
        <div class="cover"
          v-show="item.containerOptions.showInMobileLayout">
          <span class="notAllow biFont icon-forbidden"></span>
        </div>
        <svg cursor='icon svg'
          aria-hidden="true">
          <use :xlink:href="item.containerOptions.layerChartIcon"></use>
        </svg>
        <!--        <div>{{getChartlist[index].chartOptions.title.text}}</div>-->
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';
import {baseChartList} from '@/assets/SiderBar/js/basicChartData.js';

export default {
	name: 'SwitchPhoneRight',
	data() {
		return {
			baseChartIconList: baseChartList[0].children,//icon
		};
	},
	computed: {
		...mapGetters('canvasPlace', {
			isPhoneLayout: 'getPhoneLayoutState',//
			chartList: 'getChartList',//
			getCanvasPhoneDomArr: 'getCanvasPhoneDom',//canvas Arr
		}),
	  chartListFilter(state) {
		  return state.chartList.filter((item) => {
			  return item.containerStatus.isSetOptions;
			});
		},
	},

	methods: {
	  dragstart(event, index, item) {
		  event = event || window.event;
		  const ele = event.srcElement || event.target;
		  const eleHeight = ele.getBoundingClientRect().height;
		  const chartHeight = item.containerOptions.tableHeightMobile;
		  //ydiv
		  let yPosition = event.layerY;
		  let scale = yPosition / eleHeight;
		  let yPositionDeviate = (chartHeight * scale).toFixed(4);
		  event.dataTransfer.setData('chartIndex', index);
		  event.dataTransfer.setData('yPositionDeviate', yPositionDeviate);
	  },
	  saveMobileOptions() {
		  this.$store.commit('canvasPlace/setStateProperty',{
				key: 'saveMobileChartOptionsImmediately',
				value: true
			});
		}
	}
};
</script>

<style scoped>
  .sider-shrink {
    margin-top: 0.12vh;
    background-color: #273045;
    width: 15.2vw;
    height: 1.9vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.7vw;
  }

  .alignCenter {
    text-align: center;
    padding-left: 0.1vw;
  }

  .sider-items-left {
    color: #fff;
    cursor: pointer;
  }

  .phoneStyle {
    width: 15.2vw;
    color: #fff;
    background: rgb(23, 30, 43);
    padding: 0.7vw;
    display: flex;
    flex-flow: row wrap;
    align-content: flex-start;
    justify-content: space-between;
    height: calc(100vh - 4.2vw - 0.12vh);
  }

  .icon-style {
    width: 6.6vw;
    height: 4.5vw;
    line-height: 4.5vw;
    text-align: center;
    position: relative;
    margin-bottom: 0.5vw;
  }

  .icon-style.allow {
    cursor: pointer;
  }

  .icon-style .cover {
    z-index: 2;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .4);
  }

  .icon-style .cover .notAllow {
    font-size: 1.2vw;
  }


  .icon-style svg {
    position: relative;
    z-index: 1;
    width: 6.5vw;
    height: 3.2vw;
    margin-bottom: 0.1vw;
    vertical-align: middle;
  }


</style>
