import { commonOptions,handleReturnData, setMediaData, setTextData,setInteractiveData} from '../utils/setChart';
export default {
	namespaced: true,
	state: {
		chartData: {},//
		textData: {}, //
		mediaData: {}, //`
		interactiveData: {},//
	},
	getters: {
		/**
		 * 5s
		 * isDeletetrue
		 */
		//
		chartReturnData: (state) => {
			let returnData = [];
			let ChartData = state.chartData;
			if (ChartData && ChartData.amendChartList !== undefined) {
				returnData = ChartData.amendChartList.map(function (item) {
				  //isDeletetrue
					if (item['containerStatus']['isSetOptions'] && !item['containerStatus']['isDeleted']) {

						const axisOptions = ChartData.amendAxisList.find(e => e.index == item.id)['axisOptions'];
 						//setChart.js
						// itemecharts   axisOptions
						return  commonOptions(item, axisOptions);
					}
				});
			}
			return returnData;
		},

		//
		textReturnData: (state) => {
			let returnArr = [];
			if (state.textData.length) {
				returnArr = state.textData.map(function (item) {
					if (item['containerStatus']['isSetOptions'] && !item['containerStatus']['isDeleted']) {
						return setTextData(item);
					}
				});
				// console.log('returnArr', returnArr);
			}
			return returnArr;
		},
		//media
		mediaReturnData: (state) => {
			let returnArr = [];
			// console.log('', state.mediaData);
			if (state.mediaData.length) {
				returnArr = state.mediaData.map(function (item) {
					if (item['containerStatus']['isSetOptions'] && !item['containerStatus']['isDeleted']) {
						return setMediaData(item);
					}
				});
			}
			// console.log(returnArr,'')
			return returnArr;
		},
		//interactive
		interactiveReturnData:(state)=>{//interactive
			let returnArr = [];
			// console.log(state.interactiveData);
			if (state.interactiveData.length) {
				returnArr = state.interactiveData.map(function (item) {
				  console.log(item);
					if (item['containerStatus']['isSetOptions'] && !item['containerStatus']['isDeleted']) {
					  // console.log(setInteractiveData(item));
						return setInteractiveData(item);
					}
				});
			}
			// console.log(returnArr,'')
			return returnArr;
		},
		screenData: (state) => {

		}
	},
	actions: {
		/**
		 * 
		 * @param {object} param0
		 * @param {object} amendChartObj  5sindexDB
		 */
		asyncReceiveData({state}, amendChartObj) {
			return new Promise((resolve, reject) => {
				state.chartData = amendChartObj;
				resolve('');
			});
		},
		/**
		 * 
		 * @param {object} param0
		 * @param {object} amendTextObj  5sindexDB
		 */
		asyncTextScreen: ({state}, amendTextObj) => {
			return new Promise((resolve, reject) => {
				state.textData = amendTextObj;
				resolve('');
			});
		},
		/**
		 * media
		 * @param {object} param0
		 * @param {object} amendMediaObj  5sindexDBmedia
		 */
		asyncMediaScreen: ({state}, amendMediaObj) => {
			return new Promise((resolve, reject) => {
				state.mediaData = amendMediaObj;
				resolve('');
			});
		},
		/**
		 * interactive
		 * @param {object} param0
		 * @param {object} amendMediaObj  5sindexDBinteractive
		 */
		asyncInteractiveScreen: ({state}, amendInteractiveObj) => {
		  // console.log(amendInteractiveObj,'');
			return new Promise((resolve, reject) => {
				state.interactiveData = amendInteractiveObj;
				resolve('');
			});
		}
	}
};
