import Vue from 'vue';
import {store} from '@/store/store';
import Router from 'vue-router';
import {getQueryString, getCookie} from '@/utils/utilsMethods';
import {
  post
} from '@/utils/http';

Vue.use(Router);
//
const PrimaryRouter = fileName => () => import( /* webpackChunkName: "PrimaryRouter" */ `@/pages/${fileName}/index.vue`);
import Screen from '@/pages/Visualization/Project';


const CtrlMenu = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/CtrlMenu');
const RunVIP = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/RunVIP');
const RunOrder = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/RunOrder');
const BasicInfo = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/BasicInfo');
const SecuritySetting = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/SecuritySetting');
const Certification = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/Certification');
const AccountOverview = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/AccountOverview');
const Renew = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/Renew');
const consumingRecord = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/AccountOverview/ConsumingRecords');
const incomeAmount = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/AccountOverview/IncomeAmount');
const rechargeRecord = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/AccountOverview/RechargeRecord');
const accountInfo = () =>
  import( /* webpackChunkName: "userCenter" */ '@/pages/UserCenter/components/AccountOverview/AccountInfo');
const PreViewpages = () =>
  import( /* webpackChunkName: "preViewpages" */ '@/pages/Visualization/Project/components/CanvasPlace/previewPages');
//
// const M_main = () => import( /* webpackChunkName: "PrimaryRouter" */ '../mobilePages/M_main.vue');
// import M_main from '../mobilePages/M_main.vue';

const router = new Router({
  routes: [{
    path: '/navPage',
    component: PrimaryRouter('NavPage'),
    children: [{
      path: '/main',
      name: 'main',
      component: PrimaryRouter('Main'),
      meta: {
        title: ''
      },
    },
      // {
      //     path: '/login',
      //     name: 'login',
      //     component: Login,
      //     meta: {
      //         title: ''
      //     },
      // },
      {
        path: '/visualization',
        name: 'visualization',
        component: PrimaryRouter('Visualization'),
        meta: {
          title: ''
        },
      },
      {
        path: '/screen',
        name: 'screen',
        component: Screen,
        meta: {
          title: ''
        },
      },
      {
        path: '/data',
        name: 'myDataSource',
        component: PrimaryRouter('MyDataSource'),
        meta: {
          title: ''
        }

      },
      {
        path: '/dataModeling',
        name: 'dataModeling',
        component: PrimaryRouter('DataModeling'),
        meta: {
          title: ''
        }
      },
      {
        path: '/cases',
        name: 'cases',
        component: PrimaryRouter('Cases'),
        meta: {
          title: ''
        }
      },
      {
        path: '/userCenter',
        name: 'userCenter',
        component: PrimaryRouter('UserCenter'),
        redirect: '/userCenter/accountOverview/AccountInfo',
        meta: {
          title: ''
        },
        children: [{
          path: '/ctrlMenu',
          name: 'ctrlMenu',
          component: CtrlMenu,
          meta: {
            title: ''
          },
        },
          {
            path: '/runVIP',
            name: 'runVIP',
            component: RunVIP,
            meta: {
              title: ''
            },
          },
          {
            path: '/runOrder',
            name: 'runOrder',
            component: RunOrder,
            meta: {
              title: ''
            },
          },
          {
            path: '/basicInfo',
            name: 'basicInfo',
            component: BasicInfo,
            meta: {
              title: ''
            },
          },
          {
            path: '/securitySetting',
            name: 'securitySetting',
            component: SecuritySetting,
            meta: {
              title: ''
            },
          },
          {
            path: '/certification',
            name: 'certification',
            component: Certification,
            meta: {
              title: ''
            },
          },
          {
            path: '/accountOverview',
            name: 'accountOverview',
            component: AccountOverview,
            redirect: '/userCenter/accountOverview/AccountInfo',
            meta: {
              title: ''
            },
            children: [{
              name: 'accountInfo',
              component: accountInfo,
              path: '/userCenter/accountOverview/AccountInfo',
              meta: {
                title: ''
              },
            },
              {
                name: 'consumingRecord',
                component: consumingRecord,
                path: '/userCenter/accountOverview/ExpensesRecord',
                meta: {
                  title: ''
                },
              },
              {
                name: 'incomeAmount',
                component: incomeAmount,
                path: '/userCenter/accountOverview/incomeAmount',
                meta: {
                  title: ''
                },
              },
              {
                name: 'rechargeRecord',
                component: rechargeRecord,
                path: '/userCenter/accountOverview/rechargeRecord',
                meta: {
                  title: ''
                },
              },
            ]
          },
          {
            path: '/renew',
            name: 'renew',
            component: Renew,
            meta: {
              title: ''
            },
          }
        ]
      },

    ]
  },
    // {
    //   path: '/M_main',
    //   name: 'M_main',
    //   component: M_main,
    //   meta: {
    //     title: ''
    //   },
    // },
    {
      path: '/previewPages',
      name: 'previewPages',
      component: PreViewpages,
      meta: {
        title: ''
      }
    },
    {
      path: '*',
      redirect: to => {
        if (to.path === '/privacy') {
          window.location.href = 'https://dvdev.24e.co/privacy.html';
        } else {
          return {
            name: 'main'
          };

        }
      }

    }
  ],
  mode: 'history',
});
// 


router.beforeEach((to, from, next) => {
  let ssoUrl = store.getters['config/ssoUrl'];
  /* title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  let openArr = ['previewPages',];
  let openAdminArr = ['ctrlMenu', 'runVIP', 'runOrder'];
  if (openArr.includes(to.name)) {
    next();
    return false;
  }
  let authorization = getQueryString('token') || getCookie('token');
  // if (authorization && authorization != 'null') {
  //   next();
  //   return false;
  //     // authorization = authorization.replace(/\s+/g, "+");
  // }
  if (!authorization || authorization === 'null') {
    window.location.href = ssoUrl + window.location.href;
    return false;
  }
  post('api/isLogin')
    .then(res => {
      if (res.code === '000000') {
        let exdate = new Date();
        exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * 7); //7
        document.cookie = 'token=' + authorization + ';expires=' + exdate.toGMTString() + ';domain=.24e.co;path=/'; // 

        let resData = res.data;
        store.commit('userCenter/setUserInfo', resData);
        localStorage.setItem('userid', resData.userId);
        localStorage.setItem('headimgurl', resData.iconUrl);
        if (resData.identity == 1 && openAdminArr.includes(to.name)) {
          next('/main');
        } else {
          next();
        }
      } else {
        window.location.href = ssoUrl + window.location.href;
      }
    }).catch(err => {
    window.location.href = ssoUrl + window.location.href;
  });
});

export default router;
