<template>
  <div ref="contentBox">
    <ul>
      <li class="optionsItem">
        <input-common :inputData="inputData"
          :styleObj="inputStyleObj"
          @onChange="getInputData"></input-common>
      </li>
      <li class="optionsItem"
        v-show="axiosText">
        <select-color :defaultColor="colorObject"
          v-on:changeColor="getTitleColor"></select-color>
      </li>
      <li class="optionsItem"
        v-show="axiosText">
        <span class="span-title"></span>
        <silder-data :opt="settingBorder"
          v-on:rangedata="rangeBorder"
          v-on:inputData="rangeBorder"></silder-data>
      </li>
      <li class="optionsItem"
        v-show="xaisDataZoom">
        <span class="span-title"></span>
        <span><input class='dataFilter startValue'
          :value='seriesStartValue'
          @input="valueRangeOnInput($event,0)"
          @blur="changeValueRange($event,0,'startValue')"
          type='text'
          placeholder=''>
          <input class='dataFilter'
            :value='valueRangeArray[1]'
            @input="valueRangeOnInput($event,1)"
            @blur="changeValueRange($event,1,'endValue')"
            type='text'
            placeholder=''></span>

      </li>
    </ul>
  </div>

</template>
<script>
/**
   * 
   * 
   * 
   * 
   * 
   * 
   */

import SilderData from '@/pages/Visualization/Project/components/RightPane/components/BasicComponent/Slider';
import ColorSelect from '@/pages/Visualization/Project/components/RightPane/components/BasicComponent/ColorSetting';
import InputCommon from '@/pages/Visualization/Project/components/RightPane/components/BasicComponent/InputCommon';
import {getRgba} from '@/utils/optionSetting';
import {mixin} from './BasicComponent/mixin';

import {mapGetters, mapMutations} from 'vuex';

export default {
	name: 'axisContent',
	mixins:[mixin],
	components: {
		'silder-data': SilderData,
		'select-color': ColorSelect,
		'input-common': InputCommon,
	},
	data() {
		return {
			headerHeight:0,
			contentHeight:0,
			isShowXAxis: true,
			startData: 0,
			endData: 100,
			valueRangeArray: [0, 100],
			tempInputFileData: [0, 100],//
			xaisDataZoom: false,
			colorObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			//input
			inputData: {
				inputTitle: 'X',
				inputValue: ''
			},
			inputStyleObj: {
				inputWidth: '8vw',
				inputHeight: '1vw',
				placeholderDes: ''
			},
			ruleList: [
				{
					id: 1,
					inside: true,
					name: ''
				},
				{
					id: 2,
					inside: false,
					name: ''
				}
			],
			selectTitle: '',
			settingBorder: {
				name: '',
				max: 100,
				min: 1,
				valueRange: 1
			},
			dataObj: '',
			axiosScale: false,
			axiosText: false, //
			axiosSkniss: false, //
			swichOpt: {
				name: '',
				left: '',
				right: '',
				state: true
			}
		};
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter'
		}),
		/**
       * input-common getInputData()\
       * select-common  getSelectData()\\\
       */
		// 
		saveDataIndexDb({name, value}) {
			this.renderOptionSetter({
				key: name,
				value: value
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		},
		valueRangeOnInput(e, index) {
			e = e || window.event;
			let target = e.target || e.srcElement;
			let value = target.value.replace(/\D/g, '');
			target.value=value;
		},
		changeValueRange(e, index, key) {
			e = e || window.event;
			let target = e.target || e.srcElement;
			let value = target.value;
			let dataLength=this._get(this.currentRenderOptions,'series[0].data',[]).length;
			if(!value)value=dataLength;
			if(key==='endValue'&&value-1>=dataLength) value=dataLength;
			if (value == this.valueRangeArray[index]) {
				target.value=value;
				return false;
			}
			this.saveDataIndexDb({
				name: `dataZoom.0.${key}`,
				value: value-1
			});
		},

		// dataScreening(type) {//
		// 	if (type === 0) {
		// 		console.log(type, '0');
		// 	} else {
		// 		console.log(type, '1');
		//
		// 	}
		// },
		getSelectData(data) {
			// console.log('',data.data.inside)
			this.$store.commit('canvasPlace/changeXaxisWheel', {
				data: data.data.inside,
				Id: this.geiChildId
			});
			// this.saveDataIndexDb({});
		},
		//x
		getInputData(val) {
			this.saveDataIndexDb({name: 'xAxis.name', value: val});
		},
		getBtnState(data) {
			console.log(data, '');
		},
		// x
		getTitleColor(data) {
			let rgba = getRgba(data.rgba);
			this.saveDataIndexDb({name: 'xAxis.nameTextStyle.color', value: rgba});
		},
		//x
		rangeBorder(data) {
			if (this.settingBorder.valueRange !== data) {
				this.saveDataIndexDb({
					name: 'xAxis.nameTextStyle.fontSize',
					value: data
				});
			}
		},
		//
		showEvent() {
			this.isShowXAxis = !this.isShowXAxis;
		},
		showXaxios() {
			this.axiosScale = true;
		},
		showXAxisText(options) {
			let vm = this;
			vm.axiosText = true;
			vm.inputData.inputValue = options.name; //x
			vm.colorObject.rgba = options.nameTextStyle.color; //x
			// vm.colorObject.titleColor = colorTransformation(
			// 	options.nameTextStyle.color
			// );
			vm.settingBorder.valueRange = Number(options.nameTextStyle.fontSize); //x
			vm.settingBorder.garidyValue = parseInt(
				'10',
				options.nameTextStyle.fontSize
			); //x
			// console.log(vm.settingBorder.valueRange,options,'scend');
		},
	},
	computed: {
		...mapGetters('canvasPlace', {
			currentContainerOptions: 'getCurrentContainerOptions',
			currentRenderOptions: 'getCurrentChartRenderOptions'
		}),
		seriesStartValue(){
			return this._get(this.options,'dataZoom[0].startValue',0)+1;
		},
		seriesEndValue(){
			return this._get(this.options,'dataZoom[0].endValue',99)+1;
		},
	},

	watch: {
		currentRenderOptions: {
			immediate: true,
			deep: true,
			handler(val) {
				let vm = this,
					options = val;
				this.xaisDataZoom = false;
				// x // 
				// if (options.xAxis.name) {
				if (this._get(options,'xAxis.name')) {
					vm.showXAxisText(options.xAxis);
				}
				// this._get(options,'dataZoom[0].orient')
				if (this._get(options,'dataZoom[0].orient') === 'horizontal') {
					this.xaisDataZoom = true;
					// this.valueRangeArray[0] = this._get(options,'dataZoom[0].startValue',0)+1;
					this.valueRangeArray[1] = this._get(options,'dataZoom[0].endValue',99)+1;
				}
				// 
				// if (options.axisTick) {}
			}
		},

	},
};
</script>

<style scoped>
  li{
    padding: 0.4vw 0.5vw;
  }
  .optionsItem {
    /*margin-bottom: 0.8vw;*/
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }


  .dataFilter {
    width: 3.725vw;
    height: 1vw;
    background-color: #11151f;
    border: solid 0.0625vw #475566;
    color: #ffffff;
    padding: 0 0.2vw;
    font-size: 0.6vw;
  }

  .dataFilter:first-child {
    margin-right: 0.5vw;
  }
  .optionsItem .span-title {
    display: block;
    flex: 1;
    color: #acbbda;
  }

  span,
  input {
    user-select: none;
  }
</style>

