import Vue from 'vue';
import dexieDB from '../utils/db';
export default {
	namespaced: true,
	state: {
		count: 0,
		databaseType: [{
			label: 'SQLserver',
			value: 'SQLserver'
		},
		{
			label: 'MySQL',
			value: 'MySQL'
		},
		{
			label: 'Oracle',
			value: 'Oracle'
		},
		{
			label: 'PostgreSQL',
			value: 'PostgreSQL'
		},
		{
			label: 'MongoDB',
			value: 'MongoDB'
		}
		],
		dataFieldMsg: {
			tableId: '',
			fieldName: ''
		},
		dataInfo: [],
		isDataRefresh: false,
		relationData: null,
		zoomNum: null,
		modelPageNum: null,
		isConnectEnd: false, // 
	},
	getters: {
		getDataSource(state) {
			return state.dataInfo;
		},
		getConnectEnd(state) {
			return state.isConnectEnd;
		},
		getCount(state) {
			return state.count;
		},
		getDataRefresh(state) {
			return state.isDataRefresh;
		},
		getRelationData(state) {
			return state.relationData;
		},
		getZoomNum(state) {
			return state.zoomNum;
		},
		getModelPage(state) {
			return state.modelPageNum;
		}
	},
	mutations: {
		setDataSource: (state, res) => {
			if (res === 'clearData') {
				state.dataInfo = [];
			} else {
				let data = res.data;
				if (res.model === 'page') {
					for (let item in data) {
						state.dataInfo.push(data[item]);
					}
				} else {
					state.dataInfo = [];
					state.dataInfo = data;
				}
				if (res.url == 'api/dataInfo') {
					data.forEach((e, i) => {
						Vue.set(e, 'isShowField', false);
						// console.log('e===>',e);
						dexieDB.dataSourceList
							.put({
								index: e.source_sn,
								dataSourceObject: e
							})
							.then(() => {})
							.catch(e => {
								if ((e.name === 'QuotaExceededError') ||
                  (e.inner && e.inner.name === 'QuotaExceededError'))
								{
									// QuotaExceededError may occur as the inner error of an AbortError
									console.error ('QuotaExceeded error!');
								} else {
									// Any other error
									console.error (e);
								}
							});
					});
				}
			}
		},
		deleteDataSource(state, id) {
			dexieDB.dataSourceList.delete(id).catch(error => console.error(error));
		},
		setCount(state) {
			state.count++;
		},
		setDataRefresh(state, bool) {
			state.isDataRefresh = bool;
		},
		setRelationData(state, data) {
			state.relationData = [];
			state.relationData = data;
		},
		setZoomNum(state, num) {
			state.zoomNum = num;
		},
		setModelPage(state, data) {
			state.modelPageNum = data;
		},
		setConnectEnd(state, bool) {
			state.isConnectEnd = bool;
		}
	},
	actions: {}
};
