<template>
  <div class="time-father">
    <div>
      <p><span class="biFont basicChart icon-shijian time-span-style"></span>{{timebase}}</p>
    </div>
  </div>
</template>

<script>
export default {
	name: 'interactive-timeMonitor',
	components: {
	},
	props: {
		tableId: {
			type: String
		}
	},
	data() {
		return {
			timebase: '',
			timer:null,
		};
	},
	created() {
	  this.timer = setInterval(() =>{
			this.timeKeep();
	  }, 500);
	},
	beforeDestroy(){
		clearInterval(this.timer);
	},
	mounted() {
		if (this.tableId != '') {
			this.$store.commit('canvasPlace/setRenderedStatus');
		}
	},
	methods: {
		timeKeep() {
			this.timebase = this.timeMange();
		},
		timeMange() {
			let addressTime = new Date(),
				yyyy = addressTime.getFullYear(), //getFullYear()
				MM = addressTime.getMonth() + 1, //getMonth()
				dd = addressTime.getDate(), //getDate()
				hh = addressTime.getHours(), //getHours
				mm = addressTime.getMinutes(), //getMinutes
				ss = addressTime.getSeconds(), //getSeconds
				time =
          yyyy +
          '' +
          MM +
          '' +
          this.checkTime(dd) +
          '' +
          ' ' +
          hh +
          ':' +
          this.checkTime(mm) +
          ':' +
          this.checkTime(ss);
			return time;
		},
		checkTime(i) {
			if (i < 10) {
				i = '0' + i;
			}
			return i;
		}
	}
};
</script>

<style scoped>
.time-father {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 0.7vw;
  cursor: move;
  height: 100%;
}
.time-span-style {
  padding-right: 0.2vw;
  color: #00f8ff;
  vertical-align: unset;
}
</style>
