<template>
  <div>
    <div @click="textBgColor = !textBgColor"
      class="chart-basis">
      <span></span>
      <span :class="{rotateIcon:textBgColor}"
        class="biFont icon-arrowLeft icon-fontsize"></span>
    </div>
    <div class="content-box"
      v-show="textBgColor">
      <ul>
        <li class="optionsItem">
          <select-color :defaultColor="bgObject"
            v-on:changeColor="getTextBgColor"></select-color>
        </li>
        <li class="optionsItem">
          <input-common :inputData="inputData"
            :styleObj="inputStyleObj"
            @onChange="getInputData"></input-common>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="settingBorder"
            v-on:inputData="rangeBorder"
            v-on:rangedata="rangeBorder"></silder-data>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="textFontSize"
            v-on:inputData="rangeTextBorder"
            v-on:rangedata="rangeTextBorder"></silder-data>
        </li>
        <li class="optionsItem">
          <select-color :defaultColor="fontStyle"
            v-on:changeColor="getTextColor"></select-color>
        </li>
        <li class="optionsItem">
          <select-menu :heightLightLine="levelSetting"
            :ordinaryList="horizontalList"
            :selectTitle="''"
            @getReturnData="getHorizontalType"></select-menu>
        </li>
        <!--        <li class="optionsItem">-->
        <!--          <select-menu :ordinaryList="verticalList"-->
        <!--            :selectTitle="''"-->
        <!--            :heightLightLine="'center'"-->
        <!--            @getReturnData="getVerticalType"></select-menu>-->
        <!--        </li>-->
        <li class="optionsItem">
          <text-format :opt="textStyleSetting"
            @fontStyle="fontStyleSetting"></text-format>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="fristShrink"
            v-on:inputData="rangeTextIndent"
            v-on:rangedata="rangeTextIndent"></silder-data>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="fontLineHeight"
            v-on:inputData="rangeTextLineHeight"
            v-on:rangedata="rangeTextLineHeight"></silder-data>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="fontSpacing"
            v-on:inputData="rangeTextletterSpacing"
            v-on:rangedata="rangeTextletterSpacing"></silder-data>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="innerStrokes"
            v-on:inputData="outwidth"
            v-on:rangedata="outwidth"></silder-data>
        </li>
        <li class="optionsItem">
          <select-color :defaultColor="innerObject"
            v-on:changeColor="outColor"></select-color>
        </li>
        <li class="optionsItem">
          <!-- <span class="span-title"></span> -->
          <select-menu :heightLightLine="boderInnerType"
            :ordinaryList="outLineList"
            :selectTitle="''"
            @getReturnData="getOutType"></select-menu>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import ColorSelect from './BasicComponent/ColorSetting';
import SilderData from './BasicComponent/Slider';
import InputCommon from './BasicComponent/InputCommon';
import SelectMenu from '@/components/Dropdown/Dropdown';
import {mapGetters} from 'vuex';
import {getRgba} from '@/utils/optionSetting';
import TextFormat from './BasicComponent/TextFormat';

export default {
	name: 'TextSetting',
	data() {
		return {
			textBgColor: true,
			textData: '',
			bgObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)',
				// rgba: { r: 25, g: 77, b: 51, a: 0.9 }
			},
			fontStyle: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)',
				// rgba: { r: 25, g: 77, b: 51, a: 0.9 }
			},
			settingBorder: {
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			textFontSize: {
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			fristShrink: {
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			fontLineHeight: {
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			fontSpacing: {
				name: '',
				max: 100,
				min: 0,
				valueRange: 1
			},
			innerStrokes: {
				name: '',
				max: 100,
				min: 0,
				valueRange: 2
			},
			innerObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)',
				// rgba: { r: 25, g: 77, b: 51, a: 0.9 }
			},
			inputData: {
				inputTitle: '',
				inputValue: '',
				disable: 'disable'
			},
			inputStyleObj: {
				inputWidth: '8vw',
				inputHeight: '1vw',
				placeholderDes: ''
			},
			verticalList: [
				{value: 'flex-start', label: ''},
				{value: 'center', label: ''},
				{value: 'flex-end', label: ''}
			],
			levelSetting: {},
			horizontalList: [
				{value: 'left', label: ''},
				{value: 'center', label: ''},
				{value: 'right', label: ''}
			],
			outLineList: [
				{value: 'dashed', label: ''},
				{value: 'solid', label: ''}
			],
			textStyleSetting: {
				name: '',
				fontWeight: 'unset',
				fontStyle: 'unset',
				textDecoration: 'unset'
			},
			boderInnerType: {},
			indexes: 0
		};
	},
	components: {
		'select-menu': SelectMenu,
		'select-color': ColorSelect,
		'silder-data': SilderData,
		'input-common': InputCommon,
		'text-format': TextFormat
	},
	computed: {
		...mapGetters('canvasPlace', {
			renderOptions: 'getCurrentChartRenderOptions',
		}),
		...mapGetters('chartBus', {
			textReceive: 'returnData',
			chartIndex: 'returnDataIndex'
		})
	},
	watch: {
		renderOptions: {
			deep: true,
			immediate: true,
			handler(val) {
				let vm = this;
				vm.setOptions();
			}
		}
		// renderOptions() {
		// 	let vm = this;
		//
		// 	// vm.textData = vm.textReceive.chart.renderOptions;
		// 	// console.log('2-',vm.textData);
		// 	vm.setOptions();
		// }
	},
	methods: {
		setOptions() {
			let vm = this;
			vm.bgObject.rgba=vm.renderOptions.backgroundColor;
			vm.inputData.inputValue = vm.renderOptions.textContent; //
			vm.settingBorder.valueRange = Number(vm.renderOptions.borderStyle.borderRadiusWidth); //
			vm.textFontSize.valueRange = Number(vm.renderOptions.textStyle.fontSize);
			// vm.fontStyle.titleColor = colorTransformation(
			// 	vm.textData.textStyle.color
			// );
			vm.fontStyle.rgba = vm.renderOptions.textStyle.color;
			vm.fristShrink.valueRange = Number(vm.renderOptions.textStyle.indent);
			vm.fontLineHeight.valueRange = Number(vm.renderOptions.textStyle.lineHeight);// 
			vm.fontSpacing.valueRange = Number(vm.renderOptions.letterSpacing);
			vm.innerStrokes.valueRange = Number(vm.renderOptions.borderStyle.lineWidth);
			// vm.innerObject.titleColor = colorTransformation(
			// 	vm.textData.borderStyle.lineColor
			// );
			vm.innerObject.rgba = vm.renderOptions.borderStyle.lineColor;
			vm.levelSetting = vm.horizontalList.find(
				e => e.value == vm.renderOptions.textStyle.align
			);
			vm.boderInnerType = vm.outLineList.find(
				e => e.value === vm.renderOptions.borderStyle.lineType
			); // 
			vm.fontSpacing.valueRange = Number(vm.renderOptions.textStyle.letterSpacing);// 
			vm.textStyleSetting.fontWeight = vm.renderOptions.textStyle.fontWeight;
			vm.textStyleSetting.fontStyle = vm.renderOptions.textStyle.fontStyle;
			vm.textStyleSetting.textDecoration = vm.renderOptions.textStyle.decoration;
			// console.log(vm.textData.textStyle,' string');
		},
		setTextOption({name, data}) {
			// console.log('6-txt',name);
			this.$store.commit('canvasPlace/renderOptionSetter', {
				key: name,
				value: data
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		},
		//
		getHorizontalType(data) {
			if (this.levelSetting !== data) {
				this.setTextOption({
					name: 'textStyle.align',
					data: data
				});
			}
		},
		//
		getVerticalType(data) {
		},
		getTextBgColor(data) {
			let rgba = getRgba(data.rgba);
			this.setTextOption({name: 'backgroundColor', data: rgba});
		},
		fontStyleSetting(data) {
			// console.log(data,'');
			this.setTextOption({
				name: data.name,
				data: data.setting
			});
		},
		getInputData(val) {
			// console.log(data, '');
			this.setTextOption({
				name: 'textContent',
				data: val
			});
		},
		rangeBorder(data) {
			if (this.settingBorder.valueRange !== data) {
				this.setTextOption({
					name: 'borderStyle.borderRadiusWidth',
					data: data
				});
			}
		},
		rangeTextBorder(data) {
			if (this.textFontSize.valueRange !== data) {
				this.setTextOption({
					name: 'textStyle.fontSize',
					data: data
				});
			}
		},
		rangeTextIndent(data) {
			if (this.fristShrink.valueRange !== data) {
				this.setTextOption({
					name: 'textStyle.indent',
					data: data
				});
			}
		},
		rangeTextLineHeight(data) {
			if (this.fontLineHeight.valueRange !== data) {
				this.setTextOption({
					name: 'textStyle.lineHeight',
					data: data
				});
			}
		},
		rangeTextletterSpacing(data) {
			// console.log(data, this.fontSpacing.valueRange, '');
			if (this.fontSpacing.valueRange !== data) {
				this.setTextOption({
					name: 'textStyle.letterSpacing',
					data: data
				});
			}
		},
		getTextColor(data) {
			let rgba = getRgba(data.rgba);
			// let rgb =
			//   'rgba(' +
			//   data.rgba.r +
			//   ',' +
			//   data.rgba.g +
			//   ',' +
			//   data.rgba.b +
			//   ',' +
			//   data.rgba.a +
			//   ')';
			if (this.fontStyle.rgba !== rgba) {
				this.setTextOption({
					name: 'textStyle.color',
					data: rgba
				});
			}
		},
		getOutType(data) {
			if (this.boderInnerType !== data) {
				this.setTextOption({
					name: 'borderStyle.lineType',
					data: data
				});
			}
		},
		outwidth(data) {
			if (this.innerStrokes.valueRange !== data) {
				this.setTextOption({
					name: 'borderStyle.lineWidth',
					data: data
				});
			}
		},
		outColor(data) {
			let rgba = getRgba(data.rgba);
			// let rgb =
			//   'rgba(' +
			//   data.rgba.r +
			//   ',' +
			//   data.rgba.g +
			//   ',' +
			//   data.rgba.b +
			//   ',' +
			//   data.rgba.a +
			//   ')';
			if (this.innerObject.rgba !== rgba) {
				this.setTextOption({
					name: 'borderStyle.lineColor',
					data: rgba
				});
				// this.innerObject.titleColor = colorTransformation(rgb);
				this.innerObject.rgba = rgba;
			}
		},
	}
};
</script>

<style scoped>
  .chart-basis .rotateIcon {
    transform: rotate(-90deg);
  }

  .optionsItem {
    margin-bottom: 0.8vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .optionsItem .span-title {
    display: block;
    flex: 1;
    color: #acbbda;
  }
</style>
