<template>
  <div class="card"
    :style="{...setFocusStyle,backgroundImage: `url(${cardStyle.backgroundImage})`,backgroundColor: cardStyle.backgroundColor}"
    :class="{contentColor,'card-flexsetting':!options.style.switchingMode}"
    @dragleave="dragleave"
    @dragover="dragover($event)"
    @drop="axisDrop($event)">
    <div class="cardTileBox" >
      <input class="cardTitle"
        @dragstart.prevent
        :style="cardTitle"
        placeholder=""
        :value="cardData.name||''"
        maxlength="25"
        @input='requestDrop'
        @blur="changeCardtitle($event)"
        type="text">
    </div>
    <div class="cardValue"
      :style="cardValue">
      {{cardData.value?cardData.value:numberPlaceholder}}
    </div>
  </div>
</template>

<script>
import {mapGetters,mapMutations} from 'vuex';
export default {
	name: 'cardChart',
	props: {
	  containerOptions:Object,
		options: {
			type: Object,
			required: true
		},
		chartIndex: {
			type: Number
		},
		tableId: {
			type: String
		},
		yFiles: {
			type: Array
		}
	},
	data() {
		return {
			// yFileDataS: [],
			// cardStyle: '',//
			// cardData: 0,
			contrast: false,
			numberPlaceholder: '',
			// cardTitleValue: '',
			clickData: this.currentTableOptions,//
			styleBind:false,
		};
	},
	watch: {
		// contrast: {
		// 	handler(val) {
		// 		// console.log(this.contrast,'');
		// 		if (val) {
		// 			this.updateOptions();
		// 		}
		// 	}
		// }
	},
	computed: {
	  ...mapGetters('chartBus', {
			chartDataList: 'returnData',
		}),
		cardStyle(){
	    return this.options.style;
		},
		cardData(){
	    return this.options.data;
		},
		cardValue() {
			let vm = this;
			return {
				fontSize: vm.cardStyle.numberFont + 'px',
				color: vm.cardStyle.numberColor,
			};
		},
		cardTitle() {
			let vm = this;
			return {
				fontSize: vm.cardStyle.fontSize + 'px',
				color: vm.cardStyle.color,
				textAlign: vm.cardStyle.textAlign
			};
		},
		setFocusStyle() {
			if (this.isFocusDrag && this.isCurrentChart) {
				return {
					'border-color': '#28b3fc',
					'box-shadow': '0 0.1vw 0.2vw 0.2vw rgba(40, 179, 252,0.5) inset',
					'border-radius': '0.1vw'
				};
			}
		},
		...mapGetters('dataDimension', {
			dragObject: 'getDragObject',
			isFocusDrag: 'isFocusDrag',
			currentHoverIndex: 'getCurrentHoverIndex'
		}),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',
			currentTableOptions: 'getCurrentChartOptions'
		}),
		// x,y,w,h,
		tablePositionOptions() {
			const {h, w, x, y} = this;
			return {h, w, x, y};
		},
		/** */
		isCurrentChart: function () {
			return this.currentChartIndex === this.chartIndex;
		}
	},
	methods: {
	  ...mapMutations('canvasPlace', {
		  renderOptionSetter: 'renderOptionSetter'
	  }),
		changeCardtitle(e) {
			let vm = this;
	    e=e||window.event;
	    let target=e.target||e.srcElement;
	    if(target.value==this.cardData.name)return false;
			vm.renderOptionSetter({
				key: 'data.name',
				value:target.value,
				id:vm.chartIndex
				// value: vm.cardTitleValue
			});
			vm.$store.commit('canvasPlace/setChartOptionsAmendCount',{index:this.chartIndex});
		},
		requestDrop() {
			// this.contrast = true;
			// this.clickData = this.currentTableOptions;
		},
		//
		contentColor() {
			if (this.chartIndex === this.currentHoverIndex && this.isCurrentChart) {
				let type = this.dragObject.inputType;
				let obj = {
					int: 'content-int',
					date: 'content-date',
					char: 'content-char'
				};
				return obj[type] || '';
			}
			return '';
		},
		dragleave(e) {
			this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
		},
		dragover(e) {
			this.$store.commit('dataDimension/setCurrentHoverIndex', this.chartIndex);
			if(this.isFocusDrag){
				e.stopPropagation();
				e.preventDefault();
			}
		},
		axisDrop(e) {
			this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
			if(this.isCurrentChart){
			  let yData=[{
					yField: this.dragObject.name,
					ySourceId: this.dragObject.tableIndex,
					ySourceName: this.dragObject.tableName,
					yOp:'count'
				}];
				this.$store.dispatch('canvasPlace/containerOptionSetterAll',[
					{
						key: 'yData',
						value: yData
					},
					{
						key: 'xField',
						value: this.dragObject.name
					},
					{
						key: 'xSourceId',
						value: this.dragObject.tableIndex
					},
					{
						key: 'xSourceName',
						value: this.dragObject.tableName
					},
				]);
				this.updateOptions();
				// this.getYopData({
				// 	yFieldDa: this.dragObject.name,
				// 	ySourceIds: this.dragObject.tableIndex,
				// 	ySourceNames: this.dragObject.tableName,
				// });

			}
		},
		// 
		updateOptions() {
			let vm = this;
			// if (vm.currentTableOptions) {
			// 	vm.clickData = vm.currentTableOptions;
			// }
			// const  optionStyle={
			// 	title:  vm.cardTitleValue,//
			// 	fontSize:vm.cardStyle.fontSize,//
			// 	color:  vm.cardStyle.color,//
			// 	backgroundColor: vm.cardStyle.backgroundColor,//
			// 	backgroundImage:  vm.cardStyle.backgroundImage,//
			// 	textAlign:vm.cardStyle.textAlign,//
			// 	numberFont: 12,//
			// 	numberColor: 'rgba(255,255,255,1)',//
			// 	switchingMode: vm.cardStyle.switchingMode,//
			// };

			// Object.assign( vm.clickData.renderOptions.style,optionStyle);
			const {x,y,value,xData,...otherOptions}=vm.containerOptions;
			let sendOptions={
				...otherOptions,
				projectId: sessionStorage.getItem('projectId'),
				renderOptions:JSON.stringify(vm.options),
			};
			// let params =
			this.$put('api/v2/chart', [sendOptions]).then(pormise => {
				if (pormise.code=='000000') {
					vm.changeOneData();
				}
			});
			//   {
			// 	projectId: sessionStorage.getItem('projectId'), //ID
			// 	tableId: vm.tableId, //
			// 	layerName: vm.clickData['containerOptions']['layerName'],
			// 	chartsType: vm.clickData['containerOptions']['chartsType'],
			// 	xPosition: vm.clickData['containerOptions']['xPosition'], //x
			// 	yPosition: vm.clickData['containerOptions']['yPosition'],
			// 	tableWidth: vm.clickData['containerOptions']['tableWidth'], //
			// 	tableHeight: vm.clickData['containerOptions']['tableHeight'],
			// 	level: vm.clickData['containerOptions']['level'],
			// 	xField: vm.yFileDataS.length > 0 ? vm.yFileDataS[vm.yFileDataS.length - 1]['yField'] : '',
			// 	xSourceId: vm.yFileDataS.length > 0 ? vm.yFileDataS[vm.yFileDataS.length - 1]['ySourceId'] : '',
			// 	xSourceName: vm.yFileDataS.length > 0 ? vm.yFileDataS[vm.yFileDataS.length - 1]['ySourceName'] : '',
			// 	yData: vm.yFileDataS.length > 0 ? [vm.yFileDataS.pop()] : [],
			// 	series: [{}],
			// 	renderOptions:JSON.stringify(vm.clickData.renderOptions)
			// }
		},
		changeOneData() {
			let vm = this;
			vm.$get('api/v2/oneChart', {
				projectId: sessionStorage.getItem('projectId'),
				tableId: vm.tableId
			}).then(res => {
			  let data=res.data[0];
				// data.containerStatus={
				// 	isSetOptions:true,
				// 	dataDimensionShow:false
				// };
				let renderOptions = JSON.parse(data.renderOptions);
			  let {y,x,...otherOptions}=data.containerOptions;
				renderOptions.data.name=data.containerOptions.seriesData.name;
				renderOptions.data.value=data.containerOptions.seriesData.value;
			  let allOptions={
					containerOptions:otherOptions,
					containerStatus:{
						isSetOptions:true,
						dataDimensionShow:false
					},
					renderOptions,
				};
				vm.$store.commit('canvasPlace/changeIndexChart', {
					index:this.chartIndex,
					data:allOptions
				});

			});
		},
	},
	mounted() {
		if (this.tableId) {
			this.$store.commit('canvasPlace/setRenderedStatus');
		}
	}
};
</script>

<style scoped>
  .cardTileBox {
    height: 50%;
    padding: .5vw;
    width: 100%;
    display: flex;
    justify-content: center;

  }

  .cardTileBox input {
    width: 100%;
    caret-color: rgba(255, 255, 255, 1);
    text-align: center;
  }

  input::-webkit-input-placeholder {
    color: rgba(255, 255, 255, 1);
  }

  input::-moz-placeholder {
  /*/ / */
  color: rgba(255, 255, 255, 1);
  }

  input:-moz-placeholder {
  /*/ /  */
  color: rgba(255, 255, 255, 1)
  }

  input:-ms-input-placeholder {
  /*/ / ie */
  color: rgba(255, 255, 255, 1);
  }

  .cardValue {
    height: 50%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }


  .card-flexsetting{
    display: flex;
    justify-content: space-between;
  }

  .card {
    transform: translateZ(0);
    background-repeat: no-repeat !important;
    background-position: center !important;
    background-size: 100% 100%;
    color: #fff;
    height: 100%;
  }

  .cardTitle {
    background: rgba(23, 33, 50, 0);
    outline: none;
    color: #fff;
    position: relative;
  }
</style>

