<template>
  <div
    class="optionBox"
    :style="{
      overflow:'hidden',
      height:isShowContent?headerHeight+contentHeight+'px':headerHeight+'px'
    }">
    <div class="chart-basis"
      ref="headerBox"
      :class="{rotate:isShowContent}"
      @click="showEvent">
      <span>{{chartOptionName}}</span>
      <span class="biFont icon-arrowLeft arrow"
      ></span>
    </div>
    <slot></slot>
    </div>
</template>
<script>
export  default {
	name:'chartOptionsAnimate',
	props:['chartOptionName'],
	data(){
		return {
			headerHeight:0,
			contentHeight:0,
			isShowContent:true,
		};
	},
	methods:{
		showEvent(){
			this.isShowContent=!this.isShowContent;
		},
		setContentHeight(val){
      this.contentHeight=val;
		}
	},
	mounted() {
	  let vm =this;
	  //
		let headerStyle= getComputedStyle(vm.$refs.headerBox);
		let marginBottom=headerStyle.marginBottom;
		let len= marginBottom.length;
		let marginBottomVal= Number(marginBottom.substring(0, len - 2));
    this.headerHeight=vm.$refs.headerBox.offsetHeight + marginBottomVal;
	},
};
</script>
<style>
  .optionBox{
    transition: height .2s;
  }
  .chart-basis .arrow{
    transition: transform 0.3s;
  }
  .chart-basis.rotate .arrow {
    transform: rotate(-90deg);
  }
</style>
