<template>
  <!-- -->
  <div @mousedown="getContainerSize"
    class="container">
    <div class="bg"
      v-show="!isSetOptions"></div>
    <div @click="goBackMap"
      class="goBack"
      v-show="parentId&&parentId!=100000">
      
    </div>
    <v-chart :options="opt"
      @click="mapDown"
      autoresize
      ref="instance"></v-chart>
  </div>
</template>
<script>
import {cityMap} from './china-main-city-map.js';
import {cityMap2} from './china-main-city-map2.js';
import {echartsCommonMixin} from '../../mixin/echartsCommonMixin';
import vECharts from 'vue-echarts';
import 'echarts/lib/component/visualMap';
import 'echarts-gl/dist/echarts-gl.min';
import 'echarts/map/js/china.js';
import {mapGetters, mapMutations} from 'vuex';

const cityMapJson = new Map();
const mapDataList = {};
Object.entries(cityMap).map(ele => {
	cityMapJson.set(ele[1], ele[0]);
});
// const mapContext = require.context('@/../static/map/', true, /\.json$/);
// mapContext.keys().forEach(function (file) {
// 	let splitArr = file.split('/');
// 	let len = splitArr.length;
// 	let cityID = splitArr[len - 1];
// 	cityID = cityID.replace(/\.json$/, '');
// 	mapDataList[cityID] = () =>

// 		require.ensure([], () => mapContext(file), 'mapData');
// });
export default {
	name: 'echarts-choropleth',
	mixins: [echartsCommonMixin],
	props: {
		tableId: {
			type: [String, Number]
		}
	},
	data() {
		return {
			mapJsonBaseURL: 'https://img.24e.co/bi/map/',
			mapJsonId: 100000, //id
			getMapDataResult: null,
			mapNameChinese: '', //
			chinaJson: null,
			color: ['#0572d9', '#00d1ff', '#2f5eff'],
			mapStack: [],
			parentId: null,
			parentName: null,
			opt: {},
		};
	},
	watch: {
		options: {
			handler() {
				if (this.isShowLoading) {
					this.initChart();
				}
			}
		},
	},
	beforeMount() {
		let vm = this;
		vm.mapNameChinese = vm.options.series[0].savaClickMapName;
		vm.mapJsonId = vm.options.series[0].savaClickMapJson ? vm.options.series[0].savaClickMapJson : 100000;
		if (vm.mapJsonId === 100000) {
			this.getMapDataResult = this.axios.get('https://24e-img.oss-cn-shenzhen.aliyuncs.com/bi/map/100000.json').then(res => {
				mapDataList[100000] = res.data;
			});
		} else {
			this.getMapDataResult = this.axios.get(this.mapJsonBaseURL + vm.mapJsonId + '.json').then(res => {
				mapDataList[vm.mapJsonId] = res.data;
			});
			this.axios.get(this.mapJsonBaseURL + '100000.json').then(res => {
				mapDataList[100000] = res.data;
			});
		}
		Object.values(cityMap2).map(ele => {
			this.axios.get(this.mapJsonBaseURL + ele.districtCode + '.json').then(res => {
				mapDataList[ele.districtCode] = res.data;
			});
		});
	},
	mounted() {
    let vm = this;
		if (vm.$attrs.containerOptions.tableId) {
			vm.getMapDataResult.then(() => {
				vm.initChart();
			});
		}
	},
	computed: {
		...mapGetters('canvasPlace', {
			ScreenSign: 'getScreenSign'
		}),
		...mapGetters('dataDimension', {
			pageFilterList: 'getPageFilterList',
		}),
		// ...mapGetters('chartBus', {
		// 	chartDataList: 'returnData',
		// })
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter'
		}),
		goBackMap() {
			let vm = this;
			let provinceLevleReg = new RegExp(/(0{4})$/);//
			let cityLevelReg = new RegExp(/(0{2,3})$/);//
			if (this.parentId.match(provinceLevleReg)) {
				let mapData = mapDataList[100000];
				// const thisMapJson = response.data;
				vm.registerAndSetOption(
					100000,
					'',
					mapData,
					false
				);
				//IDName
				vm.parentId = 100000;
				vm.parentName = '';
				vm.filterByMap('');
				return false;
			}
			if (this.parentId.match(cityLevelReg)) {
				let mapId = this.parentId.substr(0, 2) + '0000';
				let mapName = cityMapJson.get(mapId);
				let mapData = mapDataList[mapId];
				vm.registerAndSetOption(
					mapId,
					mapName,
					mapData,
					false
				);
				//IDName
				vm.parentId = mapId;
				vm.parentName = mapName;
				vm.filterByMap(mapName);
			}

		},
		mapDown(params) {
			if (!this.isSetOptions) return false;
			let vm = this,
				cityId = cityMap[params.name];
			vm.renderOptionSetter({
				key: 'series.0.savaClickMapName',
				id: vm.chartIndex,
				value: params.name
			});
			vm.renderOptionSetter({
				key: 'series.0.savaClickMapJson',
				id: vm.chartIndex,
				value: cityId ? cityId : 100000
			});
			// vm.$store.commit('canvasPlace/setChartOptionsAmendCount');//
			// vm.$store.commit('canvasPlace/satrapModifyCount'); //
			if (cityId) {
				if (cityMap2[params.name] && !mapDataList[cityMap2[params.name]['subDistrictCode'][0]]) {
					//
					// debugger;
					vm.mapJson = mapDataList[cityId];
					vm.registerAndSetOption(
						cityId,
						params.name,
						vm.mapJson,
						true
					);
					cityMap2[params.name]['subDistrictCode'].map(ele => {
						this.axios.get(this.mapJsonBaseURL + ele + '.json').then(res => {
							mapDataList[ele] = res.data;
						});
					});
				} else {
					if (!mapDataList[cityId]) {//json
						this.axios.get(this.mapJsonBaseURL + cityId + '.json').then(res => {
							mapDataList[cityId] = res.data;
							vm.mapJson = mapDataList[cityId];
							vm.registerAndSetOption(
								cityId,
								params.name,
								vm.mapJson,
								true
							);
						});
					} else {//json
						vm.mapJson = mapDataList[cityId];
						vm.registerAndSetOption(
							cityId,
							params.name,
							vm.mapJson,
							true
						);

					}
				}
			}
			// else {
			// console.log('id');
			// //mapStack
			// vm.registerAndSetOption(
			// 	vm.mapJsonId,
			// 	vm.mapNameChinese,
			// 	vm.chinaJson,
			// 	false
			// );
			// vm.mapStack = [];
			// vm.parentId = vm.mapJsonId;
			// vm.parentName = vm.mapNameChinese;
			// }
			// vm.$store.commit('canvasPlace/setChartOptionsAmendCount');//
			// vm.$store.commit('canvasPlace/satrapModifyCount'); //
			vm.filterByMap(params.name);
			// let assicOptions = {
			// 	// screenStart,//
			// 	// screenEnd,//
			// 	// screenState: 2,//
			// 	projectId: sessionStorage.getItem('projectId'), //ID
			// 	sizerSn: vm.ScreenSign.pageSn,
			// 	sizerType: 1, //    1.  2.
			// 	screenText: [params.name],//
			// 	screenType: 'text',//
			// 	screenSourceId: vm.pageFilterList[0].tableIndex, //id
			// 	screenField: vm.pageFilterList[0].name, //
			// };
			// vm.$store.commit('canvasPlace/setChartOptionsAmendCount');//
			// vm.$store.commit('canvasPlace/satrapModifyCount'); //
			// // 
			// vm.$put('api/sizer', assicOptions).then(res => {
			// 	if (res.code === '000000') {
			// 		vm.$store.dispatch('canvasPlace/screeningRequest');//
			// 		// vm.$store.commit('dataDimension/setAmendchartRefresh'); //
			// 	}
			// });
		},
		filterByMap(name) {
			let vm = this;
      console.log(vm.pageFilterList);
			let assicOptions = {
				sizerId: this.ScreenSign.pageSn,
				screenData: [{
					selectAllData: name === '',
					// screenStart,//
					// screenEnd,//
					// screenState: 2,//
					projectId: sessionStorage.getItem('projectId'), //ID
					sizerSn: vm.ScreenSign.pageSn,
					sizerType: 1, //    1.  2.
					screenText: [name],//
					screenType: 'text',//
					screenSourceId: vm.pageFilterList[0].tableIndex, //id
					screenField: vm.pageFilterList[0].name, //
				}]
			};
			vm.$store.commit('canvasPlace/setChartOptionsAmendCount');//
			vm.$store.commit('canvasPlace/satrapModifyCount'); //
			// 
			vm.$put('api/sizer', assicOptions).then(res => {
				if (res.code === '000000') {
					vm.$store.dispatch('canvasPlace/screeningRequest');//
					// vm.$store.commit('dataDimension/setAmendchartRefresh'); //
				}
			});
		},
		initChart() {
			let vm = this;
			vm.mapNameChinese = vm.options.series[0].savaClickMapName;
			vm.mapJsonId = vm.options.series[0].savaClickMapJson ? vm.options.series[0].savaClickMapJson : 100000;
			vm.parentId = vm.mapJsonId;
			vm.parentName = 'china';
			vm.mapJson = mapDataList[vm.mapJsonId];
			vm.chinaJson = vm.mapJson;
			vm.registerAndSetOption(
				vm.mapJsonId,
				vm.mapNameChinese,
				vm.mapJson,
				false
			);
			vm.$store.commit('canvasPlace/hideLoading', this.chartIndex);
			// 	vm.parentId = vm.mapJsonId;
			// 	vm.parentName = 'china';
			// mapDataList[vm.mapJsonId]().then(response => {
			// 	vm.mapJson = response;
			// 	vm.chinaJson = vm.mapJson;
			//
			// 	vm.registerAndSetOption(
			// 		vm.mapJsonId,
			// 		vm.mapNameChinese,
			// 		vm.mapJson,
			// 		false
			// 	);
			// 	vm.parentId = vm.mapJsonId;
			// 	vm.parentName = 'china';
			// });
		},
		registerAndSetOption(id, name, mapJson, flag) {
			// console.log(mapJson);
			vECharts.registerMap(name, mapJson);
			let vm = this;
			// vm.opt = Object.assign(vm.options, vm.geo);
			vm.opt = {
				tooltip: {
					...vm.options.tooltip,
					formatter: function (params) {
						if (params.data) {
							return params.seriesName + '<br/>' + params.data.name + ':' + params.data.value[2];
						}
					}
				},
				title: vm.options.title,
				visualMap: {
					show: false,
					type: 'continuous',
					showLabel: true,
					min: vm.options.Visual.visualMapMin,
					max: vm.options.Visual.visualMapMax,
					// max: <vm className='options Visual visualMapMax'> </vm>,
					// calculable: true,
					seriesIndex: [0, 1, 2, 3],
					inRange: {
						color: ['rgb(10,25,41)', '#0572d9', '#00d1ff', '#2f5eff', 'rgb(0,248,111)'] // 
					}
				},
				geo: {
					map: name,
					label: {
						emphasis: {
							show: false
						}
					},
				},
				series: [
					{
						name: 'MAP',
						type: 'map',
						mapType: name,
						selectedMode: 'single', //
						label: {
							fontSize: 12,
							color: 'rgba(255,255,255,1)',
							normal: {
								show: true,
								color: 'rgba(255,255,255,1)'
							},
							emphasis: {
								show: false,
								color: '#fff'
							}
						},
						itemStyle: {
							areaColor: 'rgb(10,25,41)',//
							borderColor: '#00c6ff',
							borderWidth: 0.8,
							emphasis: {
								areaColor: 'rgba(51,190,255,1)',
								borderColor: '#00baff'
							}
						},
						rippleEffect: {
							brushType: 'stroke'
						}
						// data: vm.initMapData(mapJson)
						// data: vm.intMapFrieData(vm.options.series[0].data)
					},
					// ...vm.options.series
					/**
             * @
             * datavm.initMapData(mapJson)
             * @
             * data
             * 
             *   
             * 
             * data
             * */
				]
			};
			if (vm.options.series[0].echartsMapType === 'effectScatter') {//series
				vm.opt.series = [vm.opt.series[0], ...vm.options.series];
				// vm.opt.series[1] = vm.options.series[0];
			} else {
				//
				vm.opt.series[0].data = vm.options.series[0].dataBranch;
			}
			if (flag) {
				//mapStackparentIdparentName,
				vm.mapStack.push({
					mapId: vm.parentId,
					mapName: vm.parentName
				});
				vm.parentId = id;
				vm.parentName = name;
			}
		},
		// intMapFrieData(data) {
		// 	return data;
		// },
		initMapData(mapJson) {
			let mapData = [];
			for (let i = 0; i < mapJson.features.length; i++) {
				// let colorLen = vm.color.length;
				mapData.push({
					name: mapJson.features[i].properties.name,
					// itemStyle: {areaColor: 'rgb(10,25,41)'}
				});
			}
			return mapData;
		},
	}
};
</script>
<style scoped>
  .bg {
    background-image: url("~echartsBg/3dWorldMap.png");
    background-repeat: no-repeat;
  }

  .goBack {
    color: #fff;
    background: rgb(9, 48, 63);
    display: inline-block;
    width: 3vw;
    height: 1.3vw;
    border: solid 0.0625vw rgba(0, 255, 255, 1);
    text-align: center;
    line-height: 1.3vw;
    position: absolute;
    top: 1.1vw;
    left: 1vw;
    cursor: pointer;
    z-index: 1;
  }

  .container {
    z-index: 999999;
  }
</style>
