<template>
  <vue-slider ref="sliders"
    :style="style"
    @mousedown.native.stop
    :dotStyle="dotStyle"
    :railStyle="railStyle"
    :processStyle="processStyle"
    :tooltipFormatter="$attrs.tooltipFormatter||formatterPercentAge"
    v-bind="$attrs"
    @change="dataChange"
    @dragging="dataDragging"
    @dragEnd="dragEnd"
    v-model="value"
  ></vue-slider>
</template>
<script>
import vueSlider from 'vue-slider-component';
import 'vue-slider-component/theme/default.css';
import {mapGetters} from 'vuex';

export default {
	components: {
		vueSlider
	},
	props: {
		valueRange: {
			type: [Number, Array],
			// required: true
		},
		customerDotStyle:Object,
		customerProcessStyle: Object,
		customerRailStyleStyle:Object,
	},
	/**
     * 
     * valueRange[]  [100]   [0,100]
     * minValue 
     * maxValue  
     */
	data() {
		return {
			formatterPercentAge: '{value}%',
			value: [],
			style: {
				width: ' 6.2vw',
				marginLeft: '0.4vw',
				marginRight: '0.4vw'
			}
		};
	},
	computed: {
		...mapGetters('main', ['getVw']),
		dotStyle() {
			const dotStyle = {
				position: 'absolute',
				width: ~~(this.getVw(7)+0.5) + 'px',
				height: ~~(this.getVw(7)+0.5) + 'px',
				left: '50%',
				top: '50%',
				'margin-top': -~~(this.getVw(7)+0.5) / 2 + 'px',
				'margin-left': -~~(this.getVw(7)+0.5) / 2 + 'px',
				'border-width': this.getVw(2) + 'px',
				'border-style': 'solid',
				borderRadius:'50%',
				borderColor:'#fff',
				...this.customerDotStyle
			};

			return dotStyle;
		},
		processStyle() {
			const processStyle = {
				backgroundColor: '#acbbda',
				...this.customerProcessStyle
			};
			return processStyle;
		},
		railStyle() {
			const railStyle = {
				position: 'relative',
				height: this.getVw(1) + 'px',
				width: '90%',
				marginLeft: '0.3vw',
				backgroundColor: '#2a2e38',
				...this.customerRailStyleStyle
			};
			return railStyle;
		}
	},
	methods: {
		// rangeValue() {
		// 	this.$emit('rangedata', this.value);
		// },
		dataChange() {
			this.$emit('rangedata', this.value);
		},
		dataDragging(){},
		dragEnd(value){
			this.$emit('dragEnd', this.value);
		}
	},
	mounted() {
		this.value = this.valueRange;
	},
	watch: {
		valueRange() {
			this.value = this.valueRange;
		}
	}
};
</script>
<style scoped>
  .slider-box {
    width: 6.2vw;
    margin-left: 0.4vw;
    margin-right: 0.4vw;
  }
</style>
