<template>
  <div class="tableContainer"
    :class="{'bgList':tbodyList.length === 0}"
  >
    <!--    v-click-outside="{fn:hideMoreMenu}"-->
    <div ref="coordinate"
      class="table-father-position"
      :style="setFocusStyle"
      :class="contentColor"
      @dragleave="dragleave"
      @dragover="dragover($event)"
      @drop="axisDrop($event)">
      <easy-scroll
        v-show='tableHeader.length > 0'
        :barOption="opt"
        style="height: 100%"
        @xScrollToEnd="handleScroll"
        :viewStyle="viewStyle">
        <div class="table-flex-content">
          <div class="table-flex table-list-th"
            v-for="(header,index) in tableHeader"
            :key="index">
            <input class="table-input"
              type="text"
              :value="header"
              :disabled="!tableId"
              @input='changeTableHeader($event,index)'
              @focus='focusTitle'
              @blur='changeTableTitle'>
            <i @click="thDelect(index)"
              v-if='tableId'
              class="biFont icon-clear table-delect"></i>
          </div>
        </div>
        <div class="table-flex-content">
          <tr class="table-flex"
            v-for="(item,index) in tbodyList"
            :key="index"
            @click.right.prevent='rightClick($event,index)'>
            <div class="table-flex-td"
              v-for="(row,ind) in item"
              :key="ind">
              <div>{{row}}</div>
            </div>
          </tr>
        </div>
      </easy-scroll>
    </div>
    <right-click
      v-click-outside="{fn:hideMoreMenu}"
      :clientx="menux"
      :clienty="menuy"
      :selectList="menuList"
      @clickState="getSortIndex"
      v-show="rightShow">
    </right-click>
  </div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex';
import rightClick from '@/components/RightClick/index';

export default {
	name: 'interactive-tableList',
	props: {
		options: {
			type: Object,
			required: true
		},
		containerOptions: {
			type: Object,
			required: true
		},
		chartIndex: {
			type: Number
		},
		tableId: {
			type: String
		},
		yFiles: {
			type: Array
		}
	},
	components: {
		'right-click': rightClick
	},
	data() {
		return {
			// tabBg: true,
			loading: false,
			rightShow: false,//
			menuList: ['', ''],
			tablePage: {
				pageSize: 5,
				pageSizes: [5, 10, 15, 20, 50]
			},
			tableTh: [],//
			tableRemak: [], //
			tableList: [],//

			viewStyle: {
				padding: '1vw',
				paddingTop: '1.8vw'
			},
			yFileData: [],
			contrast: false,
			pageCount: 0,
			pageArs: 1,
			menux: 0,
			menuy: 0,
			thisDataAxiso: null,
			clickData: '',
		};
	},
	computed: {
		vwToPx() {
			return this.$store.getters['main/vwToPx'];
		},
		opt() {
			return {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barWidth: 5, //
				barMarginRight: 6, //px
				barOpacityMin: 0.3, //
				autoHideMode: true,
				borderWidth: this.vwToPx(0.125)
			};
		},
		...mapGetters('dataDimension', {
			dragObject: 'getDragObject',
			isFocusDrag: 'isFocusDrag',
			currentHoverIndex: 'getCurrentHoverIndex'
		}),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',
			currentTableOptions: 'getCurrentChartOptions',
			scaleLevel: 'getScaleLevel',
		}),
		tableHeader() {
			return this.options.tableData.header || [];
		},
		tbodyList() {
			return this.options.tableData.data || [];
		},
		// x,y,w,h,
		// tablePositionOptions() {
		// 	const {h, w, x, y} = this;
		// 	return {h, w, x, y};
		// },
		/** */
		isCurrentChart: function () {
			return this.currentChartIndex === this.chartIndex;
		},
		//
		//
		contentColor() {
			if (this.chartIndex === this.currentHoverIndex && this.isCurrentChart) {
				let type = this.dragObject.inputType;
				let obj = {
					int: 'content-int',
					date: 'content-date',
					char: 'content-char'
				};
				return obj[type] || '';
			}
			return '';
		},
		setFocusStyle() {
			//
			if (this.isFocusDrag && this.isCurrentChart) {
				return {
					'border-color': '#28b3fc',
					'box-shadow': '0 0.1vw 0.2vw 0.2vw rgba(40, 179, 252,0.5) inset',
					'border-radius': '0.1vw'
				};
			}
		},
	},
	watch: {
		// tablePositionOptions() {
		// 	this.focusTitle();
		// 	if (this.tableId !== '') {
		// 		this.requestDrop();
		// 	}
		// },
		// contrast: {
		// 	deep: true,
		// 	handler() {
		// 		if (this.contrast) {
		// 			// setTimeout(() => {
		// 			this.updateOptions();
		// 			// }, 2000);
		// 		}
		// 	}
		// }
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter',
			containerOptionSetter: 'containerOptionSetter',
		}),
		getSortIndex(indx) {//
			let vm = this;
			vm.rightShow = false;
			let conantState = [{
				orderCategory: 'category',
				orderCondition: '',
				orderField: vm.yFileData[vm.thisDataAxiso].yField,
				orderOp: indx === 0 ? 'ASC' : 'DESC',
				orderSourceId: vm.yFileData[vm.thisDataAxiso].ySourceId,
				orderSourceName: vm.yFileData[vm.thisDataAxiso].ySourceName
			}];
			vm.updateOptions({orderData: conantState});
		},
		hideMoreMenu() {//
			this.rightShow = false;
		},
		handleScroll(data) {
			//
			if (data) {
				this.pageArs = this.pageArs + 1;
				this.updateOptions({page: this.pageArs});
			}
		},
		rightClick(event, index) {
			let vm = this;
			vm.rightShow = true;
			const path = event.path || (event.composedPath && event.composedPath());
			const ele = path.find(ele => {
				return ele.className.indexOf('tableContainer') > -1;
			});
			let diss = ele.getBoundingClientRect();
			vm.menux = event.clientX - diss.left;
			vm.menuy = event.clientY - diss.top;
			// console.log(vm.menux, vm.menuy, diss.left);
			vm.thisDataAxiso = index;
		},
		requestDrop() {
			//2s
			setTimeout(() => {
				this.contrast = true;
			}, 2000);
		},
		dragleave() {
			this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
		},
		dragover(e) {
			if (this.isCurrentChart) {
				this.clickData = this.currentTableOptions;
				this.$store.commit('dataDimension/setCurrentHoverIndex', this.chartIndex);
			}
			e.stopPropagation();
			e.preventDefault();
		},
		axisDrop() {
			if (this.isCurrentChart) {
				this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
				let currentYData = this.containerOptions.seriesData.yData || [];
				currentYData.push({
					yField: this.dragObject.name,
					ySourceId: this.dragObject.tableIndex,
					ySourceName: this.dragObject.tableName
				});
				// let yData=[...currentYData,];
				console.log(currentYData);
				this.$store.dispatch('canvasPlace/containerOptionSetterAll', [
					{
						key: 'yData',
						value: currentYData
					},
					// {
					// 	key: 'xField',
					// 	value: this.dragObject.name
					// },
					{
						key: 'xSourceId',
						value: this.dragObject.tableIndex
					},
					// {
					// 	key: 'xSourceName',
					// 	value: this.dragObject.tableName
					// },
				]);

				this.updateOptions();
				// this.getYopData({
				// 	yFieldDa: this.dragObject.name,
				// 	ySourceIds: this.dragObject.tableIndex,
				// 	ySourceNames: this.dragObject.tableName
				// });

			}
		},

		/** */
		getYopData({yFieldDa, ySourceIds, ySourceNames}) {
			let vm = this;
			vm.yFileData.push({
				yField: yFieldDa,
				ySourceId: ySourceIds,
				ySourceName: ySourceNames
			});
			vm.updateOptions();
			if (vm.yFileData.length === 0) {
				vm.getOneChartData();
			}
		},
		changeTableHeader(e, index) {//
			const target = e.target || e.srcElement;
			this.focusTitle();
			this.renderOptionSetter({
				key: `tableData.header.${index}`,
				value: target.value
			});
		},
		focusTitle() {//
			this.clickData = this.currentTableOptions;
		},
		changeTableTitle() {//
			this.contrast = true;
		},
		// 
		updateOptions(params = {}) {
			// debugger;
			let {page = 1, orderData = ''} = params;
			let vm = this;
			// vm.tabBg = !vm.tabBg;
			// let containerOptions=vm.currentTableOptions.containerOptions;
			let {xField, xSourceName, ...othterOptions} = vm.containerOptions;
			let sendOptions = {
				...othterOptions,
				projectId: sessionStorage.getItem('projectId'),
				renderOptions: JSON.stringify(vm.options),
				limitData: {
					limitPageSize: 50,
					limitPage: page
				},
			};
			this.$put('api/v2/chart', [sendOptions]
			).then(reason => {
				if (reason.code === '000000') {
					this.getOneChartData();
				}
			});
			//   {
			// 	projectId: sessionStorage.getItem('projectId'),
			// 	tableId: vm.tableId,
			// 	layerName: containerOptions['layerName'],
			// 	chartsType: containerOptions['chartsType'],
			// 	xPosition: containerOptions['xPosition'], //x
			// 	yPosition: containerOptions['yPosition'],
			// 	tableWidth: containerOptions['tableWidth'], //
			// 	tableHeight: containerOptions['tableHeight'],
			// 	level: containerOptions['level'],
			// 	xField: vm.yFileData.length
			// 		? vm.yFileData[vm.yFileData.length - 1]['yField']
			// 		: '',
			// 	xSourceId: vm.yFileData.length
			// 		? vm.yFileData[vm.yFileData.length - 1]['ySourceId']
			// 		: '',
			// 	xSourceName: vm.yFileData.length
			// 		? vm.yFileData[vm.yFileData.length - 1]['ySourceName']
			// 		: '',
			// 	yData: vm.yFileData.length ? vm.yFileData : [],
			// 	remark: vm.tableRemak,
			// 	limitData: {
			// 		limitPageSize: 50,
			// 		limitPage: page
			// 	},
			// 	orderData: orderData,
			// 	seriesData: [],
			// 	renderOptions: JSON.stringify(vm.currentTableOptions.renderOptions)
			// }

		},
		thDelect(index) {
			this.focusTitle();
			// this.$store.dispatch('canvasPlace/containerOptionSetterAll',[
			// 	{	key: 'xField',
			// 		value: this.dragObject.name},
			// 	{
			// 		key: 'xSourceId',
			// 		value: this.dragObject.tableIndex
			// 	},
			// 	{
			// 		key: 'xSourceName',
			// 		value: this.dragObject.tableName
			// 	},
			// 	{
			// 		key: 'yField',
			// 		value: this.dragObject.name
			// 	},
			// 	{
			// 		key: 'ySourceId',
			// 		value: this.dragObject.tableIndex
			// 	},
			// 	{
			// 		key: 'ySourceName',
			// 		value: this.dragObject.tableName
			// 	}
			// ]);
			let yData = this.containerOptions.yData.splice(index, 1);
			this.containerOptionSetter({
				key: 'yData',
				value: yData
			});
			// this.yFileData.splice(index, 1);
			// this.tableTh.splice(index, 1);
			// this.tableList.splice(index, 1);
			// console.log(this.tableList);
			// this.tableRemak.splice(index, 1);
			// if(this.tableRemak.length === 1) {
			// 	this.tableRemak = [];
			// }
			this.updateOptions();
		},
		getOneChartData(options) {
			let vm = this;
			vm.contrast = false;
			vm.$get('api/v2/oneChart', {
				projectId: sessionStorage.getItem('projectId'),
				tableId: vm.tableId
			}).then(res => {
				let data = res.data[0];
				data.containerStatus = {
					isSetOptions: true,
					dataDimensionShow: false
				};
				// data.xSourceId
				data.renderOptions = JSON.parse(data.renderOptions);
				// data.renderOptions.tableData={
				// 	header:data.containerOptions.seriesData.header,
				// 	data:data.containerOptions.seriesData.data,
				// };
				if (options === 'pages') {
					data.renderOptions.tableData.data.push(...data.containerOptions.seriesData.data);
				} else {
					data.renderOptions.tableData.data = data.containerOptions.seriesData.data;
				}

				data.renderOptions.tableData.header = data.containerOptions.seriesData.header;
				// data.renderOptions.tableData.data.push(...data.containerOptions.seriesData.data);

				data.containerOptions.xField = data.containerOptions.x.xSourceName;
				data.containerOptions.xSourceId = data.containerOptions.x.xSourceId;
				data.containerOptions.xField = data.containerOptions.x.xField;
				data.containerOptions.yData = data.containerOptions.y;
				// debugger;
				vm.$store.commit('canvasPlace/changeIndexChart', {
					data,
				});
				// data.renderOptions=JSON.parse(data.renderOptions);
				// this.pageCount = data.containerOptions.seriesData.count;
				// this.tableTh =data.containerOptions.seriesData.header;
				// vm.$set(vm, 'tableList', data.containerOptions.seriesData.data);
				// if (this.tableTh && res.data[0].containerOptions.seriesData.data.length === 0) {
				// 	// vm.tableList.push('');
				// } else {
				// 	// this.tableList = res.data[0].interactiveOptions.data;
				// }
				// this.tableTh[0].forEach((item, idx) => {
				// 	if (!vm.tableRemak[idx]) {
				// 		vm.tableRemak[idx] = item;
				// 	}
				// });
			});
		}
	},
	mounted() {
		let vm = this;
		vm.$store.commit('canvasPlace/setRenderedStatus');
		if (vm.options.series) {
			// debugger;
			vm.tableTh = vm.options.series[0].header;
			console.log(vm.yFiles);
			vm.tableList = vm.options.series[0].data || [];
			vm.yFileData = [];
			vm.tableRemak = vm.options.series[0].header;
			vm.tableTh.forEach((item, index) => {
				vm.tableRemak[index] = vm.tableRemak[index] ? vm.tableRemak[index] : vm.tableTh[index];
			});
			vm.options.series[0].header.forEach((item, idx) => {
				if (!vm.tableRemak[idx]) {
					vm.tableRemak[idx] = vm.options.series[0].header[idx];
				}
			});
		}
	}
};
</script>

<style scoped>
  .tableContainer {
    position: relative;
    font-size: 0.5vw;
    color: #ffffff;
    height: 100%;
    width: 100%;
  }

  .table-flex-content {
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    /*flex-direction:column;*/
    border-top: 0.0625vw solid rgba(40, 179, 252, 0.5);
    border-left: 0.0625vw solid rgba(40, 179, 252, 0.5);
    /*border-right: 0.0625vw solid rgba(40, 179, 252, 0.5);*/
  }

  .table-flex {
    text-align: center;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap-reverse;
  }

  .table-father-position {
    position: absolute;
    color: #d0d0d8;
    width: 100%;
    height: 100%;
  }

  .table-flex-td {
    border-right: 0.0625vw solid rgba(40, 179, 252, 0.3);
    border-bottom: 0.0625vw solid rgba(40, 179, 252, 0.3);
    overflow: hidden;
    /*min-height: 1vw;*/
  }

  .table-flex-td:nth-child(event) > div {
    background: rgb(22, 50, 71);
    color: rgba(0, 248, 255, 1);
    height: 1.3vw;
    line-height: 1.3vw;
    /*min-height: 1vw;*/
  }

  .table-flex-td:nth-child(odd) > div {
    background: rgb(23, 33, 50);
    color: rgba(0, 248, 255, 1);
    height: 1.3vw;
    line-height: 1.3vw;
    /*min-height: 1vw;*/
  }

  .table-list-th {
    background: rgb(23, 33, 50);
    color: rgba(0, 248, 255, 1);
    height: 1.3vw;
    line-height: 1.3vw;
    border-right: 0.0625vw solid rgba(40, 179, 252, 0.3);
    position: relative;
  }

  .table-add-number {
    background: rgb(22, 50, 71);
    color: rgba(0, 248, 255, 1);
    height: 1.3vw;
    line-height: 1.3vw;
    /*min-height: 1vw;*/
  }

  .table-even-number {
    background: rgb(23, 33, 50);
    color: rgba(0, 248, 255, 1);
    height: 1.3vw;
    line-height: 1.3vw;
    /*min-height: 1vw;*/
  }

  .table-input {
    display: inline-block;
    width: 100%;
    background: rgb(22, 50, 71);
    text-align: center;
    outline: none;
    font-size: 0.8vw;
    color: rgba(255, 255, 255, 1);
    height: 1.8vw;
  }

  .table-list-th:hover .table-delect {
    display: inline-block;
  }

  .table-delect {
    display: none;
    width: 1vw;
    height: 1vw;
    position: absolute;
    top: 38%;
    z-index: 2;
    transform: scale(0.7) translate(0, -50%);
    cursor: pointer;
  }

  /**/
  .content-int {
    background-color: rgba(17, 190, 110, 0.3);
    border-color: #0fff00;
    box-shadow: 0.01vw 0.01vw 0.2vw 0 rgb(16, 255, 0);
    border-radius: 0.1vw;
  }

  .content-char {
    border-color: #28b3fc;
    background-color: rgba(40, 179, 252, 0.3);
    box-shadow: 0.01vw 0.01vw 0.2vw 0 rgb(40, 179, 252);
    border-radius: 0.1vw;
  }

  .content-date {
    background-color: #2e3325;
    border-color: #ffff00;
    box-shadow: 0.01vw 0.01vw 0.2vw 0 rgb(255, 255, 0);
    border-radius: 0.1vw;
  }

  .bgList {
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-position: center !important;
    height: 100%;
    background-size: 100% 100% !important;
    background: url("data:image/png;base64,") no-repeat;
  }
</style>
