<template>
  <div id="app">
    <router-view class="main"></router-view>
  </div>
</template>

<script>
import "@/assets/common/fonts/iconfont/iconfont.css";
import "@/assets/common/fonts/font/font.css";
import "@/assets/common/css/reset.css"; //
import "@/assets/common/css/common.css";
import { mapGetters } from "vuex";
export default {
  name: "App",
  computed: {
    ...mapGetters("config", ["ssoUrl"])
  },
  sockets: {
    connect() {
      let uid = localStorage.getItem("userid");
      this.$socket.emit("login", uid);
    }
  },
  mounted() {
    let vm = this;
    console.log(vm.$vwToPx(2));
    window.onresize = () => {
      vm.$store.dispatch("main/resizeASync", {
        width: window.innerWidth,
        height: window.innerHeight
      });
    };
  }
};
</script>
<style>
html {
  font-family: SR, Georgia, serif;
  overflow: hidden;
  /* touch-action: none; */
}

#app > .main {
  width: 100%;
  height: 100vh;
}
</style>
