<template>
  <div class="sider">
    <transition name="fade">
      <sider-items v-show="leftSideOpen"
        @getChartType="getChartType"></sider-items>
      <!-- <layer-controler></layer-controler> -->
    </transition>
    <transition name="slide">
      <sider-shrink v-show="!leftSideOpen"
        @getChartType="getChartType"></sider-shrink>
    </transition>
  </div>
</template>
<script>
import SiderItems from './SiderItems';
import SiderShrink from './SiderShrink';
import {baseChartList} from '@/assets/SiderBar/js/basicChartData.js';
import {mapGetters} from 'vuex';

export default {
	components: {
		'sider-items': SiderItems,
		'sider-shrink': SiderShrink
	},
	computed: {
		...mapGetters('defaultChartOptions', {
			getChartOptions: 'getEchartsDefaultOption'
		}),
		...mapGetters('defaultMediaOptions', {
			getMediaOptions: 'getMedia'
		}),
		...mapGetters('defaultInteractiveOptions', {
			getInteractive: 'getInteractiveDefault'
		}),
		...mapGetters('voiceCommand',{// 
      creatChart: 'getBasicChart', // 
		}),
		leftSideOpen() {
			return this.$store.state.dashboard.leftSideOpen;
		}
	},
	watch:{
	  // 
    creatChart(){
      if (this.creatChart==='await') return;
      // console.group(baseChartList,'list')
      this.getChartType(this.getBasicChartCode(this.creatChart))
    }
	},
	methods: {
		getChartType(params) {
			let defaultOptions = {};
			if (params.type === 'chart') {
				defaultOptions = this.getChartOptions(params.chartType);
			} else if (params.type == 'media') {
				defaultOptions = this.getMediaOptions(params.chartType);
			} else if (params.type == 'interactive') {
				defaultOptions = this.getInteractive(params.chartType);
			}else if(params.type == 'mouldle'){
		  this.$store.commit('canvasPlace/setModuleType',true );//truefalse
				return ;
			}
			// debugger;
			this.$store.commit('canvasPlace/setChartContainer', {defaultOptions:JSON.parse(JSON.stringify(defaultOptions))});
		},
    getBasicChartCode(code){
      let basicChart = {};
      switch (code) {
        //19  20 21 3D 22 2324 25
        case 'ad123481d91c3bdf7b58a9688d6ae4bd':// 
          basicChart = baseChartList[0].children[0];
          break;
        case '9a3d21e5f0f4257d054fa0673c051d05':// 
          basicChart = baseChartList[0].children[4];
          break;
        case '448f1030b0ca85680464ab308c5ab55f':// 
          basicChart = baseChartList[0].children[3];
          break;
        case '761af21894a036885e522a3926dcff08':// 
          basicChart = baseChartList[0].children[17];
          break;
        case 'b84d4588456f43279a64ebaf9c77baba':// 
          basicChart = baseChartList[0].children[13];
          break;
        case 'df0e619641f3f696153e03a129d27ccd':// 
          basicChart = baseChartList[0].children[2];
          break;
        case '4bcfe4a5426c010575e4f65058cd834d':// 
          basicChart = baseChartList[0].children[1];
          break;
        case '4e6bbd29bc6531d5db267ccdf2131968':// ()
          basicChart = baseChartList[0].children[16];
          break;
        case 'a67c21b59fec6cb3ffc3ca9bdf2594c7':// 
          basicChart = baseChartList[0].children[18];
          break;
        case '4a7fb5c67b518cefb852d6e42159c00b':// 
          basicChart = baseChartList[0].children[12];
          break;
        case 'c59f38d3cce0c4cd444557f54158afa5':// 
          basicChart = baseChartList[0].children[8];
          break;
        case 'a65e5e4f1f2a3bb17e3b8074d80765c7':// 
          basicChart = baseChartList[0].children[6];
          break;
        case '15bc5dd081d6c63cfef13656439df4b8':// 
          basicChart = baseChartList[0].children[5];
          break;
        case 'a4042c6029fcc7566835919c5aef3e87':// 
          basicChart = baseChartList[0].children[10];
          break;
        case 'b5c4e9db477fc89703959ebdf4148f32':// 
          basicChart = baseChartList[0].children[9];
          break;
        case '02fa3ca003fe022bd94fd04e79462b5f':// 
          basicChart = baseChartList[0].children[11];
          break;
        case '60d999c0f66f003cdde4c31da3c84aa0':// 
          basicChart = baseChartList[0].children[7];
          break;
        case '9e94be886a4b5832b151d34d56b7f479':// 
          basicChart = baseChartList[0].children[14];
          break;
        case '1a28e5de9add1b7b0188dfcad464fc73':// 
          basicChart = baseChartList[0].children[15];
          break;
        // case '0f18daa665f315eb49841c10540c39c0':// 
        //   basicChart = baseChartList[0].children[0];
        //   break;
      }
      return basicChart;
    }
	},
};
</script>
<style scoped>
  .fade-enter-active {
    animation: fadeInLeft 0.8s;
  }

  .fade-leave-active {
    animation: fadeOutLeft 0.8s;
  }

  .slide-enter-active {
    animation: fadeInLeft 0.8s;
  }

  .slide-leave-active {
    animation: fadeOutLeft 0.8s;
  }

  @keyframes fadeInLeft {
    from {
      opacity: 0;
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0);
    }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  }

  @keyframes fadeOutLeft {
    from {
      opacity: 1;
    }

    to {
      opacity: 0;
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0);
    }
  }
</style>


