<template>
  <div class="footerSection">
    <div :style="{ transform:`translateX(${workPlaceLeft}vw)`,width:footerWidth+'vw'}"
      class="footer">
      <!--      <div class="tabWrapper">-->
      <!--        -->
      <!--      </div>-->
      <div class="tabWrapper">
        <div class="fatherProjectName ">{{fatherProjectName}}</div>
        <ul class="itemBox">
          <li :class="{active:info.id===activeInfo.id}"
            :key="index"
            @click.stop="initCurrentSubItems(index,info)"
            class="li-label"
            v-for="(info,index) in showTabList">
            <div :title="info.name"
              class="tabItem">
            <span class="hidden input-hidden"
              ref="widthCalc">{{info.name}}</span>
              <input :class="{readonly:!info.isShowEdit}"
                :id="index"
                @blur="inputBlur(info,index)"
                @input="widthChange(info.name,index,$event)"
                @keydown.space.prevent
                @keyup.enter='keyBlur($event)'
                autocomplete="off"
                class="edit-input"
                ref="editInput"
                spellcheck="false"
                type="text"
                v-model.trim="info.name"/>
              <div @click.stop="clickSpan(info,index)"
                class="hover-rename">
                <span class="biFont icon-edit icon-class-edit"></span>
                <span class="tips"></span>
              </div>
            </div>
            <span @click.stop="deleteSonProject(info,index)"
              class="biFont icon-clear set-hover-color"></span>
          </li>
          <li :style="{
            float:subProjectInfo.length>4?'right':'left'
          }"
            @click="createSonProject"
            class="addItem biFont icon-add"></li>
        </ul>
        <!--        </ul>-->
      </div>
      <div class="tabBtn">
        <span @click="previousPage"
          class="item biFont icon-arrowLeft"></span>
        <span @click="nextPage"
          class="item biFont icon-arrowRight"></span>
        <span @click="showAllTableList"
          class="item biFont icon-option"></span>
        <!--        <ul class="tablist aCenter">-->
        <!--        </ul>-->
      </div>
      <div class="dimension">
        <div class="height">
          <span>:</span>
          <input @keyup.enter="widthInputBlur"
            class="numberInputborder vCenter"
            onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )'
            ref="widthInput"
            type="number"
            v-model.trim="canvasInfo.width">
        </div>
        <div class="width">
          <span>:</span>
          <input @keyup.enter="heightInputBlur"
            class="numberInputborder vCenter"
            onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )'
            ref="heightInput"
            type="number"
            v-model="canvasInfo.height">
        </div>
      </div>
      <div class="canvas-scale"
        v-show="hiddenScroll">
        <div class="select-box">
          <div :class="{'select-color':isRotate}"
            @click="toggleOptionsShow"
            class="select-input"
            v-click-outside="{fn:hideOptions}">
            <span class="inputSpan"
              disabled>{{scalePercentage}}</span>
            <span :class="{'rotate':isRotate}"
              class="vCenter triangleDown"></span>
          </div>
          <ul @mousedown.stop
            class="option"
            v-show="isRotate">
            <easy-scroll :barOption="reactiveScrollOpt">
              <li :key="index"
                @click="setScaleValue(item.scaleValue)"
                v-for="(item,index) in scaleList">{{item.scaleValue}}%
              </li>
            </easy-scroll>
          </ul>
        </div>
        <div class="slider">
          <div class="slider-btn">
            <span @click="zoomIn"
              class="zoomIn"></span>
          </div>
          <slider-range :customerDotStyle="{borderRadius:0}"
            :customerProcessStyle="{backgroundColor:'#00b9ff'}"
            :customerRailStyleStyle="{height:this.getVw(2) + 'px'}"
            :interval="10"
            :lazy="false"
            :maxValue="maxScale"
            :minValue="minScale"
            :valueRange="scaleValue"
            @rangedata="setScaleValue"
            tooltip="focus"></slider-range>
          <div @click="zoomOut"
            class="slider-btn">
            <span class="zoomOut"></span>
          </div>
        </div>
      </div>
      <ul class="btnWrapper">
        <li :key="index"
          @click="dashboardOperation(index)"
          v-for="(item,index) in iconArr">
          <div class="box-size">
            <span :class="`biFont icon-${item.iconName}`"></span>
          </div>
          <span class="tips">{{item.name}}</span>
        </li>
      </ul>
    </div>
    <Modal :options="promptData"
      v-if="isShowModal"></Modal>
    <PreViewModal data-class="unableDeactive"></PreViewModal>
  </div>
</template>
<script>
import Modal from '@/components/Prompt/Modal';
import PreViewModal from './PreViewModal';
import SliderRange from '@/components/SliderRange';
import {mapGetters} from 'vuex';

export default {
	name: 'Footer',
	data() {
		return {
			tabStartIndex: 0,
			tabEndIndex: 0,
			activeLi: 0,
			activeInfo: {},
			stateCut: 0,
			hiddenScroll: true,
			projectId: '',
			subProjectInfo: [],
			isShowModal: false,
			fatherProjectId: '',
			fatherProjectName: '',
			Operand: {},
			rename: '',
			delId: '',
			deletable: true,
			isShowInput: false,
			sonObj: {},
			isRotate: false,
			scaleList: [
				{scaleValue: 30},
				{scaleValue: 40},
				{scaleValue: 50},
				{scaleValue: 60},
				{scaleValue: 70},
				{scaleValue: 80},
				{scaleValue: 90},
				{scaleValue: 100}
			],
			iconArr: [
				{iconName: 'mLayout', name: ''},
				{iconName: 'share', name: ''},
				{iconName: 'preview', name: ''},
				// {iconName: 'save', name: ''},
				// {iconName: 'layout', name: ''},
			],
			minScale: 30,
			maxScale: 100,
			promptTitle: {
				title: '!',
				describe: '',
				inputType: 'text',
				placeholder: '',
				name: [],
				btn: [
					{
						text: '',
						callback: () => {
						}
					},
					{
						text: '',
						callback: () => {
							this.isShowModal = false;
						}
					}
				]
			},
			promptData: {},
			createSonData: {
				title: '',
				icon: 'biFont icon-builtNew',
				describe: '',
				inputType: 'text',
				placeholder: '',
				name: [''],
				btn: [
					{
						text: '',
						throttle: true,
						callback: this.createSubItems
					},
					{
						text: '',
						callback: data => {
							this.isShowModal = false;
						}
					}
				],
				enterKeyEventIndex: 0,
				escKeyEventIndex: 1
			},
			dashboardOperationIndex: -1,
			deleteSonData: {
				title: '!',
				describe: '',
				inputType: 'text',
				placeholder: '',
				name: [],
				btn: [
					{
						text: '',
						throttle: true,
						callback: this.deleteSubItems
					},
					{
						text: '',
						callback: () => {
							this.isShowModal = false;
						}
					}
				],
				enterKeyEventIndex: 0,
				escKeyEventIndex: 1
			},
			interfaceData: [],
			blueInput: false,
			saveDataWeight: '',
			saveDataHeight: '',
			stateArr: false,
		};
	},
	props: {
		canvasInfo: Object
	},
	components: {
		Modal,
		PreViewModal,
		'slider-range': SliderRange
	},
	computed: {
		...mapGetters('dashboard', {
			workPlaceLeft: 'getWorkPlaceLeft',
			footerWidth: 'getFooterWidth'
		}),
		...mapGetters('chartBus', {
			timeState: 'returnTime',
			isChartSaveComplete: 'getSaveComplete'
		}),
		...mapGetters('canvasPlace', {
			bgAmendCount: 'getCanvasBGAmendCount',//
			canvasBgColor: 'getCanvasBgColor',//pc
			bgImgGet: 'getCanvasBgImg',//pc
			bgPhoneColor: 'getCanvasBgPhoneColor',//
			bgPhoneImg: 'getCanvasBgPhoneImg',//
			scaleLevel: 'getScaleLevel',
			isPhoneLayout: 'getPhoneLayoutState',
			isModule: 'getModuleNum',
			failLoad: 'getFailLoad',//
		}),
		...mapGetters('main', ['getVw', 'vwToPx']),
		...mapGetters('voiceCommand', {
			creatProject: 'getProject', // 
		}),
		showTabList() {
			return this.subProjectInfo.slice(this.tabStartIndex, this.tabStartIndex + 5,);
		},
		reactiveScrollOpt() {
			return {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barOpacityMin: 0.3, //
				barWidth: this.getVw(4),
				barMarginRight: 0,
				borderWidth: this.vwToPx(0.125)
			};
		},
		scaleValue() {
			return this.scaleLevel * 100;

		},
		scalePercentage() {
			return this.scaleValue + '%';
		}
	},
	watch: {
		creatProject() {// 
			if (this.creatProject === 'await') return;
			this.createSonProject();
		},
		'showTabList.length'(length) {
			if (length === 0) {
				this.tabStartIndex -= 5;
			}
		},
		isPhoneLayout(val) {
			let vm = this;
			vm.hiddenScroll = !vm.hiddenScroll;
			if (val) {
				vm.$set(vm.iconArr, 0, {iconName: 'pcClinet', name: ''});
				this.$store.commit('dashboard/setStateProperty', {key: 'leftSideWidth', value: 0});
				this.$store.commit('dashboard/toggleRightPaneShow', true);
			} else {
				vm.$set(vm.iconArr, 0, {iconName: 'mLayout', name: ''});
				this.$store.commit('dashboard/setStateProperty', {key: 'leftSideWidth', value: 2.6});
				this.$store.commit('dashboard/setStateProperty', {key: 'leftSideOpen', value: false});
				this.$store.commit('dashboard/toggleRightPaneShow', false);
			}
		},
		'subProjectInfo.length': {
			handler(newValue, oldValue) {//
				this.deletable = !(newValue === 1);
			}
		},
		bgAmendCount: {
			handler() {
				console.log(this.bgAmendCount, '');
				if (this.bgAmendCount > 0) {
					this.stateArr = true;
				}
			}
		},
		isChartSaveComplete(val) {
			this.isShowModal = false;
			if (this.dashboardOperationIndex === 1) {
				this.$store.commit('chartBus/setSaveComplete');
				if (val) {
					this.dashboardOperationIndex = -1;
				}
			}
		},
		'scaleLevel': {
			immediate: false,
			handler(newval, oldval) {
				if (newval != oldval) {
					this.stateArr = true;
				}
			}
		},
		canvasInfo: {
			deep: true,
			handler() {
				this.stateArr = true;
			}
		},
		stateArr(val) {
			if (val) {
				setTimeout(this.dashboardDataSend, 3000);
			}

		},
		activeInfo(val) {
			// if()
			// debugger;
			// this.dashboardDataSend();
		},
		isModule() {
			if (this.isModule % 2 != 0) {
				let module = ['moduleOne', 'auto'];
				this.createSubItems(module);
			}

		},
		failLoad() {
			let vm = this;
			vm.tips.show(['']);
			let index = vm.subProjectInfo.length;
			vm.delIndex = index - 1;
			vm.sonObj = {
				id: vm.subProjectInfo[index - 1].id,
				type: 'son',
			};
			vm.deleteSubItems();
		}
	},
	beforeMount() {
		this.$store.commit('canvasPlace/setInitAllChart', false);
		if (
			localStorage.getItem('fatherProjectId') &&
        localStorage.getItem('fatherProjectName')
		) {
			this.fatherProjectId = localStorage.getItem('fatherProjectId');
			this.fatherProjectName = localStorage.getItem('fatherProjectName');
			this.initSubItems();
		}
	},
	beforeDestroy() {
		this.$store.commit('canvasPlace/setStateProperty', {key: 'shareType', value: 0});

	},
	methods: {
		clickSpan(params, index) {
			this.isShowInput = true;
			this.rename = params.name;
			let len = this.rename.length;
			this.$set(params, 'isShowEdit', true);
			this.$refs.editInput[index].focus();
			this.$refs.editInput[index].selectionStart = len;
			this.$refs.editInput[index].selectionEnd = len;
		},
		inputBlur(params, index) {
			let name = params.name;
			this.$set(params, 'name', name.replace(/\s+/g, ''));
			// this.$refs.editInput[index].blur();
			params.isShowEdit = false;
			this.Operand = params;
			if (this.rename && params.name && this.rename != params.name) {
				this.renameSubItems();
				return false;
			}
			if (!params.name) {
				this.$set(this.subProjectInfo[index], 'name', this.rename);
				this.tips.show(['']);
			}
		},
		keyBlur(e) {
			const target = e.target || e.srcElement;
			target.blur();
		},
		renameSubItems() {
			let vm = this;
			let data = {
				id: vm.Operand.id,
				name: vm.Operand.name
			};
			vm.$put('api/project', data)
				.then(res => {
					console.log('two-put', res);
					if (res.code == '000000') {
						vm.tips.show(['', true]);
						vm.rename = '';
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
				})
				.catch(error => {
					vm.tips.show(['']);
					console.error(error);
				});
		},
		dashboardDataSend() {
			let vm = this;
			let objects = {
				id: vm.activeInfo.id,
				name: vm.activeInfo.name,
				dashboardWidth: Number(vm.canvasInfo.width),
				dashboardHeight: Number(vm.canvasInfo.height),
				dashboardBgColor: vm.canvasBgColor,
				dashboardBgImage: vm.bgImgGet,
				dashboardPhoneBgColor: vm.bgPhoneColor ? vm.bgPhoneColor : 'rgba(0,0,0,0)',//
				dashboardPhoneBgImage: vm.bgPhoneImg,//
				zoomLevel: vm.scaleLevel
			};
			vm.$put('api/project', objects)
				.then(res => {
					if (res.code == '000000') {
						vm.rename = '';
						vm.$set(vm.subProjectInfo, vm.activeLi, res.data);
						let params = {  //
							pageSn: res.data.pageLevelSizer.sizerId,
							reportSn: res.data.reportLevelSizer.sizerId
						};

						vm.$store.commit('canvasPlace/setStateProperty', {key: 'screenSign', value: params});
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
					vm.stateArr = false;
					// vm.$store.commit('canvasPlace/setCanvasBGAmendCount', false);
				})
				.catch(error => {
					console.error(error);
					this.stateArr = false;
				});
		},
		asyncDashboardDataSend() {
			let vm = this;
			let objects = {
				id: vm.activeInfo.id,
				name: vm.activeInfo.name,
				dashboardWidth: Number(vm.canvasInfo.width),
				dashboardHeight: Number(vm.canvasInfo.height),
				dashboardBgColor: vm.canvasBgColor,
				dashboardBgImage: vm.bgImgGet,
				dashboardPhoneBgColor: vm.bgPhoneColor,//
				dashboardPhoneBgImage: vm.bgPhoneImg,//
				zoomLevel: vm.scaleLevel
			};
			return new Promise((resolve, reject) => {
				vm.$put('api/project', objects)
					.then(res => {
						if (res.code == '000000') {
							vm.rename = '';
							vm.$set(vm.subProjectInfo, vm.activeLi, res.data);
							resolve(res.err_msg);
						} else {
							vm.tips.show([res.err_msg.split(':')[1]]);
							reject(res.err_msg);
						}
						this.stateArr = false;
						// vm.$store.commit('canvasPlace/setCanvasBGAmendCount', false);
					})
					.catch(error => {
            reject(error);
						console.error(error);
						this.stateArr = false;
					});
			});
		},
		widthInputBlur() {
			this.$refs.widthInput.blur();
		},
		heightInputBlur() {
			this.$refs.heightInput.blur();
		},
		widthChange(name, index, e) {
			name = e.target.value;
		},
		//
		initSubItems() {
			let vm = this;
			vm.$get('api/project', {
				projectId: vm.fatherProjectId
			}).then(res => {
					if (res.code === '000000') {
						let data = res.data;
						if (data.length) {

							vm.activeInfo = data[0];
							// vm.tabEndIndex=data.length<5?data.length:5;
							vm.subProjectInfo = data;
							sessionStorage.setItem('projectId', data[0].id);
							localStorage.setItem('subProjectName', data[0].name);
							vm.interfaceData = data;
							vm.canvasInfo.width = data[0].dashboardWidth;
							vm.canvasInfo.height = data[0].dashboardHeight;
							vm.$store.commit('canvasPlace/setCurrentProjectInfo', [
								{
									key: 'scaleLevel',
									value: data[0].zoomLevel
								},
								{
									key: 'isPhoneLayoutSaved',
									value: data[0].isMobileLayoutSaved
								},
								{
									key: 'canvasColor',
									value: data[0].dashboardBgColor
								},
								{
									key: 'canvasImg',
									value: data[0].dashboardBgImage
								},
								{
									key: 'canvasBgPhoneColor',
									value: data[0].dashboardPhoneBgColor
								},
								{
									key: 'canvasBgPhoneImg',
									value: data[0].dashboardPhoneBgImage
								},
								{
									key: 'pushInfo',
									value: data[0].pushInfo
								},
							]);

							// vm.$store.commit(
							// 	'canvasPlace/setScaleLevel',
							// 	data[0].zoomLevel
							// );
							// vm.$store.commit('canvasPlace/setStateProperty', {
							// 	key: 'isPhoneLayoutSaved',
							// 	value: data[0].isMobileLayoutSaved
							// });
							// vm.$store.commit('canvasPlace/setCanvasBgColor', data[0].dashboardBgColor);
							// vm.$store.commit('canvasPlace/setCanvasBgImg', data[0].dashboardBgImage);
							// vm.$store.commit('canvasPlace/setCanvasBgMobileColor', data[0].dashboardPhoneBgColor
							// );//
							// vm.$store.commit('canvasPlace/setCanvasBgMobileImg', data[0].dashboardPhoneBgImage
							// );
							// vm.$store.commit('canvasPlace/setStateProperty', {key: 'pushInfo', value: data[0].pushInfo});
							data.forEach(e => {
								vm.$set(e, 'isShowEdit', false);
							});
							vm.$store.commit('canvasPlace/setInitAllChart', true);
						}
					} else if (res.code == '300009') {
						vm.$router.replace('project');
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
				})
				.catch(error => {
					console.error(error);
				});
		},
		indexDbClear() {
			let vm = this,
				p1 = vm.dexieDB.aXisList.clear().then(() => ''),
				p2 = vm.dexieDB.chartsOption.clear().then(() => ''),
				p3 = vm.dexieDB.saveOption.clear().then(() => ''),
				p4 = vm.dexieDB.saveTextOption.clear().then(() => ''),
				p5 = vm.dexieDB.saveMediaOption.clear().then(() => ''),
				p6 = vm.dexieDB.saveInteractiveOption.clear().then(() => '');
			Promise.all([p1, p2, p3, p4, p5, p6])
				.then(res => {
					// console.log(res);
					vm.isTableClear = true;
				})
				.catch(reason => {
					console.error(reason);
				});
		},
		//
		initCurrentSubItems(activeIndex, info, signal) {
			let id = info.id;
			let vm = this;
			if (id !== vm.activeInfo.id) {
        vm.rename = '';
        vm.$store.commit('canvasPlace/clearArrChart');
        console.log(activeIndex, info, '');
        vm.indexDbClear();
				vm.activeLi = activeIndex;
				vm.activeInfo = info;
				vm.canvasInfo.width = info.dashboardWidth;
				vm.canvasInfo.height = info.dashboardHeight;
				sessionStorage.setItem('projectId', info.id);
				localStorage.setItem('subProjectName', info.name);
				if (signal) {
					this.$store.commit('canvasPlace/setModuleType', true);
				}
				vm.$store.commit('canvasPlace/setInitAllChart', true);
				vm.$store.commit('canvasPlace/setCurrentChartIndex', -1);
				vm.$store.commit('dataDimension/resetAllSelectInput');
				vm.$store.commit('canvasPlace/setStateProperty', {
					key: 'isPhoneLayoutSaved',
					value: info.isMobileLayoutSaved
				});

				// vm.$store.dispatch('canvasPlace/setPhoneLayoutSavedState', info.isMobileLayoutSaved);
				vm.$store.commit('canvasPlace/setCanvasBgColor', info.dashboardBgColor);
				vm.$store.commit('canvasPlace/setCanvasBgImg', info.dashboardBgImage);
				vm.$store.commit('canvasPlace/setStateProperty', {key: 'scaleLevel', value: info.zoomLevel});
				if (vm.timeState === true) {
					vm.promptData = vm.promptTitle;
				}
			}
		},
		//
		createSonProject() {
			this.isShowModal = true;
			this.promptData = this.createSonData;
		},
		createSubItems(data) {
			let vm = this;
			console.log('data', data);
			let auto = data[1] || '';
			if (!data[0] || data[0].trim() === '') {
				vm.tips.show(['!']);
				return false;
			}
			let obj = {
				id: vm.fatherProjectId,
				type: 'son',
				name: data[0],
				dashboardWidth: 1920, //
				dashboardHeight: 1080, //
				dashboardBgColor: 'rgba(0, 0, 0, 0)', //
				dashboardBgImage: '', //
				dashboardPhoneBgColor: 'rgba(0, 0, 0, 0)',//
				dashboardPhoneBgImage: '',//
				zoomLevel: 0.7 //
			};
			vm.$post('api/project', obj)
				.then(res => {
					if (res.code == '000000') {
						vm.isShowModal = false;
						let data = res.data;
						data.isShowEdit = false;
						vm.subProjectInfo.push(data);
						// if(vm.subProjectInfo.length-vm.tabStartIndex<=5){
						// 	vm.tabEndIndex=vm.subProjectInfo.length;
						// }
						if (auto) {
							this.initCurrentSubItems(vm.subProjectInfo.length - 1, data, 'module');
						}
						vm.tips.show(['', true]);
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
				})
				.catch(error => {
					vm.tips.show(['']);
					console.error(error);
				});
		},
		//tabstart
		previousPage() {
			if (this.tabStartIndex > 4) {
				// debugger;

				this.tabStartIndex -= 5;
				// this.tabEndIndex-=5;
			}
		},
		nextPage() {
			if (this.subProjectInfo.length > this.tabStartIndex + 5) {
				this.tabStartIndex += 5;
				// this.tabEndIndex+=5;
			}
		},
		showAllTableList() {
		},
		//tabehd
		//
		deleteSonProject(info, index) {
			this.delIndex = this.tabStartIndex + index;
			this.delId = info.id;
			if (this.deletable) {
				this.promptData = this.deleteSonData;
				this.isShowModal = true;
				this.sonObj = {
					id: info.id,
					type: 'son',
				};
			} else {
				this.tips.show(['']);
			}
		},
		deleteSubItems() {
			let vm = this;
			vm.$del('/api/project', vm.sonObj)
				.then(res => {
					if (res.code == '000000') {
						vm.isShowModal = false;
						vm.subProjectInfo.splice(vm.delIndex, 1);
						let delIndex = vm.delIndex;
						if (vm.delId === vm.activeInfo.id) {
							sessionStorage.setItem('projectId', vm.subProjectInfo[0].id);
							localStorage.setItem('subProjectName', vm.subProjectInfo[0].name);
							// let isFirstTab = delIndex === 0;
							let ativeIndex = vm.subProjectInfo[delIndex] ? delIndex : delIndex - 1;
							// alert(ativeIndex);
							// vm.activeInfo = vm.subProjectInfo[ativeIndex];
							vm.initCurrentSubItems(ativeIndex, vm.subProjectInfo[ativeIndex]);
						}
						// alert(2);
						vm.delIndex = -1;
						vm.tips.show(['', true]);
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
				})
				.catch(error => {
					vm.tips.show(['']);
					console.error(error);
				});
		},
		/** */
		dashboardOperation(index) {
			let vm = this;
			vm.dashboardOperationIndex = index;
			let object = {
				0: () => {
					vm.$store.commit('canvasPlace/setPhoneLayoutState');
				},
				1: () => {
					/**
             * 
             * 
             */
					vm.dataIsOk = true;
					const p1 = vm.asyncDashboardDataSend();

					// const p2 = vm.$store.dispatch('canvasPlace/asyncChartOptionsChange');
          vm.$store.commit('canvasPlace/setStateProperty', {key: 'shareType', value: 1});
          this.$store.dispatch('canvasPlace/setIndexdbChartOptions', {
						callback: () => {
							p1.then(res => {
                vm.$store.commit('canvasPlace/setStateProperty', {key: 'shareType', value: 1});
							}).catch(error => console.error(error));
							// vm.$store.dispatch('canvasPlace/screeningRequest'); //++
						}
					});
          // Promise.all([]).then(value => {
					// 	/**
					//      * vuex
					//      * 
					//      * 1
					//      * 2
					//      */
					// 	console.log(value);
					// 	vm.$store.commit('canvasPlace/setStateProperty', {key: 'shareType', value: 1});
					// }, error => {
					// }).catch(error => console.error(error));
				},
				2: () => {
					//
					const p1 = vm.asyncDashboardDataSend();
					// const p2 = vm.$store.dispatch('canvasPlace/asyncChartOptionsChange');
					this.$store.dispatch('canvasPlace/setIndexdbChartOptions', {
						callback: () => {
							p1.then(res => {
								vm.previewDashboard();
							}).catch(error => console.error(error));
							// vm.$store.dispatch('canvasPlace/screeningRequest'); //++
						}
					});
					// Promise.all([p1, p2]).then(value => {
					// 	vm.previewDashboard();
					// }, error => {
					// 	console.error(error);
					// }).catch(error => console.error(error));
				},
				3: () => { //
					if (vm.isPhoneLayout) {
						this.$store.commit('canvasPlace/setStateProperty', {
							key: 'saveMobileChartOptionsImmediately',
							value: true
						});
					} else {
						this.$store.commit('canvasPlace/setStateProperty', {
							key: 'saveChartOptionsImmediately',
							value: true
						});
					}
				}
			};
			object[index] && object[index]();
		},
		/** */
		toggleOptionsShow() {
			this.isRotate = !this.isRotate;
		},
		hideOptions() {
			this.isRotate = false;
		},
		/** */
		// setScaleValue(scaleValue) {
		// 	this.$store.commit('canvasPlace/setScaleLevel', scaleValue / 100);
		// },
		zoomIn() {
			if (this.scaleValue > 30) {
				const scaleValue = this.scaleValue - 10;
				this.$store.commit('canvasPlace/setScaleLevel', scaleValue / 100);
			}
		},
		zoomOut() {
			if (this.scaleValue < 100) {
				const scaleValue = this.scaleValue + 10;
				this.$store.commit('canvasPlace/setScaleLevel', scaleValue / 100);
			}
		},
		setScaleValue(value) {
			this.isRotate = false;
			this.$store.commit('canvasPlace/setScaleLevel', value / 100);
		},
		/**/
		previewDashboard() {
			const vm = this;
			vm.dataIsOk = true;
			const shareObj = {
				project_sn: sessionStorage.getItem('projectId'),
				share_type: 1,
				is_pwd: 0
			};
			vm.$get('api/shareUnique', shareObj)
				.then(res => {
					if (res.code == '000000') {
						/** */
						let data = res.data;
						console.log('res', res.data);
						const address = `${window.location.host}/previewPages?share_sn=${data.share_sn}&password=${data.password}`;
						window.open(window.location.protocol + '//' + address, '_blank');
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
					vm.dataIsOk = false;
				}).catch(error => console.error(error));
		},
	},
	mounted() {
	},
};
</script>
