<template>
  <div class="database maskBackground">
    <div class="fileContent allCenter"
      v-show="toggle">
      <div class="fileTitle">
        <i class="icon dataSource icon-customDatabase saved"></i>
        <span></span>
      </div>
      <div class="content">
        <div class="nav"></div>
        <div class="list">
          <div class="listBase">
            <ul>
              <li v-for="(item,index) in showDBName"
                :key="item.id"
                :port="item.port"
                :host="item.host"
                :type="item.type"
                :class="{meunActive:meunNum==index}"
                @click="meunList(index,item)"
                @mouseenter="deleteShow=index"
                @mouseleave="deleteShow=null">
                <i class="icon dataSource icon-customDatabase baseIcon"></i>
                <span>{{item.db_name}}</span>
                <i v-show="deleteShow==index"
                  class="icon biFont icon-delete meunDelete"
                  @click.stop.prevent="deleteEvent(item.id)"></i>
              </li>
            </ul>
          </div>
          <div v-if="listSheetTotal">
            <div class="listSheet"
              v-if="setDatabase">
              <div class="sheetTitle">
                <span class="vertical"></span>
                <span>{{dataSheetName}}</span>
                <label class="checkeAllBtn">
                  <span class="checkNumber unSelect"
                    v-show="checkAll"></span>
                  <input type="checkbox"
                    ref="checkeAll"
                    class="checkbox"
                    @input="selectAll"
                    v-model="checkedAll"
                    :class="{checkNumber:checkAll}">
                  <span></span>
                </label>
              </div>
              <VueScrollbar class="sublist"
                classes="my-scrollbar"
                ref="Scrollbar">
                <ul>
                  <li v-for="(data,i) in dataSheetTotal"
                    :key="i">
                    <label>
                      <span class="checkNumber unSelect"
                        v-show="definite==i"></span>
                      <input type="checkbox"
                        class="checkbox"
                        @input="checked(i,$event)"
                        :class="{checkNumber:definite==i}"
                        :value="data.tablename"
                        v-model="checkedList">
                      <span>{{data.tablename}}</span>
                    </label>
                  </li>
                </ul>
              </VueScrollbar>
              <span class="listButton"
                @click="getDatabase()"></span>
            </div>
            <div class="modifyDatabase"
              v-else>
              <div class="sheetTitle">
                <span class="vertical"></span>
                <span></span>
              </div>
              <div class="createList">
                <ul>
                  <li>
                    <label></label>
                    <Selection class="selections"
                      ref="type"
                      @selectValue="selectionsEvent"
                      v-bind="{selections}">
                    </Selection>
                  </li>
                  <li>
                    <label>IP</label>
                    <input type="text"
                      :value="databaseInfo.host"
                      ref="host">
                  </li>
                  <li>
                    <label></label>
                    <input type="text"
                      :value="databaseInfo.port"
                      ref="port">
                  </li>
                  <li>
                    <label></label>
                    <input type="text"
                      :value="databaseInfo.db_name"
                      ref="dbname">
                  </li>
                  <li>
                    <label></label>
                    <input type="text"
                      :value="databaseInfo.db_username"
                      ref="user">
                  </li>
                  <li class="databasePass">
                    <label></label>
                    <input type="text"
                      :value="databaseInfo.db_password"
                      v-if="passToggle"
                      ref="pass">
                    <input type="password"
                      :value="databaseInfo.db_password"
                      v-if="!passToggle"
                      ref="pass">
                    <i class="icon biFont toggleIcon"
                      :class="[{'icon-hidden':passToggle},{'icon-show':!passToggle}]"
                      @click="passToggle=!passToggle"></i>
                  </li>
                </ul>
              </div>
              <span class="listButton"
                @click="setDatabase=!setDatabase"></span>
            </div>
          </div>
          <div v-else
            class="noList"></div>
        </div>
      </div>
      <div class="buttonDiv">
        <span class="new mainBtn"
          @click="newDatabase"></span>
        <span class="requestBtn mainBtn"
          @click="uploadEvent"
          :class="{banGestures:isUploadClick}"
          v-if="setDatabase"></span>
        <span class="requestBtn mainBtn"
          @click="definiteEvent"
          :class="{banGestures:isModifyClick}"
          v-else></span>
        <span class="cancel mainBtn"
          @click="cancelEvent"></span>
      </div>
    </div>
    <!--    <Tips v-bind="{tipsData}"-->
    <!--      ref="tips"></Tips>-->
    <Confirm :options="confirmData"
      v-if="confirmShow"></Confirm>
    <ManyPrompt :options="manyPromptData"
      v-if="promptShow"></ManyPrompt>

    <!--  -->
    <Loading v-if="dataIsOk"
      :styleObj="styleObj"></Loading>
  </div>
</template>
<script>
import Loading from '@/components/Loading/LoadingRotating.vue';
import Selection from '@/components/Selection/Selection.vue';
import ManyPrompt from '@/components/Prompt/MorePrompt.vue';
import Confirm from '@/components/Confirm/Confirm.vue';
// import Tips from '@/components/Tips/Tips.vue';
import VueScrollbar from 'vue2-scrollbar';

require('@/assets/DataSource/css/vue2-scrollbar.css');

export default {
	name: 'savedDatabase',
	props: ['message', 'savedId'],
	components: {
		// Tips,
		Confirm,
		VueScrollbar,
		Selection,
		ManyPrompt,
		Loading
	},
	data() {
		return {
			member_sn: localStorage.getItem('userid'),
			token: localStorage.getItem('token'),
			toggle: true,
			listId: null,
			delId: null,
			meunNum: null,
			showDBName: null,
			passToggle: true,
			setDatabase: true,
			listSheetTotal: false,
			definite: null,
			dataIsOk: false,
			dataSheetTotal: null,
			dataSheetName: null,
			checkedAll: false,
			checkAll: false,
			checkedList: [],
			isUploadClick: false, //
			isModifyClick: false, //
			confirmShow: false,
			promptShow: false,
			deleteShow: null,
			styleObj: {
				width: '39.4vw',
				height: '64.3vh'
			},
			tipsData: {
				text: '',
				rel: '',
				time: 1500
			},
			confirmData: {
				title: '',
				tableName: null,
				btn: [
					{
						text: '',
						callback: null
					},
					{
						text: '',
						callback: null
					},
					{
						text: '',
						callback: null
					}
				]
			},
			selections: {
				placeholder: '',
				w: '16.9vw',
				inputVal: this.message,
				data: this.$store.state.dataSource.databaseType
			},
			manyPromptData: {
				title: '',
				name: [],
				btn: [
					{
						text: '',
						callback() {
						}
					},
					{
						text: '',
						callback() {
						}
					}
				]
			}
		};
	},
	methods: {
		selectionsEvent(data) {
			this.dbname = data.value;
		},
		cancelEvent() {
			this.$emit('savedHide', false);
		},
		newDatabase() {
			this.$emit('newShow', true);
		},
		getDatabase() {
			this.$get('api/connectionInfo', {
				id: this.listId
			}).then(
				response => {
					if (response.code == '000000') {
						this.setDatabase = !this.setDatabase;
						this.databaseInfo = response.data[0];
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
						// this.tipsData.rel = 'fail';
						// this.tipsData.text = response.err_msg.split(':')[1];
						// this.$refs.tips.closeTips();
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		deleteEvent(id) {
			this.$del('api/dDataSource', {
				id: id
			}).then(
				response => {
					if (response.code == '000000') {
						this.tips.show([response.err_msg.split(':')[1]], true);
						// this.tipsData.rel = 'success';
						this.delId = id;
						setTimeout(() => {
							this.requestDataLibName('refresh');
						}, 1500);
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
						// this.tipsData.rel = 'fail';
					}
					// this.tipsData.text = response.err_msg.split(':')[1];
					// this.$refs.tips.closeTips();
				},
				error => {
					console.log(error);
					this.tips.show(['']);
					// this.tipsData.rel = 'fail';
					// this.tipsData.text = '';
					// this.$refs.tips.closeTips();
				}
			);
		},
		uploadEvent() {
			this.isUploadClick = true;
			if (this.isUploadClick) {
				this.dataIsOk = true;
				if (this.checkedList.length == 0) {
					this.tips.show(['']);
					// this.tipsData.rel = 'fail';
					// this.tipsData.text = '';
					// this.$refs.tips.closeTips();
					return;
				}
				let formData = this.cycleData(1, this.checkedList);
				this.$put('api/uDataSource', formData).then(
					response => {
						let res = response.data;
						this.dataIsOk = false;
						if (response.code == '000000') {
							if (
								(res['0000'] && res['500']) ||
									(res['1000'] && res['500']) ||
									res['500']
							) {
								// 
								this.uploadRepeat(res['500'].table_name);
							} else if (res['0000']) {
								this.tips.show(['', true]);
								// this.tipsData.text = '...';
								// this.tipsData.rel = 'success';
								this.$emit('uploadBack', 'success');
							} else{
								console.log(response.code);
								this.tips.show(['']);
								// this.tipsData.text = res['1000'].msg
								// 	? res['1000'].msg
								// 	: '';
								// this.tipsData.rel = 'fail';
							}
						} else {
							this.tips.show([response.err_msg.split(':')[1]]);
							// this.tipsData.text = response.err_msg.split(':')[1];
							// this.tipsData.rel = 'fail';
						}
						// console.log(this.$refs.tips);
						// this.$refs.tips.closeTips();
						this.isUploadClick = false;
					},
					error => {
						console.log(error);
						this.dataIsOk = false;
						this.tips.show(['']);
						// this.tipsData.rel = 'fail';
						// this.tipsData.text = '';
						// this.$refs.tips.closeTips();
						this.isUploadClick = false;
					}
				);
			}
		},
		secondRequest(formData, tag) {
			this.$put('api/uDataSource', formData).then(
				response => {
					let res = response.data;
					if (res['1000']) {
						this.tips.show([res['1000'].msg]);
						// this.tipsData.text = res['1000'].msg;
						// this.tipsData.rel = 'fail';
					} else if (res['0000']) {
						this.tips.show([res['0000'].msg,true]);
						// this.tipsData.text = res['0000'].msg;
						// this.tipsData.rel = 'success';
						this.$emit('uploadDatabase', 'success');
					} else {
			  this.tips.show([tag == 'rename'
		    ? ''
		    : '',]);
						// this.tipsData.text =
						// 		tag == 'rename'
						// 			? ''
						// 			: '';
						// this.tipsData.rel = 'fail';
					}
					this.promptShow = false;
					this.toggle = true;
					this.confirmShow = false;
					// this.$refs.tips.closeTips();
				},
				error => {
					console.log(error);
					this.tips.show(['']);
					// this.tipsData.rel = 'fail';
					// this.tipsData.text = '';
					// this.$refs.tips.closeTips();
					this.promptShow = false;
					this.toggle = true;
					this.confirmShow = false;
				}
			);
		},
		cycleData(num, data) {
			let formData = {
				data: []
			};
			for (let item of data) {
				let res = {
					action_type: num,
					config_sn: this.configSn,
					tablename: item,
					type: this.dataSheetTotal[0].type,
					dbname: this.dataSheetName,
					host: this.dataSheetTotal[0].host,
					member_sn: this.member_sn
				};
				formData.data.push(res);
			}
			return formData;
		},
		renameDatabase(data) {
			this.$put('api/updateSourceName', {
				list: data
			}).then(
				response => {
					let res = response.data;
					if (res['success']) {
						this.tips.show(['',true]);
						// this.tipsData.rel = 'success';
						// this.tipsData.text = '';
						this.$emit('uploadDatabase', 'success');
					} else {
						this.tips.show(['']);
						// this.tipsData.rel = 'fail';
						// this.tipsData.text = '';
					}
					this.promptShow = false;
					this.toggle = true;
					this.confirmShow = false;
					// this.$refs.tips.closeTips();
				},
				error => {
					console.log(error);
					this.tips.show(['']);
					// this.tipsData.rel = 'fail';
					// this.tipsData.text = '';
					// this.$refs.tips.closeTips();
				}
			);
		},
		uploadRepeat(repeatName) {
			this.toggle = false;
			let tableName = [];
			let coverName = [];
			let showName = '';
			for (let name in repeatName) {
				let data = {};
				data[name] = repeatName[name];
				tableName.push(data);
				coverName.push(repeatName[name]);
				showName += repeatName[name] + ',';
			}
			this.confirmShow = true;
			this.confirmData.tableName = showName;
			this.confirmData.tableNameLenght = coverName.length;
			this.confirmData.title = '';
			this.confirmData.btn[2].callback = () => {
				this.confirmShow = false;
				this.toggle = true;
			};
			this.confirmData.btn[0].callback = () => {
				let dataCover = this.cycleData(2, coverName);
				this.secondRequest(dataCover, 'cover');
			};
			this.confirmData.btn[1].callback = () => {
				this.promptShow = true;
				this.confirmShow = false;
				this.manyPromptData.name = tableName;
				this.manyPromptData.btn[0].callback = data => {
					if (tableName.length == data.length) {
						let renameObj = {};
						for (let i = 0; i < tableName.length; i++) {
							for (let id in tableName[i]) {
								renameObj[id] = data[i];
							}
						}
						renameObj = JSON.stringify(renameObj);
						this.renameDatabase(renameObj);
					} else {
						this.tips.show(['']);
						// this.tipsData.rel = 'fail';
						// this.tipsData.text = '';
						// this.$refs.tips.closeTips();
					}
				};
				this.manyPromptData.btn[1].callback = () => {
					this.promptShow = false;
					this.confirmShow = false;
					this.toggle = true;
				};
			};
		},
		definiteEvent() {
			this.isModifyClick = true;
			if (this.isModifyClick) {
				this.$put('api/uConnectionInfo', {
					id: this.listId,
					host: this.$refs.host.value,
					port: this.$refs.port.value,
					db_name: this.$refs.dbname.value,
					db_username: this.$refs.user.value,
					db_password: this.$refs.pass.value
				}).then(
					response => {
						if (response.code == '000000') {
							this.tips.show([response.err_msg.split(':')[1],true]);
							// this.tipsData.rel = 'success';
							this.setDatabase = !this.setDatabase;
							setTimeout(() => {
								this.requestDataLibName('refresh');
							}, 1500);
						} else {
							// this.tipsData.rel = 'fail';
						}
						this.tips.show([response.err_msg.split(':')[1]]);
						// this.tipsData.text = response.err_msg.split(':')[1];
						// this.$refs.tips.closeTips();
						// this.isModifyClick = false;
					},
					error => {
						console.log(error);
						this.tips.show(['']);
						// this.tipsData.rel = 'fail';
						// this.tipsData.text = '';
						// this.$refs.tips.closeTips();
						this.isModifyClick = false;
					}
				);
			}
		},
		checked(index, e) {
			if (e.currentTarget.checked) {
				this.definite = index;
			} else {
				this.definite = null;
			}
			this.checkAll = false;
		},
		selectAll() {
			this.checkedList = [];
			if (!this.checkedAll) {
				//
				this.dataSheetTotal.forEach(item => {
					this.checkedList.push(item.tablename);
				});
				this.checkAll = true;
			} else {
				//
				this.checkedList = [];
				this.checkAll = false;
			}
			this.definite = null;
		},
		// 
		meunList(index, item) {
			this.listSheetTotal = true;
			this.checkedList = [];
			this.checkAll = false;
			this.checkedAll = false;
			this.definite = null;
			this.meunNum = index;
			this.listId = item.id || item.config_id;
			this.configSn = item.config_sn;
			let self = this;
			this.$post('api/cDataSource', {
				member_sn: this.member_sn,
				config_sn: item.config_sn
			}).then(
				response => {
					let data = response.data;
					if (response.code == '000000') {
						self.dataSheetTotal = data;
						self.dataSheetName = data[0].dbname;
						this.$refs.checkeAll.click();
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
						// self.tipsData.text = response.err_msg.split(':')[1];
						// self.tipsData.rel = 'fail';
						// console.log(self.$refs.tips);
						// self.$refs.tips.closeTips();
					}
				},
				error => {
					console.log(error);
					// console.log(self.$refs.tips);
					this.tips.show(['']);
					// self.tipsData.text = '';
					// self.tipsData.rel = 'fail';
					// self.$refs.tips.closeTips();
				}
			);
		},
		requestDataLibName(tag) {
			let self = this;
			this.$get('api/gDataLibName', {
				type: this.message,
				member_sn: this.member_sn
			}).then(
				response => {
					this.showDBName = JSON.parse(JSON.stringify(response.data));
					if (this.showDBName.length == 0) {
						this.listSheetTotal = false;
					}
					if (response.code == '000000') {
						if (this.savedId != null) {
							this.meunList(this.showDBName.length - 1, this.savedId);
						} else if (tag == 'Initialize' || this.listId == this.delId) {
							this.meunList(0, this.showDBName[0]);
						}
					} else {
						this.tips.show([response.err_msg.split(':')[1]]);
						// self.tipsData.text = response.err_msg.split(':')[1];
						// self.tipsData.rel = 'fail';
						// self.$refs.tips.closeTips();
					}
				},
				error => {
					console.log(error);
					this.tips.show(['']);
					// self.tipsData.text = '';
					// self.tipsData.rel = 'fail';
					// self.$refs.tips.closeTips();
				}
			);
		}
	},
	mounted() {
		this.requestDataLibName('Initialize');
	}
};
</script>
<style scoped>
  .database .confirm.maskBackground,
  .database .manyPromptData.maskBackground {
    background: none;
  }

  .database i {
    vertical-align: 0;
  }

  .database .fileContent {
    width: 39.4vw;
    height: 64.3vh;
    background-color: #19202e;
    box-sizing: border-box;
  }

  .database .fileTitle {
    height: 4vh;
    line-height: 4vh;
    background-color: #273045;
    font-size: 0.9vw;
    text-align: center;
  }

  .database .fileTitle .saved {
    margin-right: 0.2vw;
    font-size: 0.8vw;
  }

  .database .content .nav {
    height: 5.8vh;
    font-size: 0.8vw;
    text-align: left;
    padding-left: 1.6vw;
    line-height: 5.8vh;
  }

  .database .content .list {
    border-bottom: 0.0625vw #080b0f solid;
    border-top: 0.0625vw #080b0f solid;
    position: relative;
    display: flex;
  }

  .database .content .listBase {
    width: 14.1vw;
    left: 0;
    height: 35.6vh;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 1.5vh 1.8vw;
    text-align: left;
  }

  .database .content .listBase li {
    line-height: 2.6vh;
    font-size: 0.7vw;
    cursor: pointer;
    padding-left: 0.5vw;
    margin-top: 0.4vh;
    position: relative;
    border: solid 0.0625vw transparent;
  }

  .database .content .listBase li:hover {
    background-color: #252e42;
  }

  .database .content .listBase .baseIcon {
    margin-right: 0.2vw;
    font-size: 0.6vw;
  }

  .database .content .listBase .meunDelete {
    position: absolute;
    right: 1vw;
    top: 0.1vh;
    font-size: 0.7vw;
  }

  .database .content .listBase .meunDelete:hover {
    color: #28b3fc;
  }

  .database .content .listBase .meunActive {
    background-color: #182b3f;
    border: solid 0.0625vw #28b3fc;
    color: #28b3fc;
  }

  .database .content .listSheet,
  .database .content .modifyDatabase,
  .database .content .noList {
    right: 0;
    width: 25.3vw;
    height: 44.7vh;
    box-sizing: border-box;
    text-align: left;
    padding: 1vh 1.6vw;
    border-left: 0.0625vw #080b0f solid;
  }

  .database .content .listSheet .sheetTitle,
  .database .content .modifyDatabase .sheetTitle {
    margin-bottom: 1vh;
    font-size: 0.8vw;
  }

  .database .content .modifyDatabase .createList ul li {
    line-height: 5.5vh;
    display: flex;
    align-items: center;
  }

  .database .content .modifyDatabase ul li .selections >>> .selection-list {
    margin-top: 0.3vh;
  }

  .database .content .modifyDatabase ul li .selections >>> input {
    height: 3vh;
  }

  .database .content .modifyDatabase .createList ul li input {
    background-color: #121721;
    border: solid 0.0625vw #475566;
    color: #fff;
    padding: 0 0.5vw;
    width: 16.9vw;
    height: 3vh;
  }

  .database .content .modifyDatabase ul li input:-webkit-autofill {
    background-color: #121721 !important;
    color: #fff !important;
