export default {
	namespaced: true,
	getters: {
		baseUrl: () => {
		  // if(s)
			if (document.domain === 'dv.24e.co') {
				return 'https://api.24e.co/';
			} else {
				return 'https://apidev.24e.co/';
			}
		},
		ssoUrl:()=>{
			if (document.domain === 'dv.24e.co') {
				return 'https://login.24e.co?callback=';
			} else {
				return 'https://logindev.24e.co?callback=';
			}
		}
	}
};
