<template>
  <div>
    <div class="optionsItem">
      <div class="photo-uplond">{{afferent.title}}</div>
      <div>
        <label v-show="isLabelShow"
          class="label-setting">
          <div class="bg-select-img"
            :style="{width:afferent.showWith+'vw',height:afferent.showHeight+'vw'}">
            <span class="basicChart iconimage bgImg"></span>
            <span class="bgFont">{{afferent.selectImg}}</span>
          </div>
          <input class="imgUpload"
            ref='file'
            type="file"
            name="file"
            :accept="afferent.accept"
            :multiple="afferent.multiple"
            @change="changeUrl">
        </label>
      </div>
      <div v-show="isImgShow"
        class="img-information"
        :style="{
          width:afferent.showWith+'vw',
          height:afferent.showHeight+'vw',
          backgroundImage:`url(${afferent.photoUrl})`}">
        <div class="img-delete"
          :style="{lineHeight:afferent.showHeight+'vw'}">
          <span class="biFont icon-delete"
            @click.stop="delectImg()"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
	name: 'pictureUpload',
	data() {
		return {
			// afferent:{
			//   photoUrl:'',//
			//   title:'',//
			//   selectImg:'',//
			//   showWith:4,//
			//   showHeight:4,//
			//   size:1024
			//   multiple:'',
			// }
			isImgDelete: false,
			isImgShow: false,
			isLabelShow: true
		};
	},
	props: {
		afferent: {
			type: Object,
			require: true
		}
	},
	watch: {
		afferent: {
		  deep:true,
			immediate:true,
			handler:function(val){
				if (val.photoUrl != '') {
					this.isImgShow = true;
					this.isLabelShow = false;
				} else {
					this.isImgShow = false;
					this.isLabelShow = true;
				}
			}
		}
	},
	methods: {
		changeUrl(e) {
			e = e || window.event;
			let files = e.target.files || e.dataTransfer.files;
			this.createImage(files[0], files[0].type, files[0].name);
			// debugger;
		},
		createImage(file, type, name) {
			let size = Math.floor(file.size / this.afferent.size), //kb
				vm = this;
			vm.isImgShow = true;
			vm.isLabelShow = false;
			// console.log(name.split('.').pop(),'');
			let reader = new FileReader(),//
				imgType = name.split('.').pop(),
				resulet = '';
			reader.readAsDataURL(file);
			reader.onload = function(e) {
				e = e || window.event;
				let target = e.target || e.srcElement;
				resulet = target.result;
				vm.afferent.photoUrl = target.result;
				if (imgType === 'jpg') { //jpgjpeg
					resulet = target.result.split('image/jpeg');
					resulet = resulet[0] + 'image/jpg' + resulet[1];
					// vm.$emit('baseUrl', {
					// 	baseUrl: resulet,
					// 	imgSize: size,
					// 	imgType: imgType
					// }); //
				}
				// else {
				vm.$emit('baseUrl', {
					baseUrl: resulet,
					imgSize: size,
					imgType: imgType
				}); //
				// }
			};

		},
		delectImg() {
			let vm = this;
			vm.isLabelShow = true;
			vm.isImgShow = false;
			vm.$emit('baseUrl', {
				baseUrl: '',
				imgSize: 0,
				imgType: ''
			});
			this.$refs.file.value = '';
		}
	}
};
</script>

<style scoped>
.label-setting {
  display: inline-block;
  background: #11151f;
  text-align: center;
  color: #fff;
}
.imgUpload {
  display: none;
  width: 0vw;
}
.optionsItem {
  margin-bottom: 0.8vw;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.photo-uplond {
  color: #acbbda;
}
.img-delete {
  background: rgba(2, 2, 2, 0.5);
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-delete span {
  display: block;
  font-size: 1.5vw;
  color: #a5b3d1;
  cursor: pointer;
}
.img-information {
  text-align: right;
  position: relative;
  border: solid 0.0625vw #475566;
  background-size: 100% 100%;
}
.img-information .img-delete {
  text-align: center;
  display: none;
}
.img-information:hover .img-delete {
  display: block;
}
.bg-select-img {
  border: solid 0.0625vw #475566;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}
.bgImg {
  display: block;
  font-size: 1.6vw;
  color: #475566;
}
.bgFont {
  user-select: none;
  display: block;
  margin-top: 0.5vw;
  color: #475566;
}
</style>
