<template>
  <div :class="selectClass"
    :style="selectStyle"
    class="public-select">
    <div class="select-input"
      :style="inputStyle"
      v-click-outside="{fn:hideOptions}"
      :class="{'select-color':showOptions}"
      @click="toggleOptionsShow">
      <span class="inputSpan"
        :style="{
          lineHeight:inputStyle.height
        }">{{SelectedOption}}</span>
      <span class="vCenter triangleDown"
        :class="{'rotate':showOptions}"></span>

      <ul class="options"
        :style="{height:selectOptions.length>5&&!unSelectale?'8vw':'auto'}"
        @click.stop
        v-show="showOptions">
        <easy-scroll :barOption="ScrollOpt">
          <li @click="clearFilter"
            v-if="options.showAllSelectName">{{getPlaceHolderName}}</li>
          <template v-if="!unSelectale">
            <li :key="index"
              @click="onSelected(option)"
              v-for="(option,index) in selectOptions">{{option}}</li>
          </template>
        </easy-scroll>
      </ul>
    </div>
    <div class="select-title"
      v-if="options.showTitle"
      :style="{
          lineHeight:selectStyle.height
        }">{{getSelectTitle}}</div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
	name: 'publicSelect',
	props: {
		opt: Object,
		addColon: {
			type: Boolean,
			default: true
		},
		showTitle: {
			type: Boolean,
			default: true
		},
		selectClass: String,
		selectStyle: Object,
		inputStyle: Object,
		selectTitle: String,
		resetAllSelect: Boolean,
		selectOptions: Array,
		unSelectale: { type: Boolean, default: false }
	},
	data() {
		return {
			SelectedOption: null,
			showOptions: false,
			defaultOpt: {
				showTitle: true,
				showAllSelectName: true
			},
			options: {}
		};
	},
	computed: {
		...mapGetters('main', ['vwToPx']),
		getSelectTitle() {
			let vm = this;
			let selectTitle = vm.selectTitle;
			if (this.addColon) {
				selectTitle = selectTitle ? selectTitle + ':' : '';
			}
			return selectTitle;
		},
		ScrollOpt() {
			return {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barOpacityMin: 0.3, //
				barWidth: 4,
				barMarginRight: 0,
				borderWidth: this.vwToPx(0.125)
			};
		},
		getPlaceHolderName() {
			return this.options.allSelectName || '' + this.selectTitle;
		}
	},
	created() {
		this.SelectedOption =
      this.options.allSelectName || '' + this.selectTitle;
	},
	methods: {
		reset() {
			this.$emit('onSelected', {
				type: 'reset',
				name: 'all'
			});
			this.SelectedOption = this.getPlaceHolderName;
		},
		resetAllSelectName() {
			this.SelectedOption = this.getPlaceHolderName;
		},
		clearFilter() {
			this.$emit('onSelected', {
				type: 'all',
				name: 'all'
			});
			this.SelectedOption = this.getPlaceHolderName;
			this.showOptions = false;
		},
		onSelected(option) {
			this.$emit('onSelected', {
				type: 'normal',
				name: option
			});
			this.SelectedOption = option;
			this.showOptions = false;
		},
		toggleOptionsShow() {
			this.showOptions = !this.showOptions;
		},
		hideOptions() {
			this.showOptions = false;
		}
	},
	watch: {
		opt: {
			handler() {
				this.options = Object.assign(this.defaultOpt, this.opt);
			},
			immediate: true,
			deep: true
		},
		unSelectale: {
			handler(val) {
				if (val) {
					this.reset();
				}
			}
		},
		resetAllSelect: {
			handler(val) {
				if (val) {
					this.resetAllSelectName();
				}
			}
		}
	}
};
</script>
<style scoped>
.public-select {
  position: relative;
  color: #fff;
}

.select-title {
  user-select: none;
  /* font-size: 1vw; */
  float: right;
}

.select-input {
  user-select: none;
  position: relative;
  float: right;
  margin-left: 0.5vw;
  /* background-image: url("~@/assets/Demo/img/filterContainerBg.png"); */
  background-size: 100% 100%;
}

.select-input .inputSpan {
  display: inline-block;
  width: auto;
  /* font-size: 0.9vw; */
  height: 100%;
  padding-left: 0.4vw;
  color: #fff;
  border-color: #475566;
  cursor: pointer;
  max-width: 6vw;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.triangleDown {
  right: 0.2vw;
  width: 0.6vw;
  height: 0.6vw;
  cursor: pointer;
  background: url("./asset/img/triangleDown.svg");
  background-size: 100% 100%;
  position: absolute;
}

.triangleDown.rotate {
  transform: translateY(-50%) rotateX(180deg);
}

ul.options {
  z-index: 9999;
  border: solid 0.0625vw #475566;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: #11151f;
  font-size: 0.6vw;
  color: #fff;
}

ul.options li {
  font-size: 0.8vw;
  height: 1.2vw;
  line-height: 1.2vw;
  text-align: left;
  cursor: pointer;
  padding: 0 0.2vw;
}

ul.options li:hover {
  background-color: #252e42;
}
</style>
