<template>
  <div class="context-ment"
    :style='munuStyle'>
    <div class="mouseRight"
      v-for="(item,idx) in selectList" :key='idx' @click='backIndex(idx)'>{{item}}
    </div>
  </div>
</template>

<script>
export default {
	name: 'index',
	props: {
		clientx: {
			type: Number
		},
		clienty: {
			type: Number
		},
		selectList: {
			type: Array
		}
	},
	computed: {
		munuStyle() {
			let vm = this;
			return {
				left: vm.clientx + 'px',
				top: vm.clienty + 'px',
		  // transform: 'translate('+this.clientx + 'px,'+this.clienty + 'px)',
			};
		}
	},
	methods:{
	  backIndex(idx){
	  	this.$emit('clickState',idx);
		}
	}
};
</script>

<style scoped>
  .context-ment {
    position: relative;
    display: inline-block;
    width: 3.6vw;
    color: rgba(0, 248, 255, 1);
    height: 1.3vw;
    line-height: 1.3vw;
    box-shadow: 0vw 0.1vw 0.3vw 0vw rgba(0, 0, 0, 0.3)
  }


  .mouseRight {
    text-align: center;
    cursor: pointer;
    background: rgb(23, 33, 50);
  }

  .mouseRight:hover {
    background: #131721;
  }
</style>
