<template>
  <div class="dataDrop-down">
    <div @click.stop="endClick($event)"
      @mousedown="startClick($event)"
      class="select-list">
      <span class="dataSource icon-char"></span>
      <span>{{dataSourceOptions.screenField}}</span>
    </div>
    <div class="div-class-padding">
      <div class="div-position parent-dom"
        v-show="dropDownShow">
        <div @click="allClickList"
          class="top-all-model"><span class="span-border-real">
            <span :class="{'check-style-unequal-width':allState}"
              class="allCenter"></span></span>
          <span class="selectAll"></span>
        </div>
        <EasyScroll :barOption="opt"
          :viewStyle="viewStyle"
          v-on="$listeners">
          <div style="height: 8vw">
            <ul>
              <li :key="idx"
                @click="dropDownClick(item)"
                class="li-click"
                v-for="(item,idx) in showList"><span class="span-border-real"><i :class="{'check-style-unequal-width':item.state}"
                class="allCenter"></i></span>{{item.name}}
              </li>
            </ul>
          </div>
        </EasyScroll>
      </div>
    </div>
  </div>
</template>
<!--xScrollToEnd-->

<script>
export default {
	name: 'dataFilterDropDown',
	data() {
		return {
			viewStyle: {
				padding: '0 0.8vw 0 0.5vw'
			},
			contentData: [],
			dropDownShow: false,
			allState: false,
			showList: [],
			clickable: true,
			startPositon: []
			// endPositon: []
		};
	},
	props: {
		// editable:Boolean,// 
		dropList: Array,
		pitchArr: Array,
		introduiction: String,
		dataSourceOptions: {
			type: Object,
			required: true
		},
		selectAll: Boolean,
	},
	watch: {
		pitchArr: {
			immediate: true,
			deep: true,
			handler() {
				// this.listComputed();
			}
		},
		dropList: {
			immediate: true,
			deep: true,
			handler() {
				this.listComputed();
			}
		},
		contentData() {
			if (this.contentData.length === this.showList.length) {
				this.allState = true;
			} else {
				this.allState = false;
			}
		},
	},
	computed: {
		vwToPx() {
			return this.$store.getters['main/vwToPx'];
		},
		opt() {
			return {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barWidth: 5, //
				barMarginRight: 6, //px
				barOpacityMin: 0.3, //

				borderWidth: this.vwToPx(0.12)
			};
		},

	},
	methods: {
		// handleScroll(data){
		//   this.$emit('handler',data);
		// },
		startClick(e) {
			this.$set(this.startPositon, 0, e.clientX);
			this.$set(this.startPositon, 1, e.clientY);
		},
		endClick(e) {
			let startX = this.startPositon[0];
			let startY = this.startPositon[1];
			let endX = e.clientX;
			let endY = e.clientY;
			let dx = Math.abs(endX - startX);
			let dy = Math.abs(endY - startY);
			let dis = Math.sqrt(Math.pow(dx, 2) + Math.pow(dy, 2));
			if (dis < 5) {
				this.dropDownShow = !this.dropDownShow;
			}
			// this.$set(this.endPositon, 0, e.clientX);
			// this.$set(this.endPositon, 1, e.clientY);
		},
		toggeleDropDownShow($event) {
			// if (this.clickable) {
			this.dropDownShow = !this.dropDownShow;
			// } else {
			// 	this.clickable = true;
			// }
		},
		stopClick() {
			this.clickable = false;
		},
		//
		listComputed() {
			this.dropList.forEach(item => {
				if (this.pitchArr.indexOf(item) > -1) {
					this.showList.push({name: item, state: true});
				} else {
					this.showList.push({name: item, state: false});
				}
			});
			if (this.dropList.length && this.dropList.length === this.pitchArr.length) {
				this.allState = true;
				// this.allClickList();
			}
			if (this.selectAll === true) {
				this.allState = false;
				this.allClickList();
			}
		},
		delectSelect(data) {
			this.contentData.splice(
				this.contentData.findIndex(item => item === data)
			);
		},
		dropDownClick(item) {
			// if (!this.editable) return;// 
			item.state = !item.state;
			this.contentData = [];
			this.showList.forEach(item => {
				if (item.state === true) {
					this.contentData.push(item.name);
				}
			});
			setTimeout(() => {
				this.$emit('receiveArr', this.contentData, this.allState);
			}, 0);
		},
		allClickList() {
			// if (!this.editable) return;// 
			this.allState = !this.allState;
			this.contentData = [];
			this.showList.forEach(item => {
				item.state = this.allState;
				if (this.allState) {
					this.contentData.push(item.name);
				}
			});
			setTimeout(() => {
				this.$emit('receiveArr', this.contentData, this.allState);
			}, 0);
		}
	},
	mounted() {
	}
};
</script>

<style scoped>
  .li-click {
    cursor: pointer;
    padding: 0.2vw 0;
    font-size: 0.4vw;
  }

  .span-i-liist {
    display: inline-block;
    width: 0.6vw;
  }

  .dataDrop-down {
    color: #ffffff;
  }

  .select-list {
    display: inline-block;
    width: 100%;
    height: 1.5vw;
    line-height: 1.1vw;
    border: 0.0625vw solid rgb(40, 179, 252);
    padding: 0.2vw 0.4vw;
    cursor: pointer;
    font-size: 0.7vw;
    background: rgb(26, 62, 90);
  }

  .select-list span:first-child {
    font-size: 0.6vw;
    color: #28b3fc;
  }

  .div-class-padding {
    position: relative;
  }

  .div-position {
    padding: 0.8vw 0;
    position: absolute;
    width: 100%;
    border: 0.0625vw solid rgb(40, 179, 252);
    /* margin-top: 0; */
    background: rgb(26, 62, 90);
  }

  .top-all-model {
    cursor: pointer;
    padding: 0 0.5vw;
  }

  .span-border-real {
    display: inline-block;
    position: relative;
    height: 0.6vw;
    width: 0.6vw;
    border: 0.0625vw solid #fff;
    margin-right: 0.5vw;
    vertical-align: middle;
  }

  .ul-padding {
    padding: 0 0.5vw;
  }

  .check-style-unequal-width {
    display: inline-block;
    /* position: relative; */
    top: -0.1vw;
    width: 0.2vw;
    left: 0.1vw;
    height: 0.4vw;
    border-color: #ffff;
    border-style: solid;
    border-width: 0 0.1vw 0.1vw 0;
    transform: rotate(50deg);
  }

  .selectAll {
    vertical-align: middle;
  }
</style>
