<template>
  <div class="spread-sider" v-if="!isPhoneLayout">
    <div class="tabs">
      <span class="chart biFont icon-projectNum"
            @click="addIconColor(0)"
            :class="{'active':leftPaneIndex === 0 }"></span>
      <span class="layer biFont icon-topLayer"
        :class="{'active':leftPaneIndex === 1 }"
        @click="addIconColor(1)"></span>
      <span @click="changeItems"
        class=" biFont icon-doubleArrowLeft recovery-left"></span>
    </div>
    <layer-controller v-show="leftPaneIndex===1"></layer-controller>
    <div class="container"
      v-show="leftPaneIndex===0">
      <search-child :searchObject="searchObject"
        @fromChild="getChildSearch"
        @inputReset="inputReset"></search-child>
      <div class="content">
          <ul v-show="isShowSearchData" style="height:32vw">
                <div @click="showAllData">
                  <span class="biFont icon-arrowLeft arrowLeft"></span>
                  <span>{{this.showBaseArr.length}}</span>
                </div>
                <easy-scroll :barOption="reactiveScrollOpt">
                  <li>
                    <div class="shape-items">
                      <div v-for="(childItems,index) in showBaseArr"
                        v-bind:key="index"
                        class="items-containter">
                        <div @click="choiceSearchShape(index,childItems)"
                          :title="childItems.name">
                          <div class="shape-size"
                            :class="{'active':index === isChoiceSingle}">
                            <svg class="icon svg"
                              aria-hidden="true">
                              <use :xlink:href="childItems.baseShapeUrl"></use>
                            </svg>
                            <div v-show="childItems.isLock"
                              :class="{lock:childItems.isLock}">
                              <span class="lock-icon"></span>
                            </div>
                          </div>
                          <span class="scale-font text">{{childItems.name}}</span>
                        </div>
                      </div>
                    </div>
              </li>
            </easy-scroll>
          </ul>
        <ul v-show="isShowAllData">
          <li v-for="(item,index) in baseShapeArr"
            v-bind:key="item.id">
            <div v-on:click="handleNodeClick(item)">
              <span class="biFont icon-arrowRight listBtn"
                :class="{dropDown:item.show}"></span>
              <span>{{item.tableTypeName}}({{item.children.length}})</span>
            </div>
            <easy-scroll :barOption="reactiveScrollOpt">
              <div v-show="item.show"
                class="shape-items basic-height">
                <!-- <div> -->
                <div v-for="(childItems,childIndex) in item.children"
                  :key="childItems.id"
                  class="items-containter">
                  <div @click.stop="choiceShape(index,childIndex,childItems)"
                    :title="childItems.name">
                    <div class="shape-size"
                      :class="{active:index==chartListShowId&&childIndex==chartShowId}">
                      <svg class="icon svg"
                        aria-hidden="true">
                        <use :xlink:href="childItems.baseShapeUrl"></use>
                      </svg>
                      <div v-show="childItems.isLock"
                        :class="{'lock':childItems.isLock}"><span class="lock-icon"></span></div>
                    </div>
                    <span class="scale-font text">{{childItems.name}}</span>
                  </div>
                </div>
              </div>
              <!-- </div> -->
            </easy-scroll>
          </li>
        </ul>
        <!-- <span @click="changeItems"
          class="biFont icon-arrowLeft recovery-left"></span> -->
      </div>
    </div>
  </div>
</template>

<script>
import '@/assets/SiderBar/js/iconfont.js';
import {baseChartList} from '@/assets/SiderBar/js/basicChartData.js';
import SearchChild from '@/components/Search';
import LayerController from './LayerController';
import {mapGetters} from 'vuex';

export default {
	name: 'spread-sider',
	components: {
		'search-child': SearchChild,
		'layer-controller': LayerController,
	},
	data() {
		return {
			isShowChildrenItems: -1,
			isChangeChildItems: -1,
			isChoiceSingle: -1,
			isShrink: false,
			chartListShowId: -1,
			chartShowId: -1,
			showBaseArr: [],
			returnArr: [],
			isShowAllData: true,
			isShowSearchData: false,
			baseShapeArr: baseChartList,
			searchObject: {
				isClearInput: false,
				isShowPanel: true,
				beQueriedList: [],
				width: '100%'
			}
		};
	},
	computed: {
		...mapGetters('defaultChartOptions', {
			getChartOptions: 'getEchartsDefaultOption'
		}),
		...mapGetters('dashboard', {
			leftPaneIndex: 'getLeftPaneIndex'
		}),
		...mapGetters('canvasPlace', {
			isPhoneLayout: 'getPhoneLayoutState',
		}),
		...mapGetters('main', ['getVw']),
    ...mapGetters('voiceCommand',{// 

    }),
		reactiveScrollOpt() {
			return {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barWidth: this.getVw(4), //
				barMarginRight: 3, //px
				barOpacityMin: 0.1, //
				borderWidth:2
			};
		}
	},
	watch: {
		showBaseArr(val) {
			if (!val.length) {
				this.isShowSearchData = false;
				this.isShowAllData = true;
			}
		},
	},
	methods: {
		addIconColor(index) {
			this.$store.commit('dashboard/setStateProperty', {key:'leftPaneIndex',value:index});
		},
		getChildSearch(fromChildData) {
      this.showBaseArr = fromChildData;
      console.log(this.showBaseArr);
      
			this.isShowAllData = false;
			this.isShowSearchData = true;
		},
		handleNodeClick(item) {
			item.show = !item.show;
		},
		choiceSearchShape(index, childItems) {
      console.log(123);
      
			this.$emit('getChartType', childItems);
			this.isChoiceSingle = index;
		},
		choiceShape(index, childIndex, childItems) {
			if (!childItems.isLock) {
				this.$emit('getChartType', childItems);
				// this.addEchart(index, childIndex, childItems.chartType);
				if ((this.chartListShowId = index && this.chartShowId == childIndex)) {
					this.chartListShowId = -1;
					this.chartShowId = -1;
					return false;
				}
				this.chartListShowId = index;
			}
		},
		changeItems() {
			this.$store.commit('dashboard/setStateProperty', {key:'leftSideWidth',value:2.6});
			this.$store.commit('dashboard/setStateProperty', {key:'leftSideOpen',value:false});
		},
		findAllChildren() {
			let vm = this;
			vm.returnArr = [];
			vm.baseShapeArr.forEach(e => {
				vm.searchObject.beQueriedList.push(...e.children);
			});
		},
		showAllData() {
			this.isShowAllData = true;
			this.isShowSearchData = false;
			this.$set(this.searchObject, 'isClearInput', true);
		},
		inputReset() {
			this.$set(this.searchObject, 'isClearInput', false);
			// this.searchObject.isClearInput = false;
		}
		/**
		 *
		 * index 
		 * childIndex  
		 * childItems.chartType 
		 * 
		 */
		// addEchart(fatherId, childId, chartType) {
		// 	this.$store.commit("canvasPlace/setChartContainer", this.getChartOptions(chartType));
		// }
	},
	mounted() {
		this.findAllChildren();
	}
};
</script>



<style scoped>
  .spread-sider {
    width: 11.6vw;
    height: 53.6vw;
    top: 0;
    left: 0;
    z-index: 20;
    background-color: #171e2b;
    border-right: 0.0625vw solid #0b0e14;
    border-top: 0.0625vw solid #0b0e14;
  }

  .spread-sider .tabs {
    width: 100%;
    background: rgba(39, 48, 69, 0.7);
    height: 3.3vh;
    line-height: 3.3vh;
  }

  .spread-sider .tabs span {
    cursor: pointer;
    float: left;
    text-align: center;
    display: block;
    font-size: 0.9vw;
    width: 2.6vw;
    height: 100%;
    color: #fff;
  }

  .spread-sider .tabs span:active,
  .spread-sider .tabs span:hover {
    background-color: #161c29;
  }

  .spread-sider .tabs span:active,
  .spread-sider .tabs span:hover {
    color: #28b3fc;
  }

  .spread-sider .tabs .active {
    background-color: #161c29;
    color: #28b3fc;
  }

  .spread-sider .tabs span.recovery-left {
    font-size: 0.6vw;
    float: right;
  }

  .shape-items .scale-font{
    color: #fff;
  }



  .spread-sider .container {
    user-select: none;
    padding-top: 1vw;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #fff;
  }

  .spread-sider .container >>> .search-box {
    width: 100%;
    padding: 0 0.4vw;
  }

  .spread-sider .container >>> .search-icon  {
    right: 8%;
  }

  .spread-sider .container >>> .select-box {
    width: 93%;
  }

  .container input {
    background-color: #121721;
    width: 10.2vw;
    height: 1.2vw;
    outline: none;
    border: solid 0.1vh #384564;
    padding: 0.2vw;
    color: #fff;
    font-size: 0.4vw;
  }

  .container input::-webkit-input-placeholder {
    width: 4.4vw;
    font-size: 0.4vw;
    color: #798194;
    padding-top: 0.1vw;
    padding-left: 0.6vw;
  }

  .content {
    margin-top: 0.5vw;
    /* padding: 0 0.4vw; */
    width: 100%;
    height: 60vh;
  }

  .content ul {
    /* width: 10.2vw; */
    width: 100%;
    height: 34.6vw;
    padding: 0;
    margin: 0;
  }

  .content ul > li {
    cursor: pointer;
    color: #fff;
    text-align: left;
    font-size: 0.6vw;
    border-bottom: 0.1vh solid #0e121a;
    padding-bottom: 0.4vw;
    margin-left: 0.4vw;
  }

  .content ul > li {
    padding-top: 0.4vw;
  }

  .content ul > li:last-child {
    border: none;
  }

  span.arrowLeft {
    display: inline-block;
    transform: scale(0.75);
    font-size: 0.6vw;
    margin: 0 0.3vw;
  }

  span.listBtn {
    display: block;
    float: left;
    font-size: 0.6vw;
    margin: 0.15vw 0.3vw 0;
    transform: scale(0.75);
  }

  span.dropDown {
    transform: rotate(90deg) scale(0.75) !important;
  }

  ul > li img {
    width: 0.5vw;
    height: 0.5vw;
  }

  .shape-items {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    margin-bottom: 1vw;
    align-content:flex-start
  }

  .basic-height {
    height: 60vh;
  }

  .shape-items .items-containter {
    margin-right: 0.4vw;
    margin-left: 0.1vw;
  }

  .shape-items .scale-font {
    user-select: none;
    display: inline-block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .shape-size {
    border: solid 0.0625vw transparent;
    width: 2.8vw;
    height: 2.5vw;
    margin-top: 1vw;
    margin-left: 0.2vw;
    position: relative;
  }

  .lock {
    background: rgba(2, 2, 2, 0.5);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .lock-icon {
    cursor: pointer;
    background: url("~@/assets/SiderBar/img/lock.svg");
    display: block;
    width: 0.8vw;
    height: 0.8vw;
    background-size: 100% 100%;
  }

  .shape-size:hover {
    background-color: #121721;
    border-color: #475566;
  }

  .shape-size .svg {
    width: 1.7vw;
    height: 1.7vw;
    margin: 0.3vw 0.5vw 0.1vw;
  }

  .text {
    line-height: 0.7vw;
    margin-top: 0.3vw;
    width: 3.2vw;
    text-align: center;
    font-size: 0.6vw;
    display: block;
    transform: scale(0.8);
    white-space: nowrap;
  }

  .icon {
    width: 1em;
    height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
  }
</style>
