    @mousedown="getContainerSize">
    <div class="bg"
      v-show="!isSetOptions"></div>
    <v-chart ref="instance"
      :options="options"
      autoresize
      v-show="isSetOptions"></v-chart>
  </div>
</template>
<script>
import { echartsCommonMixin } from '../../mixin/echartsCommonMixin';
import 'echarts/lib/chart/bar';
import 'echarts/lib/chart/line';
export default {
	name: 'echarts-stackBarAndLine',
	mixins: [echartsCommonMixin],
};
</script>
<style scoped>
.bg {
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
}
