<template>
  <div>
    <div class="chart-basis"
      @click="showEvent(5)">
      <span></span>
      <span class="biFont icon-arrowLeft icon-fontsize"
        :class="{rotateIcon:isShowPropmt}"></span>
    </div>
    <div v-show="isShowPropmt"
      class="content-box">
      <ul>
        <li class="optionsItem">
          <select-common :ordinaryList="shareList"
            :isEchart="false"
            :selectTitle="selectTitle"
            @fromSelectChild="getSelectData"></select-common>
        </li>
        <li class="optionsItem">
          <input-common :inputData="inputData"
            @onChange="getInputData"></input-common>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <Silder-data :opt="settingBorder"
            v-on:rangedata="rangeBorder"></Silder-data>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import SilderData from './BasicComponent/Slider';
import InputCommon from './BasicComponent/InputCommon';
import SelectCommon from './BasicComponent/SelectCommon';
export default {
	name: 'DataPrompt',
	components: {
		'Silder-data': SilderData,
		'input-common': InputCommon,
		'select-common': SelectCommon
	},
	data() {
		return {
			isShowPropmt: true,
			settingBorder: {
				name: '',
				max: 100,
				min: 1,
				valueRange: [1],
				unix: ''
			},
			selectTitle: '',
			inputData: {
				inputTitle: '',
				inputValue: ''
			},
			shareList: [
				{
					id: 1,
					name: '',
					value: 'yes'
				},
				{
					id: 2,
					name: '',
					value: 'no'
				}
			]
		};
	},
	methods: {
		rangeBorder(data) {},
		//input
		getInputData(val) {},
		//
		showEvent(number) {
			this.isShowPropmt = !this.isShowPropmt;
		},
		getSelectData(data) {}
	}
};
</script>

<style scoped>
.optionsItem {
  margin-bottom: 0.8vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.optionsItem .span-title {
  display: block;
  flex: 1;
  color: #acbbda;
}
span,
input {
  user-select: none;
}
.chart-basis .rotateIcon {
  transform: rotate(-90deg);
}
</style>



