<template>
  <div class="basis-grid-div label-befor">
    <span class="color-font">{{opt.name}}</span>
    <div class="line-box">
        <slider-range style="width:5.1vw;margin-left:-0.01vw;margin-right:0.3vw"
        v-on="$listeners"
        :valueRange="opt.valueRange"></slider-range>
      <div class="screen-input">
        <input class="number-input"
          :value="opt.valueRange"
          :class="{hightlight}"
          @input="inputMethod($event)"
          @focus="focusInput"
          @blur="blurInput"
          type="number">
        <span class="unit-px">{{opt.unit}}</span>
      </div>
      <!--      onkeypress='return( /[\d]/.test(String.fromCharCode(event.keyCode) ) )'-->
    </div>
  </div>
</template>

<script>
// 
import SliderRange from '@/components/SliderRange';
export default {
	name: 'Slider',
	data() {
		return {
			name: '',
			garidyValue: 2, // y
			valueRange: 1, //
			min: 0, //
			max: 100, //
			hightlight: false
		};
	},
	components: {
		'slider-range': SliderRange
	},
	props: {
		opt: {
			type: Object,
			require: true
		}
	},
	computed: {},
	methods: {
		// 
		inputMethod($event) {
			// console.log($event.target.value,typeof parseInt($event.target.value) ,'');
			if (
				typeof parseInt($event.target.value) === 'number' &&
        parseInt($event.target.value) <= 100
			) {
				this.$emit('inputData', parseInt($event.target.value));
			} else if (parseInt($event.target.value) > 100) {
				this.opt.garidyValue = 100;
				this.opt.valueRange = 100;
			} else {
				this.opt.garidyValue = this.opt.valueRange;
			}
		},
		focusInput() {
			this.hightlight = true;
		},
		blurInput($event) {
			this.hightlight = false;
			// console.log(
			// 	$event.target.value,
			// 	typeof parseInt($event.target.value),
			// 	''
			// );
			if (
				typeof parseInt($event.target.value) === 'number' &&
        parseInt($event.target.value) <= 100
			) {
				this.$emit('inputData', parseInt($event.target.value));
			} else if (parseInt($event.target.value) > 100) {
				this.opt.garidyValue = this.opt.valueRange;
			} else {
				this.opt.garidyValue = this.opt.valueRange;
			}
		}
	},
	mounted() {}
};
</script>
<style>
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type="number"] {
  -moz-appearance: textfield;
}
</style>
<style scoped>
/**/
.basis-grid-line .slider-box .vue-slider .vue-slider-rail .vue-slider-process {
  background-color: #ffffff;
}
/**/
.basis-grid-line
  .vue-slider
  .vue-slider-rail
  .vue-slider-dot
  .vue-slider-dot-handle {
  border: 0.1vw solid #ffffff;
}
.label-befor {
  position: absolute;
}
.basis-grid-div {
  float: right;
  position: relative;
}
.color-font {
  color: #acbbda;
}
.line-box {
  display: flex;
  align-items: center;
}

.screen-input {
  position: relative;
  width: 2.1vw;
  border: solid 0.0625vw #11151f;
}
.number-input {
  /*width: 1.2vw;*/
  width: 2vw;
  height: 1vw;
  background-color: #11151f;
  border: solid 0.0625vw #475566;
  color: #ffffff;
  font-size: 0.01vw;
  /*textArea-align: center;*/
  text-align: center;
}
.hightlight {
  border: solid 0.0625vw #28b3fc;
}
.unit-px {
  position: absolute;
  right: 0.02vw;
  top: 0.01vw;
  color: #acbbda;
  font-size: 0.1vw;
  transform: scale(0.9);
}
</style>
