<template>
  <div class="sider-items-right"  v-if="!isPhoneLayout">
    <ul>
      <li v-for="(item,index) in iconArr"
        :key="item.id"
        @click="addIconColor(item,index)"
        :class="{active:index == activeIndex }">
        <span :class="`icon-${item.iconName}`"
          class="biFont"></span>
        <span class="tips aCenter">{{item.name}}</span>
      </li>
    </ul>
    <div class="icon-size"
      @click="changeItems">
      <span class="biFont icon-doubleArrowRight right"></span>
    </div>
  </div>
</template>
<script>
import {mapGetters} from 'vuex';
export default {
	name: 'SiderItems',
	data() {
		return {
			iconArr: [
				{ id: 1, iconName: 'dataBase', name: '' },
				{ id: 2, iconName: 'fieldShow', name: '' },
				{ id: 3, iconName: 'format', name: '' },
				{ id: 4, iconName: 'dengpaolinggan', name: '' },
				// { id: 4, iconName: 'settingPush', name: '' }
			]
		};
	},
	computed: {
		activeIndex() {
			return this.$store.state.dashboard.rightPaneIndex;
		},
		...mapGetters('canvasPlace', {
			isPhoneLayout: 'getPhoneLayoutState',
		}),
	},
	watch: {
	},
	methods: {
		changeItems() {
			this.$store.commit('dashboard/toggleRightPaneShow',false);
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneHover',value:false});
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneIndex',value:-1});
		},
		addIconColor(item, index) {
			this.$store.commit('dashboard/setStateProperty', {key:'rightPaneIndex',value:index});
		}
	},
	mounted() {
		this.$store.commit('dashboard/setStateProperty', {key:'rightPaneIndex',value:0});
	}
};
</script>
<style scoped>
.sider-items-right {
  margin-top: 0.12vh;
  background-color: #273045;
  width: 15.2vw;
  height: 1.9vw;
}

.sider-items-right ul {
  float: left;
  width: 11vw;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.sider-items-right ul li {
  user-select: none;
  cursor: pointer;
  flex: 1;
  text-align: center;
  position: relative;
}
.sider-items-right ul li:active,
.sider-items-right ul li:hover {
  background-color: #161c29;
}
.sider-items-right ul li span:first-child {
  color: #fff;
  font-size: 0.8vw;
  line-height: 1.9vw;
}
.sider-items-right ul li.active span:first-child,
.sider-items-right ul li:hover span:first-child {
  color: #28b3fc;
}
/* .sider-items-right ul li:last-child span:first-child {
  font-size: 1vw;
} */

.sider-items-right ul li.active {
  background-color: #161c29;
}
.tips {
  visibility: hidden;
  padding: 0 0.2vw;
  color: #fff;
  white-space: nowrap;
  background-color: #28b3fc;
  position: absolute;
  display: inline-block;
  height: 1.1vw;
  line-height: 1.1vw;
  text-align: center;
  top: 2.2vw;
  z-index: 1;
}
.sider-items-right ul li:hover .tips {
  visibility: visible;
}
.icon-size {
  padding-right: 0.8vw;
  float: left;
  width: 4.2vw;
  height: 1.9vw;
  line-height: 1.9vw;
  text-align: right;
  cursor: pointer;
}
.icon-size .right {
  font-size: 0.6vw;
  color: #fff;
  height: 0.7vw;
  width: 0.8vw;
  transform: scale(0.8);
}
</style>


