<template>
  <div class="container">
    <div class="bg"
      v-show="!isSetOptions"></div>
    <v-chart ref="instance"
      :options="charOptions"
      autoresize
      v-show="isSetOptions"></v-chart>
  </div>
</template>
<script>
import { echartsCommonMixin } from '../../mixin/echartsCommonMixin';
import 'echarts/lib/chart/pie'
export default {
	name: 'echarts-basicDoughnut',
	mixins: [echartsCommonMixin],
	computed:{
		charOptions(){
			let opt = JSON.parse(JSON.stringify(this.options));
			opt.legend.formatter = function (param) {
				if (param.length > 4) {
					return param.substr(0, 4) + '...';
				} else {
					return param;
				}
			};
			return opt;
		}
	}
};
</script>
<style scoped>
.bg {
  background-image: url("~echartsBg/basicDoughnut.png");
  background-repeat: no-repeat;
}
</style>
