<template>
  <div class="layer-box"
    data-class="unableDeactive">
    <div class="adjusting-styles">
      <search-child :searchObject="searchObject"
        @fromChild="getChildSearch"
        @inputReset="inputReset"></search-child>
    </div>

    <easy-scroll :barOption="reactiveScrollOpt">
      <ul>
        <li v-show="searchReturnList.length">
          <div class="return-layerout"
            @click="returnLayerout">
            <span class="biFont icon-arrowLeft arrowLeft"></span>
            <span>{{this.searchReturnList.length}}</span>
          </div>
        </li>
        <li v-for="(item,index) in chartsLayerList"
          v-show="!item['containerStatus']['isDeleted']"
          :class="{'add-borderColor':item['containerOptions']['layerIndex'] === activeIndex}"
          :key="index"
          @click="curIndex(item['containerOptions']['layerIndex'],item)">
          <div class="layer-list"
            :title="item['containerOptions']['layerName']">
            <div class="layer-info">
              <div class="shape-size">
                <svg class="icon svg-icon-size"
                  aria-hidden="true">
                  <use :xlink:href="item['containerOptions']['layerChartIcon']"></use>
                </svg>
              </div>
              <div class="layer-name"><input type="text"
                  class="edit-input"
                  :class="{'unEditInput':!item['containerOptions']['isLayerEdit']}"
                  ref="editInput"
                  spellcheck="false"
                  @blur.stop="inputBlur(index,item)"
                  @keyup.enter.stop="inputBlur(index,item)"
                  v-model.trim="item['containerOptions']['layerName']"></div>
            </div>
            <more-option v-show="item['containerOptions']['layerIndex'] == activeIndex"
              :tableId="item['containerOptions']['tableId']"
              :chartIndex="item['containerOptions']['layerIndex']"
              :isLayer="true"></more-option>
          </div>
        </li>
      </ul>
    </easy-scroll>
  </div>
</template>
<script>
import SearchChild from '@/components/Search';
import MoreOption from '@/components/CanvasPlace/Charts/components/MoreOption';
import '@/assets/SiderBar/js/iconfont.js';
import { mapGetters } from 'vuex';

export default {
	name: 'LayerControler',
	components: {
		'search-child': SearchChild,
		'more-option': MoreOption
	},
	computed: {
		...mapGetters('main', ['getVw']),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',
			layerList: 'getChartList',
			delLayerCount: 'getDelLayerCount'
		}),

		reactiveScrollOpt() {
			return {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barOpacityMin: 0.3, //
				barWidth: this.getVw(4),
				barMarginRight: this.getVw(3)
			};
		},
		chartsLayerList() {
			if (this.searchReturnList.length) {
				return this.searchReturnList;
			} else {
				return this.layerList;
			}
		}
	},
	watch: {
		'layerList.length': {
			immediate: true,
			handler() {
				this.handlerLayerList();
			}
		},
		delLayerCount() {
			this.handlerLayerList();
			this.returnLayerout();
		},
		'curChartStatus.isDeleted'() {
			this.handlerLayerList();
		},
		currentChartIndex() {
			this.activeIndex = this.currentChartIndex;
		},
		'curChartLayer.isLayerEdit': {
			handler(val) {
				if (val && this.activeIndex > -1) {
					this.$refs.editInput[this.currentChartIndex].focus();
					let name = this.curChartLayer.layerName;
					this.$set(this.curChartLayer, 'layerName', name.replace(/\s+/g, ''));
					this.rename = this.curChartLayer.layerName;
				}
			}
		}
	},
	data() {
		return {
			activeIndex: -1,
			rename: '',
			searchReturnList: [],
			curChartLayer: {},
			curChartStatus: {},
			searchObject: {
				isClearInput: false,
				placeholderDes: '',
				isShowPanel: false,
				beQueriedList: [],
				width: '100%'
			}
		};
	},
	methods: {
		getChildSearch(fromChildData) {
			this.searchReturnList = fromChildData;
		},
		inputReset() {
			this.$set(this.searchObject, 'isClearInput', false);
		},
		returnLayerout() {
			this.$set(this.searchObject, 'isClearInput', true);
		},
		/**
     * ,indexvuex
     * canvasPlacecurrentChartIndex
     */
		curIndex(index, params) {
			this.activeIndex = index;
			this.curChartStatus = params['containerStatus'];
			this.curChartLayer = params['containerOptions'];
			this.$store.commit('canvasPlace/setCurrentChartIndex', index);
		},
		/**
     * 
     * vuex
     */
		inputBlur(index, params) {
			let containerOptions = params['containerOptions'];
			//
			this.$set(
				containerOptions,
				'layerName',
				containerOptions['layerName'].replace(/\s+/g, '')
			);
			this.$refs.editInput[index].blur();
			this.activeIndex = -1;
			//false
			this.$store.commit('canvasPlace/containerOptionSetter', {key:'isLayerEdit',value:false});
			//
			if (containerOptions['layerName'] && this.rename != containerOptions['layerName']) {
				//vuexlayerName
				this.$store.commit('canvasPlace/setLayerName', {
					layerName: containerOptions['layerName'],
					index: index
				});
				//
				this.handlerLayerList();
			} else {
				if (!containerOptions['layerName']) {
					this.$set(
						this.layerList[index]['containerOptions'],
						'layerName',
						this.rename
					);
					this.tips.show(['']);
				}
			}
		},
		//
		handlerLayerList() {
			const vm = this;
			let beQueriedList = [];
			let operationList = JSON.parse(JSON.stringify(vm.layerList));
			operationList.forEach((e, index) => {
				if (!e['containerStatus']['isDeleted']) {
					vm.$set(e, 'name', e['containerOptions']['layerName']);
					beQueriedList.push(e);
				}
			});
			this.searchObject.beQueriedList = beQueriedList;
		}
	}
};
</script>
<style scoped>
.layer-box {
  padding-top: 1vw;
}
.layer-box .adjusting-styles {
  padding-bottom: 0.8vw;
}
.layer-box .adjusting-styles >>> .search-box {
  width: 100%;
  padding: 0 0.4vw;
}
.layer-box .adjusting-styles >>> .search-icon {
  right: 8%;
}
.layer-box .adjusting-styles >>> .select-box {
  width: 93%;
}
ul {
  height: 41vw;
}
ul > li {
  padding: 0 0.39vw;
  border: solid 0.0625vw transparent;
  cursor: pointer;
  user-select: none;
  margin-right: 0.45vw;
  color: #fff;
}
ul > li .return-layerout {
  margin-bottom: 0.5vw;
}
ul > li span.arrowLeft {
  display: inline-block;
  transform: scale(0.75);
  font-size: 0.6vw;
  margin: 0 0.3vw;
}
ul > li.add-borderColor {
  border-color: #28b3fc;
  background-color: #182b3f;
}

.layer-list {
  display: flex;
  padding: 0.2vw 0.5vw;
  align-items: center;
  justify-content: space-between;
  border-bottom: #0e121a 0.0625vw solid;
}
.layer-info {
  display: flex;
  align-items: center;
}

.layer-list .layer-name {
  color: #fff;
  /* display: inline-block; */
  margin-left: 0.5vw;
}
.layer-list .layer-name input {
  width: 5vw;
  background-color: inherit;
  outline: none;
  color: #fff;
}

.layer-list .layer-name .edit-input {
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 5vw;
  user-select: none;
}

.layer-list .layer-name .unEditInput {
  pointer-events: none;
}

.layer-list .shape-size {
  width: 2.7vw;
  height: 1.8vw;
  background-color: #121721;
  border: solid 0.0625vw #232d38;
  line-height: 1.8vw;
  text-align: center;
  display: inline-block;
}
.layer-list .shape-size .svg-icon-size {
  width: 1.1vw;
  height: 1.1vw;
  margin-top: 0.3vw;
}
</style>

