<template>
  <div class="data-source">
    <!--  -->
    <DataSourceList @fileUploadBack="newDataSourceShow"
      @relationshipSource="relationshipEvent"
      @requestCompleted="completedEvent"
      @modelInfoChosen='modelInfoChosen'
      @dataInfoChosen='dataInfoChosen'
      @modelInfoBack="modelInfoEvent"
      @uploadSource="sourceDelEvent"
      @requestSuccess="requestEvent"
      @dataInfoBack="dataInfoEvent"
      :requestUrl="requestUrl"
      :dataTitle="dataTitle"
      ref="dataSourceList"></DataSourceList>

    <!--  -->
    <NewDataSource v-show="fileSystem"
      @indexCallBack="indexCall"
      @dataPreviewBack="dataPreviewEvent"
      @messageBack="messageEvent"
      @csvFileBack="csvFileEvent"
      @IoTDataShow="IoTDataEvent"
      @customizeBack="customizeEvent"></NewDataSource>

    <!--  -->
    <DataPreview v-if="previewSystem"
      @closePreviewBack="closePreview"
      @previewHideBack="previewHide"
      @uploadBack="uploadEvent"
      v-bind="{previewData}"></DataPreview>

    <!--  -->
    <NewDatabase v-if="newDatabaseShow"
      :message="message"
      @NewHide="NewMaskHide"
      @savedShow="savedMaskShow"
      @cDatabaseShow="savedMaskShow"></NewDatabase>

    <!--  -->
    <SavedDatabase :message="message"
      :savedId="savedId"
      v-if="savedDatabaseShow"
      @newShow="newMaskShow"
      @savedHide="savedMaskHide"
      @uploadBack="uploadEvent"
      @uploadDatabase="uploadEvent"></SavedDatabase>

    <!-- csv -->
    <NewCsvFile v-if="newCsvFileShow"
      @newCsvFileBack="newCsvFileEvent"
      @dataPreviewBack="dataPreviewEvent"></NewCsvFile>

    <!-- IoT -->
    <IoTData v-if="IoTDataShow"
      @IoTDataClose="IoTDataClose"></IoTData>

    <!--  -->
    <CustomData v-if="customizeShow"
      @customizeHide="customizeMaskShow">
    </CustomData>

    <!--  -->
    <loadingProgress v-if="progressShow"
      :dataIsOk="dataIsOk"
      :styleObj="styleObj"></loadingProgress>
<!--    <loadingRotating v-if="true"-->
<!--      :styleObj="rotatingObj"></loadingRotating>-->
  </div>
</template>

<script>
import DataSourceList from '@/pages/DataSource/components/DataSourceList.vue';
import SavedDatabase from '@/pages/DataSource/components/SavedDatabase.vue';
import NewDataSource from '@/pages/DataSource/components/NewDataSource.vue';
import DataPreview from '@/pages/DataSource/components/DataPreview.vue';
import NewDatabase from '@/pages/DataSource/components/NewDatabase.vue';
import NewCsvFile from '@/pages/DataSource/components/NewCsvFile.vue';
import IoTData from '@/pages/DataSource/components/IoTData.vue';
import CustomData from '@/pages/DataSource/components/CustomData.vue';
import loadingProgress from '@/components/Loading/LoadingProgress.vue';
import '@/assets/DataSource/fonts/iconfont.css';

//
//,
// dataTitle:{
//    title:"",//
//    icon:"",//nullicon- icon-dataSourceListdataSourceList
// }
export default {
	name: 'dataSource',
	props: ['dataTitle', 'requestUrl'],
	data() {
		return {
			savedDatabaseShow: false,
			newDatabaseShow: false,
			newCsvFileShow: false,
			IoTDataShow: false,
			customizeShow: false,
			previewSystem: false,
			rotatingShow: false,
			progressShow: true,
			previewData: null,
			fileSystem: false,
			dataIsOk: false,
			savedId: null,
			message: '',
			styleObj: {
				width: '100vw',
				height: '100vh',
				top: '10vh'
			},
			// rotatingObj: {
			// 	width: '15.2vw',
			// 	height: '85.4vh'
			// }
		};
	},
	components: {
		loadingProgress,
		// loadingRotating,
		DataSourceList,
		NewDataSource,
		SavedDatabase,
		NewDatabase,
		DataPreview,
		NewCsvFile,
		IoTData,
		CustomData
	},
	methods: {
		IoTDataClose() {
			this.IoTDataShow = false;
			this.fileSystem = true;
		},
		newDataSourceShow(bool) {
			this.fileSystem = bool.fileSystem;
		},

		dataInfoEvent(data) {
			this.$emit('myDataBack', data);
		},

		modelInfoEvent(data) {
			this.$emit('dataModelingBack', data);
		},

		indexCall(bool) {
			this.fileSystem = bool.fileSystem;
		},

		dataPreviewEvent(data) {
			this.previewData = data.uploadData;
			this.fileSystem = data.bool.fileSystem;
			this.previewSystem = data.bool.previewSystem;
			this.newCsvFileShow = false;
		},

		closePreview(bool) {
			this.previewSystem = bool.previewSystem;
		},

		previewHide(bool) {
			this.previewSystem = bool.previewSystem;
			this.fileSystem = !this.fileSystem;
		},

		messageEvent(str) {
			this.message = str;
			this.newDatabaseShow = true;
			this.fileSystem = !this.fileSystem;
		},

		NewMaskHide(bool) {
			this.newDatabaseShow = bool;
			this.fileSystem = !this.fileSystem;
		},

		savedMaskShow(data) {
			if (data.data != true) {
				this.savedId = data.data;
			}
			this.message = data.type;
			this.savedDatabaseShow = true;
			this.newDatabaseShow = false;
		},

		savedMaskHide(bool) {
			this.savedDatabaseShow = bool;
			this.fileSystem = !this.fileSystem;
		},

		newMaskShow(bool) {
			this.savedDatabaseShow = false;
			this.newDatabaseShow = bool;
		},

		dataInfoChosen(data) {
			this.$emit('chosenDataBack', data);
		},

		modelInfoChosen(data) {
			this.$emit('chosenListBack', data);
		},

		csvFileEvent(bool) {
			this.fileSystem = !bool;
			this.newCsvFileShow = bool;
		},

		IoTDataEvent(bool) {
			this.fileSystem = !bool;
			this.IoTDataShow = bool;
		},
		customizeEvent(bool) {
			this.fileSystem = !bool;
			this.customizeShow = bool;
		},

		customizeMaskShow(data) {
			if (data != false) {
				this.uploadEvent();
			}
			this.fileSystem = true;
			this.customizeShow = false;
		},

		newCsvFileEvent(bool) {
			this.fileSystem = !bool;
			this.newCsvFileShow = bool;
		},

		uploadEvent(data) {
      this.$refs.dataSourceList.requestData('initialize');
		},

		sourceDelEvent() {
			this.$emit('modelingrRefresh', 'refresh');
		},

		requestEvent(str) {
			if (this.$route.fullPath == '/screen') {
				this.rotatingShow = false;
			} else {
				this.dataIsOk = true;
			}
		},

		completedEvent(data) {
			//
			this.$emit('requestCompleted', data);
		},

		relationshipEvent(bool) {
			this.$emit('relationshipSuccess', bool);
		},
	},
	mounted() {
		if (this.$route.fullPath == '/screen') {
			this.rotatingShow = true;
			this.progressShow = false;
		}
	}
};
</script>

<style scoped>
.data-source {
  margin-top: 0.1vh;
}
.plLoading.maskBackground {
  left: inherit;
  top: inherit;
}
</style>
