export default {
	namespaced: true,
	state: {
		balance: {},
		userInfo: {},
	},
	getters: {
		getUserInfo: (state) => {
			return state.userInfo;
		},
		getBalance: (state) => {
			return state.balance;
		},
	},
	mutations: {
		setUserInfo: (state, obj) => {
			state.userInfo = obj;
		},
		setBalance: (state, obj) => {
			state.balance = obj;
		},
	},
	actions: {

	}
};