<template>
  <div class="fileSystem maskBackground">
    <div class="fileContent allCenter">
      <div class="fileTitle">
        <i class="icon biFont icon-builtNew addNew"></i>
        <span class="unSelect"></span>
        <i class="icon biFont icon-clear close"
          @click="close"></i>
      </div>
      <div class="content">
        <div class="fileType">
          <ul>
            <li :class="{active: dataFilter=='all'}"
              name="all"
              @click="filter('all')"></li>
            <li :class="{active: dataFilter=='files'}"
              name="files"
              @click="filter('files')"></li>
            <li :class="{active: dataFilter=='sql'}"
              name="sql"
              @click="filter('sql')"></li>
            <li :class="{active: dataFilter=='api'}"
              name="api"
              @click="filter('api')">API</li>
            <li :class="{active: dataFilter=='IoT'}"
              name="IoT"
              @click="filter('IoT')">IoT</li>
            <li :class="{active: dataFilter=='other'}"
              name="other"
              @click="filter('other')"></li>
          </ul>
        </div>
        <div class="fileImg">
          <ul>
            <li name="EXCEL"
              class="files"
              v-if="dataFilter=='all'||dataFilter=='files'"
              @click="datafileUploadBack='EXCEL'"
              :class="{activeFile: dataUpload=='EXCEL'}">
              <input type="file"
                title
                name="excel"
                class="fileSystemFiles"
                @change="uploadFiles($event)">
              <img src="@/assets/DataSource/img/EXCEL.png"
                alt="EXCEL">
              <span>EXCEL</span>
            </li>
            <li name="CSV"
              class="files"
              v-if="dataFilter=='all'||dataFilter=='files'"
              @click="csvFile"
              :class="{activeFile: dataUpload=='CSV'}">
              <img src="@/assets/DataSource/img/CSV.png"
                alt="CSV">
              <span>/CSV</span>
            </li>
            <li name="XML"
              class="files"
              v-if="dataFilter=='all'||dataFilter=='files'"
              @click="dataUpload='XML'"
              :class="{activeFile: dataUpload=='XML'}">
              <input type="file"
                title
                name="xml"
                class="fileSystemFiles"
                @change="uploadFiles($event)">
              <img src="@/assets/DataSource/img/XML.png"
                alt="XML">
              <span>XML</span>
            </li>
            <li name="JSON"
              class="files"
              v-if="dataFilter=='all'||dataFilter=='files'"
              @click="dataUpload='JSON'"
              :class="{activeFile: dataUpload=='JSON'}">
              <input type="file"
                title
                name="json"
                class="fileSystemFiles"
                @change="uploadFiles($event)">
              <img src="@/assets/DataSource/img/JSON.png"
                alt="JSON">
              <span>JSON</span>
            </li>
            <li name="SQLserver"
              class="sql SQLserver"
              v-if="dataFilter=='all'||dataFilter=='sql'"
              @click="dataUpload='SQLserver',databaseShow('SQLserver')"
              :class="{activeFile: dataUpload=='SQLserver'}">
              <img src="@/assets/DataSource/img/sql.png"
                alt="SQL server">
              <span>SQL server</span>
            </li>
            <li name="MySQL"
              class="sql"
              v-if="dataFilter=='all'||dataFilter=='sql'"
              @click="dataUpload='MySQL',databaseShow('MySQL')"
              :class="{activeFile: dataUpload=='MySQL'}">
              <img src="@/assets/DataSource/img/MYSQL.png"
                alt="MYSQL">
              <span>MySQL</span>
            </li>
            <li name="Oracle"
              class="sql"
              v-if="dataFilter=='all'||dataFilter=='sql'"
              @click="dataUpload='Oracle',databaseShow('Oracle')"
              :class="{activeFile: dataUpload=='Oracle'}">
              <img src="@/assets/DataSource/img/ORACLE.png"
                alt="ORACLE">
              <span>Oracle</span>
            </li>
            <li name="PostgreSQL"
              class="sql"
              v-if="dataFilter=='all'||dataFilter=='sql'"
              @click="dataUpload='PostgreSQL',databaseShow('PostgreSQL')"
              :class="{activeFile: dataUpload=='PostgreSQL'}">
              <img src="@/assets/DataSource/img/postgresql.png"
                alt="postgresql">
              <span>PostgreSQL</span>
            </li>
            <li name="MongoDB"
              class="sql"
              v-if="dataFilter=='all'||dataFilter=='sql'"
              @click="dataUpload='MongoDB',databaseShow('MongoDB')"
              :class="{activeFile: dataUpload=='MongoDB'}">
              <img src="@/assets/DataSource/img/MongoDB.png"
                alt="MongoDB">
              <span>MongoDB</span>
            </li>
            <li name="api"
              class="api"
              v-if="dataFilter=='all'||dataFilter=='api'"
              @click="dataUpload='api'"
              :class="{activeFile: dataUpload=='api'}">
              <img src="@/assets/DataSource/img/API.png"
                alt="API">
              <span>API</span>
            </li>
            <li name="IoT"
              class="IoT"
              v-if="dataFilter=='all'||dataFilter=='IoT'"
              @click="dataUpload='IoT',IoTDataShow()"
              :class="{activeFile: dataUpload=='IoT'}">
              <img src="@/assets/DataSource/img/IoT.png"
                alt="IoT">
              <span>IoT</span>
            </li>
            <li name="other"
              class="other"
              v-if="dataFilter=='all'||dataFilter=='other'"
              @click="dataUpload='other',customize()"
              :class="{activeFile: dataUpload=='other'}">
              <img src="@/assets/DataSource/img/customize.png"
                alt="">
              <span></span>
            </li>
          </ul>
        </div>
      </div>
    </div>
<!--    <Tips ref="tips"-->
<!--      v-bind="{tipsData}"></Tips>-->

    <!--  -->
    <Loading v-if="dataIsOk"
      :styleObj="styleObj"></Loading>
  </div>
</template>

<script>
// import Tips from '@/components/Tips/Tips.vue';
import Loading from '@/components/Loading/LoadingRotating.vue';

export default {
	name: 'fileUpload',
	components: { Loading },
	data() {
		return {
			url: '',
			dataFilter: 'all',
			dataUpload: null,
			dataIsOk: false,
			token: localStorage.getItem('token'),
			tipsData: {
				text: '',
				rel: '',
				time: 1500
			},
			styleObj: {
				width: '39vw',
				height: '61vh'
			}
		};
	},
	methods: {
		filter(name) {
			this.dataFilter = name;
		},
		//
		close(bool) {
			this.$emit('indexCallBack', { fileSystem: false });
			this.dataFilter = 'all';
			this.dataUpload = null;
		},
		databaseShow(str) {
			this.$emit('messageBack', str);
		},
		csvFile() {
			this.dataUpload = 'CSV';
			this.$emit('csvFileBack', true);
		},
		IoTDataShow() {
			this.$emit('IoTDataShow', true);
		},
		customize() {
			this.$emit('customizeBack', true);
		},
		uploadFiles(e) {
			this.dataIsOk = true;
			let name = e.target.files[0].name;
			let type = e.target.name;
			let file = e.target.files[0];
			let formData = new FormData();
			formData.append('examfile', file);
			formData.append('token', this.token);

			this.$post('api/' + type + 'Info', formData).then(
				res => {
					// let res = response.data;
					this.dataIsOk = false;
					if (res.code == '000000') {
						this.$emit('dataPreviewBack', {
							bool: {
								fileSystem: false,
								previewSystem: true
							},
							uploadData: {
								data: res.data,
								name: name,
								type: type,
								examfile: file
							}
						});
						this.dataFilter = 'all';
					} else {
						this.tips.show([res.err_msg.split(':')[1]]);
						// this.tipsData.rel = 'fail';
						// this.tipsData.text = res.err_msg.split(':')[1];
						// this.$refs.tips.closeTips();
					}
				},
				error => {
					console.log(error);
					this.dataIsOk = false;
					this.tips.show(['']);
					// this.tipsData.rel = 'fail';
					// this.tipsData.text = '';
					// this.$refs.tips.closeTips();
				}
			);
			e.target.value = '';
		}
	},
	mounted() {}
};
</script>

<style scoped>
.fileSystem.maskBackground .maskBackground {
  background: none;
}

.fileSystem .fileContent {
  width: 39vw;
  height: 61vh;
  background-color: #19202e;
  box-sizing: border-box;
  text-align: center;
}

.fileSystem .fileTitle {
  height: 4vh;
  line-height: 4vh;
  background-color: #273045;
  font-size: 0.8vw;
}
.fileSystem .fileTitle .addNew {
  margin-right: 0.2vw;
  vertical-align: 0;
  font-size: 0.8vw;
}

.fileSystem .close {
  font-size: 0.8vw;
  position: absolute;
  right: 1vw;
  cursor: pointer;
}

.fileSystem .close:hover {
  color: #28b3fc;
}

.fileSystem .content {
  margin: 3vh 0vw 3vh 2vw;
}

.fileSystem .fileType {
  overflow: hidden;
}

.fileSystem .fileType li {
  float: left;
  width: 4vw;
  height: 3vh;
  font-size: 0.7vw;
  line-height: 3vh;
  background-color: #283247;
  margin-right: 1vw;
  cursor: pointer;
  border: solid 0.0625vw transparent;
}

.fileSystem .fileType li:hover {
  border: solid 0.0625vw #28b3fc;
}

.fileSystem .fileImg {
  margin-top: 1vh;
}

.fileSystem .fileImg li {
  width: 6vw;
  height: 6vw;
  background-color: #121721;
  border-radius: 0.2vw;
  float: left;
  margin: 2vh 1.3vw 2vh 0;
  box-sizing: border-box;
  cursor: pointer;
  position: relative;
  border: solid 0.0625vw transparent;
}

.fileSystem .fileImg li:hover {
  border: solid 0.0625vw #28b3fc !important;
}

.fileSystem .fileImg img {
  width: 2vw;
  margin-top: 2vh;
}

.fileSystem .fileImg span {
  display: block;
  font-size: 0.6vw;
  margin-top: 0.8vh;
}

.fileSystem .active {
  border: 0.0625vw solid #28b3fc !important;
  background-color: rgba(40, 179, 252, 0.2) !important;
}

.fileSystem .activeFile {
  border: 0.0625vw solid #28b3fc !important;
  background-color: rgba(40, 179, 252, 0.2) !important;
}
.fileSystem .fileSystemFiles {
  opacity: 0;
  width: 6vw;
  height: 6vw;
  position: absolute;
  left: 0;
  cursor: pointer;
}
</style>

