<template>
  <div class="database maskBackground">
    <div class="fileContent allCenter">
      <div class="fileTitle">
        <i class="icon biFont icon-builtNew addNew"></i>
        <span></span>
      </div>
      <div class="createList">
        <ul>
          <li>
            <label></label>
            <Selection class="selections"
              @selectValue="selectionsEvent"
              v-bind="{selections}"></Selection>
          </li>
          <li>
            <label>IP</label>
            <input type="text"
              ref="host">
          </li>
          <li>
            <label></label>
            <input type="text"
              ref="port">
          </li>
          <li>
            <label></label>
            <input type="text"
              ref="dbname">
          </li>
          <li>
            <label></label>
            <input type="text"
              ref="user">
          </li>
          <li>
            <label></label>
            <input type="password"
              ref="pass">
          </li>
        </ul>
      </div>
      <div class="buttonDiv">
        <span class="saved mainBtn"
          @click="savedDatabase"></span>
        <span class="create mainBtn"
          :class="{banGestures:isCreateClick}"
          @click="createDatabase"></span>
        <span class="cancel mainBtn"
          @click="cancel"></span>
      </div>
    </div>
  </div>
</template>
<script>
import Selection from '@/components/Selection/Selection.vue';

export default {
	name: 'newDatabase',
	data() {
		return {
			member_sn: localStorage.getItem('userid'),
			isCreateClick: false, //
			dbname: '',
			dbType: '',
			selections: {
				placeholder: '',
				w: '16.2vw',
				inputVal: this.message,
				data: this.$store.state.dataSource.databaseType
			}
		};
	},
	components: { Selection },
	props: ['message'],
	methods: {
		selectionsEvent(data) {
			this.dbType = data.value;
		},
		cancel() {
			this.$emit('NewHide', false);
		},
		savedDatabase() {
			this.$emit('savedShow', { data: true, type: this.dbType });
		},
		createDatabase() {
			this.isCreateClick = true;
			if (this.isCreateClick) {
				this.$get('api/sDataSource', {
					member_sn: this.member_sn,
					host: this.$refs.host.value,
					dbname: this.$refs.dbname.value,
					type: this.dbType,
					user: this.$refs.user.value,
					pass: this.$refs.pass.value,
					port: this.$refs.port.value
				}).then(
					res => {
						let tipsStatus;
						// console.log(res.data);
						if (res.code == '000000') {
							tipsStatus = true;
							tipsStatus = 'success';
							this.$emit('cDatabaseShow', {
								data: res.data,
								type: this.dbType
							});
						} else {
							tipsStatus = false;
						}
						let tipsText = res.err_msg.split(':')[1];
						this.tips.show([tipsText, tipsStatus]);
						this.isCreateClick = false;
					},
					error => {
						this.tips.show(['', false]);
						this.isCreateClick = false;
					}
				);
			}
		}
	},
	mounted() {
		this.dbType = this.message;
	}
};
</script>

<style scoped>
.database.maskBackground .maskBackground {
  background: none;
}

.database .fileContent {
  width: 26vw;
  height: 55vh;
  background-color: #19202e;
  box-sizing: border-box;
  text-align: center;
}

.database .fileTitle {
  height: 4vh;
  line-height: 4vh;
  background-color: #273045;
  font-size: 0.8vw;
}

.database .fileTitle .addNew {
  margin-right: 0.2vw;
  vertical-align: 0;
  font-size: 0.8vw;
}

.database .createList {
  margin: 4vh 1.6vw;
}

.database .createList ul li {
  line-height: 6vh;
}

.database .createList ul li .selections {
  line-height: 4.4vh;
  display: inline-block;
}

.database .createList ul li .selections >>> .triangleImg {
  top: 1.7vh;
}

.database .createList ul li .selections >>> input {
  height: 3vh;
}

.database .createList ul li input {
  background-color: #121721;
  border: solid 0.0625vw #475566;
  color: #fff;
  padding: 0 0.5vw;
  width: 16.2vw;
  height: 3vh;
}

.database .createList ul li input:-webkit-autofill {
  background-color: #121721 !important;
  color: #fff !important;
}

.database .createList ul li input:focus {
  border: solid 0.0625vw #28b3fc;
  outline-offset: 0;
}

.database .createList ul li label {
  font-size: 0.7vw;
  text-align: right;
  width: 3.5vw;
  float: left;
}

.database .buttonDiv span {
  font-size: 0.7vw;
  padding: 0 0.7vw;
  height: 3vh;
  line-height: 3vh;
}

.database .buttonDiv .saved {
  position: absolute;
  left: 2.7vw;
}

.database .buttonDiv .create {
  position: absolute;
  right: 7vw;
}

.database .buttonDiv .cancel {
  position: absolute;
  right: 2.7vw;
}
</style>
