<template>
  <div>
    <EasyScroll :barOption="opt"
      :viewStyle="viewStyle">
      <chartOptionsAnimate
        :chartOptionName="chartoptionName[item.name]"
        :key="index"
        v-for="(item,index) in pageArr"
      >
        <component
          :is="item.name">
        </component>
      </chartOptionsAnimate>
      <!--        <component v-for="item in pageArr"-->
      <!--          :is="item.name"-->
      <!--          v-bind:key="item.id"></component>-->
    </EasyScroll>
  </div>
</template>
<script>
import chartOptionsAnimate from '@/components/ChartOptionsAnimate/ChartOptionsAnimate.vue';
import {chartoptionName} from './components/data.js';
import {mapGetters} from 'vuex';
import 'vue-slider-component/theme/antd.css';
import Dexie from 'dexie';
// webpack
const path = require('path');
const files = require.context('./components/', false, /\.vue$/);
const module = {};
files.keys().forEach(key => {
	const name = path.basename(key, '.vue');
	module[name] = files(key).default || files(key);
});
export default {
	components: {
		...module,
		chartOptionsAnimate
		// 'slider-range': SliderRange,

	},
	data() {
		return {
			chartoptionName,
			// settings: {
			// 	maxScrollbarLength: 60
			// },
			opt: {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barWidth: 5, //
				barMarginRight: 6, //px
				barOpacityMin: 0.3, //
			},
			viewStyle: {
				height: '100%',
				padding: '0 0.8vw 0 0.5vw'
			},
			pageArr: [
				{name: 'page-setting'},
				{name: 'push-setting'},
				// {name: 'map-settings'},
				// {name:'Proportion-Setting'}
				// { name: 'text-setting' },
				// { name: 'picture-setting' },
				// { name: 'basic-setting' },
				// { name: 'x-axis' },
				// { name: 'y-axis' },
				// { name: 'image-example' },
				// { name: 'data-prompt' },
				// { name: 'data-color' },
				// { name: 'data-width' },
				// { name: 'data-label' }
			],
			saveChartOptionsTimer: null,
			hasTimeState: false,
			preserveState: true,
		};
	},
	computed: {
		...mapGetters('canvasPlace', {
			currentContainerOptions: 'getCurrentContainerOptions',
			currentRenderOptions: 'getCurrentChartRenderOptions',
			currentChartIndex: 'getCurrentChartIndex',
			isPhoneLayoutSaved: 'getPhoneLayoutSavedState',
			saveChartOptionsImmediately: 'isSaveChartOptionsImmediately',//
			saveMobileChartOptionsImmediately: 'isSaveMobileChartOptionsImmediately',//
			updateOptionsImmediately: 'isUpdateOptionsImmediately',//
			updateOptionsCallback: 'getUpdateOptionsCallback',//
			chartOptionsAmendCount: 'getChartOptionsAmend',//
			getMapModifyCount: 'getMapModifyCount',//map
			shareType: 'getShareType',
			// preserveChart: 'getPreserve',
		}),
		...mapGetters('handleChart', {
			saveChartList: 'chartReturnData', //
			saveTextList: 'textReturnData', //
			saveMediaList: 'mediaReturnData', //
			saveInteractiveList: 'interactiveReturnData',//
		}),

	},
	watch: {
    chartOptionName(val){
      console.log('val', val);
      immediate: true;
      deep:true;
    },
		chartOptionsAmendCount() {
			this.getAmendDataList();//
		},
		saveChartOptionsImmediately(val) {
			if (val) {
				window.clearTimeout(this.saveChartOptionsTimer);
				this.getAmendDataList();//
			}
		},
		saveMobileChartOptionsImmediately(val) {
			if (val) {
				window.clearTimeout(this.saveChartOptionsTimer);
				this.getAmendDataList();//
			}
		},
		updateOptionsImmediately(val) {
			if (val) {
				window.clearTimeout(this.saveChartOptionsTimer);
				this.getAmendDataList();//
			}
		},
		currentContainerOptions(val) {
			let vm = this;
			if (vm.currentChartIndex === -1) return false;
			// console.log(vm.chartDataList.chart.containerOptions.chartsType, '');
			vm.resetPageArs(); // 
			let options = val; //
			let chartFullType = options.chartsType;
			let chartType = chartFullType.split('-')[0];
			if (chartType.indexOf('chart') > -1) {
				if (options.chartsType === 'echarts-choropleth') {//
					vm.pageArr.push({name: 'map-settings'});
				}
				vm.pageArr.push({name: 'basic-setting'});
			} else if (chartType === 'text') {
				vm.pageArr.push({name: 'text-setting'});
			} else if (chartType === 'media') {
				vm.pageArr.push({name: 'picture-setting'});
			} else if (chartType === 'interactive') {
				switch (chartFullType) {
				case 'interactive-cardChart':
					vm.pageArr.push({name: 'card-chart'});
					break;
				case 'interactive-proportionRing':
					vm.pageArr.push({name: 'Proportion-Setting'});
					break;
				}
			}
			// if (options){ //
			//   vm.pageArr.push({name:'image-example'},);
			// }
			// if (options){ //
			//   vm.pageArr.push({name:'data-prompt'},);
			// };
			// if (options){ //
			//   vm.pageArr.push({name:'data-color'},);
			// };
			// if (options){ //
			//   vm.pageArr.push({name:'data-width'},);
			// };
			// if (options){ //
			//   vm.pageArr.push({ name: 'picture-setting' },);
			// };
		},
		// },
		currentRenderOptions(val) {
			let options = val;
			let vm = this;
			if (vm.currentChartIndex === -1) return false;
			if (options.xAxis) {
				//x
				vm.pageArr.push({name: 'X-axis'});
			}
			if (options.yAxis) {
				//y
				vm.pageArr.push({name: 'Y-axis'});
			}
			if (
				options.grid &&
          vm._get(options, 'series[0]type') !== 'pie'
          // Array.isArray(options.series)&&
          // options.series[0].type !== 'pie'
			) {
				vm.pageArr.push({name: 'grid-assembly'});
			}
			if (
				vm._get(options, 'series[0]type') !== 'map3D'
			// Array.isArray(options.series)&&
			// options.series.length&&
			// options.series[0].type&&options.series[0].type === 'map3D'
			) {
				// vm.pageArr.push({name: 'map-3d'});
			}
		},
		currentChartIndex(val) {
			if (val === -1) {
				this.resetPageArs();
			}
		},
		getMapModifyCount() {//
			let vm = this;
			vm.handleChartOptions({dbName: 'saveOption', dbFieldArrayName: 'chartArray'}, 0);
			setTimeout(() => {
				vm.getAmendDataList();//
			}, 1000);
			// vm.sendAmendData();
		},

		hasTimeState(val) {
			// console.log('5s');
			let vm = this;
			if (val === true) {
				vm.saveChartOptionsTimer = setTimeout(function () {
					vm.sendAmendData();//
					// console.log('');
				}, 3000);
			}
		},
	},
  mounted() {
    console.log('');
    console.log(this.pageArr);
  },
	methods: {
		// 
		resetPageArs() {
			this.pageArr = [
				{name: 'page-setting'},
				{name: 'push-setting'},
			];
		},
		saveChartTips(status = 'success', message = '') {
			let vm = this;
			let isSuccess = status === 'success';
			if (vm.saveChartOptionsImmediately) {
				vm.tips.show([`${message}`, isSuccess]);
				vm.$store.commit('canvasPlace/setStateProperty', {
					key: 'saveChartOptionsImmediately',
					value: false
				});
			}
			if (vm.saveMobileChartOptionsImmediately) {
				vm.tips.show([`${message}`, isSuccess]);
				vm.$store.commit('canvasPlace/setStateProperty', {
					key: 'saveMobileChartOptionsImmediately',
					value: false
				});
				vm.$store.commit('canvasPlace/modifyAllMobileChartOptions');
				if (!vm.isPhoneLayoutSaved) vm.modifyMobileLayoutSavedState();
			}
			if (vm.updateOptionsImmediately) {
				vm.$store.commit('canvasPlace/setStateProperty', {
					key: 'updateOptionsImmediately',
					value: false
				});
				this.updateOptionsCallback && this.updateOptionsCallback();
			}
		},
		modifyMobileLayoutSavedState() {
			let data = {
				isMobileLayoutSaved: true,
				name: localStorage.getItem('subProjectName'),
				id: sessionStorage.getItem('projectId'),
			};
			let vm = this;
			vm.$put('api/project', data)
				.then(res => {
					console.log('', res);
					if (res.code === '000000') {
						vm.$store.commit('canvasPlace/setStateProperty', {key: 'isPhoneLayoutSaved', value: true});
						vm.rename = '';
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
				})
				.catch(error => {
					console.error(error);
				});
		},
		//
		sendAmendData() {
			const vm = this;
			vm.preserveState = false;
			//vuex
			let amendDataList = [
				...vm.saveChartList,
				...vm.saveTextList,
				...vm.saveMediaList,
				...vm.saveInteractiveList
			];
			if (amendDataList.length && amendDataList[0]) {
				vm.$store.commit('chartBus/setRouter', false);
				const saveList = amendDataList.filter(e => e != null);
				if (saveList.length) {
					vm.$store.commit('chartBus/setRouter', false);
					// console.log('saveList.length', saveList.length);
					vm.$put('api/v2/chart', saveList)
						.then(res => {
							vm.indexDbClear().then(() => {
								if (res.code === '000000') {
									vm.$store.commit('chartBus/setTimeSetting', false);
									vm.$store.commit('chartBus/setRouter', true);
									vm.$store.commit('chartBus/setSaveComplete');
									//indexdb
									vm.hasTimeState = false;
									vm.preserveState = true;
									this.saveChartTips();
									// resolve('');
								} else {
									vm.tips.show([res.err_msg.split(':')[1]]);

									// reject(res.err_msg);
								}
							});
						})
						.catch(error => {
							console.error(error);
							vm.$store.commit('chartBus/setRouter', true);
							vm.hasTimeState = false;
							if (vm.saveChartOptionsImmediately) {
								vm.tips.show(['']);
								vm.saveChartOptionsImmediately = false;
							}
						});
				}
			} else {
				this.saveChartTips();
				// if (vm.saveChartOptionsImmediately) {
				// 	vm.tips.show(['', true]);
				// 	vm.$store.commit('canvasPlace/setStateProperty', {
				// 		key: 'saveChrtOptionsImmediately',
				// 		value: false
				// 	});
				// }
				// if (vm.saveMobileChartOptionsImmediately) {
				// 	vm.tips.show(['', true]);
				// 	vm.$store.commit('canvasPlace/setStateProperty', {
				// 		key: 'saveMobileChartOptionsImmediately',
				// 		value: false
				// 	});
				// 	vm.$store.commit('canvasPlace/modifyAllMobileChartOptions');
				// 	if (!vm.isPhoneLayoutSaved) vm.modifyMobileLayoutSavedState();
				// }
				// if (vm.updateOptionsImmediately) {
				// 	vm.$store.commit('canvasPlace/setStateProperty', {
				// 		key: 'updateOptionsImmediately',
				// 		value: false
				// 	});
				// }
				// vm.tips.show(['',true]);
				// vm.preserveState = true;
			}
		},
		/**
       * 
       * 
       */
		getAmendDataList() {
			let vm = this;
			let recurrentDb = [
				//
				{dbName: 'saveOption', dbFieldArrayName: 'chartArray'},
				//
				{dbName: 'saveMediaOption', dbFieldArrayName: 'mediaArray'},
				//
				{dbName: 'saveTextOption', dbFieldArrayName: 'textArray'},
				// 
				{dbName: 'saveInteractiveOption', dbFieldArrayName: 'interactiveArray'}
			];
			const doSomething = Dexie.async(function* () {
				let promise = yield Promise.all(
					recurrentDb.map(async (ele, index) => {
						return (async () => {
							return vm.handlerDbType(ele, index);
						})();
					})
				);
				return promise;
			});
			// console.log('')
			let promises = doSomething();
			//
			//this.hasTimeState = true;
			// console.log(promises,'');
			const immediatelyExcuteSigns = [
				vm.saveChartOptionsImmediately,
				vm.saveMobileChartOptionsImmediately,
				vm.updateOptionsImmediately
			];
			// console.log(immediatelyExcuteSigns);
			promises
				.then(res => {
					if (immediatelyExcuteSigns.includes(true)) {
						console.log('');
						vm.sendAmendData();
						return false;
					}
					this.hasTimeState = true;
					// console.log(this.hasTimeState, 'state');
				})
				.catch(res => {
					console.log(res);
				});
		},
		/**
       * hasTimeState
       * 5s
       */
		// timingTrigger() {
		// 	let vm = this;
		// 	if (vm.hasTimeState === true) {
		// 		setTimeout(function () {
		// 			vm.sendAmendData();//
		// 			// console.log('');
		// 		}, 5000);
		// 	}
		// },
		/**
       * 
       */
		handlerDbType(dbInfo, index) {
			const vm = this;
			switch (dbInfo['dbName']) {
			case 'saveOption':
				return vm.handleChartOptions(dbInfo, index);
			case 'saveMediaOption':
				return vm.handleMediaOptions(dbInfo, index);
			case 'saveTextOption':
				return vm.handleTextOptions(dbInfo, index);
			case 'saveInteractiveOption':
				return vm.handleInteractiveOptions(dbInfo, index);
			}
		},
		/**
       * echarts
       * 
       * 1{containtercontainterStatuschartOptions}
       * 2axisList
       * 3handleChart.js
       */
		handleChartOptions(dbInfo, index) {
			const vm = this;
			let getChartArr = async () => {
				return vm.dexieDB[dbInfo.dbName].toArray();
			};
			return new Promise((resolve, reject) => {
				const doSomething = Dexie.async(function* () {
					let count = yield vm.dexieDB[dbInfo.dbName].count();
					if (count > 0) {
						let chartArr = yield getChartArr();
						//
						let result = yield Promise.all(
							chartArr.map(async ele => {
								return (async () => {
									return ele[dbInfo.dbFieldArrayName].chart;
								})();
							})
						);
						//
						let amendAxisList = yield Promise.all(
							result.map(async ele => {
								return (async () => {
									return vm.dexieDB.aXisList.get(ele.id);
								})();
							})
							// return vm.dexieDB.aXisList.get(ele.id);
						);
						if (amendAxisList.length == result.length) {
							//handleChart.js
							let test = yield vm.$store.dispatch(
								'handleChart/asyncReceiveData',
								{
									amendChartList: result,
									amendAxisList: amendAxisList
								}
							);
							resolve(test);
						}
					} else {
						resolve('no data');
					}
				});
				doSomething();
			});
		},
		/**
       * 
       *  
       * 1{containtercontainterStatustextOptions}
       * 2handleChart.js
       */
		handleTextOptions(dbInfo, index) {
			const vm = this;
			const getTextArr = async () => {
				return vm.dexieDB[dbInfo.dbName].toArray();
			};
			return new Promise((resolve, reject) => {
				const doSometing = Dexie.async(function* () {
					const count = yield vm.dexieDB[dbInfo.dbName].count();
					if (count > 0) {
						let textArr = yield getTextArr();
						//
						let result = yield Promise.all(
							textArr.map(async ele => {
								return (async () => {
									return ele[dbInfo.dbFieldArrayName].chart;
								})();
							})
						);
						if (result.length) {
							//handleChart.js
							let test = yield vm.$store.dispatch(
								'handleChart/asyncTextScreen',
								result
							);
							resolve(test);
						}
					} else {
						resolve('no data');
					}
				});
				doSometing();
			});
			// vm.dexieDB[dbInfo.dbName].count().then(resCount => {
			// 	if (resCount > 0) {
			// 		// console.log('');
			// 		vm.dexieDB[dbInfo.dbName]
			// 			.each(resTextObj => {
			// 				amendTextList.push(resTextObj[dbInfo.dbFieldArrayName].chart);
			// 			})
			// 			.then(() => {
			// 				// console.log('amendTextObj', amendTextList);
			// 				vm.$store.commit('handleChart/setTextScreen', amendTextList);
			// 			})
			// 			.catch(error => console.error(error));
			// 	}
			// });
		},
		/**
       * 
       */
		handleMediaOptions(dbInfo, index) {
			const vm = this;
			const getMediaArr = async () => {
				return vm.dexieDB[dbInfo.dbName].toArray();
			};
			return new Promise((resolve, reject) => {
				const doSometing = Dexie.async(function* () {
					const count = yield vm.dexieDB[dbInfo.dbName].count();
					if (count > 0) {
						let mediaArr = yield getMediaArr();
						let result = yield Promise.all(
							mediaArr.map(async ele => {
								return (async () => {
									return ele[dbInfo.dbFieldArrayName].chart;
								})();
							})
						);
						if (result.length) {
							let test = yield vm.$store.dispatch(
								'handleChart/asyncMediaScreen',
								result
							);
							resolve(test);
						}
					} else {
						resolve('no data');
					}
				});
				doSometing();
			});
			// let amendMediaList = [];
			// vm.dexieDB[dbInfo.dbName].count().then(resCount => {
			// 	if (resCount > 0) {
			// 		vm.dexieDB[dbInfo.dbName]
			// 			.each(resMediaObj => {
			// 				amendMediaList.push(resMediaObj[dbInfo.dbFieldArrayName].chart);
			// 			})
			// 			.then(() => {
			// 				vm.$store.commit('handleChart/setMediaScreen', amendMediaList);
			// 			})
			// 			.catch(error => console.error(error));
			// 	}
			// });
		},
		/**
       * 
       */
		handleInteractiveOptions(dbInfo, index) {
			const vm = this;
			const getInteractiveArr = async () => {
				return vm.dexieDB[dbInfo.dbName].toArray();
			};
			return new Promise((resolve, reject) => {
				const doSometing = Dexie.async(function* () {
					const count = yield vm.dexieDB[dbInfo.dbName].count();
					if (count > 0) {
						let interactiveArr = yield getInteractiveArr();
						let result = yield Promise.all(
							interactiveArr.map(async ele => {
								return (async () => {
									return ele[dbInfo.dbFieldArrayName].chart;
								})();
							})
						);
						// let amendAxisList = yield Promise.all(
						// 	result.map(async ele => {
						// 		return (async () => {
						// 			return vm.dexieDB.aXisList.get(ele.id);
						// 		})();
						// 	})
						// 	// return vm.dexieDB.aXisList.get(ele.id);
						// );
						if (result.length) {
							let test = yield vm.$store.dispatch(
								'handleChart/asyncInteractiveScreen',
								result
							);
							resolve(test);
						}
					} else {
						resolve('no data');
					}
				});
				doSometing();
			});
		},
		indexDbClear() {
			let vm = this,
				// p1 = vm.dexieDB.aXisList.clear(),
				// p2 = vm.dexieDB.chartsOption.clear(),
				p3 = vm.dexieDB.saveOption.clear(),
				p4 = vm.dexieDB.saveTextOption.clear(),
				p5 = vm.dexieDB.saveMediaOption.clear(),
				p6 = vm.dexieDB.saveInteractiveOption.clear();
			return Promise.all([p3, p4, p5, p6]);
			// Promise.all([p3, p4, p5, p6])
			// 	.then(res => {
			// 		// vm.isTableClear = true;
			// 	})
			// 	.catch(error => {
			// 		console.error(error);
			// 	});
		}
	},
};
</script>
<style scoped>
  .chartOptions >>> .scrollWrapper {
    width: 100%;
    height: 100%;
  }

  .chartOptions >>> .chart-basis {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.3vw 0vw;
    padding: 0.5vw 0.5vw 0.5vw 0.6vw;
    font-size: 0.7vw;
    background-color: #1f2838;
    color: #ffffff;
    cursor: pointer;
    user-select: none;
  }

  .chartOptions >>> .content-box {
    padding: 0.4vw 0.5vw;
  }

  .chartOptions >>> .fontsize {
    font-size: 0.5vw;
  }


  .chartOptions >>> .fontsize:hover {
    font-size: 0.5vw;
    color: #28b3fc;
  }
</style>
<style>
  .vc-saturation-pointer .vc-saturation-circle {
    width: 0.3vw;
    height: 0.3vw;
    box-shadow: 0 0 0 1.5px #fff, inset 0 0 1px 1px rgba(0, 0, 0, 0.3),
    0 0 1px 2px rgba(0, 0, 0, 0.4);
    border-radius: 50%;
    transform: translate(-2px, -2px);
  }

  /*.vc-sketch {*/
  /*  z-index: 10;*/
  /*  position: absolute;*/
  /*  top: 1.2vw;*/
  /*  left: -5vw;*/
  /*  width: 240px;*/
  /*  background-color: #161c29;*/
  /*  border: solid 0.0625vw #475566;*/
  /*  border-radius: 0;*/
  /*}*/

  /*switch*/
  .v-switch-label.v-left,
  .v-right {
    transform: scale(0.9);
  }

  .vue-js-switch .v-switch-core {
    background-color: #171e2b;
    border: solid 0.0625vw #475566;
  }
</style>



