import Dexie from 'dexie';
let db;
let LatestVersion = 7;

function creatDB() {
	db = new Dexie('24e-bi');
	//// id
	//// chartsType
	//// projectId
	//// isSetOptions
	//// coordinateSystem
	//// chartOptions
	//// isChart
	db.version(1).stores({
		aXisList: 'index,' +
            'axisOptions'
	});
	db.version(1).stores({
		dataSourceList: 'index,dataSourceObject'
	});
	db.version(1).stores({
		chartsOption: 'id,' +
            'containerOptions,' +
            'chartOptions'
	});
	db.version(1).stores({
		saveOption: 'id,' +
            'chartArray'
	});
	db.version(1).stores({
		saveTextOption: 'id,' +
            'textArray'
	});
	db.version(1).stores({
		saveMediaOption: 'id,' +
            'mediaArray'
	});
	db.version(1).stores({
		saveInteractiveOption: 'id,' +
            'interactiveArray'
	});
}
// db.exists('24e-bi').then(res => {
// 	if (!res) {
// 		creatDB();
// 		localStorage.setItem('version', 1);
// 		return false;
// 	}
let version = localStorage.getItem('version');
if ((version && version < LatestVersion) || !version) {
	Dexie.delete('24e-bi');
	localStorage.setItem('version', LatestVersion);
	creatDB();
} else {
	creatDB();
	localStorage.setItem('version', LatestVersion);
}
// });

export default db;
