/**
 *  
 * */
import Vue from 'vue';
import defaultChartOptions from '@/store/defaultChartOptions';

export default {
	namespaced: true,// 
	state: {// 
		createVisual: 'await',// creat || await // 
		creatProject: 'await',// creat || await // 
		creatBasicChart: 'await',// await || ... // 
		statelist: 0,
		rightChartCommend: {value: '', position: ''},// 
		chartCommentDrag: false,// 
	},
	getters: {// 
		getVisual: state => state.createVisual,
		getProject: state => state.creatProject,
		getBasicChart: state => state.creatBasicChart,
		getList: state => state.statelist,
		getCommentDrag: state => state.chartCommentDrag,
		getCreatChart: state => {
			let defaultOptions = {
				projectId: state.rightChartCommend.value.projectId,
				chartsType: state.rightChartCommend.value.chartsType,
				xPosition: state.rightChartCommend.position?state.rightChartCommend.position.x:0,
				yPosition: state.rightChartCommend.position?state.rightChartCommend.position.y:0,
				tableWidth: 320,
				tableHeight: 267,
				xPositionMobile: 0,
				yPositionMobile: 0,
				tableWidthMobile: 200,
				tableHeightMobile: 200,
				level: 1,
				xField: state.rightChartCommend.value.xField,
				xSourceId: state.rightChartCommend.value.xSourceId,
				yData: state.rightChartCommend.value.yData,
				seriesData: [state.rightChartCommend.value.seriesData],
				renderOptions: JSON.stringify(defaultChartOptions.state.echarts[state.rightChartCommend.value.chartsType].renderOptions),
			};
			return new Promise((resolve, reject) => {
				Vue.prototype.$post('api/v2/chart', defaultOptions).then(res => {
					if (res.code === '000000') {
						resolve(res);
					} else {
						reject(res);
					}
				});
			});
		}
	},
	mutations: {// 
		setAiVoice(state, stateCode) {
			state[stateCode.keys] = stateCode.value;
			setTimeout(() => {
				state[stateCode.keys] = 'await';
			}, 2000);
		},
		/**
     * @stateCode
     * {keys:value:}
     * */
		setIntParameter(state, stateCode) {// inter
			state[stateCode.keys] = stateCode.value;
		},
		setList(state) {
			state.statelist++;
		},
		setCharCommentDrag(state,status){// 
			console.log(status,'');
			state.chartCommentDrag = status;
		},
		setMouseData(state, {value = null, position = null}) {
			/**
       * 1
       * 2typecanvasPlace.vue
       * 3
       * */
			if (position) {state.rightChartCommend.position = position;}
			if (value) {state.rightChartCommend.value = value;}
		},
	},

};
