import Vue from 'vue';
import dexieDB from '../utils/db';
import {isNumber, mergeJSON} from '../utils/utilsMethods';
import {chartDataFill} from '@/utils/chartRender';//
import {interactiveDataFill} from '@/utils/interactiveTypeRender';//
import {beforeThemeHandle} from '@/utils/optionSetting.js';

export default {
	namespaced: true,
	state: {
		phoneLayoutState: false,//
		isPhoneLayoutSaved: false,//
		thisPhoneIndex: -1,//
		curProjectId: sessionStorage.getItem('projectId'),
		canvasColor: 'rgba(0,0,0,0)',//
		canvasImg: '',//
		canvasBGAmend: 0,//
		tabcanvasAmend: 0,//
		canvasBgPhoneColor: 'rgba(0,0,0,0)',//
		canvasBgPhoneImg: '',//
		mapModify: 0,//
		saveChartOptionsImmediately: false,//
		saveMobileChartOptionsImmediately: false,//
		updateOptionsImmediately: false,//indexdb
		updateOptionsCallback: null,//indexdb
		chartOptionsAmendCount: 0,
		delLayerCount: 0,
		currentChartIndex: -1,//
		lastActivedChartIndex: -1,//
		currentChartType: '',
		isOpenPrompt: false,
		isInitAllChart: false,
		finishAxisInit: false,
		scaleLevel: 0.7,
		pushInfo: {
			pushState: false,
			pushTime: 10,
		},
		/**
     * 
     * 
     *  */
		dataDimensionDefault: {
			isSelectInput: false,//
			isChoice: false,//
			name: '',//
			inputType: '',//
			tableIndex: '',//(ID)
			tableName: '',//
			xOp: '',//x
			yOp: '',//y
			categoryAxis: '',//categoryAxis/valueAxis
			orderCondition: '',//
			orderOp: ''//
		},
		/**chartList
     * 
     */
		containerStatus: {
			lastMobileSavedOptions: {//
				xPositionMobile: 10,//x
				yPositionMobile: 0,//y
				tableWidthMobile: 0.8,//  80%
				tableHeightMobile: 200,//
			},
			isSetOptions: false,//
			dataDimensionShow: true,//
			isDeleted: false,//
			// isFirstLoad: true,//
			isShowLoading: false//loading
		},

		currentChartAxis: {},
		shareType: 0,
		chartList: [],//
		chartBackups: {},//
		renderedChartCount: 0,//
		screenSign: {},//
		screeningCondtion: false,//
		showSignal: false,//
		// preserveMark: 0,//
		initNum: 0,//
		failLoad: 0,//
		aiStore: false,//
	},
	getters: {
		getCurProjectId: state => state.curProjectId,
		getCurrentChartsType(state) {//
			let index = state.currentChartIndex;
			if (index > -1) {
				return state.chartList[index]['containerOptions']['chartsType'];
			}
		},
		getChartsType: state => chartIndex => {
			if (chartIndex > -1) {
				return state.chartList[chartIndex]['containerOptions']['chartsType'];
			}
		},
		getCurrentChartOptions(state) {
			let index = state.currentChartIndex;
			if (index > -1) {
				return state.chartList[index];
			}
		},
		getCurrentContainerOptions(state) {
			let index = state.currentChartIndex;
			if (index > -1) {
				return state.chartList[index].containerOptions;
			}
		},
		getCurrentChartRenderOptions(state) {
			let index = state.currentChartIndex;
			if (index > -1) {
				return state.chartList[index].renderOptions;
			}
		},
		getDelLayerCount: state => state.delLayerCount,
		getCanvasBgColor: state => state.canvasColor,
		getCanvasBgImg: state => state.canvasImg,
		getCanvasBgPhoneColor: state => state.canvasBgPhoneColor,//
		getPhoneLayoutSavedState: state => state.isPhoneLayoutSaved,//
		getCanvasBgPhoneImg: state => state.canvasBgPhoneImg,//
		getCanvasBGAmendCount: state => state.canvasBGAmend,
		getMapModifyCount: state => state.mapModify,
		startInitAllChart: state => state.isInitAllChart,
		isFinishAxiasInit: state => state.finishAxisInit,
		getContainerStatus: state => state.containerStatus,
		getChartList: state => state.chartList,
		getChartBackups: state => state.chartBackups,//
		openPrompt(state) {
			return state.isOpenPrompt;
		},
		getCurrentChartIndex(state) {
			return state.currentChartIndex;
		},
		getDimensionDefault: state => state.dataDimensionDefault,
		hasDateDimension: state => (index = state.currentChartIndex) => {
			if (index > -1) {
				return state.chartList[index].containerOptions.chartsType.indexOf('chart') > -1;
				// return Boolean(state.chartList[index].renderOptions);
			} else {
				return false;
			}
		},
		getCurrentChartType(state) {
			if (state.currentChartIndex > -1) {
				return state.currentChartType;
			}
		},
		getShareType: state => state.shareType,
		getCurrentTableId(state) {
			if (state.currentChartIndex > -1) {
				return state.chartList[state.currentChartIndex].containerOptions.tableId;
			}
		},
		getScaleLevel: (state) => state.scaleLevel,
		getIsSetOptions: state => index => state.chartList[index]['containerStatus']['isSetOptions'],
		isSaveChartOptionsImmediately: state => state.saveChartOptionsImmediately,
		isSaveMobileChartOptionsImmediately: state => state.saveMobileChartOptionsImmediately,
		isUpdateOptionsImmediately: state => state.updateOptionsImmediately,
		getUpdateOptionsCallback: state => state.updateOptionsCallback,
		//
		getChartOptionsAmend: state => state.chartOptionsAmendCount,
		getRenderedChartCount: state => state.renderedChartCount,
		getPushInfo: state => state.pushInfo,
		getPhoneLayoutState: state => state.phoneLayoutState,//
		chartMobileFilter(state) {
			return state.chartList.filter(item =>
				item.containerStatus.isSetOptions
			);
		},
		getCurrentPhoneIndex: state => state.thisPhoneIndex,//
		getScreenSign(state) {
			return state.screenSign;
		},
		dataFilterState: state => state.screeningCondtion,
		getDataShow: state => state.showSignal, //
		// getPreserve: state => state.preserveMark,
		getModuleNum: state => state.initNum,
		getFailLoad: state => state.failLoad,
		getAichart: state => state.aiStore,
	},
	mutations: {
		setStateProperty(state, {key, value}) {
			Vue.set(state, key, value);
		},
		toggleStateProperty(state, key) {
			let status = !state[key];
			Vue.set(state, key, status);
		},

		setChartBackups(state, chart) {
			let id = chart.containerOptions.tableId;
			Vue.set(state.chartBackups, id, JSON.parse(JSON.stringify(chart)));
			// state.chartBackups.push(JSON.parse(JSON.stringify(chart)));
			// state.chartBackups.push(JSON.parse(JSON.stringify(chart)));
		},
		setPhoneLayoutState(state) {		//pc
			state.phoneLayoutState = !state.phoneLayoutState;
		},
		resetPhoneLayoutState(state) {		//pc
			state.phoneLayoutState = false;
		},

		setRenderedStatus(state) {		//
			// debugger;
			state.renderedChartCount++;
		},
		setLayerEditStatus(state, status) {		//
			Vue.set(state.chartList[state.currentChartIndex]['containerOptions'], 'isLayerEdit', status);
		},
		setLayerName(state, params) {		//
			const index = params['index'];
			const chartLargeType = state.chartList[index]['containerOptions']['chartsType'].split('-')[0];
			Vue.set(state.chartList[index]['containerOptions'], 'layerName', params['layerName']);
			switch (chartLargeType) {		//
			case 'echarts':
				this.commit('dataDimension/setAmendChartCount');
				break;
			case 'media':
				this.commit('defaultMediaOptions/sendMediaOptions', {
					index: index,
					mediaData: state.chartList[index],
					operationType: 'amend'
				});
				break;
			case 'text':
				this.commit('defaultMediaOptions/sendMediaOptions', {
					index: index,
					mediaData: state.chartList[index],
					operationType: 'amend'
				});
				break;
			case 'interactive':
				this.commit('defaultInteractiveOptions/sendInteractiveOptions', {
					index: index,
					interactiveData: state.chartList[index],
					operationType: 'amend'
				});
				break;
			default:
				break;
			}
		},
		/**
     * @param {*} state
     * 5s
     * 
     * 
     * indexDB
     */
		setChartOptionsAmendCount(state, params = {}) {
			const {index = state.currentChartIndex} = params;
			// const index =params.index|| state.currentChartIndex;
			//3s
			if (index > -1 && state.chartList[index]['containerStatus']['isSetOptions']) {
				const currentChartData = {
					index: index,
					chart: state.chartList[index]
				};
				//indexDB
				const curChartType = currentChartData['chart']['containerOptions']['chartsType'].split('-')[0];
				let dbType = '';
				let dbFieldObject = '';
				switch (curChartType) {
				case 'echarts':
					dbType = 'saveOption';
					dbFieldObject = 'chartArray';
					break;
				case 'text':
					dbType = 'saveTextOption';
					dbFieldObject = 'textArray';
					break;
				case 'media':
					dbType = 'saveMediaOption';
					dbFieldObject = 'mediaArray';
					break;
				case 'interactive':
					dbType = 'saveInteractiveOption';
					dbFieldObject = 'interactiveArray';
					break;
				default:
					break;
				}
				const curAmendChartData = {
					id: index,
					[dbFieldObject]: currentChartData
				};
				// debugger;
				dexieDB[dbType].put(curAmendChartData)
					.then((res) => {
						state.chartOptionsAmendCount++;
					})
					.catch(error => {
						console.error(error);
					});
				//chartOptions.vue
			}
		},

		/**
     * @tableid
     * @vuetableid
     * @
     **/
		setCurrentProjectInfo(state, arr) {
			for (let i = 0; i < arr.length; i++) {
				const key = arr[i].key;
				const value = arr[i].value;
				state[key] = value;
			}
		},
		//canvas
		setScaleLevel: (state, scale) => {
			state.scaleLevel = scale;
		},
		//canvas
		setCanvasBgColor(state, data) {
			state.canvasColor = data;
		},
		//canvas
		setCanvasBgImg(state, data) {
			state.canvasImg = data;
			// state.canvasBGAmend++;
		},

		// 
		setCanvasBgMobileColor(state, data) {
			state.canvasBgPhoneColor = data;
		},
		setCanvasBgMobileImg(state, data) {
			state.canvasBgPhoneImg = data;
		},
		setCanvasBGAmendCount(state) {
			state.canvasBGAmend++;
		},
		satrapModifyCount(state, data) {
			state.mapModify++;
		},
		setInitAllChart(state, status) {
			state.chartList = [];
			state.renderedChartCount = 0;
			if (!state.chartList.length) {
				state.isInitAllChart = status;
			}
		},
		setAiChart(state) {
			state.aiStore = true;
		},
		//index
		setCurrentChartIndex(state, index) {
			Vue.set(state, 'currentChartIndex', index);
		},
		//
		setCurrentChartType(state, index) {//index1
			if (index > -1) {
				state.currentChartType = state.chartList[index].containerOptions.chartsType;//
			}
		},
		/** */
		setCurrentChartId(state, index) {
			Vue.set(state.chartList[index], 'id', index);
		},
		/**
     * 
     *    isSetOptions
     * 
     * 
     * isSetOptions
     */
		setCurrentTableId(state, params) {
			state.chartList.find((e, index) => {
				if (params.index == index) {
					Vue.set(e.containerOptions, 'tableId', params.tableId);
					if (params.flag == '1') {
						Vue.set(e.containerStatus, 'isSetOptions', true);
						Vue.set(e.containerStatus, 'dataDimensionShow', false);
					}
				}
			});
		},
		setIsSetOptions(state, params) {
			if (params.index > -1) {
				/**
         * 5s
         */
				state.chartList[params.index]['containerStatus']['isSetOptions'] = false;
				const currentChartData = {
					chart: state.chartList[params.index],
					index: params.index
				};
				dexieDB.saveOption
					.put({
						id: params.index,
						chartArray: currentChartData
					})
					.then(() => {
					})
					.catch(error => console.error(error));
			}
		},
		/**
     * echarts
     * @param {object} state
     * @param {object} chartAllOptions SiderBar.vue
     *
     * 
     * {
     * containerStatus:{},
     * containerOptions:{},
     * chartOptions/textOptions/mediaOptions/interactiveOptions:{}
     * }
     */
		setChartContainer(state, chartAllOptions) {
			let formattedChartOption = chartAllOptions.defaultOptions;
			let containerStatus = formattedChartOption.containerStatus ? formattedChartOption.containerStatus : {};
			let newChartOptions = {
				containerStatus: {
					...JSON.parse(JSON.stringify(state.containerStatus)),
					...containerStatus,
				},
				containerOptions: formattedChartOption.containerOptions,
				marks: formattedChartOption.marks || '', //
				id: state.chartList.length
			};
			//  echarts-barArea
			const chartType = newChartOptions.containerOptions.chartsType;
			Vue.set(newChartOptions, 'renderOptions', formattedChartOption.renderOptions);
			//  echarts
			const curChartType = chartType.split('-')[0];
			//
			newChartOptions.containerOptions.level = state.chartList.length;
			//
			newChartOptions.containerOptions.layerIndex = state.chartList.length;
			//
			// newChartOptions.containerOptions.chartIndex = state.chartList.length-1;
			//
			state.currentChartIndex = state.chartList.length;
			state.chartList.push(newChartOptions);
			//
			const index = state.currentChartIndex;
			let array = state.chartList.map((ele) => {
				return ele.containerOptions.level || 0;
			});
			let maxZindex = Math.max(...array);
			this.commit('canvasPlace/setChartZindex', {
				index,
				level: maxZindex + 1
			});
			//chartOptions
			// defaultMediaOptions.js/defaultInteractiveOptions.js
			if (curChartType === 'echarts') {
				let countObject = formattedChartOption.dataDimension;
				let formattedDefault = JSON.parse(JSON.stringify(state.dataDimensionDefault));
				let formattedDefaultY = JSON.parse(JSON.stringify(state.dataDimensionDefault));
				let formattedDefaultX = JSON.parse(JSON.stringify(state.dataDimensionDefault));
				/**
         * dataDimensioncategoryAxis
         * categoryAxis
         * valueAxis
         * categoryAxis
         * 
         */
				if (countObject.categoryAxis == 'X') {
					// Vue.set(formattedDefaultY, 'categoryAxis', 'valueAxis');
					// Vue.set(formattedDefaultY, 'categoryAxis', 'categoryAxis');
					formattedDefaultY.categoryAxis = 'valueAxis';
					formattedDefaultX.categoryAxis = 'categoryAxis';
				} else {
					formattedDefaultX.categoryAxis = 'valueAxis';
					formattedDefaultY.categoryAxis = 'categoryAxis';
				}
				//
				let dataSeries = '';
				if (countObject.dataSeriesLength) {
					dataSeries = [JSON.parse(JSON.stringify(formattedDefault))];
				} else {
					dataSeries = [];
				}
				/**
         * axisOptions
         */
				let axisOptions = {
					//x
					xAxisList: chartAllOptions.copyAxisOptions ? chartAllOptions.copyAxisOptions.xAxisList : new Array(countObject.xAxisLength).fill(formattedDefaultX), //Copy
					//y
					yAxisList: chartAllOptions.copyAxisOptions ? chartAllOptions.copyAxisOptions.yAxisList : new Array(countObject.yAxisLength).fill(formattedDefaultY),
					// 
					groupData: [{
						groupField: '',
						groupSourceId: '',
						groupSourceName: '',
						groupOp: '',
						groupCategory: '',
						groupCondition: '',
						name: ''
					}],
					//3d[]
					cityName: [],
					//3d[]
					columnHeight: [],
					//
					dataSeries: chartAllOptions.copyAxisOptions ? chartAllOptions.copyAxisOptions.dataSeries : dataSeries
				};
				//3d
				if (chartType === 'echarts-3dWorldMap') {
					axisOptions['cityName'] = [JSON.parse(JSON.stringify(state.dataDimensionDefault))];
					axisOptions['columnHeight'] = [JSON.parse(JSON.stringify(state.dataDimensionDefault))];
				}
				//echartsaxisOptions indexDBaXisList
				if (index > -1) {
					state.currentChartAxis = axisOptions;
					dexieDB.aXisList
						.put({
							index: index,
							axisOptions: axisOptions
						})
						.then(() => {
						})
						.catch(error => console.error(error));
				}
				this.commit('dataDimension/setUpdateCount');

			} else if (curChartType === 'media' || curChartType === 'text') {
				this.commit('defaultMediaOptions/sendMediaOptions', {
					index: index,
					mediaData: newChartOptions,
					operationType: 'create'
				});
			} else if (curChartType === 'interactive') {
				this.commit('defaultInteractiveOptions/sendInteractiveOptions', {
					index: index,
					interactiveData: JSON.parse(JSON.stringify(newChartOptions)),
					operationType: newChartOptions.marks ? 'amend' : 'create'
				});
			}
		},
		//chartListchartOptions
		setChartList(state, list) {
			let chartType = list.containerOptions.chartsType.split('-')[0];
			if (chartType === 'text') {
				state.chartList.push(list);
			} else if (chartType === 'media') {
				state.chartList.push(list);
			} else if (chartType === 'interactive') {
				interactiveDataFill(list);
				state.chartList.push(list);
			} else {//
				state.chartList.push(chartDataFill(list));
			}
		},
		setChartZindex(state, params) {
			if (params.index > -1) {
				Vue.set(state.chartList[params.index].containerOptions,
					'level', params.level
				);
			}

		},
		toggleDataDimensionShow(state, index) {
			let show = !state.chartList[index].containerStatus.dataDimensionShow;
			Vue.set(state.chartList[index].containerStatus,
				'dataDimensionShow', show
			);
		},
		// toggleFirstLoad(state, index) {
		// 	// let show = !state.chartList[index].containerStatus.isFirstLoad;
		// 	Vue.set(state.chartList[index].containerStatus,
		// 		'isFirstLoad', false
		// 	);
		// },
		setShowLoading(state, index) {
			const isShowLoading = !state.chartList[index].containerStatus.isShowLoading;
			state.chartList[index].containerStatus.isShowLoading = isShowLoading;
		},
		hideLoading(state, index) {
			state.chartList[index].containerStatus.isShowLoading = false;
		},
		setChartWidth(state, index, width) {
			Vue.set(state.chartList[index].containerOptions,
				'tableWidth', width
			);
		},
		setChartHeight(state, index, height) {
			Vue.set(state.chartList[index].containerOptions,
				'tableHeight', height
			);
		},
		setChartSize(state, {paramsSize, mobileSize}) {//
			let index = state.currentChartIndex;
			if (paramsSize) {
				if (isNumber(paramsSize.index)) {
					index = paramsSize.index;
				}
				Vue.set(state.chartList[index].containerOptions,
					'tableWidth', paramsSize.width
				);
				Vue.set(state.chartList[index].containerOptions,
					'tableHeight', paramsSize.height
				);
			} else if (mobileSize) {// 
				if (isNumber(mobileSize.index)) {
					index = mobileSize.index;
				}
				Vue.set(state.chartList[index].containerOptions,
					'tableWidthMobile', mobileSize.width
				);
				Vue.set(state.chartList[index].containerOptions,
					'tableHeightMobile', mobileSize.height
				);
			}
		},
		setChartPosition(state, {position, mobilePosition}) {// 
			let index = state.currentChartIndex;
			if (position) {
				if (isNumber(position.index)) {
					index = position.index;
				}
				Vue.set(state.chartList[index].containerOptions,
					'xPosition', position.x
				);
				Vue.set(state.chartList[index].containerOptions,
					'yPosition', position.y
				);
			}
			if (mobilePosition) {//
				if (isNumber(mobilePosition.index)) {
					index = mobilePosition.index;
				}
				Vue.set(state.chartList[index].containerOptions,
					'xPositionMobile', mobilePosition.x
				);
				Vue.set(state.chartList[index].containerOptions,
					'yPositionMobile', mobilePosition.y
				);
			}
		},
		resetMobileChartOptionsByIndex(state, index) {
			const element = state.chartList[index];
			Vue.set(element.containerOptions, 'xPositionMobile', element.containerStatus.lastMobileSavedOptions.xPositionMobile);
			Vue.set(element.containerOptions, 'yPositionMobile', element.containerStatus.lastMobileSavedOptions.yPositionMobile);
			Vue.set(element.containerOptions, 'tableWidthMobile', element.containerStatus.lastMobileSavedOptions.tableWidthMobile);
			Vue.set(element.containerOptions, 'tableHeightMobile', element.containerStatus.lastMobileSavedOptions.tableHeightMobile);
		},
		setMobileChartPosition(state, {index, mobilePosition}) {
			if (mobilePosition.x >= 0) Vue.set(state.chartList[index].containerOptions, 'xPositionMobile', mobilePosition.x);
			if (mobilePosition.y >= 0) Vue.set(state.chartList[index].containerOptions, 'yPositionMobile', mobilePosition.y);

		},

		modifyAllMobileChartOptions(state) {
			for (let i = 0; i < state.chartList.length; i++) {
				const element = state.chartList[i];
				const obj = {
					showInMobileLayout: element.containerOptions.showInMobileLayout,
					xPositionMobile: element.containerOptions.xPositionMobile,
					yPositionMobile: element.containerOptions.yPositionMobile,
					tableWidthMobile: element.containerOptions.tableWidthMobile,
					tableHeightMobile: element.containerOptions.tableHeightMobile,
				};
				Vue.set(element.containerStatus, 'lastMobileSavedOptions', obj);
			}
		},
		deleteCurrentChart(state, params) {
			let index = params.index;
			if (index > -1) {
				//delLayerCount
				state.delLayerCount++;
				Vue.set(state.chartList, index, params.data);
				//currentChartIndex-1
				state.currentChartIndex = -1;
			}
		},
		changeIndexChart(state, params) {
			params.data.id = index;
			let hanldeComponentData = beforeThemeHandle(params.data, index);
			let {renderOptions} = hanldeComponentData;
			let chartfullType = params['data']['containerOptions']['chartsType'];
			const {index = state.currentChartIndex} = params;
			Vue.set(state.chartList[index], 'containerOptions', params.data.containerOptions);
			if (params.data.containerStatus) Vue.set(state.chartList[index], 'containerStatus',
				{...state.chartList[index].containerStatus, ...params.data.containerStatus});
			if (chartfullType.indexOf('chart') > -1) {
				Vue.set(state.chartList[index], 'renderOptions', chartDataFill(hanldeComponentData).renderOptions);
			} else if (chartfullType.indexOf('interactive') > -1) {
				interactiveDataFill(hanldeComponentData);
				state.chartList[index].renderOptions = params.data.renderOptions;
			} else {
				state.chartList[index].renderOptions = renderOptions;
			}

		},
		/*  start*/
		changeChartTheme(state, index = state.currentChartIndex) {
			let {renderOptions} = chartDataFill(state.chartList[index]);
			// console.log(renderOptions.Visual.theme);
			Vue.set(state.chartList[index], 'renderOptions', renderOptions);
		},

		/*  end*/

		// eChart renderOptions
		renderOptionSetter: (state, params) => {
			const {id = state.currentChartIndex} = params;
			let key = params.key.split('.');
			let len = key.length;
			let result = {};
			let firstK = key[0];
			result[key[len - 1]] = params.value;
			for (let i = key.length - 2; i >= 0; i--) {
				let k = key[i];
				let lastK = key[i + 1];
				result[k] = {};
				result[k][lastK] = result[lastK];
			}
			// console.log(firstK,state.chartList[params.Id].chartOptions[firstK],result[firstK]);
			let obj = {};
			obj[firstK] = result[firstK];
			mergeJSON(obj, state.chartList[id].renderOptions);
		},
		containerOptionSetter: (state, params) => {
			const {id = state.currentChartIndex} = params;
			let key = params.key.split('.');
			let len = key.length;
			let result = {};
			let firstK = key[0];
			result[key[len - 1]] = params.value;
			for (let i = key.length - 2; i >= 0; i--) {
				let k = key[i];
				let lastK = key[i + 1];
				result[k] = {};
				result[k][lastK] = result[lastK];
			}
			// console.log(firstK,state.chartList[params.Id].chartOptions[firstK],result[firstK]);
			let obj = {};
			obj[firstK] = result[firstK];
			mergeJSON(obj, state.chartList[id].containerOptions);
		},
		// 
		clearArrChart: (state) => {

			for (let key of Object.keys(state.chartBackups)) {
				Vue.delete(state.chartBackups, key);
			}
			state.chartList = [];
			state.renderedChartCount = 0;
		},

		// setPreserve(state) {  //
		// 	state.preserveMark++;
		// },
		setModuleType(state, params) {
			if (params) {
				state.initNum++;
			} else {
				state.failLoad++;
			}
		}
	},
	actions: {
		containerOptionSetterAll({state, commit}, keyValues = []) {
			for (let i = 0; i < keyValues.length; i++) {
				commit('containerOptionSetter', keyValues[i]);
			}
		},
		resetMobileChartOptions({state, getters}) {
			for (let i = 0; i < getters.chartMobileFilter.length; i++) {
				const element = getters.chartMobileFilter[i];
				let id = element.id;
				let lastMobileSavedOptions = element.containerStatus.lastMobileSavedOptions;
				Vue.set(state.chartList[id], 'containerOptions', Object.assign(state.chartList[id]['containerOptions'], lastMobileSavedOptions));
			}
		},
		chartOnDeactivated({state}) {
			// this.getters['/'];
			let index = state.currentChartIndex;
			if (index > -1) {
				state.currentChartIndex = -1;
			}
		},
		asyncChartOptionsChange({state}, params = {}) {
			const {index = state.currentChartIndex} = params;
			if (index > -1) {
				const currentChartData = {
					index: index,
					chart: state.chartList[index]
				};
				let chartType = currentChartData.chart.chartType.split('-')[0];
				const dbTypeTable = {
					echarts: 'saveOption',
					media: 'saveMediaOption',
					text: 'saveTextOption',
					interactive: 'saveInteractiveOption',
				};
				const dbType = dbTypeTable[chartType];
				const dbFieldObjectTable = {
					saveOption: 'chartArray',
					saveMediaOption: 'mediaArray',
					saveTextOption: 'textArray',
					saveInteractiveOption: 'interactiveArray'
				};
				const curAmendChartData = {
					id: index,
					[dbFieldObjectTable[dbType]]: currentChartData
				};
				return new Promise((resolve, reject) => {
					dexieDB[dbType].put(curAmendChartData).then(() => {
						state.chartOptionsAmendCount++;
						resolve('hahah');
					});
				});
			}
		},
		setIndexdbChartOptions({state}, params = {}) {
			// debugger;
			// const
			const {index = state.lastActivedChartIndex} = params;
			//5s
			if (index > -1 && state.chartList[index]['containerStatus']['isSetOptions']) {
				const currentChartData = {
					index: index,
					chart: state.chartList[index]
				};
				//indexDB
				const curChartType = currentChartData['chart']['containerOptions']['chartsType'].split('-')[0];
				let dbType = '';
				let dbFieldObject = '';
				switch (curChartType) {
				case 'echarts':
					dbType = 'saveOption';
					dbFieldObject = 'chartArray';
					break;
				case 'text':
					dbType = 'saveTextOption';
					dbFieldObject = 'textArray';
					break;
				case 'media':
					dbType = 'saveMediaOption';
					dbFieldObject = 'mediaArray';
					break;
				case 'interactive':
					dbType = 'saveInteractiveOption';
					dbFieldObject = 'interactiveArray';
					break;
				default:
					break;
				}
				const curAmendChartData = {
					id: index,
					[dbFieldObject]: currentChartData
				};
				// return new Promise((resolve,reject)=>{
				dexieDB[dbType].put(curAmendChartData)
					.then((res) => {
						if (params.callback) {
							state.updateOptionsCallback = params.callback;
						} else {
							state.updateOptionsCallback = null;
						}
						state.updateOptionsImmediately = true;
						// resolve('');
					})
					.catch(reason => {
						console.log('reason');
					});

				// });
				//chartOptions.vue
			} else {
				if (params.callback) {
					state.updateOptionsCallback = params.callback;
				} else {
					state.updateOptionsCallback = null;
				}
				state.updateOptionsImmediately = true;
			}
		},

		//
		setPhoneLayoutSavedState({state}, isSaved) {
			state.isPhoneLayoutSaved = isSaved;
		},

		getChartData({state, rootGetters}, params) { //waitasync
			return new Promise((resolve, reject) => {
				if (params.sourceType === 'initAPI') {
					resolve(Vue.prototype.$get('api/v2/chart', {
						projectId: params.project
					}));
				} else if (params.sourceType === 'module') {
					let dataList = JSON.parse(JSON.stringify(rootGetters['moduleCharts/getChartList']));
					dataList.map(e => {
						e.projectId = sessionStorage.getItem('projectId');
						e.renderOptions = JSON.stringify(e.renderOptions);
						return e;
					});
					Vue.prototype.$post('api/v2/chartMultiple', dataList).then(res => {
						// this.dispatch('moduleCharts/getTableId', dataList)F.then(res => {
						if (res.code == '000000') {
							resolve(Vue.prototype.$get('api/v2/chart', {projectId: sessionStorage.getItem('projectId')}));
						} else {
							reject();
						}
					}).catch(err => {
						reject(err);
					});
				}
			});
		},
		setFilterChart: ({state, commit}, data) => {
			//tableId()
			// let oldArray=state.chartList.sort.filter(item=>item.containerOptions.tableId).sort((a,b)=>a.containerOptions.tableId-b.containerOptions.tableId);
			// let newArray= data.chartList.sort((a,b)=>a.containerOptions.tableId-b.containerOptions.tableId);
			data.forEach((item, index) => {
				item.renderOptions = JSON.parse(item.renderOptions);
				// console.log(item.chartOptions,'item');
				state.chartList.forEach((i, chartIndex) => {
					if (item.containerOptions.tableId === i.containerOptions.tableId) {
						let chartType = i.containerOptions.chartsType.split('-')[0];
						const {xPosition, yPosition, tableWidth, tableHeight} = i.containerOptions;
						item.containerOptions = Object.assign(item.containerOptions, {
							xPosition,
							yPosition,
							tableWidth,
							tableHeight
						});
						commit('changeIndexChart', {data: item, index: chartIndex});
						/**
             * 
             * */
						// if (chartType === 'echarts') {
						// 	state.chartList[index].renderOptions.series.forEach((y, index) => {
						// 		y.data = item.renderOptions.series[index].data;
						// 	});
						// 	state.chartList[index].renderOptions.xAxis ? state.chartList[index].renderOptions.xAxis.data = item.renderOptions.xAxis.data : '';
						// } else {
						// 	state.chartList[index].renderOptions =JSON.parse(item.renderOptions);
						// }
						// else if (chartType==='echarts') {
						// 	state.chartList[index].interactiveOptions = item.interactiveOptions;
						// } else if (item.textOptions) {
						// 	state.chartList[index].textOptions = item.textOptions;
						// } else if (item.mediaOptions) {
						// 	state.chartList[index].mediaOptions = item.mediaOptions;
						// }
					}
				});
			});
		},
		screeningRequest({state, dispatch}) {//
			Vue.prototype.$get('api/v2/chart', {
				projectId: sessionStorage.getItem('projectId')
			}).then(success => {
				// dataComparison(success);
        dispatch('setFilterChart', dataComparison(state.chartList, success));//
			});
		},
	}
};

function dataComparison(chartList, success) {//
	let contrastResult = [];
	const equals = (a, b) => {
		if (a === b) return true;
		if (a instanceof Date && b instanceof Date) return a.getTime() === b.getTime();
		if (!a || !b || (typeof a !== 'object' && typeof b !== 'object')) return a === b;
		if (a.prototype !== b.prototype) return false;
		let keys = Object.keys(a);
		if (keys.length !== Object.keys(b).length) return false;
		return keys.every(k => equals(a[k], b[k]));
	};
	success.data.forEach((item, index) => {
		if (equals(item.containerOptions.seriesData, chartList[index].containerOptions.seriesData) === false) {
			contrastResult.push(item);
		}
	});
	return contrastResult;
}
