<template>
  <div class="manyPromptData maskBackground">
    <div class="box allCenter2">
      <div class="title">{{options.title}}</div>
      <div class="content"
        :class="{tableHeigth:heightShow}">
        <EasyScroll ref="Scrollbar"
          :barOption="srcollTable">
          <ul>
            <li v-for="(item,index)  in options.name"
              :key="index">
              <label v-for="(name,i) in item"
                :key="i"
                :title="name">{{name}}</label>
              <input type="text"
                class="inputBox"
                v-model="message[index]">
            </li>
          </ul>
        </EasyScroll>
      </div>
      <div class="buttonDiv">
        <span class="btn mainBtn"
          v-for="(item,index) in options.btn"
          :key="index"
          @click="funArr[index](message)">{{item.text}}</span>
      </div>
    </div>
