</template>
<script>
import { modal } from '../mixin/modal';
import { mapGetters } from 'vuex';

export default {
	name: 'Prompt',
	props: ['options'],
	mixins: [modal],
	computed: {
		...mapGetters('main', ['getVw']),
		// eslint-disable-next-line vue/no-unused-components
		srcollTable() {
			return {
				barColor: '#95A0B6', //
				railColor: '#475566', //
				barWidth: this.getVw(5), //
				barMarginRight: 0, //px
				barOpacityMin: 0.3, //
			};
		}
	},
	data() {
		return {
			message: [],
			heightShow: false
		};
	},
	mounted() {
		let len = this.options.name.length;
		if (len > 6) {
			this.heightShow = true;
		}
	}
