<template>
  <div class="wrapper">
    <div class="chart-basis"
      @click="showEvent(8)">
      <span></span>
      <span class="biFont icon-arrowLeft icon-fontsize"
        :class="{rotateIcon:isShowLabel}"></span>
    </div>

    <div v-show="isShowLabel"
      class="content-box">
      <ul>
        <li class="optionsItem">
          <span class="span-title">Labels</span>
          <span class="span-title">{{labelsValue}}</span>
          <toggle-button :value="isSwitchLabels"
            :color="boxColor"
            :switch-color="switchColor"
            :labels="{checked: '', unchecked: ''}"
            @change="onChangeLabels"
            :css-colors="true" />
        </li>
        <li class="optionsItem">
          <span class="span-title">legend</span>
          <span class="span-title">{{legendValue}}</span>
          <toggle-button :value="isSwitchLegend"
            :color="boxColor"
            :switch-color="switchColor"
            :labels="{checked: '', unchecked: ''}"
            @change="onChangelegend" />
        </li>
        <li>
          <select-color :defaultColor="colorObject"
            v-on:changeColor="getTitle"></select-color>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { ToggleButton } from 'vue-js-toggle-button';
import ColorSelect from './BasicComponent/ColorSetting';
export default {
	name: 'DataLabel',
	components: {
		'select-color': ColorSelect,
		'toggle-button': ToggleButton
	},
	data() {
		return {
			clientHeight: 0,
			bottom: 0,
			disBottom: 0,
			colorHeight: 0,
			isShowLabel: true,
			labelsValue: '',
			legendValue: '',
			boxColor: { checked: '#11151f', unchecked: '#11151f' }, //switch
			switchColor: { checked: '#fff', unchecked: '#475566' }, //switch
			isSwitchLabels: false,
			isSwitchLegend: false,
			colorObject: {
				titleColor: '#194D33',
				titleName: ''
			}
		};
	},
	methods: {
		stopAffair(e) {
			e.stopPropagation();
			return false;
		},
		//labels
		onChangeLabels(checkValue) {
			this.labelsValue = checkValue.value ? '' : '';
		},
		//legend
		onChangelegend(checkValue) {
			this.legendValue = checkValue.value ? '' : '';
		},
		getTitle(data) {},
		//
		showEvent(number) {
				this.isShowLabel = !this.isShowLabel;
		}
	}
};
</script>

<style scoped>
.optionsItem {
  margin-bottom: 0.8vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.optionsItem .span-title {
  display: block;
  flex: 1;
  color: #acbbda;
}

.chart-basis .rotateIcon {
  transform: rotate(-90deg);
}
</style>
<style scoped>
.x-axas-position .sketch .vc-sketch-field span {
  color: #fff;
}
.x-axas-position
  .sketch
  .vc-sketch-field
  .vc-sketch-field--double
  .vc-editable-input
  input {
  padding-left: 0;
}
/*swich*/
.optionsItem .vue-js-switch .v-switch-core .v-switch-button {
  width: 0.6vw;
  height: 0.6vw;
  top: 0.1vw;
}
.vc-saturation-pointer .vc-saturation-circle {
  /*cursor: */
  width: 0.3vw;
  height: 0.3vw;
  box-shadow: 0 0 0 1.5px #fff, inset 0 0 1px 1px rgba(0, 0, 0, 0.3),
    0 0 1px 2px rgba(0, 0, 0, 0.4);
  border-radius: 50%;
  transform: translate(-2px, -2px);
}
/* .vc-sketch {
																											                        z-index: "10";
																											                        position: absolute;
																											                        top: 1.2vw;
