																											                        width: 240px;
																											                        background-color: #161c29;
																											                        border: solid 0.0625vw #475566;
																											                        border-radius: 0;
																											                    } */
/*switch*/
.v-switch-label.v-left,
.v-right {
  transform: scale(0.9);
}
.vue-js-switch .v-switch-core {
  background-color: #171e2b;
  border: solid 0.0625vw #475566;
}
