.wrapper >>> .dataTableAnimation-enter-active,
.dataTableAnimation-leave-active {
  transition: all 0.3s ease;
}
.wrapper >>> .dataTableAnimation-leave-active,
.wrapper >>> .dataTableAnimation-enter {
  height: 0;
}
