<template>
  <div class="basis-grid-div">
    <span class="color-font">{{opt.name}}</span>
    <div class="line-box">
      <span class="span-1" @click="styleB(1)" :class="{'color-blue':textB}">B</span>
      <span class="span-2" @click="styleB(2)" :class="{'color-blue':textI}">I</span>
      <span class="span-3" @click="styleB(3)" :class="{'color-blue':textU}">U</span>
      <span class="span-4" @click="styleB(4)" :class="{'color-blue':textS}">S</span>
    </div>
  </div>
</template>
<script>
export default {
	name: 'TextFormat',
	data() {
	  return {
	    textB:false,
			textI:false,
			textU:false,
			textS:false
		};
	},
	props: {
		opt:{
		  type:Object,
			required:true
		}
	},
	watch: {
	  'opt':{
	    immediate:true,
			deep:true,
			handler(){
	      let vm = this;
				vm.textB = vm.opt.fontWeight === 'bold';
				vm.textI = vm.opt.fontStyle === 'oblique';
				vm.textU = vm.opt.textDecoration === 'underline';
				vm.textS = vm.opt.textDecoration === 'line-underline';
			}
		}
	},
	methods: {
	  emitStyle(data){
	    this.$emit('fontStyle',data);
		},
		styleB(data) {
	    let vm = this,
				setWeight,
				setStyle,
				setDecoration;
			switch (data) {
			case 1:
				vm.textB = !vm.textB;
				setWeight = vm.textB===true?'bold':'unset';
				vm.emitStyle({name:'textStyle.fontWeight',setting:setWeight});
				break;
			case 2:
			  vm.textI = !vm.textI;
				setStyle = vm.textI===true?'oblique':'unset';
				vm.emitStyle({name:'textStyle.fontStyle',setting:setStyle});
				break;
			case 3:
				vm.textU = !vm.textU;
				if (vm.textU === true) {
					vm.textS = false;
					setDecoration = 'underline';
					vm.emitStyle({name:'textStyle.decoration',setting:setDecoration});
				} else if (vm.textS === false && vm.textS === false){
					setDecoration = 'unset';
					vm.emitStyle({name:'textStyle.decoration',setting:setDecoration});
				}
				break;
			case 4:
				vm.textS = !vm.textS;
				if (vm.textS === true) {
					vm.textU = false;
					setDecoration = 'line-through';
					vm.emitStyle({name:'textStyle.decoration',setting:setDecoration});
				} else if (vm.textS === false && vm.textS === false){
					setDecoration = 'unset';
					vm.emitStyle({name:'textStyle.decoration',setting:setDecoration});
				}
				break;
			default: break;
			}
		},
	},
	created(){
	  // let vm =
	}
};
</script>

<style scoped>
  .basis-grid-div {
    /*float: right;*/
    /*position: relative;*/
    display: flex;
    justify-content: space-between;
    width: 100%;
    user-select: none;
  }
  .line-box {
    display: flex;
    justify-content: space-between;
    color: #fff;
    width: 8.5vw;
    padding: 0 1vw 0 1vw;
  }
  .line-box .span-1 {
    cursor: pointer;
    font-weight: bolder;
    font-size: 0.1vw;
  }
  .line-box .span-2 {
    cursor: pointer;
    font-style: oblique;
    font-size: 0.1vw;
  }
  .line-box .span-3 {
    cursor: pointer;
    text-decoration: underline;
    font-size: 0.1vw;
  }
  .line-box .span-4 {
    cursor: pointer;
    text-decoration: line-through;
    font-size: 0.1vw;
  }
  .color-font {
    color: #acbbda;
  }
  span.color-blue{
    color: #28b3fc;
  }
</style>
