<template>
  <!--  <div>-->
  <!--    <div class="chart-basis">-->
  <!--      <span></span>-->
  <!--      <span class="biFont icon-arrowLeft icon-fontsize"-->
  <!--        :class="{rotateIcon:developer}"></span>-->
  <!--    </div>-->
  <div ref="contentBox">
    <ul class="content-box">
      <li class="optionsItem">
        <span class="title"></span>
        <toggle-button :css-colors="true"
          :labels="{checked: '', unchecked: ''}"
          :sync="true"
          @change="toggleType"
          v-model="pushState"/>
      </li>
      <li>
      </li>
    </ul>
  </div>
  <!--  </div>-->
</template>

<script>
import {ToggleButton} from 'vue-js-toggle-button';
import {mapGetters, mapMutations} from 'vuex';
import {mixin} from './BasicComponent/mixin';

export default {
	mixins: [mixin],
	name: 'MapSettings',
	components: {
		ToggleButton,
	},
	data() {
		return {
			pushState: true,
		};
	},
	computed: {
		...mapGetters('chartBus', {
			chartchartData: 'returnData',
		})
	},
	watch: {
		chartchartData() {
			let vm = this;
			if (vm.chartchartData.chart.renderOptions.series[0].echartsMapType === 'classification') {
				vm.pushState = false;
			}
		}
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter'
		}),
		toggleType(val) {
			/**
         * @params:truefalse 
         *
         * */
			this.renderOptionSetter({
				key: 'echartsMapType',
				id: 0,
				value: val.value ? 'effectScatter' : 'classification'
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		}
	}
};
</script>

<style scoped>
  .optionsItem {
    margin-bottom: 0.8vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .title {
    float: left;
    flex: 1;
    color: #acbbda;
  }
  .optionsItem /deep/ .vue-js-switch{
    float: right;
  }
  .optionsItem /deep/ .vue-js-switch .v-switch-label.v-left {
    left: 6px;
  }

  .optionsItem /deep/ .vue-js-switch .v-switch-label.v-right {
    right: 6px;
  }
</style>
