<template>
  <div class="filter"
    :class="{'filter-bg':!isSetOptions}">
    <div class="box"
      :style="setFocusStyle"
      :class="contentColor"
      @dragleave="dragleave"
      @dragover="dragover($event)"
      @drop="axisDrop($event)">
      <div class="hidden-y">
        <div v-if="options['screenType']==='number'"
          class="filterWrapper filterNumber">
          <div class="number-02">
            <input type="text"
              class="number-input"
              :value="valueRange[0]"
              @blur='listenValue($event,0)'
              @input="inputData">
              <slider-range
                class="valueSilder"
                :valueRange="valueRange"
                :min="min"
                :max="max"
                :lazy="true"
                tooltipFormatter="{value}"
                @rangedata="rangegariDyValue"></slider-range>
<!--            <div class="valueSilder">-->
<!--            </div>-->
            <input type="text"
              class="number-input"
              :value="valueRange[1]"
              @blur='listenValue($event,1)'
              @input="inputData">
          </div>
        </div>
        <div v-if="options['screenType']==='time'"
          class="filterWrapper">
          <div class="solid-time">
            <calendar-time
              :value="defalutDate"
              :time="time"
              :isCurrentChart="isCurrentChart"
              @changeDefalutDate="changeDefalutDate"
              @selectDate="select"
              @confirmSelectDate='confirmSelect'
              @clearALlDate="clearALlDate"
              @applySelectDate="applySelectDate"
              inputStyle="width:40%;minWidth:'4vw'"
            ></calendar-time>
            <!--              @clearDateByIndex='clearDate'-->
          </div>
          <div>
          </div>
        </div>
        <div v-if="options['screenType']==='text'"
          class="filterWrapper">
          <div class="contentDataStyle">
            <data-filterDropDown :dropList="fieldDataList"
              @scroll="onScroll(e)"
              :dataSourceOptions="options"
              :pitchArr="options.screenText?options.screenText:[]"
              :introduiction="introduictionData"
              :selectAll='selectAll'
              @xScrollToEnd="handler"
              @receiveArr="receData(arguments)"
            ></data-filterDropDown>
            <!--            @comeOut="getDropDownData"-->
            <span class="span-i-style"></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import SliderRange from '@/components/SliderRange';
import Calendar from '@/components/TimeSelector/index.vue';
import dataFilterDropDown from './dataFilterDropDown';
import {mapGetters, mapMutations} from 'vuex';

export default {
	name: 'interactive-dataFilter',
	components: {
		'slider-range': SliderRange,
		// 'calendar-time':Calendar,
		'calendar-time': Calendar,
		'data-filterDropDown': dataFilterDropDown
	},
	props: {
		editable: Boolean,// 
		options: {
			type: Object,
			required: true
		},
		isSetOptions: {
			type: Boolean,
			required: true
		},
		chartIndex: {
			type: Number,
			required: true
		},
		tableId: {
			type: String
		},
		containerOptions: [Object, String]
	},
	computed: {
		...mapGetters('dataDimension', {
			dragObject: 'getDragObject',
			isFocusDrag: 'isFocusDrag',
			currentHoverIndex: 'getCurrentHoverIndex'
		}),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',
			isInitAllChart: 'startInitAllChart',
		}),
		...mapGetters('defaultInteractiveOptions', {
			defaultInteractiveObject: 'getInteractiveObject'
		}),
		...mapGetters('handleChart', {
			//
			hasScreen: 'screenData'
		}),
		time() {
			return [this.options.screenStart, this.options.screenEnd];
		},
		valueRange() {
			return [this.options.screenStart || this.min, this.options.screenEnd || this.max];
		},
		/** */
		isCurrentChart() {
			return this.currentChartIndex == this.chartIndex;
		},
		//
		contentColor() {
			if (this.chartIndex == this.currentHoverIndex && this.isCurrentChart) {
				let type = this.dragObject.inputType;
				let obj = {
					int: 'content-int',
					time: 'content-date',
					char: 'content-char'
				};
				return obj[type] || '';
			}
			return '';
		},
		setFocusStyle() {
			if (
				this.isFocusDrag &&
        this.isCurrentChart &&
        this.options.screenType === ''
			) {
				return {
					'border-color': '#28b3fc',
					'box-shadow': '0.01vw 0.1vw 1vw rgba(40, 179, 252,0.5) inset',
					'border-radius': '0.1vw'
				};
			} else {
				return '';
			}
		},
		handleSendFilterData() {
			const vm = this;
			let defaultObject = JSON.parse(JSON.stringify(vm.defaultInteractiveObject));
			vm.$set(defaultObject, 'xPosition', vm.containerOptions.xPosition);
			vm.$set(defaultObject, 'yPosition', vm.containerOptions.yPosition);
			vm.$set(defaultObject, 'tableWidth', vm.containerOptions.tableWidth);
			vm.$set(defaultObject, 'tableHeight', vm.containerOptions.tableHeight);
			let filterDataSource = {
				screenType:
          vm.dragObject['inputType'] === 'int'
          	? 'number'
          	: vm.dragObject['inputType'] === 'time'
          		? 'time'
          		: 'text',
				screenField: vm.dragObject['name'],
				screenSourceId: vm.dragObject['tableIndex'],
			};
			Object.assign(defaultObject.renderOptions, filterDataSource);
			defaultObject.renderOptions = JSON.stringify(defaultObject.renderOptions);
			return defaultObject;

		},
	},
	data() {
		return {
			selectAll: false,
			// valueRange: [1, 1000], //
			min: 0, //
			max: 9999, //
			allElected: false,
			fieldDataList: [],
			textPageLength: 0,
			textDateLength: 0,
			allDataDrop: [],
			contentData: [],
			introduictionData: '',
			page: 1,
			totalPage: 1,//
			value: [],
			defalutDate: [],
		};
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter'
		}),
		changeDefalutDate(index) {
			let key = index === 0 ? 'screenStart' : 'screenEnd';
			this.defalutDate = this.options[key].split('/');
		},
		select(date, index) {
			let key = index === 0 ? 'screenStart' : 'screenEnd';
			this.renderOptionSetter({key, value: date, id: this.chartIndex});
		},
		confirmSelect(date, index) {
			let key = index === 0 ? 'screenStart' : 'screenEnd';
			this.renderOptionSetter({key, value: date, id: this.chartIndex});
			// this.renderOptionSetter({key:'screenStart.1',value:date[1]});
			// this.$set(this.time, index, date);
		},
		clearDate(date, index) {
			let key = index === 0 ? 'screenStart' : 'screenEnd';
			this.renderOptionSetter({key, value: '', id: this.chartIndex});
			// this.$set(this.time, index, '');
		},
		clearALlDate() {
			let startValue = this.options.min;
			let endValue = this.options.max;
			if (this.options.screenStart === startValue && this.options.screenEnd === endValue) return false;
			this.renderOptionSetter({key: 'screenStart', value: startValue, id: this.chartIndex});
			this.renderOptionSetter({key: 'screenEnd', value: endValue, id: this.chartIndex});
			// this.time=['',''];
			this.sendDateFilterData(this.time);
			// this.$emit('completeSelect',this.time);
		},
		applySelectDate() {
			this.sendDateFilterData(this.time);
		},
		listenValue(e, index) {
			let val = e.target.value;
			let key = index === 0 ? 'screenStart' : 'screenEnd';
			if (!this.editable) return;// 
			if (val < this.min) {
				e.target.value = Number(this.min);
				this.renderOptionSetter({
					key,
					// id: this.clickData.id,
					value: e.target.value,
					id:this.chartIndex
				});
				// this.$set(this.valueRange, index, Number(this.min));
			} else if (val > this.max) {
				e.target.value = Number(this.max);
				this.renderOptionSetter({
					key,
					value: e.target.value,
					id:this.chartIndex
				});
				// this.$set(this.valueRange, index, Number(this.max));
			} else {
				this.renderOptionSetter({
					key,
					value: Number(val),
					id:this.chartIndex
				});
				// this.$set(this.valueRange, index, Number(val));
			}
			if (index == 1 && val < this.valueRange[0]) {
				e.target.value = Number(this.max);
				this.renderOptionSetter({
					key,
					value: e.target.value,
					id:this.chartIndex
				});
				// this.$set(this.valueRange, index, Number(this.max));
			}
		},
		dragleave(e) {
			this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
		},
		dragover(e) {
			this.$store.commit('dataDimension/setCurrentHoverIndex', this.chartIndex);
			e.stopPropagation();
			e.preventDefault();
		},
		axisDrop(e) {
			this.createFilter();
			this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
		},
		onScroll(e) {
			// console.log(e);
		},

		getDropDownData(data) {
		},
		sendDateFilterData(time) {
			let vm = this;
			if (vm.isCurrentChart) {
				vm.amendFilter();
			}
		},
		rangegariDyValue(data) {
			let vm = this;
			if (!vm.editable){ //
				let renderOptions =JSON.parse(JSON.stringify(vm.options));
				renderOptions.screenStart=data[0];
				renderOptions.screenEnd= data[1];
				let tableId = vm.containerOptions.tableId;
				let params=[{tableId,renderOptions:JSON.stringify(renderOptions)}];
				if(data[0]!=vm.min ||data[1]!=vm.max){
					this.$parent.closeSocket();
					this.$parent.getUrlParams(params,'close'); //socket
				}else{
					this.$parent.getUrlParams(params);
				}
				return;
			}// 
			this.renderOptionSetter({key: 'screenStart', value: data[0],id:this.chartIndex});
			this.renderOptionSetter({key: 'screenEnd', value: data[1],id:this.chartIndex});
			// console.log('rangegariDyValue', data);
			// vm.valueRange = data;
			// if (vm.isCurrentChart) {
			vm.amendFilter();
			// }
		},

		handler(data) {
			let vm = this;
			let interactive = this.options;
			if (data) {
				this.requestData(
					interactive.screenType,
					interactive.screenSourceId,
					interactive.screenField,
					++this.page
				);
			}
		},

		inputData(data) {
			// console.log(data,'');
		},

		/** */
		createFilter() {
			const vm = this;
			vm.$post('api/v2/chart', vm.handleSendFilterData)
				.then(res => {
					if (res.code == '000000') {
						vm.initFilter(res.data.tableId);
					} else {
						vm.tips.show([res.err_msg.split(':')[1]]);
					}
				})
				.catch(error => {
					console.error(error);
					vm.tips.show(['']);
				});
		},
		/** */
		initFilter(tableId) {
			const vm = this;
			vm.$get('api/v2/oneChart', {
				projectId: sessionStorage.getItem('projectId'),
				tableId: tableId
			}).then(res => {
				if (res.code == '000000') {
					let data = res.data[0];
					data['renderOptions']=JSON.parse(data['renderOptions']);
					let renderOptions =data['renderOptions'];
					vm.selectAll=renderOptions.selectAllData;
					Object.assign(data,{
						layerIndex: vm.chartIndex,
						layerChartIcon: '#icon-' + data['containerOptions']['chartsType'].split('-')[1],
						xPosition:vm.containerOptions.xPosition,
						yPosition:vm.containerOptions.yPosition,
						tableWidth:vm.containerOptions.tableWidth,
						tableHeight:vm.containerOptions.tableHeight,
					});
					// vm.$set(data['containerOptions'], 'layerIndex', vm.chartIndex);
					// vm.$set(
					// 	data['containerOptions'],
					// 	'layerChartIcon',
					// 	'#icon-' + data['containerOptions']['chartsType'].split('-')[1]
					// );
					// vm.$set(data['containerOptions'], 'xPosition', vm.containerOptions.xPosition);
					// vm.$set(data['containerOptions'], 'yPosition', vm.containerOptions.yPosition);
					// vm.$set(data['containerOptions'], 'tableWidth', vm.containerOptions.tableWidth);
					// vm.$set(data['containerOptions'], 'tableHeight', vm.containerOptions.tableHeight);
					vm.$store.commit('canvasPlace/changeIndexChart', {
						data: data,
						index: vm.chartIndex
					});
					vm.$store.commit('canvasPlace/setCurrentTableId', {
						index: vm.chartIndex,
						tableId: data['containerOptions']['tableId'],
						flag: 1
					});
					vm.$store.commit('canvasPlace/setCurrentChartId', vm.chartIndex);
					vm.requestDataPage(
						renderOptions['screenType'],
						renderOptions['screenSourceId'],
						renderOptions['screenField'],
					);//
					vm.requestData(
						renderOptions['screenType'],
						renderOptions['screenSourceId'],
						renderOptions['screenField'],
						this.page
					); //
				} else {
					vm.tips.show([res.err_msg.split(':')[1]]);
				}
			})
				.catch(error => {
					console.error(error);
					vm.tips.show(['']);
				});
		},
		requestDataPage(type, sourceId, field) { //
			let vm = this;
			let params = {
				type,
				sourceId,
				field,
				num: 10
			};
			vm.$get('api/tableDataPage', params).then(res => {
				if (res.code == '000000') {
					vm.totalPage = res.data.pageNum;
				}
			});
		},
		requestData(type, index, name, page) {
			let vm = this;
			vm.fieldDataList = [];
			if (this.page <= this.totalPage) {
				vm.$get('api/v2/tableData', {
					sourceId: index, //ID
					field: name, //
					num: 10, //10
					page: page ? page : 1,//1
					// order:'',//asc
					type: type
				}).then(res => {
					switch (type) {
					case 'number':
						vm.$set(vm, 'min', parseInt(res.data.min));
						vm.$set(vm, 'max', parseInt(res.data.max));
						break;
					case 'text':
						// vm.fieldDataList.push(res.data);
						vm.$set(vm, 'fieldDataList', res.data);
						break;
					case 'time':
						let startTime = res.data.min.split(' ')[0].split('-').join('/');
						let endTime = res.data.max.split(' ')[0].split('-').join('/');
						this.renderOptionSetter({key: 'screenStart', value: startTime, id: this.chartIndex});
						this.renderOptionSetter({key: 'screenEnd', value: endTime, id: this.chartIndex});
						this.renderOptionSetter({key: 'min', value: startTime, id: this.chartIndex});
						this.renderOptionSetter({key: 'max', value: endTime, id: this.chartIndex});
						// vm.set(vm,time,)
						break;
					}
				});
			}
		},
		receData(data) {
			let vm = this;
			if (!vm.editable){
				let renderOptions =JSON.parse(JSON.stringify(vm.options));
				renderOptions.screenText=data[0];
				renderOptions.selectAllData= data[1];
				let tableId = vm.containerOptions.tableId;
				let params=[{tableId,renderOptions:JSON.stringify(renderOptions)}];
				if(data[0].length!=vm.fieldDataList.length &&data[0].length>0){
					this.$parent.closeSocket();
					this.$parent.getUrlParams(params,'close'); //socket
				}else{
					this.$parent.getUrlParams(params);
				}
				return;
			}// 

			// vm.$store.dispatch(
			// 	'handleChart/asyncFillterScreen',
			// 	data
			// );
			// this.renderOptionSetter({key:'screenText',value:''});
			this.renderOptionSetter({key: 'screenText', value: data[0],id:this.chartIndex});
			this.renderOptionSetter({key: 'selectAllData', value: data[1],id:this.chartIndex});
			vm.selectAll = data[1];
			vm.amendFilter();
		},
		amendFilter() {
			const vm = this;
			let containerOptions = this.containerOptions;
			let renderOptions = JSON.stringify(this.options);
			let tableId = containerOptions.tableId;
			// vm.currentFilterOptions['containerOptions'];
			// vm.currentFilterOptions['renderOptions']['selectAllData']=mark;
			const amendFilterObject = [
				{
					projectId: sessionStorage.getItem('projectId'),
					...containerOptions,
					renderOptions,
				},
			];
			if (tableId) {
				vm.$put('api/v2/chart', amendFilterObject)
					.then(res => {
						if (res.code == '000000') {
							// 
							// vm.$store.commit('canvasPlace/setCurrentChartIndex', -1);
							// vm.$store.commit('canvasPlace/setInitAllChart', true);
							// vm.$store.commit('canvasPlace/setStateProperty', {key: 'screeningCondtion', value: true});//
							vm.$store.dispatch('canvasPlace/screeningRequest');//
						} else {
							vm.tips.show([res.err_msg.split(':')[1]]);
						}
					})
					.catch(error => {
						console.error(error);
						vm.tips.show(['']);
					});
			}
		}
	},

	watch: {
		// tableId() {
		// 	this.$store.commit('canvasPlace/setRenderedStatus');
		// 	console.log(111);
		// 	// debugger;
		// 	this.requestData(
		// 		this.options.screenType,
		// 		this.options.screenSourceId,
		// 		this.options.screenField
		// 	);
		// }
	},
	mounted() {
		if (this.tableId !== '') {
			this.$store.commit('canvasPlace/setRenderedStatus');
			this.requestDataPage(
				this.options.screenType,
				this.options.screenSourceId,
				this.options.screenField);
			this.requestData(
				this.options.screenType,
				this.options.screenSourceId,
				this.options.screenField
			);
		}
	}
};
</script>
<style scoped>
  .dagou-icon {
    display: inline-block;
    width: 0.1vw;
    height: 0.4vw;
    border-color: #1d7cfa;
    border-style: solid;
    border-width: 0 0.1vw 0.1vw 0;
    transform: rotate(45deg);
  }

  .name-style {
    padding: 0.5vw 1vw;
  }

  .filter {
    position: absolute;
    /* overflow: hidden; */
    width: 100%;
    height: 100%;
    color: #ffffff;
    user-select: none;
  }

  .box {
    height: 100%;
    /*width: calc(100% + 17px);*/
    /*overflow-y: auto;*/
    /*overflow-x: hidden;*/
    /*display: flex;*/
    /*align-items: center;*/
    /*justify-content: center;*/
  }

  /* .filterNumber {
    padding: 1vw;
  } */
  .model-header {
    color: #fff;
    display: flex;
    justify-content: space-between;
    padding: 0.5vw 1vw;
  }

  .filterWrapper {
    padding: 2vw 1vw 0;
    /* padding-top: 2vw; */
    color: #fff;
  }

  .hidden-y {
    /*width: calc(100% - 17px);*/
  }

  .number-01 {
    padding: 0.5vw 1vw;
  }

  .number-02 {
    padding: 0 1vw;
    display: flex;
    justify-content: space-between;
  }

  .number-input {
    background: #193c6e;
    width: 25%;
    height: 2vh;
    text-align: center;
    color: #ffffff;
    border: 0.0625vw solid #1d7cfa;
  }

  .valueSilder {
    display: inline-block;
    width: 50%;
  }

  .number-03 {
    padding: 0 0.6vw;
  }

  .solid-time {
    text-align: center;
  }

  .input-select {
    display: inline-block;
    text-align: center;
    width: 0.6vw;
    height: 0.6vw;
    line-height: 0.6vw;
    border: 0.0625vw solid #1d7cfa;
    margin: 0 0.3vw;
  }

  .contentDataStyle {
    position: relative;
  }

  .span-i-style {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
  }

  .delect-icon {
    right: 0;
    cursor: pointer;
    display: inline-block;
    width: 0.2vw;
    height: 0.7vw;
    background: red;
    line-height: 0;
    font-size: 0;
    vertical-align: middle;
    -webkit-transform: rotate(45deg);
    margin-left: 0.2vw;
  }

  .delect-icon:after {
    content: "/";
    display: block;
    width: 0.2vw;
    height: 0.7vw;
    background: red;
    -webkit-transform: rotate(-90deg);
  }

  .valueSilder /deep/ .slider-box {
    margin: 0;
  }

  .valueSilder /deep/ .vue-slider-rail {
    width: 100% !important;
  }

  .valueSilder /deep/ .vue-slider-ltr {
    padding: 7px 7px !important;
  }

  .valueSilder /deep/ .vue-slider-rail {
    margin-left: 0 !important;
  }

  /**/
  .content-int {
    background-color: rgba(17, 190, 110, 0.3);
    border-color: #0fff00;
    box-shadow: 0.01vw 0.01vw 0.2vw 0 rgb(16, 255, 0);
    border-radius: 0.1vw;
  }

  .content-char {
    border-color: #28b3fc;
    background-color: rgba(40, 179, 252, 0.3);
    box-shadow: 0.01vw 0.01vw 0.2vw 0 rgb(40, 179, 252);
    border-radius: 0.1vw;
  }

  .content-date {
    background-color: #2e3325;
    border-color: #ffff00;
    box-shadow: 0.01vw 0.01vw 0.2vw 0 rgb(255, 255, 0);
    border-radius: 0.1vw;
  }
  .filter-bg {
    z-index: 2;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-position: center !important;
    height: 100%;
    background-size: 100% 100% !important;
    background: url("data:image/png;base64,");
  }
</style>
