<template>
<!--  <div>-->
<!--    <div class="chart-basis"-->
<!--      @click="showBgColor = !showBgColor">-->
<!--      <span></span>-->
<!--      <span class="biFont icon-arrowLeft icon-fontsize"-->
<!--        :class="[showBgColor?'leftRotate-90':'leftRotate-0']"></span>-->
<!--    </div>-->
<!--  :class="[showBgColor?'heightStart':'heightOver']"-->
    <div
      ref="contentBox"
      class="content-box">
      <ul>
        <li class="optionsItem">
          <select-color :defaultColor="bgObject"
            @changeColor="getTitleColor"></select-color>
        </li>
        <li>
          <ChoicePhoto :afferent="bgBaseUrl"
            @baseUrl="urlAddress"></ChoicePhoto>
        </li>
      </ul>
    </div>
<!--  </div>-->
</template>
<script>
import ColorSelect from './BasicComponent/ColorSetting';
import ChoicePhoto from '@/components/Picture/PictureUpload.vue';
import { mapGetters, mapMutations } from 'vuex';
// import '@/assets/common/css/animate.css';
import {mixin} from './BasicComponent/mixin';

export default {
	mixins:[mixin],
	components: {
		'select-color': ColorSelect,
		ChoicePhoto
	},
	name: 'pageSetting',
	data() {
		return {
			showBgColor: true,
			bgObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: { r: 25, g: 77, b: 51, a: 0.9 }
			},
			bgBaseUrl: {
				photoUrl: this.imgBase64,
				title: '',
				selectImg: '',
				showWith: 8,
				showHeight: 6,
				size: 1024,
				accept: 'image/jpeg,image/png,image/gif'
			}
		};
	},
	computed: {
		...mapGetters('canvasPlace', {
			bgColor: 'getCanvasBgColor',
			imgBase64: 'getCanvasBgImg'
		})
	},
	watch: {
		imgBase64: {
			immediate: true,
			handler() {
				this.bgBaseUrl.photoUrl = this.imgBase64;
			}
		},
		bgColor: {
			immediate: true,
			handler(val) {
				// console.log(45678911548641);
				// console.log(this.bgColor);
				this.bgObject.rgba = this.bgColor;
			}
		}
	},
	methods: {
		...mapMutations('canvasPlace', {
			renderOptionSetter: 'renderOptionSetter'
		}),
		getTitleColor(data) {
		  const {r,g,b,a}=data.rgba;
			let rgba = 'rgba(' + r + ',' + g + ',' + b + ',' + a + ')';
			this.$store.commit('canvasPlace/setCanvasBgColor', rgba);
			this.$store.commit('canvasPlace/setCanvasBGAmendCount');
			// this.bgObject.titleColor = this.colorTransformation(rgba);
			this.bgObject.rgba = rgba;
		},
		urlAddress(data) {
			if (data.baseUrl) {
				let imgType = data.imgType;
				if (imgType === 'gif' && data.imgSize >= 1024) {
					this.tips.show(['1Mbgif']);
					this.bgBaseUrl.photoUrl = '';
				} else {
					if (data.imgSize >= 2048) {
						this.tips.show(['2MB']);
						this.bgBaseUrl.photoUrl = '';
					} else {
						this.$store.commit('canvasPlace/setCanvasBgImg', data.baseUrl);
						this.$store.commit('canvasPlace/setCanvasBGAmendCount');
					}
				}
			} else {
				this.$store.commit('canvasPlace/setCanvasBgImg', data.baseUrl);
				this.$store.commit('canvasPlace/setCanvasBGAmendCount');
			}
		},
	},
};
</script>

<style scoped>
  li{
    padding: 0.4vw 0.5vw;
  }

.optionsItem {
  margin-bottom: 0.8vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.chart-basis .rotateIcon {
  transform: rotate(-90deg);
  /* transition: 0.5s; */
}
.chart-basis .rotateIconUp {
  /* transform: rotate(-90deg); */
  transition: 0.5s;
}
</style>
