<template>
  <transition name="fade">
    <div class="wrapper"
      data-class="dataDimension"
      ref="chartContainer"
      v-show="isContainerShow">
      <transition name="slide">
        <div class="container"
          v-show="dataDimensionShow">
          <!-- xy -->
          <div :class="axisWidthLeft"
            class="axis-container">
            <div :class="[contentColor(0),setVertical(0,'xAxisList'),{'contentheight':isBasicChart}]"
              :currentIndex="currentIndex"
              :style="setFocusStyle"
              @dragleave="dragleave"
              @dragover="dragover($event,0)"
              @drop="axisDrop($event,'xAxisList')"
              class="content"
              data-class="dataDimension-list xAxisList"
              data-number=0
              data-type='xAxisList'>
              <span class="axis-title">{{axisTitle.namex}}</span>
              <!-- X -->
              <div :key="index"
                v-for="(x,index) in currentChartAxis.xAxisList">
                <div :class="x.inputType"
                  :data-index="index"
                  :ref="`valueAxis${index}`"
                  :title="x.name"
                  @click.right.prevent.stop="toggleyop('xAxisList',index,$event,x)"
                  class="axis-data"
                  data-class="alreadyAdd"
                  v-show="x.name!=''"
                  v-touch:longtap.stop="(e)=>toggleyop('xAxisList',index,e,x)">
                  <div class="axis-data-name">
                    <span :class="`dataSource icon-${x.inputType}`"></span>
                    <span class="set-text-name">{{x.name}}</span>
                  </div>
                  <span @click="clearInput(index,'xAxisList')"
                    class="clearBtn"></span>
                </div>

              </div>
            </div>
            <!-- -->
            <div :class="[contentColor(5),setVertical(5,'groupData')]"
              :currentIndex="currentIndex"
              @dragleave="dragleave"
              @dragover="dragover($event,5)"
              @drop="axisDrop($event,'groupData')"
              class="lengthon contentheight content"
              data-number=5
              data-type='groupData'
              v-if="isBasicChart">
              <span class="axis-title">{{axisTitle.nameLegend}}</span>
              <!--              -->
              <div :key="index"
                v-for="(x,index) in currentChartAxis.groupData"
                v-show="x.name">
                <div :class="x.inputType"
                  :data-index="index"
                  :ref="`valueAxis${index}`"
                  :title="x.name"
                  @click.right.prevent.stop="toggleyop('groupData',index,$event,x)"
                  class="axis-data"
                  data-class="alreadyAdd"
                  v-show="x.name!=''"
                  v-touch:longtap.stop="(e)=>toggleyop('groupData',index,e,x)">
                  <div class="axis-data-name">
                    <span :class="`dataSource icon-${x.inputType}`"></span>
                    <span class="set-text-name">{{x.name}}</span>
                  </div>
                  <span @click="clearInput(index,'groupData')"
                    class="clearBtn"></span>
                </div>
                <!--                <data-select :chartIndex="currentIndex"-->
                <!--                  :curChartType="curChartType"-->
                <!--                  :dataSelectPostion="dataSelectPostion"-->
                <!--                  :axisData="x"-->
                <!--                  :axisIndex="index"-->
                <!--                  :axisType="'xAxisList'"-->
                <!--                  :closeDataSelect="closeDataSelect"-->
                <!--                  referencePosition='chartPanl'></data-select>-->
              </div>
            </div>
            <div :class="[contentColor(1),setVertical(1,'yAxisList')]"
              :currentIndex="currentIndex"
              @dragleave="dragleave"
              @dragover="dragover($event,1)"
              @drop="axisDrop($event,'yAxisList')"
              class="content"
              data-class="dataDimension-list yAxisList"
              data-number=1
              data-type='yAxisList'>
              <span class="axis-title">{{axisTitle.namey}}</span>
              <!-- Y -->
              <div :key="index"
                v-for="(y,index) in currentChartAxis.yAxisList">
                <div :class="y.inputType"
                  :data-index="index"
                  :ref="`valueAxis${index}`"
                  :title="y.name"
                  @click.right.prevent.stop="toggleyop('yAxisList',index,$event,y)"
                  @touchstart.
                  class="axis-data"
                  data-class="alreadyAdd"
                  v-show="y.name!=''"
                  v-touch:longtap="(e)=>toggleyop('yAxisList',index,e,y)">
                  <div class="axis-data-name">
                    <span :class="`dataSource icon-${y.inputType}`"></span>
                    <span class="set-text-name">{{y.name}}</span>
                  </div>
                  <span @click="clearInput(index,'yAxisList')"
                    class="clearBtn"></span>
                </div>

              </div>
            </div>
            <!-- exchangeXY -->
            <span :class="{'rotate':isDataSeriesShow}"
              @click="exchangeAxis"
              class="axis-exchange allCenter"
              v-show="isExchangeShow && isSatisfyTableWidth"></span>
          </div>
          <div :class="[contentColor(2),setVertical(2,'dataSeries')]"
            :currentIndex="currentIndex"
            @dragleave="dragleave"
            @dragover="dragover($event,2)"
            @drop="axisDrop($event,'dataSeries')"
            class="content flex-right"
            data-class="dataDimension-list"
            data-number=2
            data-type='dataSeries'
            v-show="isDataSeriesShow">
            <span class="axis-title"></span>
            <div :class="s.inputType"
              :key="index"
              class="axis-data"
              v-for="(s,index) in currentChartAxis.dataSeries"
              v-show="s.name!==''">
              <div class="axis-data-name">
                <span :class="`dataSource icon-${s.inputType}`"></span>
                <span :title="s.name"
                  class="set-text-name">{{s.name}}</span>
              </div>
              <span @click="clearInput(index,'dataSeries')"
                class="clearBtn"></span>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex';
import {composedPath} from '@/utils/domMethods';
import {beforeThemeHandle} from '@/utils/optionSetting.js';
import {get as _get} from 'lodash';

export default {
	name: 'DataDimension',
	props: {
		isSetOptions: {//false
			type: Boolean,
			required: true
		},
		currentIndex: {//
			type: Number,
			required: true
		},
		dataDimensionShow: {//
			type: Boolean,
			required: true,
		},
		isShowLoading: {
			type: Boolean,
			required: true
		},
		curChartType: {
			type: String,
			required: true
		},
		x: {
			type: Number,
			required: true,
		},
		y: {
			type: Number,
			required: true,
		},
		w: {
			type: Number,
			required: true,
		},
		h: {
			type: Number,
			required: true,
		}
	},
	data() {
		return {
			uniqueChartIndex: -1,
			currentChartAxis: {},
			setColor: '',
			curSourceId: '',
			curAxisFieldArr: [],
			// setColorIndex: -1,
			xAlreadyList: [],
			yAlreadyList: [],
			isSatisfyTableWidth: true,
			mergeAxisListLength: 0,
			lastChartIndex: -1,
			dataZoomOption: [
				{
					show: true,
					type: 'slider',
					xAxisIndex: [0],
					realtime: true,
					height: 20,
					bottom: 10,
					start: 0,
					end: 10,
					textStyle: {
						color: '#fff'
					}
				},
				{
					show: true,
					type: 'slider',
					yAxisIndex: [0],
					realtime: true,
					width: 20,
					right: 20,
					start: 0,
					end: 10,
					textStyle: {
						color: '#fff'
					}
				},
				{
					type: 'inside',
					xAxisIndex: [0],
					start: 0,
					end: 10,
				},
				{
					type: 'inside',
					yAxisIndex: [0],
					start: 0,
					end: 10,
				}
			],
			/**dataselect */
			dataSelectPostion: {
				left: '0px',
				top: '0px'
			},
			closeDataSelect: false
		};
	},
	computed: {
		...mapGetters('main', ['getVw', 'vwToPx']),
		...mapGetters('dataDimension', {
			currentChartAxisfromVuex: 'getCurrentChartAxis',//
			defaultChartAxis: 'getDefaultChartAxisAll',
			updateCount: 'getUpdateCount',
			dragObject: 'getDragObject',//
			curAxisObject: 'getCurAxisObject',//
			currentHoverIndex: 'getCurrentHoverIndex',
			isFocusDrag: 'isFocusDrag',
			amendChartCount: 'getAmendChartCount',
			amendChartRefreshCount: 'getAmendChartRefresh',//
			isOnDragging: 'isOnDragging',
			setColorIndex: 'getColorIndex',
			getValueAxisArr: 'getValueAxisArr',
			resetCurSourceId: 'getCurSourceId'
		}),
		...mapGetters('canvasPlace', {
			currentChartIndex: 'getCurrentChartIndex',//
			currentChartType: 'getCurrentChartType',
			dimensionDefault: 'getDimensionDefault',
			chartList: 'getChartList',
			currentIsSetOptions: 'getIsSetOptions',
			getChartType: 'getChartsType'//
		}),
		...mapGetters('defaultChartOptions', {
			getTotal: 'getAxisTotalLength',
			getCategoryAxis: 'getCategoryAxis',
			getxAxisLength: 'getxAxisLength',
			getyAxisLength: 'getyAxisLength',
			getGroupData: 'getGroupData'
		}),
		...mapGetters('chartBus', {
			chartDataList: 'returnData',
		}),
		isBasicChart() {
			let basicTypeArr = ['echarts-basicBar', 'echarts-basicArea', 'echarts-basicLine', 'echarts-basicHorizontalBar', 'echarts-smoothLine'];
			return basicTypeArr.includes(this.getChartType(this.currentIndex));
		},
		axisTitle() {
			if (this.getChartType(this.currentIndex) !== 'echarts-3dWorldMap'
          && this.getChartType(this.currentIndex) !== 'echarts-choropleth'
			) { //3d
				return {
					namex: 'X',
					namey: 'Y',
					nameLegend: ''
				};
			} else {
				return {
					namex: '',
					namey: '',
				};
			}
		},
		isMap() {
			if (this.getChartType(this.currentIndex) === 'echarts-3dWorldMap') {
				return true;
			} else {
				return false;
			}
		},
		axisWidthLeft() {
			if (this.isDataSeriesShow) {
				return 'flex-left';
			} else {
				return 'direction-row'; //space-between flex-direction:row 
			}
		},
		/** */
		tableWidth() {
			if (this.isCurrentChart) {
				return this.chartList[this.currentChartIndex]['containerOptions']['tableWidth'];
			}
		},
		/** */
		isContainerShow() {
			return (this.isCurrentChart && this.isOnDragging) || this.isBorderShow;
		},
		// /** */
		// isWrapperShow() {
		// 	return this.dataDimensionShow;
		// },
		/** */
		leftSideOpen() {
			return this.$store.state.dashboard.leftSideOpen;
		},
		/** */
		isDataSeriesShow() {
			//1
			return (
				this.currentChartAxis.dataSeries &&
          this.currentChartAxis.dataSeries.length
			);
		},
		/** */
		isBorderShow() {
			let object = this.currentChartAxis;
			if (object.xAxisList) {
				let mergeAxisList = [
					...object.xAxisList,
					...object.yAxisList,
					...object.groupData
				];
				let condition = mergeAxisList.some(e => e.name != '');
				return condition;
			} else {
				return false;
			}
		},
		/** */
		setVertical() {
			return (number, type) => {
				let name = this.currentChartAxis[type] && this.currentChartAxis[type].length && this.currentChartAxis[type][0].name;
				// console.log(this.setColorIndex, number, '405');
				if (this.setColorIndex === number || name) {
					return '';
				} else {
					return 'vertical';//space-betten,clum
				}
			};
		},
		/** */
		setFocusStyle() {
			if (this.setColorIndex < 0 && this.isCurrentChart) {//-1 isCurrentCharttrue
				return {
					'border-color': '#28b3fc',
					'box-shadow': '0.01vw 0.01vw 0.2vw 0 rgb(40, 179, 252)',
					'border-radius': '0.1vw'
				};
			}
		},
		/** */
		isExchangeShow() {
			let vm = this;
			let object = vm.currentChartAxisfromVuex;//
			if (object.xAxisList && object.xAxisList.length
			) { //3d
			  // debugger;
				let mergeAxisList = JSON.parse(
					JSON.stringify([
						// ...(object.xAxisList||{}),
						// ...object.yAxisList,
						// ...object.groupData,
						// ...object.dataSeries
						... this._get(object,'xAxisList',{}),
						... this._get(object,'yAxisList',{}),
						... this._get(object,'groupData',{}),
					])
				);//
				let mergeAxisListLength = mergeAxisList.filter(e => e.name !== '').length;
				if (mergeAxisListLength >= 2 && vm.isCurrentChart) {
					const xAxisExitName = object.xAxisList.every(e => e.name !== '');
					const yAxisExitName = object.yAxisList.every(e => e.name !== '');
					if (object.xAxisList.length === object.yAxisList.length
              && object.yAxisList.length === 1
              && xAxisExitName && yAxisExitName) { // x y 
						return true;
					} else {
						return false;
					}
				} else {
					return false;
				}
			} else {
				return false;
			}
		},
		/** */
		isCurrentChart() {//
			return this.currentChartIndex === this.currentIndex;
		},
		/** */
		isApplyCurrentChart() {
			let vm = this;
			let object = vm.currentChartAxisfromVuex;//
			let mergeAxisList = JSON.parse(
				JSON.stringify([
					...object.xAxisList,
					...object.yAxisList,
					// ...object.dataSeries
				])
			);
			const categoryAxis = vm.getCategoryAxis(vm.currentChartType);
			const chartType = vm.getChartType(vm.currentIndex);

			/**
         * @categoryAxis x*/
			function condition(axis) {
				// toLowerCase ()        axis.toLowerCase() + 'AxisList'//xAxisList
				let categoryAxis = axis.toLowerCase() + 'AxisList';
				let valueCategoryAxis = categoryAxis == 'xAxisList' ? 'yAxisList' : 'xAxisList';
				const isSatisfyCategoryAxis =
            object[categoryAxis].length === 1 && object[categoryAxis].every(e => e.name !== '');
				if (isSatisfyCategoryAxis) {
					/** */
					const obj = {
						XLength: vm.getyAxisLength(vm.currentChartType),
						YLength: vm.getxAxisLength(vm.currentChartType),
						LegendLength: vm.getGroupData(vm.currentChartType)
					};
					const multipleValueAxis = mergeAxisList.length >= 3 && object[valueCategoryAxis].filter(e => e.name !== '').length >= 2;
					const singleValueAxis = mergeAxisList.length === 2 && object[valueCategoryAxis][0].name;
					if (object['dataSeries'].length > 0) {
						/** */
						return object['dataSeries'][0]['name'] !== '' && singleValueAxis;

					} else {
						return obj[axis + 'Length'] > 1 ? multipleValueAxis : singleValueAxis;
						/** */
						// if (obj[axis + 'Length'] > 1) {
						// 	/** >=3 */
						// 	return multipleValueAxis;
						// 	// eslint-disable-next-line no-empty
						// } else {
						// 	/** */
						// 	return singleValueAxis;
						// }
					}
				}
				// else if (chartType === 'echarts-3dWorldMap' || chartType === 'echarts-choropleth') {
				// 	/** */
				// 	const mapDList = [...object['xAxisList'], ...object['yAxisList']];//...object['cityName'], ...object['columnHeight'],
				// 	const isSatisfy = mapDList.length === 2 && mapDList.every(e => e.name !== '');
				// 	return isSatisfy;
				// }
				else {
					return false;
				}
			}

			return condition(categoryAxis);

		},
		/**xy */
		isResetChart() {
			let vm = this;
			let object = vm.currentChartAxisfromVuex;
			if (object.xAxisList) {
				let mergeAxisList = JSON.parse(
					JSON.stringify([
						...object.xAxisList,
						...object.yAxisList,
						...object.groupData
					])
				);
				const isNull = mergeAxisList.every(e => e.name === '');
				return isNull;
			} else {
				return false;
			}
		},
		isSourceId() {
			if (this.curSourceId) {
				if (this.curSourceId === this.curAxisObject['tableIndex']) {
					return true;
				} else {
					this.tips.show([',']);
					return false;
				}
			} else {
				return true;
			}
		},
		isNotRepeatField() {
			const curAxisFieldArr = this.getValueAxisArr(this.getCategoryAxis(this.currentChartType)).map(e => e.name);
			// console.log('',this.curAxisObject);
			const isFiledName = curAxisFieldArr.find(e => e === this.curAxisObject['name']);
			if (this.isSourceId && isFiledName === undefined) {
				return true;
			} else {
				this.tips.show([',']);
				return false;
			}
		}
	},
	watch: {
		updateCount: {
			immediate: true,
			handler(newVal, oldVal) {
				let vm = this;
				if (newVal > 0 && vm.currentChartIndex > -1) {//updateCount0 updateCount0currentChartIndex-1
					// console.log(newVal,oldVal);
					vm.getCurrentChartFromDB();//
					vm.$store.commit(
						'canvasPlace/setCurrentChartType',
						vm.currentChartIndex//1
					);
					// self.$store.commit('canvasPlace/toggleFirstLoad', this.currentChartIndex, false);
				}
			}
		},
		tableWidth() {
			if (this.tableWidth > 284) {
				this.isSatisfyTableWidth = true;
			} else {
				this.isSatisfyTableWidth = false;
			}
		},
		/**  ps:0*/
		amendChartCount() {
			this.sendRequestFilter();
		},
		amendChartRefreshCount() {//
			let vm = this;
			if (vm.currentChartIndex === vm.currentIndex)
				vm.initSingleChart(vm.chartDataList.chart.containerOptions.tableId);
		},
		/** */
		currentChartIndex: {
			handler(newVal, oldVal) {
				if (newVal > -1) {
					// this.isExchangeShow;
					this.$store.commit(
						'canvasPlace/setCurrentChartType',
						newVal
					);
					this.curSourceId = this.resetCurSourceId;
				} else {
					this.lastChartIndex = oldVal;
				}
			}
		}
	},
	methods: {
		dragover(e, number) {
			/*number0x1y2345**/
			e = e || window.event;
			this.$store.commit(
				'dataDimension/setCurrentHoverIndex',
				this.currentIndex
			);
			this.$store.commit(//
				'dataDimension/setColorIndex',
				number
			);
			if (this.isFocusDrag) {
				e.stopPropagation();
				e.preventDefault();
			}
		},
		dragleave: function (e) {//-1
			e = e || window.event;
			this.$store.commit(
				'dataDimension/setColorIndex',
				-1
			);
			this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
		},
		//
		axisDrop: function (event, axisType) {
			const e = event || window.event;
			const target = e.target || e.srcElement;
			let path = e.path || (e.composedPath && e.composedPath());
			if (!path) {
				path = composedPath(target);
			}
			// const curCategoryPlace = this.getCategoryAxis(this.currentChartType).toLowerCase()+'AxisList';
			// let isRepeatField = true;
			this.$store.commit(
				'dataDimension/setColorIndex',
				-1
			);

			function isAlreadyAdd(e) {
				return (e.getAttribute && (e.getAttribute('data-class') === 'alreadyAdd'));
			}

			// console.log('curCategoryPlace',curCategoryPlace);
			// if(curCategoryPlace !== axisType){
			// 	if(this.isNotRepeatField){
			// 		isRepeatField=true;
			// 	}else{
			// 		isRepeatField=false;
			// 	}
			// }else{
			// 	isRepeatField=true;
			// }
			/** */
			this.curSourceId = this.resetCurSourceId;//tableId
			// if(this.isSourceId && isRepeatField){
			// else{}
			if (path.some(isAlreadyAdd)) {
				// console.log(isAlreadyAdd, '');
				//index
				const amendIndex = Number(path.find(e => e.classList[0] === 'axis-data').dataset.index);
				if (this.isCurrentChart) {//
					//indexDB.aXisList
					this.dexieDB.aXisList.get(this.currentIndex).then(res => {
						let vm = this;
						let currentAxisData = res.axisOptions;
						let dragObject = JSON.parse(JSON.stringify(vm.dragObject));
						//categoryAxis categoryAxis/valueAxis
						vm.$set(dragObject, 'categoryAxis', currentAxisData[axisType][amendIndex].categoryAxis);
						//currentAxisDataindexDB
						vm.$set(currentAxisData[axisType], amendIndex, dragObject);
						//indexDB
						vm.dexieDB.aXisList
							.put({
								index: vm.currentIndex,
								axisOptions: currentAxisData
							})
							.then(() => {
							});
            console.log('currentAxisData');
            console.log(currentAxisData);
            vm.$store.commit('dataDimension/setCurrentChartAxis', currentAxisData);
						vm.$store.commit('dataDimension/setAmendChartCount');
						vm.$store.commit('dataDimension/setUpdateCount');
					});
				} else {
					return;
				}
			} else {
				//
				//axisTyoe
				this.axisDbAdd(axisType);
			}
			// else if(path.some(isAlreadyAdd) !== true && this.currentChartAxis.xAxisList[0].name && this.currentChartAxis.yAxisList[0].name){
			//     console.log(this.currentChartAxis,'shuuj')
			//   }
			// }
			this.$store.commit('dataDimension/setCurrentHoverIndex', -1);
			event.preventDefault();
			event.stopPropagation();
		},
		axisDropAmend() {
		},
		/**
       * indexDB canvasPlace.js
       * axisType xAxisList/yAxisList/dataSerise/columnHeight/cityName
       * axisListaxisOptions
       */
		axisDbAdd(axisType) {
			//
			if (this.isCurrentChart) {
				//indexDB
				//indexDb this.currentIndex
				this.dexieDB.aXisList.get(this.currentIndex).then(res => {
					let vm = this;
					let currentAxisData = res.axisOptions;
					//currentAxisData[axisType]
					const isNull = currentAxisData[axisType].every(e => e.name === '');//Boolean
					//currentAxisData[axisType]
					const axisTypelength = currentAxisData[axisType].length;
					let dragObject = JSON.parse(JSON.stringify(vm.dragObject)); //
					//idnex
					let index = 0;
					if (!isNull) {
						index = currentAxisData[axisType].length - 1;//
					}
					//categoryAxis categoryAxis/valueAxis
					//categoryAxis indexDB
					vm.$set(dragObject, 'categoryAxis', currentAxisData[axisType][index].categoryAxis);
					//currentAxisDataindexDB
					//currentAxisData[axisType]
					vm.$set(currentAxisData[axisType], index, dragObject);//currentAxisData[axisType]indexdragObject
					// console.log(currentAxisData);
					vm.dexieDB.aXisList //index
						.put({
							index: vm.currentIndex,
							axisOptions: currentAxisData
						})
						.then(err => {
							console.log('');
