		clearInput(index, type) {

			/**
         *  
         * 
         */
			let vm = this;
			const categoryAxis = vm.currentChartAxis[type][index]['categoryAxis'];
			let dimensionDefault = JSON.parse(JSON.stringify(vm.dimensionDefault));
			if (vm.currentChartAxis[type].length > 2) {
				vm.currentChartAxis[type].splice(
					index,
					1);// 1
			} else {
				dimensionDefault.categoryAxis = categoryAxis;
				vm.currentChartAxis[type].splice(
					index,
					1,
					dimensionDefault
				);
				vm.$set(
					vm.currentChartAxis[type],
					index,
					dimensionDefault
				);
			}
			vm.dexieDB.aXisList
				.put({
					index: vm.currentIndex,
					axisOptions: vm.currentChartAxis
				})
				.then(() => {
				})
				.catch(error => console.error(error));

			vm.$store.commit(
				'dataDimension/setCurrentChartAxis',
				vm.currentChartAxis
			);
			if (!vm.isResetChart) {
				vm.$store.commit('canvasPlace/setIsSetOptions', {
					index: vm.currentIndex,
					clearSingleAxis: true
				});
			}
			vm.$store.commit('dataDimension/setAmendChartCount');
			vm.curSourceId = vm.resetCurSourceId;
