		//
		formatterOptions(params) {
			let list = params.some(e => e.yPercent !== '');
			if (list) {
				let tip = '{b}<br />';
				for (let i in params) {
					tip += `{a${i}}:{c${i}}%<br />`; //
				}
				return tip;
			} else {
				return '';
			}
