		//
		toggleyop(axisType, index, event, info) {
			let leftSideOpen = this.leftSideOpen;  //sider
			let a;
			const target = event.target || event.srcElement;
			let x = event.x;
			if (event.type === 'touchend') {
				x = event.changedTouches[0].clientX;
			}
			this.$store.commit('dataDimension/setValueAxisfilterPanl', 'chartPanl');
			this.$store.commit('dataDimension/setValueAxisfilterCount', {
				index: index,
				axisType: axisType
			});
			let path = event.path || (event.composedPath && event.composedPath());
			if (!path) {
				path = composedPath(target);
			}
			const ele = path.find(ele => {
				return ele.className.indexOf('axis-data') > -1;
			});
			const pos = this.$refs.chartContainer.getBoundingClientRect();
			const eleClient = ele.getBoundingClientRect();

			// const boundingXDistance = pos.right - x;
			let clientWidth = document.body.clientWidth;
			const boundingXDistance = clientWidth - x; //
			const minDistance = 68 + this.vwToPx(0.5);
			let h = eleClient.top + ele.clientHeight / 2 - this.vwToPx(2.6);//
			this.dataSelectPostion.top = h + 'px';
			let safeZone = boundingXDistance < minDistance * 1.5 + this.vwToPx(2.6);//
			leftSideOpen ? a = boundingXDistance - this.vwToPx(11.6) : a = boundingXDistance;  //sider 
			if (safeZone) {   //
				if (a - minDistance + this.vwToPx(2.6) >= minDistance) {   //
					this.$store.commit('dataDimension/setSafety', 1);//1--
				} else {
					this.$store.commit('dataDimension/setSafety', 0);//0--
				}
				let one = x - this.vwToPx(11.6) - minDistance + 'px';  //sider
				let two = x - minDistance * 1.5 + 'px'; //sider
				leftSideOpen ? this.dataSelectPostion.left = one : this.dataSelectPostion.left = two;
			} else {
				this.$store.commit('dataDimension/setSafety', 1);
				if (leftSideOpen) { //sider
					this.dataSelectPostion.left = x - this.vwToPx(11.6) + 'px';  //
				} else {
					this.dataSelectPostion.left = x - (minDistance / 2) + 'px';  //
				}

			}
			let listType = {  //indexdata-select
				index: index,//XY
				axisType: axisType,//XY
				dataType: info,//XY
				dataSelectPostion: this.dataSelectPostion,//XY
				selectedIndex: this.currentIndex, //
			};
			this.$store.commit('dataDimension/setSelectList', listType);  //listType
		}
	}
};

</script>
