<template>
  <div>
<!--    <Tips :tipsData="tipsData"-->
<!--      ref="tips"></Tips>-->
    <div class="chart-basis"
      @click="pictureShow = !pictureShow">
      <span></span>
      <span class="biFont icon-arrowLeft icon-fontsize"
        :class="{rotateIcon:pictureShow}"></span>
    </div>
    <div class="content-box"
      v-show="pictureShow">
      <ul>
        <li>
          <ChoicePhoto :afferent="bgBaseUrl"
            v-on:baseUrl="urlAddress"></ChoicePhoto>
        </li>
        <li class="optionsItem">
          <select-color :defaultColor="bgObject"
            v-on:changeColor="getPicleColor"></select-color>
        </li>
        <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="settingBorder"
            v-on:rangedata="rangeBorder"
            v-on:inputData="rangeBorder"></silder-data>
        </li>
        <!-- <li class="optionsItem">
          <span class="span-title"></span>
          <silder-data :opt="innerStrokes"
            v-on:rangedata="innerwidth"
            v-on:inputData="innerwidth"></silder-data>
        </li> -->
        <!-- <li class="optionsItem">
          <select-color :defaultColor="innerObject"
            v-on:changeColor="innerColor"></select-color>
        </li> -->
        <!-- <li class="optionsItem">
          <select-menu :ordinaryList="outLineList"
            :selectTitle="''"
            :heightLightLine="boderInnerType"
            @getOutLineType="getInnerType"></select-menu>
        </li> -->
        <!--        <li class="optionsItem">-->
        <!--          <span class="span-title"></span>-->
        <!--          <silder-data :opt="outerStrokes"-->
        <!--            v-on:rangedata="outerwidth"-->
        <!--            v-on:inputData="outerwidth"></silder-data>-->
        <!--        </li>-->
        <!--        <li class="optionsItem">-->
        <!--          <select-color :defaultColor="outerObject"-->
        <!--            v-on:changeColor="outerColor"></select-color>-->
        <!--        </li>-->
        <!--        <li class="optionsItem">-->
        <!--          <select-menu :ordinaryList="outLineList"-->
        <!--            :selectTitle="''"-->
        <!--            :heightLightLine="borderOuterType"-->
        <!--            @getReturnData="getOuterType"></select-menu>-->
        <!--        </li>-->
      </ul>
    </div>
    <slot></slot>
  </div>
</template>

<script>
import ColorSelect from './BasicComponent/ColorSetting';
import ChoicePhoto from '@/components/Picture/PictureUpload.vue';
import SelectMenu from '@/components/Dropdown/Dropdown';
import SilderData from './BasicComponent/Slider';
import {getRgba} from '@/utils/optionSetting';
import {mapGetters,mapMutations} from 'vuex';
// import Tips from '@/components/Tips/Tips.vue';

export default {
	name: 'pictureAssembly',
	components: {
		ChoicePhoto,
		'select-color': ColorSelect,
		'silder-data': SilderData,
		// Tips,
		'select-menu': SelectMenu
	},
	data() {
		return {
			pictureShow: true,
			bgBaseUrl: {
				photoUrl: '',
				title: '',
				selectImg: '',
				showWith: 8.5,
				showHeight: 6,
				size: 1024,
				accept: 'image/jpeg,image/png,image/gif'
			},
			bgObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			defaultStyle:{
				titleColor: 'rgba(0,0,0,0)',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			settingBorder: {
				name: '',
				max: 50,
				min: 0,
				valueRange: 1
			},
			innerType: '',
			innerObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			outLineList: [
				{value: 'dashed', label: ''},
				{value: 'solid', label: ''}
			],
			innerStrokes: {
				name: '',
				max: 100,
				min: 1,
				valueRange: 1
			},
			outerStrokes: {
				name: '',
				max: 100,
				min: 1,
				valueRange: 1
			},
			outerType: '',
			outerObject: {
				titleColor: '#194D33',
				titleName: '',
				rgba: 'rgba(25,77,51,0.9)'
			},
			tipsData: {
				text: '',
				rel: '',
				time: 1500
			},
			boderInnerType: {},
			borderOuterType: {}
		};
	},
	computed: {
		// bgObject(){
		//
		// },
		...mapGetters('chartBus', {
			chartIndex: 'returnDataIndex',
			resultPost: 'getResultData'
		}),
		...mapGetters('canvasPlace', {
			renderOptions: 'getCurrentChartRenderOptions',
		})
	},

	watch: {
		renderOptions: {
			deep: true,
			immediate: true,
			handler(val){
				let vm = this;
				if (val) {
					vm.defaultOptions();
				}
			}
		}
	},
	methods: {
	  ...mapMutations('canvasPlace',{
			renderOptionSetter:'renderOptionSetter'
		}),
		defaultOptions() {
			let vm = this,
				options = vm.renderOptions;
			vm.bgObject.rgba = options.backgroundColor ? options.backgroundColor : 'rgba(0,0,0,0)';
			// vm.bgObject.titleColor = colorTransformation(options.backgroundColor?options.backgroundColor:'rgba(0,0,0,0)');
			vm.bgBaseUrl.photoUrl =
				options.backgroundImage === ' ' ? '' : options.backgroundImage;
			// debugger;
			// console.log(vm.bgBaseUrl.photoUrl,options,'6')
			vm.settingBorder.valueRange = Number(
				options.borderStyle.borderRadiusWidth
			);
		},
		setMediaOption({name, data}) {
			// console.log('5-img',name);
			this.renderOptionSetter( {
				key: name,
				value: data
			});
			this.$store.commit('canvasPlace/setChartOptionsAmendCount');
		},
		urlAddress(data) {
			let imgType = data.imgType,
				vm = this;
			if (data.baseUrl) {
				if (imgType === 'jpg' || imgType === 'gif' || imgType === 'png') {
					if (imgType === 'gif' && data.imgSize >= 1024) {
						vm.tips.show(['1Mbgif']);
						vm.bgBaseUrl.photoUrl = '';
					} else {
						if (data.imgSize >= 2048) {
							vm.tips.show(['2MB']);
							vm.bgBaseUrl.photoUrl = '';
						} else {
							this.renderOptionSetter({
								key: 'backgroundImage',
								value: data.baseUrl
							});
							this.$store.dispatch('canvasPlace/setIndexdbChartOptions',{index:this.currentChart});
						}
					}
				} else {
					vm.tips.show(['']);
					vm.bgBaseUrl.photoUrl = '';
				}
			} else {
				this.renderOptionSetter({
					key: 'backgroundImage',
					value: data.baseUrl
				});
				this.$store.dispatch('canvasPlace/setIndexdbChartOptions',{index:this.currentChart});
			}
		},
		getPicleColor(data) {
			let rgba=  getRgba(data.rgba);
			this.setMediaOption({
				name: 'backgroundColor',
				data: rgba
			});
			this.bgObject.rgba = rgba;
			// this.bgObject.titleColor = colorTransformation(rgba);
		},
		innerColor(data) {
			let rgba=  getRgba(data.rgba);
			this.renderOptionSetter( {
				key: 'innerColor',
				value: rgba
			});
			// this.innerObject.titleColor = colorTransformation(rgba);
			this.innerObject.rgba = rgba;
		},
		getInnerType(data) {
			this.innerType = data;
		},
		getOuterType(data) {
			this.OuterType = data;
		},
		rangeBorder(data) {
			console.log('data',data);
			if (this.settingBorder.valueRange !== data) {
				this.setMediaOption({
					name: 'borderStyle.borderRadiusWidth',
					data: data
				});
			}
		},
		innerwidth(data) {
			//
			// console.log(data);
			this.renderOptionSetter( {
				key: 'innerStrokes',
				value: data
			});
		},
		outerwidth(data) {
			//
			// console.log(data);
			this.renderOptionSetter( {
				key: 'outerStrokes',
				value: data
			});
		},
		outerColor(data) {
			let rgba=  getRgba(data.rgba);
			// const {r, g, b, a} = data.rgba;
			// let rgba = 'rgba('+ r + ',' + g + ',' + b + ',' + a + ')';
			this.renderOptionSetter( {
				key: 'outerColor',
				value: rgba
			});
			// this.innerObject.titleColor = colorTransformation(rgba);
			this.innerObject.rgba = rgba;
		},
	}
};
</script>

<style scoped>
  .optionsItem {
    margin-bottom: 0.8vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .chart-basis .rotateIcon {
    transform: rotate(-90deg);
  }

  .optionsItem .span-title {
    display: block;
    flex: 1;
    color: #acbbda;
  }
</style>
