export function composedPath(el) {
	let path = [];
	while (el) {
		path.push(el);
		if (el.tagName === 'HTML') {
			path.push(document);
			path.push(window);
			return path;
		}
		el = el.parentElement;
	}
}
export function stopProp(e) {
	if (e.stopPropagation) {
		e.stopPropagation(); //IE
	} else {
		e.cancelBubble = true;
	} //IE
}

