import {isJSON} from '@/utils/utilsMethods';
import {get as _get,set as _set} from 'lodash';
export function beforeThemeHandle(options,id){
	if(!isJSON(options.renderOptions)){
		options.renderOptions=options.renderOptions===''?{}:JSON.parse(options.renderOptions);
	}
	options.renderOptions.xSourceId=options.containerOptions.x.xSourceId;
	options.renderOptions.xField=options.containerOptions.x.xField;
	options.renderOptions.chartId=id;
	options.renderOptions.tableId=options.containerOptions.tableId;
	// renderOptions.source
	let categoryAxis =_get(options,'containerOptions.categoryAxis','').toLowerCase() ;
	if (categoryAxis && options.renderOptions[categoryAxis + 'Axis']) {
		options.renderOptions[categoryAxis + 'Axis'].data = options.containerOptions.xData;
	}
	let seriesDataArr = Array.isArray(options.containerOptions.seriesData) ?
		options.containerOptions.seriesData : [{...options.containerOptions.seriesData}];
	// if(!options.renderOptions.series){
	// 	options.renderOptions.series=new Array(seriesDataArr.length);
	// }
	for (let i = 0; i < seriesDataArr.length; i++) {
		// let series=options.renderOptions.series[i]?options.renderOptions.series[i]:{};
		let series=_get(options,`renderOptions.series[${i}]`,{});
		_set(options,`renderOptions.series[${i}]`,{...series, ...seriesDataArr[i]});
		// options.renderOptions.series[i] = {...series, ...seriesDataArr[i]};
	}
	return options;
}
export function getRgba(color) {
	let {r,g,b,a}=color;
	return  'rgb(' + r+','+ + g+','+ + b +','+ a + ')';
}
export function colorTransformation(rgba) {
	// if(!rgba.match)debugger;
	rgba = rgba.match(
		/^rgba?[\s+]?\([\s+]?(\d+)[\s+]?,[\s+]?(\d+)[\s+]?,[\s+]?(\d+)[\s+]?/i
	);
	return rgba && rgba.length === 4
		? '#' +
    ('0' + parseInt(rgba[1], 10).toString(16)).slice(-2) +
    ('0' + parseInt(rgba[2], 10).toString(16)).slice(-2) +
    ('0' + parseInt(rgba[3], 10).toString(16)).slice(-2)
		: '';
}
