import Vue from 'vue';

export default {
	namespaced: true,
	state: {
		leftSideOpen: false,
		leftSideWidth: 2.6,
		leftPaneIndex: 0,
		rightSideWidth: 15.2,
		rightPaneShow: true,
		rightPaneHover: false,
		rightPaneIndex: -1,
		rightPaneTitleArr: ['', '', '', ''],
		workPlaceWidth: 97.9, //vw
	},
	getters: {
		getWorkPlaceLeft: (state) =>
			state.leftSideWidth,
		getWorkPlaceWidth: (state) =>
			state.workPlaceWidth - state.leftSideWidth,
		getFooterWidth: (state) =>
			100 - (state.leftSideWidth + state.rightSideWidth),
		isRightPaneHover: (state) =>
			state.rightPaneHover,
		isRightPaneShow: (state) =>
			state.rightPaneShow,
		getLeftPaneIndex: (state) => state.leftPaneIndex,
	},
	mutations: {
		setStateProperty(state,{key,value}){
			Vue.set(state, key, value);
		},
		toggleStateProperty(state,key){
			let status=!state[key];
			Vue.set(state, key, status);
		},
		toggleRightPaneShow(state,status){
		  state.rightPaneShow=status;
			state.rightSideWidth=status?15.2:2.6;
		},
	},

};
