<template>
  <div class="maskBackground">
    <div class="newCsvFile allCenter2">
      <div class="title">
        <i class="icon biFont icon-builtNew addNew"></i>
        <span>CSV</span>
      </div>
      <div class="container">
        <div class="content">
          <ul>
            <li class="files">
              <span></span>
              <input :value="fileName"
                class="fileName">
              <label class="mainBtn fileBtn">
                <span> </span>
                <input type="file"
                  class="fileInput"
                  @change="onChange($event)">
              </label>
            </li>
            <li>
              <span></span>
              <Separator :separtorObj="separtorObj"
                @separatorBack="separatorEvent"></Separator>
            </li>
            <li v-if="customInfer">
              <span></span>
              <input type="text"
                class="customInfer"
                v-model="customVal">
            </li>
          </ul>
        </div>
        <div class="btnDiv">
          <span class="mainBtn"
            :class="{banGestures:isCreateClick}"
            @click="submitEvnet"></span>
          <span class="mainBtn"
            @click="cancel"></span>
        </div>
      </div>
    </div>
<!--    <Tips v-bind="{tipsData}"-->
<!--      ref="tips"></Tips>-->

    <!--  -->
    <Loading v-if="dataIsOk"
      :styleObj="styleObj"></Loading>
  </div>
</template>

<script>
import Separator from '@/components/Selection/SeparatorSelection.vue';
import Loading from '@/components/Loading/LoadingRotating.vue';
// import Tips from '@/components/Tips/Tips.vue';

export default {
	name: 'newCsvFile',
	components: {
		// Tips,
		Separator,
		Loading
	},
	data() {
		return {
			url: '',
			token: localStorage.getItem('token'),
			isCreateClick: false, //
			dataIsOk: false, //
			files: '',
			infer: ',',
			inferName: '',
			fileName: '',
			customVal: '',
			customInfer: false,
			tipsData: {
				text: '',
				rel: '',
				time: 1500
			},
			separtorObj: {
				infer: ',',
				inferName: ''
			},
			styleObj: {
				width: '29.3vw',
				height: '23.9vh'
			}
		};
	},
	methods: {
		onChange(event) {
			this.files = event.target.files[0];
			this.fileName = this.files.name;
		},
		separatorEvent(data) {
			if (data.type == 'infer') {
				this.customInfer = false;
				this.infer = data.infer;
				this.inferName = data.name;
				this.styleObj.height = '23.9vh';
			} else {
				this.customInfer = true;
				this.styleObj.height = '28vh';
				this.inferName = data.name;
			}
		},
		cancel() {
			this.$emit('newCsvFileBack', false);
		},
		submitEvnet() {
			this.isCreateClick = true;
			if (this.isCreateClick) {
				this.dataIsOk = true;
				let file = this.files;
				let val = file.name;
				let formData = new FormData();
				let csv_infer = this.customInfer ? this.customVal : this.infer;
				formData.append('csv_infer', csv_infer);
				formData.append('examfile', file);
				formData.append('token', this.token);
				this.$post('/api/csvInfo', formData).then(
					response => {
						let res = response;
						this.dataIsOk = true;
						if (res.code == '000000') {
							this.$emit('dataPreviewBack', {
								bool: {
									fileSystem: false,
									previewSystem: true
								},
								uploadData: {
									data: res.data,
									type: 'csv',
									name: val,
									examfile: file,
									csv_name: this.inferName,
									csv_infer: csv_infer
								}
							});
							this.dataFilter = 'all';
						} else {
							this.dataIsOk = false;
							this.tips.show([res.err_msg.split(':')[1]]);
							// this.tipsData.rel = 'fail';
							// this.tipsData.text = res.err_msg.split(':')[1];
							// this.$refs.tips.closeTips();
						}
						this.isCreateClick = false;
					},
					error => {
						console.log(error);
						this.dataIsOk = false;
						this.tips.show(['']);
						// this.tipsData.rel = 'fail';
						// this.tipsData.text = '';
						// this.$refs.tips.closeTips();
						this.isCreateClick = false;
					}
				);
			}
		}
	},
	created() {}
};
</script>

<style scoped>
.newCsvFile {
  width: 29.3vw;
  min-height: 23.9vh;
  max-height: 28vh;
  background-color: #19202e;
}

.newCsvFile .title {
  height: 4vh;
  line-height: 4vh;
  background-color: #273045;
  font-size: 0.8vw;
  text-align: center;
}

.newCsvFile .title .addNew {
  margin-right: 0.2vw;
  vertical-align: 0;
  font-size: 0.8vw;
}

.newCsvFile .container {
  padding: 0 2.1vw;
}

.newCsvFile .container .content {
  margin-top: 2.6vh;
}

.newCsvFile .container .content > ul > li {
  line-height: 5vh;
  font-size: 0.7vw;
}

.newCsvFile .container .content ul li > span {
  margin-right: 0.8vw;
  width: 3vw;
  display: inline-block;
  text-align: right;
}

.newCsvFile .container .content .files .fileName {
  height: 3vh;
  background-color: #121721;
  color: #fff;
  padding-left: 0.3vw;
  border: solid 0.0625vw #475566;
}

.newCsvFile .container .content .files .fileBtn {
  height: 3vh;
  width: 3.7vw;
  font-size: 0.7vw;
  text-align: center;
  margin-left: 0.3vw;
  line-height: 2.7vh;
  position: relative;
  display: inline-block;
}

.newCsvFile .container .content .files input.fileInput {
  opacity: 0;
  position: absolute;
  right: 0;
  line-height: 2.9vh;
  width: 3.7vw;
  height: 3vh;
}

.newCsvFile .container .content ul li .customInfer {
  height: 3vh;
  background-color: #121721;
  color: #fff;
  padding-left: 0.3vw;
  border: solid 0.0625vw #475566;
}

.newCsvFile .container .content .files .fileName,
.newCsvFile .container .content ul li .customInfer,
.newCsvFile .container .content >>> .separator-component .separator-list,
.newCsvFile .container .content >>> .separator-component .separator-show input {
  width: 16.2vw;
}

.newCsvFile .btnDiv {
  margin: 2.2vh 0 1.6vh;
  text-align: right;
}

.newCsvFile .btnDiv span {
  display: inline-block;
  font-size: 0.7vw;
  width: 3.7vw;
  height: 3vh;
  margin: 0 0.5vw;
  text-align: center;
  line-height: 2.9vh;
}
</style>
